import React, { useState } from "react";
import { Layout, Row, Col, Breadcrumb , Typography} from "antd";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import { NavLink } from "react-router-dom";
import ReactHtmlParser from "react-html-parser";
import { INFO } from "../../headings";
import InfoPopover from "../table/InfoPopover";
import {SHOW_LESS} from "../../headings";

const { Content } = Layout;
const {Paragraph} = Typography;

const InfoDetailsView = (props) => {
  const info = props.location.state ? props.location.state : {};
  const [expand, setExpand] = useState(false);
  const [counter, setCounter] =useState (0);
  const renderParagraph = (item) =>{
    return (
      <div className="note-wrapper m-2" key={counter}>
       
        <Paragraph
          ellipsis={{
            rows: 8,
            expandable: true,
            symbol: "Show more",
            onExpand:typoExpand,
          }}
        >
          {ReactHtmlParser(item)}
        </Paragraph>
      </div>
    );
  }

  const typoExpand = (value)=>{
    setExpand(true);
    if(!expand){
      setCounter(counter+0);
    }else{
      setCounter(counter+1);
    }
  }

  const typoClose = (value)=>{
    setExpand(false)
    if(!expand){
      setCounter(counter+0);
    }else{
      setCounter(counter+1);
    }
  }

  return (
    <React.Fragment>
      {" "}
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={props} />
            <Content>
              <Row className="pt-3">
                <Breadcrumb separator=">">
                  <NavLink to="/idb/info">
                    <Breadcrumb.Item>{INFO}</Breadcrumb.Item>
                  </NavLink>
                  <Breadcrumb.Item>{info.title}</Breadcrumb.Item>
                </Breadcrumb>
              </Row>
              <Row className="py-3 justify-content-between">
                <h1 className="mb-0"> {info.title}</h1>
              </Row>

              <div className="mid-content">
                <Row>
                  <Col span={22} className="editor-data">
                    <Row>
                      <Col span={24} className="mb-2">
                      <div className="show-less show-more-wrapper" >
                        {renderParagraph(info.message)}
                         {expand && (
                           <span>
                            <a
                                data-test="showLess"
                                className="d-flex justify-content-end"
                                onClick={typoClose}
                              >
                              {SHOW_LESS}
                            </a>
                            </span>
                         )}
                         </div>
                      </Col>
                    </Row>
                  </Col>
                  
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </React.Fragment>
  );
};

export default InfoDetailsView;
