import React, { useState, useEffect } from "react";
import { Layout, Row, Col, Breadcrumb, Spin, Select } from "antd";
import { NavLink, useLocation } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import {
  COUNT,
  PERCENTAGE,
  POLLS,
  POLL_OPTION_TEXT,
  POLL_RESPONSES,
  POLL_RESULTS,
  POLL_TOTAL_AUDIENCE,
} from "../../headings";
import { POLL_RESULTS_NOT_AVAILABLE } from "../../messages";
import BarChart from "./BarChart";
import PieChart from "./PieChart";
import html2canvas from "html2canvas";
import { jsPDF } from "jspdf";
import {
  BAR_CHART,
  BETTER_IF_NOT,
  DEFINITELY_NOT,
  DOES_NOT_MATTER,
  EXCITED,
  I_AM_OK,
  PIE_CHART,
} from "../../values";
import PollResponsePopover from "./PollResponsePopover";
import IdbSidebar from "../reports/IdbSidebar";

const { Content } = Layout;

const PollResponseView = (props) => {
  let location = useLocation();
  const [loader, setLoader] = useState(false);
  const [data, setData] = useState({});
  const [chartType, setChartType] = useState("1");
  const [dataPoints, setDataPoints] = useState([]);
  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    const data = {
      poll_id: location.state.poll_id,
    };
    axios.post(BASE_URL + "pollResult/", data, options).then((res) => {
      let resData = {};
      let finalResData = [];
      res.data.map((value) => {
        resData = {
          ...value,
          option_text:
            value.option_text === "1"
              ? DEFINITELY_NOT
              : value.option_text === "2"
              ? BETTER_IF_NOT
              : value.option_text === "3"
              ? DOES_NOT_MATTER
              : value.option_text === "4"
              ? I_AM_OK
              : value.option_text === "5"
              ? EXCITED
              : value.option_text,
        };
        finalResData.push(resData);
      });
      setData(finalResData);
      setLoader(false);
    });
  }, []);

  const downloadPDF = () => {
    let element =
      chartType === "1"
        ? document.getElementById("barChart")
        : document.getElementById("pieChart");
    html2canvas(element).then((canvas) => {
      const imgData = canvas.toDataURL("image/png");
      let pdf = "";
      if (canvas.width > canvas.height) {
        pdf = new jsPDF("l", "mm", [canvas.width, canvas.height]);
      } else {
        pdf = new jsPDF("p", "mm", [canvas.height, canvas.width]);
      }
      pdf.addImage(imgData, "JPEG", 0, 0, canvas.width, canvas.height);
      const filename = location.state.title + ".pdf";
      pdf.save(filename);
    });
  };

  function convertChartDataToCSV(data) {
    var result, ctr, keys, columnDelimiter, lineDelimiter, data;
    data = data || null;
    if (data == null || !data.length) {
      return null;
    }
    columnDelimiter = ",";
    lineDelimiter = "\n";
    keys = Object.keys(data[0]);
    result = "";
    result += keys.join(columnDelimiter);
    result += lineDelimiter;
    data.forEach(function (item) {
      ctr = 0;
      keys.forEach(function (key) {
        if (ctr > 0) result += columnDelimiter;
        result += item[key];
        ctr++;
      });
      result += lineDelimiter;
    });
    return result;
  }

  const downloadCSV = () => {
    let points = [];
    let x_index = POLL_OPTION_TEXT;
    let y_index = "";
    dataPoints[0].forEach((xpoint, index) => {
      dataPoints[1].forEach((ypoint, key) => {
        if (index === key) {
          if (chartType === "1") {
            y_index = COUNT;
          } else {
            y_index = PERCENTAGE;
          }
          points.push({ [x_index]: xpoint, [y_index]: ypoint });
        }
      });
    });
    var data, filename, link;
    var csv = `${POLL_RESULTS} for,${location.state.title}\n`;
    csv += `"",""\n`;
    csv += convertChartDataToCSV(points);
    if (csv == null) return;
    filename = location.state.title + ".csv";
    if (!csv.match(/^data:text\/csv/i)) {
      csv = "data:text/csv;charset=utf-8," + csv;
    }
    data = encodeURI(csv);
    link = document.createElement("a");
    link.setAttribute("href", data);
    link.setAttribute("download", filename);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const sendBackData = (x_data, y_data) => {
    setDataPoints([x_data, y_data]);
  };

  const showChartOptions = (type) => {
    setChartType(type);
  };

  const showDownloadOptions = (value) => {
    if (value === "pdf") {
      downloadPDF();
    } else {
      downloadCSV();
    }
  };

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amIDBUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <IdbSidebar amIDBUser data={props} />
              <Content>
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/idb/pollsdirectory">
                      <Breadcrumb.Item>{POLLS}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{POLL_RESULTS}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Spin spinning={loader} size="large">
                  <Row className="py-3 justify-content-between">
                    <h1 className="mb-0">{location.state.title}</h1>

                    <Col
                      span={24}
                      md={24}
                      lg={12}
                      className="search-row column d-block d-lg-flex align-items-center justify-content-start justify-content-lg-end px-3 px-lg-0 my-4 my-lg-0"
                    >
                      {data && data.length > 0 ? (
                        <span className="pr-3 w-md-100">
                          {POLL_TOTAL_AUDIENCE}: {data[0]?.total_employee_count}
                        </span>
                      ) : null}
                      {data && data.length > 0 ? (
                        <>
                          {" "}
                          <Col
                            span={24}
                            lg={6}
                            className="ml-0 ml-lg-4 mt-2 mt-lg-0 mr-3"
                          >
                            <Select
                              className="w-100"
                              defaultValue="1"
                              onSelect={(value) => showChartOptions(value)}
                            >
                              <Option value="1" defaultValue>
                                {BAR_CHART}
                              </Option>
                              <Option value="2">{PIE_CHART}</Option>
                            </Select>
                          </Col>
                          <Col
                            span={24}
                            lg={6}
                            className="d-flex align-items-center justify-content-end"
                          >
                            <PollResponsePopover
                              showDownloadOptions={showDownloadOptions}
                            />
                          </Col>
                        </>
                      ) : null}
                    </Col>
                  </Row>
                  <div className="">
                    <Row>
                      <Col span={24}>
                        <p className="font-weight-md6">
                          {location.state.question}
                        </p>
                        {data && data.length > 0 ? (
                          <span className="font-weight-md6 pt-3 d-flex">
                            {data[0]?.total_response} {POLL_RESPONSES}
                          </span>
                        ) : null}
                        <div className="mid-content d-flex justify-content-center align-items-center">
                          <h1 className="text-center pt-3">
                            {data &&
                              data.length === 0 &&
                              POLL_RESULTS_NOT_AVAILABLE}
                          </h1>
                          {data && data.length > 0 ? (
                            chartType === "1" ? (
                              <BarChart
                                data={data}
                                chartTitle={
                                  POLL_RESULTS + " for " + location.state.title
                                }
                                sendBackData={sendBackData}
                              />
                            ) : (
                              <PieChart
                                data={data}
                                chartTitle={
                                  POLL_RESULTS + " for " + location.state.title
                                }
                                sendBackData={sendBackData}
                              />
                            )
                          ) : null}
                        </div>
                      </Col>
                    </Row>
                  </div>
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default PollResponseView;
