import React from "react";
import { Popover } from "antd";
import DeleteEmployee from "../modal/DeleteEmployeeModal";
import dotsicon from "../../assets/icons/dots-icon.svg";
import { Link } from "react-router-dom";
import { EDIT } from "../../headings";

const content = (props) => (
  <div>
    <ul>
      <Link
        to={{
          pathname: `/orgcc/editemployee/${props.data.user_id}`,
          state: props.data,
        }}
      >
        <li>
          <a href={`/orgcc/editemployee/${props.data.user_id}`}>{EDIT}</a>{" "}
        </li>
      </Link>
      <li>
        <DeleteEmployee
          userId={props.data.user_id}
          name={props.data.first_name + " " + props.data.last_name}
          setLoader={props.setLoader}
        />
      </li>
    </ul>
  </div>
);

class ManageEmpPopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="ico-grid-dots">
            <img src={dotsicon} alt="DotIcon" className="pr-4" />
          </span>
        </Popover>
      </>
    );
  }
}

export default ManageEmpPopover;
