import React from "react";
import { Popover, Button } from "antd";
import { DownOutlined } from "@ant-design/icons";
import { DOWNLOAD, DOWNLOAD_AS_CSV, DOWNLOAD_AS_PDF } from "../../headings";

const content = (props) => (
  <div>
    <ul>
      <li>
        <Button onClick={() => props.showDownloadOptions("pdf")}>
          {DOWNLOAD_AS_PDF}
        </Button>
      </li>
      <li>
        <Button onClick={() => props.showDownloadOptions("csv")}>
          {DOWNLOAD_AS_CSV}
        </Button>
      </li>
    </ul>
  </div>
);

class PollResponsePopover extends React.Component {
  state = {
    visible: false,
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props !== nextProps && this.state.visible) {
      this.hide();
    }
  }

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <Button type="primary" className="w-md-100 mt-3 mt-lg-0">
            {DOWNLOAD}
          </Button>
          <DownOutlined className="down-arrow-ico white btn-arrow" />
        </Popover>
      </>
    );
  }
}

export default PollResponsePopover;
