import React, { useState } from "react";
import { Layout, Row, Col, Steps, Breadcrumb } from "antd";
import { LoggedinHeader } from "../../containers";
import "./form.scss";
// import WorkChart from "../../assets/images/work-chart.svg";
import AccSidebar from "../manageOrg/AccSidebar";
import CreateOrgStepOne from "../form/CreateOrgStepOne";
import CreateOrgStepTwo from "../form/CreateOrgStepTwo";
import CreateOrgStepThree from "../form/CreateOrgStepThree";
import { NavLink } from "react-router-dom";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import moment from "moment";
import axios from "axios";
import {
  CREATE_ORGANIZATION,
  ORGANIZATIONS,
  ORGANIZATION_CREATE_STEP3,
  ORGANIZATION_DETAILS,
  SEEDED_USERS_DETAILS,
} from "../../headings";
import * as AppStorage from "../../utils/AppStorage";
var CryptoJS = require("crypto-js");

const { Content } = Layout;

const { Step } = Steps;

const steps = [
  {
    title: ORGANIZATION_DETAILS,
    content: "First-content",
  },
  {
    title: SEEDED_USERS_DETAILS,
    content: "Second-content",
  },
  {
    title: ORGANIZATION_CREATE_STEP3,
    content: "Last-content",
  },
];

const CreateOrgStepper = () => {
  const [current, setCurrent] = useState(0);
  const [loader, setLoader] = useState(false);
  const [apiError, setApiError] = useState(false);
  const [stepOneOrganization, setStepOneOrganization] = useState({
    organizationName: "",
    organizationId: "",
    status1: "",
    s1Date: "",
    primary: 1,
  });
  const [orgNameAlreadyExists, setOrgNameExists] = useState("");
  const [orgIDAlreadyExists, setOrgIDExists] = useState("");
  const [stepTwoOrganization, setStepTwoOrganization] = useState({
    firstName: "",
    lastName: "",
    userId: "",
    status2: "",
    email: "",
    // password: "",
    //s2Date: "",
    mobile: "",
    startDate: "",
    endDate: "",
  });
  const [seededUsers, setSeededUsers] = useState([]);
  const [showAddUserForm, setShowAddUserForm] = useState(false);
  const [showEditUserForm, setShowEditUserForm] = useState(false);
  const [orgNameCount, setOrgNameCount] = useState(false);
  const [orgIdCount, setOrgIDCount] = useState(false);
  const [fNameCount, setFNameCount] = useState(false);
  const [lNameCount, setLNameCount] = useState(false);
  const [CUGAttribute, setCUGAttribute] = useState({
    cug1: "",
    cug2: "",
    cug3: "",
    cug4: "",
    cug5: "",
  });

  const [flexAttribute, setFlexAttribute] = useState({
    flex1: "",
    flex2: "",
    flex3: "",
    flex4: "",
    flex5: "",
  });
  
  const next = () => {
    setShowAddUserForm(false);
    setShowEditUserForm(false);
    setCurrent(current + 1);
  };

  const prev = () => {
    setShowAddUserForm(false);
    setShowEditUserForm(false);
    setCurrent(current - 1);
  };

  const addUser = (type) => {
    if (type === "viaNext") {
      setCurrent(current + 1);
    }
    const userID = Math.floor(Math.random() * 100) + 1;
    const finalUser = { ...stepTwoOrganization, userId: userID };
    setSeededUsers([...seededUsers, finalUser]);
    setShowAddUserForm(false);
  };

  const editUser = () => {
    seededUsers.forEach((user) => {
      if (
        JSON.stringify(user) !== JSON.stringify(stepTwoOrganization) &&
        user.userId === stepTwoOrganization.userId
      ) {
        let index = seededUsers.indexOf(user);
        let users = JSON.parse(JSON.stringify(seededUsers));
        users[index] = stepTwoOrganization;
        setSeededUsers(users);
      }
    });
    setShowEditUserForm(false);
  };

  const addSeededUserFromThirdStep = () => {
    setCurrent(current - 1);
    showAddSeededUserForm();
  };

  const handleOk = () => {
    window.location = "/amcc/manageorganizations";
  };

  const handleCancel = () => {
    window.location = "/amcc/manageorganizations";
  };

  const deleteUser = (userID, type) => {
    let filteredUsers = seededUsers?.filter((user) => user?.userId !== userID);
    setSeededUsers(filteredUsers);
    if (filteredUsers.length === 0) {
      setStepTwoOrganization({
        firstName: "",
        lastName: "",
        userId: "",
        status2: "",
        email: "",
        //  password: "",
        //s2Date: "",
        mobile: "",
        startDate: "",
        endDate: "",
      });
      if (type === "viaStepThree") {
        setCurrent(current - 1);
      }
    }
  };

  const showAddSeededUserForm = () => {
    setShowAddUserForm(true);
    setFNameCount(false);
    setLNameCount(false);
    setStepTwoOrganization({
      firstName: "",
      lastName: "",
      userId: "",
      status2: "",
      email: "",
      // password: "",
      //s2Date: "",
      mobile: "",
      startDate: "",
      endDate: "",
    });
  };

  const showEditSeededUserForm = (userID, type) => {
    if (type === "viaStepThree") {
      setCurrent(current - 1);
    }
    setFNameCount(false);
    setLNameCount(false);
    setShowEditUserForm(true);
    let editableUser = {};
    seededUsers.forEach((seededUser) => {
      if (seededUser.userId === userID) {
        editableUser = seededUser;
      }
    });
    setStepTwoOrganization(editableUser);
  };

  const cancelAddUser = () => {
    setShowAddUserForm(false);
  };

  const cancelEditUser = () => {
    setShowEditUserForm(false);
  };

  const setOrgNameAlreadyExists = (value) => {
    setOrgNameExists(value);
  };

  const setOrgIDAlreadyExists = (value) => {
    setOrgIDExists(value);
  };

  const handleStepOneOnChange = (e) => {
    if (e.target.id === "organizationId" && e.target.value.length === 20) {
      setOrgIDCount(true);
    } else if (
      e.target.id === "organizationName" &&
      e.target.value.length === 80
    ) {
      setOrgNameCount(true);
    } else {
      setOrgNameCount(false);
      setOrgIDCount(false);
    }
    setStepOneOrganization({
      ...stepOneOrganization,
      [e.target.id]: e.target.value,
    });
  };

  const handleStepOneOnSelect = (value, name) => {
    setStepOneOrganization({ ...stepOneOrganization, [name]: value });
  };

  const handleStepOneDatePickerChange = (date, name) => {
    setStepOneOrganization({ ...stepOneOrganization, [name]: date });
  };

  const handleStepTwoOnChange = (e) => {
    if (e.target.id === "firstName" && e.target.value.length === 50) {
      setFNameCount(true);
    } else if (e.target.id === "lastName" && e.target.value.length === 50) {
      setLNameCount(true);
    } else {
      setFNameCount(false);
      setLNameCount(false);
    }
    setStepTwoOrganization({
      ...stepTwoOrganization,
      [e.target.id]: e.target.value,
    });
  };

  const handleMobile = (mobile) => {
    setStepTwoOrganization({
      ...stepTwoOrganization,
      mobile: mobile,
    });
  };

  const handleStepTwoOnSelect = (value, name) => {
    setStepTwoOrganization({ ...stepTwoOrganization, [name]: value });
  };

  const handleStepTwoDatePickerChange = (date, name) => {
    setStepTwoOrganization({ ...stepTwoOrganization, [name]: date });
  };

  const createOrganization = () => {
    setLoader(true);
    const user = JSON.parse(AppStorage.get("user"));
    const token = authHeader();
    let seededUsersArray = [];
    seededUsers.map((value) => {
      let user = value.endDate
        ? {
            first_name: value.firstName,
            last_name: value.lastName,
            email: value.email,
            // password: value.password,
            status_id: parseInt(value.status2),
            start_date: value.startDate
              ? moment(value.startDate).format("DD-MM-YYYY")
              : "",
            end_date: value.endDate
              ? moment(value.endDate).format("DD-MM-YYYY")
              : "",
            mobile_number: "+" + value.mobile,
            is_seeded_user: 1,
          }
        : {
            first_name: value.firstName,
            last_name: value.lastName,
            email: value.email,
            // password: value.password,
            status_id: parseInt(value.status2),
            start_date: value.startDate
              ? moment(value.startDate).format("DD-MM-YYYY")
              : "",
            mobile_number: "+" + value.mobile,
            is_seeded_user: 1,
          };
      seededUsersArray.push(user);
    });
    const data = {
      organization: {
        name: stepOneOrganization.organizationName,
        identifier: stepOneOrganization.organizationId,
        status_id: 1,
        start_date: stepOneOrganization.s1Date[0]
          ? moment(stepOneOrganization.s1Date[0]).format("DD-MM-YYYY")
          : "",
        end_date: stepOneOrganization.s1Date[1]
          ? moment(stepOneOrganization.s1Date[1]).format("DD-MM-YYYY")
          : "",
        app_authentication_key_id: stepOneOrganization.primary,
      },
      seeded_users: seededUsersArray,
      cug_flex_fields:{
        cug_parameters:{
          cug_field_1:CUGAttribute.cug1,
          cug_field_2:CUGAttribute.cug2,
          cug_field_3:CUGAttribute.cug3,
          cug_field_4:CUGAttribute.cug4,
          cug_field_5:CUGAttribute.cug5,
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        },
       flex_parameters:{
          flex_field_1:flexAttribute.flex1,
          flex_field_2:flexAttribute.flex2,
          flex_field_3:flexAttribute.flex3,
          flex_field_4:flexAttribute.flex4,
          flex_field_5:flexAttribute.flex5,
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        }
      }
    };
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    }; 
    axios
      .post(BASE_URL + "saveOrganization/", data, options)
      .then(() => {
        setLoader(false);
        window.location = "/amcc/manageorganizations";
      })
      .catch((error) => {
        if (error.message.includes("500")) {
          setApiError(true);
        }
      });
  };

  const handleCUGChange = (e, key) =>{
    const re = /^[0-9a-zA-Z\s]*$/;
    if(re.test(e.target.value)){
      if(key==='flex'){
        setFlexAttribute({
          ...flexAttribute,
          [e.target.id]: e.target.value,
        });
      }
      else{
        setCUGAttribute({
          ...CUGAttribute,
          [e.target.id]: e.target.value,
        })
      }
    }
  }

  const clearAttribute = () =>{
    setCUGAttribute({
      cug1: "",
      cug2: "",
      cug3: "",
      cug4: "",
      cug5: "",
    });
    setFlexAttribute({
      flex1: "",
      flex2: "",
      flex3: "",
      flex4: "",
      flex5: "",
    })
  }
  const saveOrganizationAsDefault = () => {
    setLoader(true);
    const token = authHeader();
    let seededUsersArray = [];
    seededUsers.map((value) => {
      let user =  {
            first_name: value.firstName,
            last_name: value.lastName,
            email: value.email,
            // password: value.password,
            status_id: 4,
            start_date: value.startDate
              ? moment(value.startDate).format("DD-MM-YYYY")
              : null,
            end_date: value.endDate 
              ? moment(value.endDate).format("DD-MM-YYYY")
              : null,
            mobile_number: value.mobile ? "+" + value.mobile : 0,
            is_seeded_user: 0,
          }
      seededUsersArray.push(user);
    });
    const data = {
      organization: {
        name: stepOneOrganization.organizationName,
        identifier: stepOneOrganization.organizationId,
        status_id: 1,
        start_date: stepOneOrganization.s1Date[0]
          ? moment(stepOneOrganization.s1Date[0]).format("DD-MM-YYYY")
          : null,
        end_date: stepOneOrganization.s1Date[1]
          ? moment(stepOneOrganization.s1Date[1]).format("DD-MM-YYYY")
          : null,
        app_authentication_key_id: stepOneOrganization.primary,
      },
      seeded_users: seededUsersArray,
    };
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    
    axios
      .post(BASE_URL + "saveOrganization/", data, options)
      .then(() => {
        setLoader(false);
        window.location = "/amcc/manageorganizations";
      })
      .catch((error) => {
        if (error.message.includes("500")) {
          setApiError(true);
        }
      });
  }

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amCCUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <AccSidebar />
              <Content className="">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/amcc/manageorganizations">
                      <Breadcrumb.Item>{ORGANIZATIONS}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{CREATE_ORGANIZATION}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">{CREATE_ORGANIZATION}</h1>
                </Row>
                <Steps className="mt-2" current={current}>
                  {steps.map((item) => (
                    <Step key={item.title} title={item.title} />
                  ))}
                </Steps>
                <div className="mid-content mt-4">
                  <Row>
                    <Col span={24}>
                      <div className="steps-content org-form">
                        {current == 0 ? (
                          <CreateOrgStepOne
                            next={next}
                            stepOneOrganizationInfo={stepOneOrganization}
                            handleStepOneOnChange={handleStepOneOnChange}
                            handleStepOneOnSelect={handleStepOneOnSelect}
                            handleStepOneDatePickerChange={
                              handleStepOneDatePickerChange
                            }
                            orgNameCount={orgNameCount}
                            orgIdCount={orgIdCount}
                            setOrgNameAlreadyExists={setOrgNameAlreadyExists}
                            orgNameAlreadyExists={orgNameAlreadyExists}
                            setOrgIDAlreadyExists={setOrgIDAlreadyExists}
                            orgIDAlreadyExists={orgIDAlreadyExists}
                            setStepOneOrganization={setStepOneOrganization}
                            flexAttribute={flexAttribute}
                            CUGAttribute={CUGAttribute}
                            handleCUGChange={handleCUGChange}
                            clearAttribute={clearAttribute}
                          />
                        ) : current == 1 ? (
                          <CreateOrgStepTwo
                            next={next}
                            prev={prev}
                            stepTwoOrganizationInfo={stepTwoOrganization}
                            handleStepTwoOnChange={handleStepTwoOnChange}
                            handleStepTwoOnSelect={handleStepTwoOnSelect}
                            handleStepTwoDatePickerChange={
                              handleStepTwoDatePickerChange
                            }
                            handleMobile={handleMobile}
                            addUser={addUser}
                            editUser={editUser}
                            deleteUser={deleteUser}
                            seededUsers={seededUsers}
                            showAddUserForm={showAddUserForm}
                            showAddSeededUserForm={showAddSeededUserForm}
                            showEditUserForm={showEditUserForm}
                            showEditSeededUserForm={showEditSeededUserForm}
                            cancelAddUser={cancelAddUser}
                            cancelEditUser={cancelEditUser}
                            fNameCount={fNameCount}
                            lNameCount={lNameCount}
                          />
                        ) : current == 2 ? (
                          <CreateOrgStepThree
                            prev={prev}
                            createNewOrganization={createOrganization}
                            stepOneOrganizationInfo={stepOneOrganization}
                            stepTwoOrganizationInfo={stepTwoOrganization}
                            handleStepOneOnChange={handleStepOneOnChange}
                            handleStepOneOnSelect={handleStepOneOnSelect}
                            handleStepOneDatePickerChange={
                              handleStepOneDatePickerChange
                            }
                            handleStepTwoOnChange={handleStepTwoOnChange}
                            handleStepTwoOnSelect={handleStepTwoOnSelect}
                            handleStepTwoDatePickerChange={
                              handleStepTwoDatePickerChange
                            }
                            seededUsers={seededUsers}
                            showEditUserForm={showEditUserForm}
                            deleteUser={deleteUser}
                            showEditSeededUserForm={showEditSeededUserForm}
                            addSeededUserFromThirdStep={
                              addSeededUserFromThirdStep
                            }
                            handleOk={handleOk}
                            handleCancel={handleCancel}
                            loader={loader}
                            apiError={apiError}
                            flexAttribute={flexAttribute}
                            CUGAttribute={CUGAttribute}
                            saveOrganizationAsDefault={saveOrganizationAsDefault}
                          />
                        ) : (
                          <></>
                        )}
                      </div>
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default CreateOrgStepper;
