import UserService from '../services/user.service';

import{
    HELP_SUCCESS,
    HELP_FAIL,
    SET_MESSAGE,
    CREATEHELP_SUCCESS,
    CREATEHELP_FAIL,
    CREATEHELPSET_MESSAGE
    } from "./types";

    export const getHelpAsync=()=>(dispatch)=>{
        return UserService.getHelps().then((data)=>{
            dispatch({
                type:HELP_SUCCESS,
                payload:{helpsdata:data.data},
                
            });    
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:HELP_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };

    export const createHelpAsync=(data)=>(dispatch)=>{
        return UserService.createHelp(data).then((data)=>{
            
            dispatch({
                type:CREATEHELP_SUCCESS,
                payload:{createhelpdata:data.data},
                
            });    
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:CREATEHELP_FAIL,
            });
            
            dispatch({
                type:CREATEHELPSET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };    