import React, { useState } from "react";
import { Form, Checkbox, Button, DatePicker, Alert } from "antd";
import AddIcoModal from "../../components/modal/AddIcoModal";
import ReactHtmlParser from "react-html-parser";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { CheckCircleTwoTone } from "@ant-design/icons";
import FloatLabel from "../form/FloatLabels";
import {
  ENTER_DIRECTORY_NAME,
  REQUIRED_MESSAGE,
  HOTLINE_ALREADY_EXISTS,
  ENTER_DESCRIPTION,
  SELECT_DATE,
} from "../../errors";
import { DIRECTORY_NAME, DESCRIPTION, DATE, SAVE_AS_DRAFT } from "../../labels";
import { NEXT, SAVE_AS_DRAFT_BTN } from "../../headings";
import "../form/form.scss";
import { BASE_URL } from "../../userconfig";
import TextArea from "antd/lib/input/TextArea";

const CreateHotlineStepOne = ({
  next,
  stepOneHotLineInfo,
  handleStepOneOnChange,
  handleStepOneOnSelect,
  handleStepOneDatePickerChange,
  addIcon,
  onChangeCheckbox,
  getIconText,
  setNameValidHandle,
  hotlineErrorUpdated,
  createHotLine
}) => {
  const [hotlineForm, setHotlineForm] = useState({
    iconText: "",
  });

  const [error, showError] = useState(false);
  const [disabledbtn, setdisabledbtn] = useState(true);
  const [form] = Form.useForm(); 
  const [dirNameError, setDirNameError] = useState(false);

  const stepOneNext = (e) => {
    e.preventDefault();
    if (
      stepOneHotLineInfo.directoryName &&
      stepOneHotLineInfo.directoryName.replace(/^\s*/, "").replace(/\s*$/, "")
        .length > 0 &&
      stepOneHotLineInfo.description &&
      stepOneHotLineInfo.description.replace(/^\s*/, "").replace(/\s*$/, "")
        .length > 0 &&
      stepOneHotLineInfo.iconHtml &&
      stepOneHotLineInfo.sDate
    ) {
      if (hotlineErrorUpdated == "status: false") {
        showError(false);
        setdisabledbtn(false);
        setDirNameError(false);
        next();
      }
    } else {
      setDirNameError(false);
      showError(true);
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const CheckHotlineName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL + "checkhotline/",
        { hotline_name: stepOneHotLineInfo.directoryName },
        options
      )
      .then((res) => {
        if (res.data) {
          setNameValidHandle(res.data[0], "hotlineErrorUpdated");
        }
      });
  };

  const onClickSaveAsDraft = () =>{
    if (
      stepOneHotLineInfo.directoryName &&
      stepOneHotLineInfo.directoryName.replace(/^\s*/, "").replace(/\s*$/, "")
        .length > 0
    ) {
      if (hotlineErrorUpdated == "status: false") {
        showError(false);
        setDirNameError(false);
        createHotLine(true);
      }
    } else {
      showError(false);
      setDirNameError(true);
    }
  }

  const { RangePicker } = DatePicker;

  return (
    <Form
      data-test="createHotlineStepOne"
      className="input-md w-md-100 w-75 floating-label"
      name="validate_other"
      form={form}
    >
      {error && (
        <Form.Item>
          <Alert message={REQUIRED_MESSAGE} type="error" showIcon></Alert>
        </Form.Item>
      )}
      {dirNameError && (
        <Form.Item>
          <Alert message="Please fill directory name" type="error" showIcon></Alert>
        </Form.Item>
      )}
      <FloatLabel
        label={DIRECTORY_NAME}
        value={stepOneHotLineInfo.directoryName}
      >
        <Form.Item
          className="required"
          name="directoryName"
          rules={[
            {
              required: true,
              message: { ENTER_DIRECTORY_NAME },
            },
          ]}
        >
          <TextArea
            showCount
            maxLength={80}
            value={stepOneHotLineInfo.directoryName}
            onChange={(e) => handleStepOneOnChange(e)}
            onBlur={(e) => CheckHotlineName(e)}
            id="directoryName"
            className="input-control"
            htmlFor="directoryName"
            autoComplete="off"
          />
          {hotlineErrorUpdated === "status: false" &&
            stepOneHotLineInfo.directoryName
              .replace(/^\s*/, "")
              .replace(/\s*$/, "").length > 0 &&
            stepOneHotLineInfo.directoryName.length > 0 && (
              <CheckCircleTwoTone
                className="successCheck"
                twoToneColor="#52c41a"
              />
            )}
          {hotlineErrorUpdated == "status: true" && (
            <Alert
              message={HOTLINE_ALREADY_EXISTS}
              type="error"
              showIcon
            ></Alert>
          )}
        </Form.Item>
      </FloatLabel>
      <FloatLabel label={DESCRIPTION} value={stepOneHotLineInfo.description}>
        <Form.Item
          className="required"
          name="description"
          rules={[
            {
              required: true,
              message: { ENTER_DESCRIPTION },
            },
          ]}
        >
          <TextArea
            className="input-control"
            value={stepOneHotLineInfo.description}
            onChange={(e) => handleStepOneOnChange(e)}
            id="description"
            htmlFor="description"
            autoComplete="off"
          />
        </Form.Item>
      </FloatLabel>
      {stepOneHotLineInfo.statusMessage.length > 0 ? (
        <>{stepOneHotLineInfo.statusMessage}</>
      ) : null}
      <FloatLabel label={DATE} value="sDate">
        <Form.Item className="input-control datepicker-wrapper">
          <RangePicker
            className="datepicker w-100 w-lg-50 mr-0 mr-lg-5 required"
            name="sDate"
            value={stepOneHotLineInfo.sDate}
            onChange={(date) => handleStepOneDatePickerChange(date, "sDate")}
            disabledDate={disabledDate}
            rules={[
              {
                required: true,
                message: { SELECT_DATE },
              },
            ]}
          />
        </Form.Item>
      </FloatLabel>
      {/* <Form.Item name="status">
        <>
          <Checkbox
            className="font-13"
            onChange={(e) => onChangeCheckbox(e)}
            checked={stepOneHotLineInfo.status}
          >
            {SAVE_AS_DRAFT}
          </Checkbox>
        </>
      </Form.Item> */}
      <Form.Item className="">
        <Form.Item
          className="upload-control"
          name="upload"
          valuePropName="fileList"
        >
          <AddIcoModal data-test="addIconComponent" addIcon={addIcon} />
          {getIconText(stepOneHotLineInfo.hotlineIconId) && (
            <div className="ant-upload-list ant-upload-list-picture">
              <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
                <div className="ant-upload-list-item-info d-inline-flex w-100">
                  <span className="d-inline-flex align-items-center">
                    <a className="ant-upload-list-item-thumbnail">
                      {ReactHtmlParser(stepOneHotLineInfo.iconHtml)}
                    </a>

                    <span
                      className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                      title={getIconText(stepOneHotLineInfo.hotlineIconId)}
                    >
                      {getIconText(stepOneHotLineInfo.hotlineIconId)}
                    </span>
                  </span>
                </div>
              </div>
            </div>
          )}
        </Form.Item>
      </Form.Item>
      <Form.Item>
        <Button
          type="primary"
          htmlType="submit"
          onClick={stepOneNext}
          data-test="stepOneNext"
          className="mr-4"
        >
          {NEXT}
        </Button>
        <Button
          type="secondary"
          onClick={onClickSaveAsDraft}
        >
          {SAVE_AS_DRAFT_BTN}
        </Button>
      </Form.Item>
    </Form>
  );
};
export default CreateHotlineStepOne;
