import { BROADCAST_SUCCESS, BROADCAST_FAIL } from "../actions/types";

const deafaultState = {};
deafaultState.broadcastmessagedata = null;

export default function (state = deafaultState, action) {
  const { type, payload } = action;

  switch (type) {
    case BROADCAST_SUCCESS:
      return {
        ...state,
        broadcastmessagedata: payload.broadcast,
      };
    case BROADCAST_FAIL:
      return {
        ...state,
        broadcastmessagedata: [],
      };
    default:
      return state;
  }
}
