import React from "react";
import { Layout, Menu } from "antd";
import { Link } from "react-router-dom";
const { Sider } = Layout;
import { MenuUnfoldOutlined, MenuFoldOutlined } from "@ant-design/icons";
import {
  HELP,
  HOTLINE_MESSAGES,
  INFO,
  OPENLINE_MESSAGES,
} from "../../headings";

class AmWebSidebar extends React.Component {
  state = {
    collapsed: false,
  };

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    let defaultKey = "";
    if (this.props.data?.location) {
      if (this.props.data.location.pathname.includes("hotline")) {
        defaultKey = "1";
      } else if (this.props.data.location.pathname.includes("openline")) {
        defaultKey = "2";
      }
    }
    return (
      <>
        {React.createElement(
          this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
          {
            className: "trigger d-lg-none d-block",
            onClick: this.toggle,
          }
        )}
        <Sider
          className={
            !this.state.collapsed
              ? "d-none d-lg-block w-md-100 sider-width"
              : " d-lg-block w-md-100 sider-width"
          }
          trigger={null}
          collapsible
          collapsed={this.state.collapsed}
        >
          <Menu theme="dark" mode="inline" defaultSelectedKeys={[defaultKey]}>
            <Menu.Item
              key="1"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-openlines" />
                </span>
              }
            >
              {" "}
              <Link to="/amweb/hotlinemessages">{HOTLINE_MESSAGES}</Link>
            </Menu.Item>
            <Menu.Item
              key="2"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-polls" />
                </span>
              }
            >
              {" "}
              <Link to="/amweb/openlinemessages">{OPENLINE_MESSAGES}</Link>
            </Menu.Item>
          </Menu>
          <div className="btm-div">
            <Link to="/orgcc/info" className="px-2" disabled>
              {INFO}
            </Link>
            <Link to="/orgcc/help" className="px-2" disabled>
              {HELP}
            </Link>
          </div>
        </Sider>
      </>
    );
  }
}

export default AmWebSidebar;
