import React, { useState } from "react";
import { AutoComplete, Alert } from "antd";
import "./autocomplete.scss";
import { SEARCH_EMPLOYEE } from "../../headings";

const SearchUser = ({ searchData, addAssignUser, data }) => {
  const [isAdded, setIsAdded] = useState(false);
  const [addedUser, setAddedUser] = useState("");
  //const [value, setValue] = useState('');
  const onSelectSearch = (value, option) => {
    let user = data.filter((d) => d.user_id === option.user_id);
    if (user.length > 0) {
      setIsAdded(true);
      setAddedUser(value);
      // setValue("");
    } else {
      addAssignUser(value, option);
    }
  };

  const onChange = () => {
    setIsAdded(false);
    setAddedUser("");
  };

  return (
    <>
      {isAdded && (
        <Alert
          data-test="alert"
          message={`${addedUser} is already assigned for this hotline.`}
          type="error"
          showIcon
        ></Alert>
      )}
      <AutoComplete
        data-test="Searchuser"
        //value={value}
        options={searchData}
        placeholder={SEARCH_EMPLOYEE}
        filterOption={(inputValue, option) =>
          option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
        }
        onSelect={(val, option) => onSelectSearch(val, option)}
        onChange={() => onChange()}
      />
    </>
  );
};
export default SearchUser;
