import React, { useState, useEffect } from "react";
import { Layout, Row, Col, Form, Input, Button, Modal, Alert } from "antd";
import { LoggedinHeader } from "../../containers";
import "./form.scss";
import AccSidebar from "../manageOrg/AccSidebar";
import AmWebSidebar from "../hotlineMessages/AmWebSidebar";
import IdbSidebar from "../reports/IdbSidebar";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "./FloatLabels";
import axios from "axios";
import * as AppStorage from "../../utils/AppStorage";
import authHeader from "../../services/auth-header";
import AuthService from "../../services/auth-service";
import { BASE_URL } from "../../userconfig";
import { CHANGE_PASSWORD_FOR, OK_TEXT_MODAL, SUBMIT } from "../../headings";
import { PASSWORD_CHANGED } from "../../messages";
import {
  OLD_PASSWORD,
  ENTER_CURRENT_PASSWORD,
  ENTER_NEW_PASSWORD,
  PASSWORD_RULES,
  CONFIRM_YOUR_PASSWORD,
  PASSWORD_NOT_MATCH,
} from "../../errors";
import { CURRENT_PASSWORD, NEW_PASSWORD, CONFIRM_PASSWORD } from "../../labels";

const { Content } = Layout;

var CryptoJS = require("crypto-js");
const ChangePassword = (props) => {
  const type = AppStorage.get("userType");
  const userType = type
    ? CryptoJS.AES.decrypt(type, "anonymoose").toString(CryptoJS.enc.Utf8)
    : "";
  const [pwd, setPwd] = useState({
    currentPwd: "",
    newPwd: "",
    confirmPwd: "",
  });
  const [passwordChanged, setPasswordChanged] = useState(false);
  const [error, setError] = useState(false);
  const [userName, setUserName] = useState("");
  const [passwordValidationError, showPasswordValidationError] = useState(
    false
  );

  useEffect(() => {
    setError(false);
    showPasswordValidationError(false);
    const user = JSON.parse(AppStorage.get("user"));
    const userName = user
      ? CryptoJS.AES.decrypt(
          user.user_details.first_name,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8) +
        " " +
        CryptoJS.AES.decrypt(
          user.user_details.last_name,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      : "";
    setUserName(userName);
  }, []);

  const validatePassword = (e) => {
    var re = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,}$/;
    if (!re.test(e.target.value)) {
      showPasswordValidationError(true);
    } else {
      showPasswordValidationError(false);
    }
  };

  const handleOnChange = (e) => {
    setPwd({
      ...pwd,
      [e.target.id]: e.target.value,
    });
  };

  const handleOk = () => {
    if (AppStorage.get("user")) {
      AuthService.logout();
    }
  };

  const handleCancel = () => {
    if (AppStorage.get("user")) {
      AuthService.logout();
    }
  };

  const handleSubmit = () => {
    const token = authHeader();
    const data = {
      old_password: pwd.currentPwd,
      new_password: pwd.newPwd,
      confirm_password: pwd.confirmPwd,
    };
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (pwd.newPwd === pwd.confirmPwd && !passwordValidationError) {
      axios
        .put(BASE_URL + "changepassword/", data, options)
        .then(() => {
          setError(false);
          setPasswordChanged(true);
        })
        .catch((error) => {
          if (error.message.includes("400")) {
            setError(true);
          }
        });
    }
  };

  return (
    <React.Fragment>
      <React.Fragment>
        <Row className="inner-header">
          {userType === "AM WEB" ? (
            <LoggedinHeader amWEBUser />
          ) : userType === "AM CC" ? (
            <LoggedinHeader amCCUser />
          ) : userType === "Org CC" ? (
            <LoggedinHeader amORGUser />
          ) : userType === "IDB" ? (
            <LoggedinHeader amIDBUser />
          ) : null}
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              {userType === "AM WEB" ? (
                <AmWebSidebar data={props} />
              ) : userType === "Org CC" ? (
                <OrgSidebar data={props} />
              ) : userType === "AM CC" ? (
                <AccSidebar />
              ) : userType === "IDB" ? (
                <IdbSidebar />
              ) : null}
              <Content className="">
                <Row className="py-3">
                  <h1 className="mb-0">
                    {CHANGE_PASSWORD_FOR} {userName}
                  </h1>
                </Row>
                <div className="mid-content mt-4">
                  <Modal
                    visible={passwordChanged}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    centered
                    width={350}
                    okButtonProps={{
                      children: "Custom OK",
                    }}
                    cancelButtonProps={{ style: { display: "none" } }}
                    okText={OK_TEXT_MODAL}
                  >
                    <p>{PASSWORD_CHANGED}</p>
                  </Modal>
                  <Row>
                    <Col span={24}>
                      <Form className="input-md floating-label w-100">
                        <Col span={24}>
                          <Row
                            gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}
                            className="d-block"
                          >
                            <Col span={24} lg={8} className="gutter-row">
                              {error && (
                                <Form.Item>
                                  <Alert
                                    message={OLD_PASSWORD}
                                    type="error"
                                  ></Alert>
                                </Form.Item>
                              )}
                              <FloatLabel
                                label={CURRENT_PASSWORD}
                                value={pwd.currentPwd}
                              >
                                <Form.Item
                                  name="currentpassword"
                                  className="pwd required"
                                  rules={[
                                    {
                                      required: true,
                                      message: ENTER_CURRENT_PASSWORD,
                                    },
                                  ]}
                                >
                                  <Input.Password
                                    htmlFor="currentpassword"
                                    id="currentPwd"
                                    value={pwd.currentPwd}
                                    onChange={handleOnChange}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} lg={8} className="gutter-row">
                              <FloatLabel
                                label={NEW_PASSWORD}
                                value={pwd.newPwd}
                              >
                                <Form.Item
                                  name="newpassword"
                                  className="pwd required"
                                  rules={[
                                    {
                                      required: true,
                                      message: ENTER_NEW_PASSWORD,
                                    },
                                  ]}
                                >
                                  <Input.Password
                                    htmlFor="newpassword"
                                    id="newPwd"
                                    value={pwd.newPwd}
                                    onChange={handleOnChange}
                                    onBlur={(e) => validatePassword(e)}
                                  />
                                </Form.Item>
                                {passwordValidationError && (
                                  <Alert
                                    message={PASSWORD_RULES}
                                    type="error"
                                    showIcon
                                  ></Alert>
                                )}
                              </FloatLabel>
                            </Col>
                            <Col span={24} lg={8} className="gutter-row">
                              <FloatLabel
                                label={CONFIRM_PASSWORD}
                                value={pwd.confirmPwd}
                              >
                                <Form.Item
                                  name="confirmpassword"
                                  className="pwd confirpwd required"
                                  rules={[
                                    {
                                      required: true,
                                      message: CONFIRM_YOUR_PASSWORD,
                                    },
                                    ({ getFieldValue }) => ({
                                      validator(_, value) {
                                        if (
                                          !value ||
                                          getFieldValue("newpassword") === value
                                        ) {
                                          return Promise.resolve();
                                        }

                                        return Promise.reject(
                                          PASSWORD_NOT_MATCH
                                        );
                                      },
                                    }),
                                  ]}
                                  dependencies={["newpassword"]}
                                  hasFeedback
                                >
                                  <Input.Password
                                    htmlFor="confirmpassword"
                                    id="confirmPwd"
                                    value={pwd.confirmPwd}
                                    onChange={handleOnChange}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>

                        <Form.Item>
                          <div className="mt-5">
                            <Button
                              type="primary"
                              htmlType="submit"
                              onClick={handleSubmit}
                            >
                              {SUBMIT}
                            </Button>
                          </div>
                        </Form.Item>
                      </Form>
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default ChangePassword;
