import React from "react";
import { Bar } from "@reactchartjs/react-chart.js";
import { RESOLVED_CONVERSATIONS, UNRESOLVED_CONVERSATIONS } from "../../labels";

const ResolvedUnresolvedChart = (props) => {
  const data = {
    labels: [RESOLVED_CONVERSATIONS, UNRESOLVED_CONVERSATIONS],
    datasets: [
      {
        label: RESOLVED_CONVERSATIONS,
        data: [props.resolvedMessages, 0],
        backgroundColor: "rgba(234, 80, 55)",
        borderColor: "rgba(255, 255, 255)",
      },
      {
        label: UNRESOLVED_CONVERSATIONS,
        data: [0, props.totalMessages - props.resolvedMessages],
        backgroundColor: "rgba(32, 50, 92)",
        borderColor: "rgba(255, 255, 255)",
      },
    ],
  };
  const options = {
    title: {
      display: true,
      text: props.chartTitle,
    },
    legend: {
      onClick: null,
    },
  };

  return <Bar id="ResolvedUnresolvedChart" data={data} options={options} />;
};

export default ResolvedUnresolvedChart;
