import React, { Component } from "react";
import { Row, Col, Button, Divider, Timeline } from "antd";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import moment from "moment";
import ReactHtmlParser from "react-html-parser";
import { Scrollbars } from "react-custom-scrollbars";
import {
  AUDIT_LOG1,
  AUDIT_LOG2,
  AUDIT_LOG3,
  AUDIT_LOGO4,
  STATUS_CLOSED,
  STATUS_NEW,
  STATUS_OPEN,
  URGENCY_LEVEL_HIGH,
  URGENCY_LEVEL_LOW,
  URGENCY_LEVEL_MEDIUM,
} from "../../values";
import { ACTIONS } from "../../labels";
import { ACTIVITY } from "../../headings";

const audit_log = [
  { id: 5, name: AUDIT_LOG1 },
  { id: 6, name: AUDIT_LOG2 },
  { id: 7, name: AUDIT_LOG3 },
  { id: 8, name: AUDIT_LOGO4 },
];

const obj_status = [
  { id: 1, name: STATUS_NEW },
  { id: 2, name: STATUS_OPEN },
  { id: 3, name: STATUS_CLOSED },
];

const obj_urgencyLevel = [
  { id: 1, name: URGENCY_LEVEL_LOW },
  { id: 2, name: URGENCY_LEVEL_MEDIUM },
  { id: 3, name: URGENCY_LEVEL_HIGH },
];

class Activity extends Component {
  constructor() {
    super();
    this.state = {
      myclass: "",
      activity: [],
    };
    this.divclicked = this.divclicked.bind(this);
  }

  divclicked() {
    if (this.state.myclass === "") {
      this.setState({
        myclass: "rgt-panel",
      });
    } else {
      this.setState({
        myclass: "",
      });
    }
  }

  componentDidMount() {
    this.getAcitivity(this.props.conversation_id);
  }

  getAcitivity(conversationId) {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .get(
        BASE_URL +
          "getOpenlineConversationActivity/?conversation_id=" +
          conversationId,
        options
      )
      .then((res) => {
        this.setState({ activity: res.data });
      });
  }

  mapAuditLog(auditlog) {
    let str = "";
    audit_log.map((k) => {
      if (k.id === auditlog.audit_log_event_id) {
        str = k.name;
        if (auditlog.status_value) {
          obj_status.map((item) => {
            if (auditlog.status_value == item.id) {
              str = str + " <b>" + item.name + "</b>";
            }
          });
        }

        if (auditlog.urgency_level_value) {
          obj_urgencyLevel.map((item) => {
            if (auditlog.urgency_level_value == item.id) {
              str = str + " <b>" + item.name + "</b>";
            }
          });
        }
      }
    });
    return str;
  }

  render() {
    const { activity } = this.state;
    return (
      <>
        {/* <span onClick={this.divclicked} className="icon icon-create"></span> */}
        <div id="rgt-panel" className={this.props.class}>
          <p>
            <Row>
              <Col span={24}>
                <Row className="filter-wrapper align-items-center justify-content-between px-3 pt-3">
                  <Col span="12">
                    <h6>{ACTIVITY}</h6>
                  </Col>
                  <Button
                    onClick={this.showModal}
                    type="link"
                    className="link pl-0 pt-0 align-items-center d-flex"
                  >
                    <span
                      className="icon icon-close small"
                      onClick={(e) => this.props.activityClicked(e)}
                    />
                  </Button>
                </Row>
                <Divider className="my-2 w-100" />
                <Scrollbars
                  autoHeight
                  autoHeightMin={0}
                  autoHeightMax={450}
                  thumbMinSize={30}
                  universal={true}
                >
                  <Col span="24" className="px-3 pt-2">
                    <Timeline>
                      {activity.map((item) => (
                        <Timeline.Item>
                          <span className="note-user">
                            {moment(item.created_on)
                              .utc()
                              .local()
                              .format("MM:DD:YY • hh:mm A•")}{" "}
                            {item.name}
                          </span>
                          <br />
                          {ReactHtmlParser(this.mapAuditLog(item))}
                        </Timeline.Item>
                      ))}
                    </Timeline>
                  </Col>
                </Scrollbars>
              </Col>
            </Row>
          </p>
        </div>
      </>
    );
  }
}

export default Activity;
