import React, { useState } from "react";
import { Form, Checkbox, Button, DatePicker, Alert, Upload, Modal } from "antd";
import ImgCrop from "antd-img-crop";
import ReactHtmlParser from "react-html-parser";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { CheckCircleTwoTone } from "@ant-design/icons";
import FloatLabel from "../form/FloatLabels";
import "../form/form.scss";
import { BASE_URL } from "../../userconfig";
import TextArea from "antd/lib/input/TextArea";
import { CLOSE_TEXT_MODAL, NEXT, UPLOAD_IMAGE } from "../../headings";
import { DATE, DESCRIPTION, OPENLINE_NAME, SAVE_AS_DRAFT } from "../../labels";
import {
  ENTER_DESCRIPTION,
  ENTER_DIRECTORY_NAME,
  OPENLINE_ALREADY_EXISTS,
  REQUIRED_MESSAGE,
  SELECT_DATE,
  UPLOAD_IMAGE_REQUIRED,
} from "../../errors";

const CreateOpenlineStepOne = ({
  next,
  imageError,
  stepOneOpenLineInfo,
  handleStepOneOnChange,
  onChangeCheckbox,
  handleStepOneUpload,
  handleStepOnePreview,
  handleCancel,
  handleBeforeUpload,
  handleStepOneDatePickerChange,
  addIcon,
  getIconText,
  setNameValidHandle,
  hotlineErrorUpdated,
  createOpenline
}) => {
  const [hotlineForm, setHotlineForm] = useState({
    iconText: "",
  });
  const [error, showError] = useState(false);
  const [disabledbtn, setdisabledbtn] = useState(true);
  const [form] = Form.useForm();
  const [openlineNameError, setOpenlineNameError] = useState(false);

  const stepOneNext = (e) => {
    e.preventDefault();
    if (
      stepOneOpenLineInfo.openelineName &&
      stepOneOpenLineInfo.openelineName.replace(/^\s*/, "").replace(/\s*$/, "")
        .length > 0 &&
      stepOneOpenLineInfo.description &&
      stepOneOpenLineInfo.description.replace(/^\s*/, "").replace(/\s*$/, "")
        .length > 0 &&
      stepOneOpenLineInfo.file &&
      stepOneOpenLineInfo.file.length > 0 &&
      stepOneOpenLineInfo.sDate
    ) {
      if (!imageError && hotlineErrorUpdated == "status: false") {
        showError(false);
        setdisabledbtn(false);
        setOpenlineNameError(false);
        next();
      }
    } else {
      showError(true);
      setOpenlineNameError(false);
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const CheckHotlineName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL + "checkOpenline/",
        { openline_name: stepOneOpenLineInfo.openelineName },

        options
      )
      .then((res) => {
        if (res.data) {
          setNameValidHandle(res.data[0]);
        }
      });
  };

  const onClickSaveAsDraft = () =>{
    if (
      stepOneOpenLineInfo.openelineName &&
      stepOneOpenLineInfo.openelineName.replace(/^\s*/, "").replace(/\s*$/, "")
        .length > 0
    ) {
      if (hotlineErrorUpdated == "status: false") {
        showError(false);
        setOpenlineNameError(false);
        createOpenline(true)
      }
    } else {
      showError(false);
      setOpenlineNameError(true);
    }
  }

  const { RangePicker } = DatePicker;

  return (
    <Form
      data-test="createHotlineStepOne"
      className="input-md w-md-100 w-75 floating-label"
      name="validate_other"
      form={form}
    >
      {error && (
        <Form.Item>
          <Alert message={REQUIRED_MESSAGE} type="error" showIcon></Alert>
        </Form.Item>
      )}
      {openlineNameError && (
        <Form.Item>
          <Alert message="Please fill openline name" type="error" showIcon></Alert>
        </Form.Item>
      )}

      <FloatLabel
        label={OPENLINE_NAME}
        value={stepOneOpenLineInfo.openelineName}
      >
        <Form.Item
          className="required"
          name="openelineName"
          rules={[
            {
              required: true,
              message: ENTER_DIRECTORY_NAME,
            },
          ]}
          autoComplete="off"
        >
          <TextArea
            showCount
            maxLength={80}
            value={stepOneOpenLineInfo.openelineName}
            onChange={(e) => handleStepOneOnChange(e)}
            onBlur={(e) => CheckHotlineName(e)}
            id="openelineName"
            className="input-control"
            htmlFor="openelineName"
            autoComplete="off"
          />
          {hotlineErrorUpdated == "status: false" &&
            stepOneOpenLineInfo.openelineName
              .replace(/^\s*/, "")
              .replace(/\s*$/, "").length > 0 &&
            stepOneOpenLineInfo.openelineName.length > 0 && (
              <CheckCircleTwoTone
                className="successCheck"
                twoToneColor="#52c41a"
              />
            )}
          {hotlineErrorUpdated == "status: true" && (
            <Alert
              message={OPENLINE_ALREADY_EXISTS}
              type="error"
              showIcon
            ></Alert>
          )}
        </Form.Item>
      </FloatLabel>
      <FloatLabel label={DESCRIPTION} value={stepOneOpenLineInfo.description}>
        <Form.Item
          className="required"
          name="description"
          rules={[
            {
              required: true,
              message: ENTER_DESCRIPTION,
            },
          ]}
        >
          <>
            <TextArea
              className="input-control"
              value={stepOneOpenLineInfo.description}
              onChange={(e) => handleStepOneOnChange(e)}
              id="description"
              htmlFor="description"
              autoComplete="off"
            />
          </>
        </Form.Item>
      </FloatLabel>
      {stepOneOpenLineInfo.statusMessage.length > 0 ? (
        <>{stepOneOpenLineInfo.statusMessage}</>
      ) : null}
      <FloatLabel label={DATE} value="sDate">
        <Form.Item className="input-control datepicker-wrapper">
          <RangePicker
            className="datepicker w-100 w-lg-50 mr-0 mr-lg-5 required"
            name="sDate"
            value={stepOneOpenLineInfo.sDate}
            onChange={(date) => handleStepOneDatePickerChange(date, "sDate")}
            disabledDate={disabledDate}
            rules={[
              {
                required: true,
                message: SELECT_DATE,
              },
            ]}
          />
        </Form.Item>
      </FloatLabel>
      {/* <Form.Item name="status">
        <>
          <Checkbox
            className="font-13"
            onChange={(e) => onChangeCheckbox(e)}
            checked={stepOneOpenLineInfo.status}
          >
            {SAVE_AS_DRAFT}
          </Checkbox>
        </>
      </Form.Item> */}

      <Form.Item className="">
        <Form.Item
          className="upload-control required"
          name="upload"
          valuePropName="fileList"
        >
          <ImgCrop rotate modalOk="Upload">
            <Upload
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
              listType="picture"
              fileList={stepOneOpenLineInfo.file}
              onChange={(value) => handleStepOneUpload(value, "file")}
              onPreview={(value) => handleStepOnePreview(value)}
              accept="image/png, image/jpeg, image/gif"
            >
              {stepOneOpenLineInfo.file.length === 0 && (
                <Button type="link" className="link pl-0">
                  <span className="icon icon-upload pr-2"></span>
                  {UPLOAD_IMAGE}
                </Button>
              )}
            </Upload>
          </ImgCrop>
          <Modal
            className="modal-openline"
            visible={stepOneOpenLineInfo.previewVisible}
            title={stepOneOpenLineInfo.previewTitle}
            centered
            onOk={(value) => handleCancel(value)}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText={CLOSE_TEXT_MODAL}
          >
            <img
              alt="example"
              style={{ width: "100%" }}
              src={stepOneOpenLineInfo.previewImage}
            />
          </Modal>

          {/* <AddIcoModal data-test="addIconComponent" addIcon={addIcon} /> */}
          {getIconText(stepOneOpenLineInfo.hotlineIconId) && (
            <div className="ant-upload-list ant-upload-list-picture">
              <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
                <div className="ant-upload-list-item-info d-inline-flex w-100">
                  <span className="d-inline-flex align-items-center">
                    <a className="ant-upload-list-item-thumbnail">
                      {ReactHtmlParser(stepOneOpenLineInfo.iconHtml)}
                    </a>

                    <span
                      className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                      title={getIconText(stepOneOpenLineInfo.hotlineIconId)}
                    >
                      {getIconText(stepOneOpenLineInfo.hotlineIconId)}
                    </span>
                  </span>
                </div>
              </div>
            </div>
          )}
        </Form.Item>
      </Form.Item>
      {imageError && (
        <Form.Item>
          <Alert message={UPLOAD_IMAGE_REQUIRED} type="error" showIcon></Alert>
        </Form.Item>
      )}
      <Form.Item>
        <Button
          type="primary"
          htmlType="submit"
          onClick={stepOneNext}
          data-test="stepOneNext"
          className="mr-4"
        >
          {NEXT}
        </Button>
        <Button
          type="secondary"
          onClick={onClickSaveAsDraft}
        >
          {SAVE_AS_DRAFT}
        </Button>
      </Form.Item>
    </Form>
  );
};
export default CreateOpenlineStepOne;
