import React, { useEffect, useState } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Button,
  Input,
  Breadcrumb,
  Alert,
  Spin,
} from "antd";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import { NavLink, useHistory } from "react-router-dom";
import { connect } from "react-redux";
import authHeader from "../../services/auth-header";
import axios from "axios";
import RichTextEditor from "../broadcastMessages/Editor";
import { BASE_URL } from "../../userconfig";
import { REQUIRED_MESSAGE } from "../../errors";
import { CANCEL, CREATE_HELP, EDIT_HELP, HELP, SAVE } from "../../headings";
import { MESSAGE, MESSAGE_TITLE } from "../../labels";
import { useForm } from "antd/lib/form/Form";
import TextArea from "antd/lib/input/TextArea";
const { Content } = Layout;

const CreateHelpForm = (props) => {
  const history = useHistory();
  const [form] = useForm();
  const [help, sethelp] = useState({
    title: "",
    message: "",
    help_id: "",
  });
  const [title, setTitle] = useState(CREATE_HELP);
  const [error, showError] = useState(false);
  const [loader, setLoader] = useState(false);

  useEffect(() => {
    if (props.location.state && props.location.state.help) {
      sethelp({
        title: props.location.state.help.title,
        message: props.location.state.help.message,
        help_id: props.location.state.help.help_id,
      });
      setTitle(EDIT_HELP);
      form.setFieldsValue({
        title: props.location.state.help.title,
        message: props.location.state.help.message,
      });
    }
  }, []);

  const onCreateChange = (e) => {
    sethelp({ ...help, [e.target.id]: e.target.value });
  };

  const onMessageChange = (content, name) => {
    sethelp((prevState) => {
      return { ...prevState, [name]: content };
    });
  };

  const saveHelp = () => {
    setLoader(true);
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (help.title && help.message) {
      showError(false);
      const data = help;
      if (title === "Create Help") {
        axios.post(BASE_URL + "createhelp/", data, options).then(() => {
          setLoader(false);
          window.location = "/orgcc/help";
        });
      } else if (title === "Edit Help") {
        axios.put(BASE_URL + "createhelp/", data, options).then((res) => {
          if (res.status === 200) {
            setLoader(false);
            window.location = "/orgcc/help";
          }
        });
      }
    } else {
      showError(true);
    }
  };

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content>
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/help">
                      <Breadcrumb.Item>{HELP}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{title}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">{title}</h1>
                </Row>
                <div className="mid-content">
                  <Spin spinning={loader} size="large">
                    <Row>
                      <Col span={24}>
                        <Form
                          className="input-md floating-label w-100"
                          name="validate_other"
                          form={form}
                        >
                          {error ? (
                            <Form.Item>
                              <Alert
                                message={REQUIRED_MESSAGE}
                                type="error"
                                showIcon
                              ></Alert>
                            </Form.Item>
                          ) : (
                            ""
                          )}
                          <Col span={24}>
                            <Row>
                              <Col span={24} md={18} className="gutter-row">
                                <FloatLabel
                                  label="Message Title"
                                  value={help.title}
                                >
                                  <Form.Item
                                    className="required"
                                    name="title"
                                    rules={[
                                      {
                                        required: true,
                                        message: "Please enter description"
                                      },
                                    ]}
                                  >
                                    <TextArea
                                      showCount
                                      maxLength={80}
                                      value={help.title}
                                      onChange={(e) => onCreateChange(e)}
                                      id="title"
                                      name="title"
                                      className="input-control"
                                      htmlFor="directoryName"
                                      autoComplete="off"
                                    />

                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                            </Row>
                          </Col>
                          <Col span={24}>
                            <Row>
                              <Col span={24} md={18} className="gutter-row">
                                <FloatLabel
                                  label={MESSAGE}
                                  value={help.message}
                                >
                                  <Form.Item
                                    className="required"
                                    name="message"
                                    validateStatus="success"
                                  >
                                    <RichTextEditor
                                      value={help.message}
                                      onChange={(content) =>
                                        onMessageChange(content, "message")
                                      }
                                      id="message"
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                            </Row>
                          </Col>

                          <Form.Item>
                            <div className="steps-action mt-5">
                              <Button
                                onClick={saveHelp}
                                type="primary"
                                htmlType="submit"
                              >
                                {SAVE}
                              </Button>
                              <Button
                                onClick={() => {
                                  history.push("/orgcc/help");
                                }}
                                className="ml-4"
                                type="secondary"
                              >
                                {CANCEL}
                              </Button>
                            </div>
                          </Form.Item>
                        </Form>
                      </Col>
                    </Row>
                  </Spin>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

function mapStateToProps(state) {
  const { createhelpdata } = state.help;
  return {
    createhelpdata,
  };
}

export default connect(mapStateToProps)(CreateHelpForm);
