import React from "react";
import { Upload, Button, Modal, Divider } from "antd";
import { CLOSE_TEXT_MODAL, IMAGE } from "../../headings";

const UploadBroadcast = ({
  file,
  handleStepOneUpload,
  handleBeforeUpload,
  handleOnRemove,
  handlePollImagePreview,
  handleCancel,
  broadcast,
  uploadtitle,
  disabledbutton,
}) => {
  console.log("------file---", file);
  return (
    <>
      <Upload
        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
        fileList={file}
        maxCount={4}
        onChange={(value) =>
          handleStepOneUpload(
            value,
            uploadtitle === "Image" ? "broadCastImage" : "broadCastDocument"
          )
        }
        beforeUpload={(value) =>
          handleBeforeUpload(
            value,
            uploadtitle === "Image" ? "broadCastImage" : "broadCastDocument"
          )
        }
        onRemove={(value) => handleOnRemove(value, uploadtitle === "Image" ? "pollImageValidation" : "document")}
        onPreview={(value) => handlePollImagePreview(value)}
        className="upload-list-inline c-upload upload-list-block"
        accept={
          uploadtitle === "Image"
            ? "image/png, image/jpeg, image/gif"
            : "application/pdf, .doc, .csv, .pptx, application/vnd.ms-excel, .csv "
        }
      >
        {
          <Button type="link" className="link pl-0" disabled={disabledbutton}>
            {uploadtitle === "Image" ? (
              <span className="icon icon-image pr-2"></span>
            ) : (
              <span className="icon icon-doc pr-2"></span>
            )}
            {uploadtitle}
          </Button>
        }
      </Upload>
      <Divider type="vertical" />
      <Modal
        className="modal-openline"
        visible={broadcast.previewVisible}
        title={broadcast.previewTitle}
        centered
        onOk={(value) => handleCancel(value)}
        okButtonProps={{
          children: "Custom OK",
        }}
        cancelButtonProps={{ style: { display: "none" } }}
        okText={CLOSE_TEXT_MODAL}
      >
        <img
          alt="image"
          style={{ width: "100%" }}
          src={broadcast.previewImage}
        />
      </Modal>
    </>
  );
};

export default UploadBroadcast;
