import React, { Component } from "react";
import { Form, Input, Button, Row, Col, Alert, Spin } from "antd";
import FloatLabel from "../form/FloatLabels";
import { CommonHeader } from "../../containers";
import Amccvector from "../../assets/images/amcc-vector.svg";
import "./commonLogin.scss";
import { Link } from "react-router-dom";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import { VALID_EMAIL, RESET_EMAIL_INCORRECT } from "../../errors";
import { EMAIL } from "../../labels";
import {
  BACK_TO,
  FORGOT_PASSWORD_TEXT1,
  LOGIN_PART1,
  LOGIN_PART2,
  REQUEST_PASSWORD_RESET,
  RESET_EMAIL_SENT,
} from "../../messages";
import { FORGOT_PASSWORD, SIGN_IN } from "../../headings";

const layout = {
  wrapperCol: {
    span: 24,
  },
};
const tailLayout = {
  wrapperCol: {
    span: 24,
  },
};

class ForgotPassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      error: false,
      emailSent: false,
      incorrectEMail: false,
      loader: false,
    };
  }

  onChangeUsername = (e) => {
    this.setState({
      email: e.target.value,
    });
  };

  sendEmail = (e) => {
    const options = {
      headers: {
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (this.state.email !== "") {
      const data = {
        email: this.state.email,
      };
      this.setState({ error: false, loader: true });
      axios
        .post(BASE_URL + "request-reset-email/", data, options)
        .then((res) => {
          if (res.status === 200) {
            this.setState({
              emailSent: true,
              loader: false,
              incorrectEMail: false,
            });
          }
        })
        .catch(() =>
          this.setState({
            emailSent: false,
            loader: false,
            incorrectEMail: true,
          })
        );
    } else {
      this.setState({ error: true, emailSent: false, incorrectEMail: false });
    }
  };

  render() {
    return (
      <>
        <Row className="px-4 py-1 py-lg-3 header">
          <CommonHeader />
        </Row>
        <Row className="content-center mt-0 px-4">
          <Col
            sm={24}
            lg={12}
            className="vector-area justify-content-center mt-1"
          >
            <Col span={24} className="text-center">
              <h5 className="text-center primary-text">{LOGIN_PART1}</h5>
            </Col>
            <Row className="justify-content-center mt-3">
              <img src={Amccvector} alt="Anonymoose" className="pr-0 pr-lg-4" />
            </Row>
            <Row className="pt-3">
              <p className="w-100 text-center text-wgt-md mb-1 d-none d-lg-block">
                {LOGIN_PART2}
              </p>
            </Row>
          </Col>
          <Col
            lg={{ span: 12 }}
            md={{ span: 24 }}
            xs={{ span: 24 }}
            className="d-flex justify-content-center mt-2"
          >
            <div className=" col-md-24 col-lg-12 col-xl-10 login-box justify-content-center d-flex">
              {this.state.emailSent
                ? RESET_EMAIL_SENT + this.state.email
                : null}
              <Row className="mb-4 d-none d-lg-block">
                <h2>{FORGOT_PASSWORD}</h2>
                <p className="mt-3 font-weight-bold">{FORGOT_PASSWORD_TEXT1}</p>
              </Row>
              <Form
                {...layout}
                ref={(c) => {
                  this.form = c;
                }}
                name="basic"
                initialValues={{
                  remember: true,
                }}
              >
                <Spin spinning={this.state.loader} size="large">
                  {this.state.error && (
                    <Alert message={VALID_EMAIL} type="error" showIcon></Alert>
                  )}
                  {this.state.incorrectEMail && (
                    <Form.Item>
                      <Alert
                        message={RESET_EMAIL_INCORRECT}
                        type="error"
                      ></Alert>
                    </Form.Item>
                  )}
                  <FloatLabel label={EMAIL} value={this.state.email}>
                    <Form.Item name="email" className="required">
                      <Input
                        type="Email"
                        className="input-control"
                        placeholder=" "
                        value={this.state.email}
                        onChange={this.onChangeUsername}
                      />
                    </Form.Item>
                  </FloatLabel>

                  <Form.Item {...tailLayout}>
                    <Button
                      type="primary"
                      htmlType="submit"
                      className="primary-btn-lg w-100 mt-4"
                      onClick={(e) => this.sendEmail()}
                    >
                      {REQUEST_PASSWORD_RESET}
                    </Button>
                  </Form.Item>
                </Spin>
                <div className="justify-content-center d-flex">
                  <Button type="link" className="link">
                    <Link to="/login">
                      {" "}
                      {BACK_TO} <b className="pl-1">{SIGN_IN}</b>
                    </Link>
                  </Button>
                </div>
              </Form>
            </div>
          </Col>
        </Row>
      </>
    );
  }
}

export default ForgotPassword;
