import React from "react";
import { Upload, Button, Modal } from "antd";
import { CLOSE_TEXT_MODAL, DOCUMENT_IMAGE } from "../../headings";

const UploadImage = ({
  file,
  handleStepOneUpload,
  handleBeforeUpload,
  handleOnRemove,
  handlePollImagePreview,
  handleCancel,
  poll,
}) => {
  return (
    <>
      <Upload
        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
        listType="picture"
        fileList={file}
        onChange={(value) => handleStepOneUpload(value, "pollDocument")}
        beforeUpload={(value) => handleBeforeUpload(value, "pollDocument")}
        onRemove={(value) => handleOnRemove(value, "pollImageValidation")}
        onPreview={(value) => handlePollImagePreview(value)}
        className="upload-list-inline c-upload"
        accept="image/png, image/jpeg, image/gif, application/pdf, .doc, .csv, .pptx, application/vnd.ms-excel, .csv "
      >
        <Button type="link" className="link pl-0">
          <span className="icon icon-upload pr-2"></span>
          {DOCUMENT_IMAGE}
        </Button>
      </Upload>
      <Modal
        className="modal-openline"
        visible={poll.previewVisible}
        title={poll.previewTitle}
        centered
        onOk={(value) => handleCancel(value)}
        okButtonProps={{
          children: "Custom OK",
        }}
        cancelButtonProps={{ style: { display: "none" } }}
        okText={CLOSE_TEXT_MODAL}
      >
        <img alt="image" style={{ width: "100%" }} src={poll.previewImage} />
      </Modal>
    </>
  );
};

export default UploadImage;
