import React, { useEffect, useState } from "react";
import { Bar } from "@reactchartjs/react-chart.js";
import { ALL, NUMBER_OF_MESSAGES, MONTHS, YEARS, DAYS } from "../../values";
import "chartjs-plugin-zoom";

const VerticalChart = (props) => {
  const [data, setData] = useState({
    labels: [],
    datasets: [],
  });
  const [options, setOptions] = useState({
    title: {
      display: true,
      text: props.chartTitle,
    },
    legend: {
      onClick: null,
    },
  });

  useEffect(() => {
    let finalData = {};
    let xAxisData = [];
    let labelsData = [];
    const dataInMonths = props.dateRange === "months";
    const dataInYears = props.dateRange === "years";
    setOptions({
      title: {
        display: true,
        text: props.chartTitle,
      },
      legend: {
        onClick: null,
        labels: {
          filter: function (item, chart) {
            // remove a particular legend item
            return !item?.text?.includes("Line");
          },
        },
      },
      scales: {
        xAxes: [
          {
            title: {
              display: true,
              text: props.chartTitle,
            },
            scaleLabel: {
              display: true,
              labelString: dataInMonths ? MONTHS : dataInYears ? YEARS : DAYS,
            },
          },
        ],
        yAxes: [
          {
            title: {
              display: true,
              text: props.chartTitle,
            },
            scaleLabel: {
              display: true,
              labelString: NUMBER_OF_MESSAGES,
            },
          },
        ],
      },
      plugins: {
        zoom: {
          zoom: {
            enabled: true,
            drag: false,
            mode: "x",
            limits: {
              max: 10,
              min: 0.5,
            },
          },
          pan: {
            enabled: true,
            mode: "x",
            speed: 100,
            threshold: 100,
          },
        },
      },
    });

    let reportData = [];
    let nestedReportData = {};
    let allReportData = {};
    let reportKeys = [];
    reportKeys = Object.keys(props.report).map((key) =>
      key.substring(key.indexOf("_") + 1, key.length)
    );
    let finalReportKeys = [];
    reportKeys.forEach((key) => {
      var date = new Date(key);
      var monthName = date.toLocaleString("en-us", { month: "short" });
      var day = date.getDate();
      var year = date.getFullYear().toString();
      finalReportKeys.push(
        day +
          "-" +
          monthName +
          "-" +
          year.substring(year.length - 2, year.length)
      );
    });
    if (props.isAllSelected) {
      reportData = Object.entries(props.report);
      reportData.forEach((report) => {
        allReportData = {
          ...allReportData,
          [report[0]]: report[1],
        };
      });
    } else {
      const reportEntries = Object.entries(props.report);
      reportEntries.forEach((entry) => {
        if (entry[1]) {
          reportData.push(entry[1]);
        }
      });
      reportKeys = [];
      finalReportKeys = [];
      reportData.forEach((report) => {
        let dummy = {};
        Object.keys(report).forEach((value) => {
          dummy = {
            ...dummy,
            [value.substring(0, value.indexOf("_"))]: report,
          };
          const changedValue = value.substring(
            value.indexOf("_") + 1,
            value.length
          );
          if (!reportKeys.includes(changedValue)) {
            reportKeys.push(changedValue);
          }
        });
        nestedReportData = {
          ...nestedReportData,
          ...dummy,
        };
      });
      if (!dataInYears) {
        reportKeys.sort(function compare(a, b) {
          var dateA = new Date(a);
          var dateB = new Date(b);
          return dateA - dateB;
        });
        reportKeys.forEach((key) => {
          var date = new Date(key);
          var monthName = date.toLocaleString("en-us", { month: "short" });
          var day = date.getDate();
          var year = date.getFullYear().toString();
          finalReportKeys.push(
            day +
              "-" +
              monthName +
              "-" +
              year.substring(year.length - 2, year.length)
          );
        });
      }
    }

    if (dataInMonths) {
      labelsData = props.months;
      if (props.isAllSelected) {
        for (
          let dataItem = 0;
          dataItem <= props.months.length - 1;
          dataItem++
        ) {
          xAxisData.push(0);
        }
        Object.keys(allReportData).forEach((value) => {
          const trimmedValue = value.substring(value.indexOf("_") + 1);
          props.months.forEach((month, key) => {
            if (parseInt(trimmedValue) - 1 === key) {
              xAxisData[key] = allReportData[value];
            }
          });
        });
      }
    } else if (dataInYears) {
      labelsData = reportKeys;
      if (props.isAllSelected) {
        for (let dataItem = 0; dataItem <= reportKeys.length - 1; dataItem++) {
          xAxisData.push(0);
        }
        Object.keys(allReportData).forEach((value) => {
          const trimmedValue = value.substring(value.indexOf("_") + 1);
          reportKeys.forEach((year, key) => {
            if (parseInt(trimmedValue) === year) {
              xAxisData[key] = allReportData[value];
            }
          });
        });
      }
    } else {
      labelsData = finalReportKeys;
      if (props.isAllSelected) {
        Object.values(allReportData).forEach((value) => {
          xAxisData.push(value);
        });
      }
    }

    let sendData = [];
    if (!props.isAllSelected) {
      let sets = [];
      props.selectedLines.forEach((line) => {
        const xData = [];
        let set = {};
        const keysData = dataInMonths ? labelsData : reportKeys;
        keysData.forEach((label, key) => {
          let initialReportKey = "";
          let reportKey = "";
          let trimmedValue = "";
          if (nestedReportData[line.line_id]) {
            initialReportKey = Object.keys(nestedReportData[line.line_id])[key];
            reportKey = initialReportKey?.substring(
              0,
              initialReportKey?.indexOf("_")
            );
          }
          if (nestedReportData[reportKey]) {
            trimmedValue = Object.keys(nestedReportData[reportKey])[
              key
            ]?.substring(
              Object.keys(nestedReportData[reportKey])[key]?.indexOf("_") + 1
            );
          }
          const comparisonKey = !dataInMonths ? label : key;
          if (dataInMonths) {
            if (parseInt(trimmedValue) - 1 === comparisonKey) {
              xData.push(nestedReportData[line.line_id][initialReportKey]);
            } else {
              xData.push(0);
            }
          } else {
            if (trimmedValue === comparisonKey) {
              xData.push(nestedReportData[line.line_id][initialReportKey]);
            } else {
              xData.push(0);
            }
          }
          set = {
            label: line.line_name,
            data: xData,
            backgroundColor: random_hex_color_code(line.line_name),
            borderWidth: 0,
          };
        });
        if (reportKeys.length === 0) {
          set = {
            label: line.line_name,
            data: xData,
            backgroundColor: random_hex_color_code(line.line_name),
            borderWidth: 0,
          };
        }
        sets.push(set);
      });
      finalData = {
        ...data,
        labels: labelsData,
        datasets: sets,
      };
      if (props.selectedLines.length > 0) {
        sendData = finalData.datasets[0]?.data;
      } else {
        sendData = xAxisData;
      }
      setData(finalData);
    } else {
      finalData = {
        ...data,
        labels: labelsData,
        datasets: [
          {
            type: "bar",
            label: ALL,
            data: xAxisData,
            backgroundColor: random_hex_color_code(ALL),
            borderWidth: 0,
          },
          {
            type: "line",
            fill: false,
            pointRadius: 0,
            label: "Line",
            data: xAxisData,
            borderColor: "rgb(54, 162, 235)",
            borderWidth: 2,
          },
        ],
      };
      setData(finalData);
      sendData = xAxisData;
    }
    props.sendBackData(labelsData, sendData);
  }, [props.selectedLines, props.report]);

  const random_hex_color_code = (string) => {
    var hash = 0;
    for (var i = 0; i < string.length; i++) {
      hash = string.charCodeAt(i) + ((hash << 5) - hash);
    }
    var colour = "#";
    for (var i = 0; i < 3; i++) {
      var value = (hash >> (i * 8)) & 0xff;
      colour += ("00" + value.toString(16)).substr(-2);
    }
    return colour;
  };

  return <Bar id="barChart" data={data} options={options} />;
};

export default VerticalChart;
