import React from "react";
import { Table, Select, Input } from "antd";
import { AudioOutlined } from "@ant-design/icons";
import "./table.scss";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);

const Grid = ({ columns, data, rowKey }) => {
  return (
    <div id="no-more-tables">
      <Table
        pagination={{ pageSize: 10 }}
        rowKey={rowKey}
        columns={columns}
        dataSource={data}
        className="cf"
      />
    </div>
  );
};

export default Grid;
