import React from "react";
import {
  Layout,
  Row,
  Col,
  Input,
  Avatar,
  Form,
  DatePicker,
  Button,
} from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageOrg/manageOrg.scss";
import AccSidebar from "../manageOrg/AccSidebar";
import FloatLabel from "./FloatLabels";

const { Content } = Layout;

const EditOrg = () => {
  const onFinish = (values) => {};
  const { RangePicker } = DatePicker;
  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amCCUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <AccSidebar />
            <Content>
              <Row className="py-3">
                <h1 className="mb-0">Edit Profile</h1>
              </Row>
              <div className="mid-content">
                <Row>
                  <Col span={24}>
                    <div className="avatar-wrapper pt-2 pb-4">
                      <Avatar>W</Avatar>
                      <span className="md-name">Alan Charles</span>
                    </div>
                    <Form
                      className="input-md floating-label w-100"
                      name=""
                      onFinish={onFinish}
                    >
                      <Col span={24}>
                        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="User Name">
                              <Form.Item
                                className="required"
                                name="organization name"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter user name ",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  htmlFor="user name"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="User Id">
                              <Form.Item
                                className="required"
                                name="user id"
                                validateStatus="success"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter user id ",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  htmlFor="user name"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Email Id">
                              <Form.Item
                                className="required"
                                name="email id"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter email id ",
                                    type: "email",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  htmlFor="email id"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Status">
                              <Form.Item
                                className="required"
                                name="status"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please enter status ",
                                  },
                                ]}
                              >
                                <Input
                                  className="input-control"
                                  htmlFor="status"
                                />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                          <Col span={24} md={8} className="gutter-row">
                            <FloatLabel label="Select Date">
                              <Form.Item className="input-control datepicker-wrapper required">
                                <RangePicker className="w-100 " />
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                        </Row>
                      </Col>
                      <Form.Item>
                        <div className="steps-action mt-5">
                          <Button type="primary" htmlType="submit">
                            Update
                          </Button>
                          <Button className="ml-4" type="secondary">
                            Cancel
                          </Button>
                        </div>
                      </Form.Item>
                    </Form>
                  </Col>
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default EditOrg;
