import React, { useRef } from "react";
import { Input } from "antd";
import axios from "axios";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import * as AppStorage from "../../utils/AppStorage";
import { highlightText } from "../../utils/HighlightTextHotline";
import { SEARCH } from "../../headings";

var CryptoJS = require("crypto-js");
const { Search } = Input;
const SearchHotlineMessages = (props) => {
  const searchBy = useRef("");

  // return the filtered conversations on search filter.
  const onSearch = (e) => {
    var str = e.trim();
    searchBy.current = str;
    const statusFil = props.statusFilterData.map((status) => {
      status = status.value;
      return status;
    });
    let allCheckedOnes = {
      ...(statusFil.length > 0 && { status_id: statusFil }),
      ...(props.urgencyFilterData.length > 0 && {
        urgency_level_id: props.urgencyFilterData.map((ugc) => ugc.value),
      }),
      ...(props.hotlineFilterData.length > 0 && {
        hotline_id: props.hotlineFilterData.map((ht) => ht.value),
      }),
    };
    const finalInput = {
      ...allCheckedOnes,
      hotline_id:
        allCheckedOnes.hotline_id && allCheckedOnes.hotline_id.toString(),
      status_id:
        allCheckedOnes.status_id && allCheckedOnes.status_id.toString(),
      urgency_level_id:
        allCheckedOnes.urgency_level_id &&
        allCheckedOnes.urgency_level_id.toString(),
    };
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const baseUrl =
      BASE_URL +
      "hotlineResponderConversation/?user_id=" +
      parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      );
    const finalUrl =
      str === ""
        ? baseUrl + "&filter=" + JSON.stringify(finalInput)
        : baseUrl +
          "&filter=" +
          JSON.stringify(finalInput) +
          "&search=" +
          str.toString();
    axios.get(finalUrl, options).then((res) => {
      props.setSearchString(searchBy.current);
      AppStorage.set("searchBy", searchBy.current);
      let filteredData = [];
      if (searchBy.current.length > 0) {
        // highlight the text found during search.
        filteredData = highlightText(res.data, searchBy.current);
      } else {
        filteredData = res.data;
      }
      props.setConversations(filteredData);
    });
  };

  return (
    <Search
      name="search"
      id="search"
      placeholder={SEARCH}
      onChange={(e) => onSearch(e.target.value)}
      onSearch={onSearch}
      autoComplete="off"
    />
  );
};
export default SearchHotlineMessages;
