import React, { useEffect, useState } from "react";
import { Table, Checkbox } from "antd";
import "../table/table.scss";
import {
  MAKE_APP_USER,
  MAKE_SUPER_USER,
  MAKE_CONTROL_CENTER,
  MAKE_IDB_USER,
} from "../../values";

const UserRoles = (props) => {
  const [data, setData] = useState([]);
  useEffect(() => {
    setData([
      {
        title: MAKE_APP_USER,
        value: "0",
        key: "0",
        disabled: true,
        roleValue: "1",
      },
      {
        title: MAKE_SUPER_USER,
        value: "1",
        key: "1",
        roleValue:
          props.roles[MAKE_SUPER_USER] === true ||
          props.roles[MAKE_SUPER_USER] === "1"
            ? "1"
            : "0",
      },
      {
        title: MAKE_CONTROL_CENTER,
        value: "2",
        key: "2",
        roleValue:
          props.roles[MAKE_CONTROL_CENTER] === true ||
          props.roles[MAKE_CONTROL_CENTER] === "1"
            ? "1"
            : "0",
      },
      {
        title: MAKE_IDB_USER,
        value: "3",
        key: "3",
        roleValue:
          props.roles[MAKE_IDB_USER] === true ||
          props.roles[MAKE_IDB_USER] === "1"
            ? "1"
            : "0",
      },
    ]);
  }, [props.roles]);

  const columns = [
    { title: "", dataIndex: "title", key: "title", width: "30%" },
    {
      title: "",
      dataIndex: "",
      key: "x",
      width: "20%",
      editable: true,
      render: (record) => (
        <>
          <Checkbox
            name={record.title}
            value={record.value}
            checked={record.roleValue === "1" ? true : false}
            onChange={(e) => props.handleRoleOnSelect(e)}
            disabled={record.title === MAKE_APP_USER}
          ></Checkbox>
        </>
      ),
    },
  ];

  var locale = {
    emptyText: "  ",
  };
  return (
    <Table
      className="no-bg-table"
      locale={locale}
      columns={columns}
      dataSource={data}
      pagination={false}
      rowClassName={() => "editable-row"}
      rowKey="rowkey"
    />
  );
};

export default UserRoles;
