import React, { Component } from "react";
import { Modal, Form, Col, DatePicker, Alert } from "antd";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import "../modal/modal.scss";
import "date-fns";
import { TimePicker } from "@progress/kendo-react-dateinputs";
import {
  CANCEL,
  EXTEND_END_DATE,
  EXTEND_TEXT_MODAL,
  POLL,
} from "../../headings";
import {
  EXTEND_END_DATE_TEXT_PART1,
  EXTEND_END_DATE_TEXT_PART2,
} from "../../messages";
import momentUtil from "../../utils/momentUtil";
import { SELECT_END_DATE, SELECT_END_TIME } from "../../labels";
import { SELECT_DATE, PAST_TIME } from "../../errors";

class ExtendEndDate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      endDate: "",
      error: false,
      endTime: new Date(),
      end_time: new Date(),
      endTimeError: false,
    };
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  componentDidMount() {
    let { end_date, end_time } = this.props.pollsData;
    const edate = end_date.split("/")[1];
    const emonth = end_date.split("/")[0];
    const eyear = end_date.split("/")[2];
    const formattedEndDate = momentUtil.formatAsDDMMYYYY(end_date);
    this.setState({
      endDate: moment(formattedEndDate, "DD-MM-YYYY"),
      endTime: new Date(eyear + "-" + emonth + "-" + edate + "T" + end_time),
      end_time: new Date(eyear + "-" + emonth + "-" + edate + "T" + end_time),
    });
  }

  handleOk = () => {
    if (
      !this.state.endDate ||
      this.state.endDate === null ||
      !this.state.endTime
    ) {
      this.setState({ error: true });
    } else {
      this.setState({ error: false, visible: false });
      let obj = {
        poll_id: this.props.pollsData.poll_id,
        pollName: this.props.pollsData.title,
      };
      this.props.extendPollDate(obj, this.state);
    }
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
    window.location = "/orgcc/pollsdirectory";
  };

  onChangeEndDate = (date) => {
    this.setState({
      endDate: date,
    });
  };

  onChangeEndTime = (event) => {
    const endTime = event.target.value;
    const formattedEndDate = momentUtil.formatAsDDMMYYYY(this.state.endDate);
    const edate = formattedEndDate.split("-")[0];
    const emonth = formattedEndDate.split("-")[1];
    const eyear = formattedEndDate.split("-")[2];
    let minutes = endTime.getMinutes();
    let hours = endTime.getHours();
    if (minutes.toString().length === 1) {
      minutes = "0" + minutes;
    }
    if (hours.toString().length === 1) {
      hours = "0" + hours;
    }
    if (hours.toString() === "0") {
      hours = "00";
    }
    const extractedTime = hours + ":" + minutes + ":" + "00";
    const final_endTime = new Date(
      eyear + "-" + emonth + "-" + edate + "T" + extractedTime
    );
    this.setState({ endTimeError: false, endTime: final_endTime });
  };

  onBlurEndTime = (event) => {
    const currentTime = new Date();
    if (
      currentTime > this.state.endTime &&
      this.state.endTime.getTime() !== this.state.end_time.getTime()
    ) {
      this.setState({
        endTimeError: true,
        endTime: this.state.end_time,
      });
    } else {
      this.setState({ endTimeError: false });
    }
  };

  disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  range = (start, end) => {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  };

  render() {
    return (
      <>
        <a onClick={this.showModal}>{EXTEND_END_DATE}</a>
        <Modal
          title={EXTEND_END_DATE}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={520}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText={EXTEND_TEXT_MODAL}
          cancelText={CANCEL}
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              {this.state.error && (
                <Form.Item>
                  <Alert message={SELECT_DATE} type="error" showIcon></Alert>
                </Form.Item>
              )}
              <div className="mb-4 subtext-md font-weight-md6">
                {EXTEND_END_DATE_TEXT_PART1}{" "}
                <span className="pr-1"> {EXTEND_END_DATE_TEXT_PART2}</span>
                {this.props.pollsData.title} {POLL}
              </div>

              <FloatLabel label={SELECT_END_DATE} value="eDate">
                <Form.Item
                  name="eDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  <DatePicker
                    defaultValue={this.state.endDate}
                    disabledDate={this.disabledDate}
                    onChange={(date) => this.onChangeEndDate(date)}
                    className="w-100"
                    name="eDate"
                    className="required"
                  />
                  {this.state.endTimeError && (
                    <Alert message={PAST_TIME} type="error" showIcon></Alert>
                  )}
                  <TimePicker
                    value={this.state.endTime}
                    onChange={this.onChangeEndTime}
                    onBlur={this.onBlurEndTime}
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default ExtendEndDate;
