import React from "react";
import { Row, Col, Form, Typography, Badge, Tag } from "antd";
import FilterPopover from "./FilterPopover";
import moment from "moment";
import Messages from "./Messages";
import { Scrollbars } from "react-custom-scrollbars";
import axios from "axios";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import * as AppStorage from "../../utils/AppStorage";
import SearchOpenlineMessages from "./SearchOpenlineMessages";
import {
  CONVERSATIONS,
  NO_CONVERSATION_SELECETD,
  NO_MESSAGES_FOUND,
  OPENLINE,
  SELECT_CONVERSATION,
  URGENCY,
} from "../../headings";
import { STATUS } from "../../labels";

const { Paragraph } = Typography;

var CryptoJS = require("crypto-js");
const OpenlineMessagesLeftSection = (props) => {
  const onChangeUrgencyLevel = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      ),
      urgency_level_id: event,
    };

    axios
      .put(BASE_URL + "openlineConvoMessagesUrgencyLevel/ ", data, options)
      .then((res) => {
        if (res.status === 200) {
          props.getConversations();
          props.setUrgencyLevel(event);
        }
      });
  };

  const onChangeStatus = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      ),
      status_id: event,
    };

    axios
      .put(BASE_URL + "openlineConvoMessagesStatus/", data, options)
      .then((res) => {
        if (res.status === 200) {
          props.getConversations();
          props.setStatus(event);
        }
      });
  };

  return (
    <div className="mid-content p-0">
      <Row>
        <Col
          span={24}
          lg={6}
          className={
            props.showdiv
              ? "message-section py-4 d-none"
              : "message-section py-4"
          }
        >
          <Col span={24} className=" d-flex justify-content-between px-3">
            <h6>
              {CONVERSATIONS}{" "}
              <Badge className="pl-1" count={props.unreadCount} />
            </h6>

            <FilterPopover
              searchString={props.searchString}
              removeFilter={props.removeFilter}
              loadFilteredConversations={props.loadFilteredConversations}
            />
          </Col>

          <Col span={24} className="mt-4 px-3 d-block">
            <Scrollbars
              autoHeight
              autoHeightMin={0}
              autoHeightMax={150}
              thumbMinSize={30}
              universal={true}
            >
              <>
                {props.urgencyFilterData.map((data, index) => (
                  <>
                    <Tag
                      visible={true}
                      key={index}
                      closable
                      onClose={() =>
                        props.removeFilterLevel("urgencyLevel", data.value)
                      }
                      className="mb-2"
                    >
                      {URGENCY}: <span>{data.label}</span>
                    </Tag>
                  </>
                ))}
                {props.openlineFilterData.map((openline, index) => (
                  <>
                    <Tag
                      visible={true}
                      closable
                      onClose={() =>
                        props.removeFilterLevel(
                          "urgencyOpenline",
                          openline.value
                        )
                      }
                      className="mb-2"
                      key={index}
                    >
                      {OPENLINE}: <span>{openline.label} </span>
                    </Tag>
                  </>
                ))}
                {props.statusFilterData.map((status, index) => (
                  <>
                    <Tag
                      visible={true}
                      closable
                      onClose={() =>
                        props.removeFilterLevel("urgencyStatus", status.value)
                      }
                      className="mb-2"
                      key={index}
                    >
                      {STATUS}: <span>{status.label} </span>
                    </Tag>
                  </>
                ))}
              </>
            </Scrollbars>
          </Col>
          <Col span={24} className="search-box mt-3 px-3">
            <Form form={props.form}>
              <Form.Item name="search" id="search">
                <SearchOpenlineMessages
                  statusFilterData={props.statusFilterData}
                  urgencyFilterData={props.urgencyFilterData}
                  openlineFilterData={props.openlineFilterData}
                  setSearchString={props.setSearchString}
                  setConversations={props.setConversations}
                />
              </Form.Item>
            </Form>
          </Col>
          <Scrollbars
            autoHeight
            autoHeightMin={0}
            autoHeightMax={450}
            thumbMinSize={30}
            universal={true}
          >
            <Col span={24} className=" inbox-messages">
              {props.conversations.length > 0 ? (
                props.conversations.map((item) => {
                  return (
                    <div
                      className={
                        props.conversation.conversation_id
                          ?.toString()
                          .replace(/(<([^>]+)>)/gi, "") ===
                        item.conversation_id
                          ?.toString()
                          .replace(/(<([^>]+)>)/gi, "")
                          ? "msg-wrapper active"
                          : "msg-wrapper"
                      }
                      onClick={() => props.onConversationClick(item)}
                    >
                      <div className="d-flex justify-content-between">
                        <p
                          ellipsis
                          style={{ fontWeight: "bold" }}
                          className="w-75 pr-2 mb-1"
                          dangerouslySetInnerHTML={props.createMarkup(
                            item.responder_conversation_name
                          )}
                        />
                        <span className="small">
                          {" "}
                          {moment(item.created_on).isSame(moment(), "day")
                            ? moment(item.created_on)
                                .utc()
                                .local()
                                .format("hh:mm")
                            : moment(item.created_on)
                                .utc()
                                .local()
                                .format("MM:DD:YY")}
                        </span>
                      </div>
                      <div className="d-flex justify-content-between">
                        <Paragraph
                          ellipsis
                          className="font-weight-normal mb-1 sm para_wrapper"
                        >
                          <span
                            className="ellipsisText"
                            dangerouslySetInnerHTML={props.createMarkup(
                              item.openline_name
                            )}
                          />
                          &nbsp; |&nbsp;
                          <span
                            className="ellipsisText"
                            dangerouslySetInnerHTML={props.createMarkup(
                              item.conversation_id
                            )}
                          />
                        </Paragraph>
                        <span className="small">
                          {" "}
                          <Badge count={item.unread_messages_count} />
                        </span>
                      </div>
                    </div>
                  );
                })
              ) : (
                <div className="msg-wrapper">{NO_MESSAGES_FOUND}</div>
              )}
            </Col>
          </Scrollbars>
        </Col>

        {props.conversation ? (
          <Messages
            conversation={props.conversation}
            urgencyLevel={props.urgencyLevel}
            status={props.status}
            onChangeUrgencyLevel={onChangeUrgencyLevel}
            onChangeStatus={onChangeStatus}
            searchString={props.searchString}
            newState={props.newState}
            socket={props.socket}
            setCounter={props.setCounter}
            data-test="Messages"
            chatdiv={props.chatdiv}
          />
        ) : (
          <Col
            span={24}
            lg={18}
            className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
          >
            <Row className="justify-content-center my-3">
              <img
                src={props.NoOpenlines}
                alt="No Openlines Selected"
                style={{ opacity: "0.8" }}
              />
            </Row>
            <h5>{NO_CONVERSATION_SELECETD}</h5>
            <small>{SELECT_CONVERSATION}</small>
          </Col>
        )}
      </Row>
    </div>
  );
};

export default OpenlineMessagesLeftSection;
