import React from "react";
import { Layout, Row, Col, Divider, Button } from "antd";
import { LoggedinHeader } from "../../containers";
import CreateHelpImg from "../../assets/images/create-help.svg";
import OrgSidebar from "../../containers/OrgSidebar";
import { CREATE_HELP } from "../../headings";
import { HELP_TEXT } from "../../messages";
import { useHistory } from "react-router-dom";
const { Content } = Layout;

const CreateHelp = (props) => {
  const history = useHistory();
  const showCreateHelp = () => {
    history.push("/orgcc/createhelp");
  };

  return (
    <React.Fragment>
      {" "}
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={props.data} />
            <Content data-test="Broadcast" className="">
              <Row className="py-3">
                <h1 className="mb-0">{CREATE_HELP}</h1>
              </Row>
              <div className="mid-content d-flex justify-content-center align-items-center">
                <Row>
                  <Col span={24}>
                    <div className="c-vector-area d-flex justify-content-center">
                      <img src={CreateHelpImg} alt="Create Help" />
                    </div>
                    <p className="text-center pt-3">{HELP_TEXT}</p>
                    <Row className="d-flex justify-content-center">
                      <Divider className="mt-1" />
                    </Row>
                    <Col
                      span={24}
                      className="d-flex align-items-center justify-content-center"
                    >
                      <Button onClick={showCreateHelp} type="primary">
                        {CREATE_HELP}
                      </Button>
                    </Col>
                  </Col>
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </React.Fragment>
  );
};

export default CreateHelp;
