import React, { useEffect, useState } from "react";
import OpenlineDirectory from "./OpenlineDirectory";
import ShowAllOpenlines from "./ShowAllOpenlines";
import { connect } from "react-redux";
import { getOpelineAsync } from "../../actions/openline";

const OpenlineContainer = (props) => {
  const [statusClicked, setStatusClicked] = useState(false);
  const [loader, setLoader] = useState(false);
  useEffect(() => {
    const { dispatch } = props;
    dispatch(getOpelineAsync());
  }, [statusClicked]);

  useEffect(() => {
    if (props.openlinesdata === null) {
      setLoader(true);
    } else if (props.openlinesdata !== null) {
      setLoader(false);
    }
  }, [props.openlinesdata]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  return !loader &&
    props.openlinesdata !== null &&
    props.openlinesdata.length === 0 ? (
    <OpenlineDirectory data={props} />
  ) : (
    <ShowAllOpenlines
      statusOnClicked={statusOnClicked}
      openlines={props.openlinesdata}
      data={props}
      loader={loader}
    />
  );
};

function mapStateToProps(state) {
  const { openlinesdata } = state.openline;
  return {
    openlinesdata,
  };
}

export default connect(mapStateToProps)(OpenlineContainer);
