import UserService from '../services/user.service';

import{
    MANAGEEMP_FAIL,
    MANAGEEMP_SUCCESS,
    SET_MESSAGE
    } from "./types";

    export const getManageEmpAsync=(filterStatus, search)=>(dispatch)=>{
        return UserService.getEmployees(filterStatus,search).then((data)=>{
            dispatch({
                type:MANAGEEMP_SUCCESS,
                payload:{manageEmpData:data.data},
                
            });    
            
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:MANAGEEMP_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };