import React, { useEffect, useState } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Button,
  Input,
  DatePicker,
  notification,
  Spin,
  Alert,
} from "antd";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import RichTextEditor from "./Editor";
import Location from "./Location";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import UploadBroadcast from "./UploadBroadcast";
import { IMAGE, DOCUMENT, CANCEL, UPDATE , EDIT_BROADCAST_MESSAGE, UPLOAD} from "../../headings";
import { REQUIRED_MESSAGE, UPLOAD_FORMAT_ERROR } from "../../errors";
import { UPLOAD_NOTE } from "../../messages";
import momentUtil from "../../utils/momentUtil";
import { BROADCAST_MESSAGE_UPDATE, DELETE_ATTACHMENT_UPLOADED_FILE } from "../../messages";
import "date-fns";
import { TimePicker } from "@progress/kendo-react-dateinputs";

import moment from "moment";

const { Content } = Layout;
const { TextArea } = Input;

const EditBroadcast = (props) => {
  const [loader, setLoader] = useState(false);
  const [imageArray, setImageArray] = useState([]);
  const [docArray, setDocArray] = useState([]);
  const [form] = Form.useForm();
  const [disabledbtn, setDisabledbtn] = useState(false);
  const [uploadError, showuploadError] = useState(true);
  const [isImage, setIsImage] = useState(false);
  const [error, showError] = useState(false);
  const [startDate, setStartDate] = useState(
    moment(
      momentUtil.formatAsDDMMYYYY(props?.location?.state?.scheduled_date),
      "DD_MM_YYYY"
    )
  );
  const [startTime, setStartTime] = useState(
    new Date(
      props?.location?.state?.scheduled_date.split("-")[0] +
        "-" +
        props?.location?.state?.scheduled_date.split("-")[1] +
        "-" +
        props?.location?.state?.scheduled_date.split("-")[2] +
        "T" +
        props?.location?.state?.scheduled_time
    )
  );
  const [start_time, setStart_Time] = useState(
    new Date(
      props?.location?.state?.scheduled_date.split("-")[0] +
        "-" +
        props?.location?.state?.scheduled_date.split("-")[1] +
        "-" +
        props?.location?.state?.scheduled_date.split("-")[2] +
        "T" +
        props?.location?.state?.scheduled_time
    )
  );
  const [broadcast, setBroadcast] = useState({
    messageTittle: "",
    meesage: "",
    attachmentType: "",
    location_latitude: "",
    location_longitude: "",
    broadCastImage: "",
    broadCastDocument: "",
    previewVisible: false,
    previewImage: "",
    previewTitle: "",
    statusId: "",
    mapLoction: "",
    brodcastId: "",
    address: "",
    pastStartTimeError: false,
  });
  const [broadcastErrorMessage, setBroadcastErrorMessage] = useState("");
  const [broadcastError, setBroadcastError] = useState(false);
  const [api, contextHolder] = notification.useNotification();
  const [countUploadDocument, setCountUploadDocument] = useState(0);
  
  const broadcastId = props.computedMatch?.params.id;

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    axios
      .get(BASE_URL + `getBroadcast/${broadcastId}`, options)
      .then((res) => {
        console.log("Check the get getBroadcast api call ", res);
        const data = res.data;
        var obj = {
          location_latitude: data.location_latitude
            ? parseFloat(data.location_latitude)
            : data.location_latitude,
          location_longitude: data.location_longitude
            ? parseFloat(data.location_longitude)
            : data.location_longitude,
          address: data.address,
        };

        var broadCastImageArray = [];
        var broadCastDocumentArray = [];
        data.broadcast_images?.map((item) => {
          var key = "broadcastimg" + Math.floor(Math.random() * 100);
          broadCastImageArray.push({
            uid: key,
            name: item.name,
            status: "done",
            url: item.url,
          });
        });

        data.broadcast_documents?.map((item) => {
          var key = "broadcastdoc" + Math.floor(Math.random() * 100);
          broadCastDocumentArray.push({
            uid: key,
            name: item.name,
            status: "done",
            url: item.url,
          });
        });
        setBroadcast({
          ...broadcast,
          brodcastId: data.broadcast_id,
          messageTittle: data.title,
          meesage: data.message,
          location_longitude: data.location_longitude,
          location_latitude: data.location_latitude,
          statusId: data.status_id,
          address: data.address,
          mapLoction: obj,
          broadCastImage: broadCastImageArray,
          broadCastDocument: broadCastDocumentArray,
        });

        const imagesUploadedCount = broadCastImageArray && broadCastImageArray.length ? broadCastImageArray.length : 0;
        const documentUploadedCount = broadCastDocumentArray && broadCastDocumentArray.length ? broadCastDocumentArray.length : 0;

        const alreadyUploadedDocument = parseInt(imagesUploadedCount) + parseInt(documentUploadedCount);

        setCountUploadDocument(alreadyUploadedDocument);
        console.log( "count check ", data.broadcast_images.length + data.broadcast_documents.length);
        if (
          data.broadcast_images.length + data.broadcast_documents.length  >
          4
        ) {
          setDisabledbtn(true);
        }

        form.setFieldsValue({
          messageTittle: data.title,
          message: data.message,
        });

        const imgArray = [];
        const docuArray = [];

        // broadCastDocumentArray?.forEach((item) => {

        getDocBase64(broadCastDocumentArray[0].url, function (res) {
          console.log("res: ", res);
          docuArray.push({ name: item.name, url: res });
          console.log("docuArray: ", docuArray);
          setDocArray([...docArray, docuArray]);
        });
        // });

        broadCastImageArray?.forEach((item) => {
          convertImgToBase64(item.url, function (base64Img) {
            console.log("base64Img: ", base64Img);
            imgArray.push({ name: item.name, url: base64Img });
            console.log("imgArray: ", imgArray);
            setImageArray([...imageArray, imgArray]);
          });
        });

        setLoader(false);
      })
      .catch((error) => {
        console.log("Error in  get getBroadcast api call ", error);
        setLoader(false);
      });
  }, []);

  useEffect(() => {
    const time = new Date(
      props?.location?.state?.scheduled_date.split("-")[0] +
        "-" +
        props?.location?.state?.scheduled_date.split("-")[1] +
        "-" +
        props?.location?.state?.scheduled_date.split("-")[2] +
        "T" +
        props?.location?.state?.scheduled_time
    );
    setStartTime(time);
  }, [props.location.state.scheduled_time]);

  const onBroadcastChange = (e) => {
    console.log("Check the broadcast details -----1---", e.target.id, e.target.value);
    setBroadcast({ ...broadcast, [e.target.id]: e.target.value });
  };

  const onBroadMessageChange = (content, name) => {
    console.log("Check the broadcast details -----2---", name, content);

    setBroadcast((prevState) => {
      return { ...prevState, [name]: content };
    });
  };

  const onChangeStartDate = (date) => {
    setStartDate(date);
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const onChangeStartTime = (event) => {
    const startTime = event.target.value;
    const formattedStartDate = momentUtil.formatAsDDMMYYYY(startDate);
    const edate = formattedStartDate.split("-")[0];
    const emonth = formattedStartDate.split("-")[1];
    const eyear = formattedStartDate.split("-")[2];
    let minutes = startTime.getMinutes();
    let hours = startTime.getHours();
    if (minutes.toString().length === 1) {
      minutes = "0" + minutes;
    }
    if (hours.toString().length === 1) {
      hours = "0" + hours;
    }
    if (hours.toString() === "0") {
      hours = "00";
    }
    const extractedTime = hours + ":" + minutes + ":" + "00";
    const final_startTime = new Date(
      eyear + "-" + emonth + "-" + edate + "T" + extractedTime
    );
    console.log("Check the broadcast details -----3---", final_startTime);

    setBroadcast({ ...broadcast, pastStartTimeError: false });
    setStartTime(final_startTime);
  };

  const onBlurStartTime = (event) => {
    const currentTime = new Date();
    if (
      currentTime > startTime &&
      startTime.getTime() !== start_time.getTime()
    ) {
      setStartTime(start_time);
      setBroadcast({
        pastStartTimeError: true,
      });
    } else {
      setBroadcast({ ...broadcast, pastStartTimeError: false });
      setStartTime(startTime);
    }
  };

  const handleStepDocumentUpload = (value, name) => {
    chekMaxUpload();
    if (value.file.status === "removed") {
      showuploadError(true);
      chekMaxUpload(value.file.status);
    }
    let fileList = value.fileList;
    fileList.forEach(function (file, index) {
      if (!file.uid.includes("broadcastdoc")) {
        let reader = new FileReader();
        reader.onload = (e) => {
          file.base64 = e.target.result;
          file.response = "Uploaded successfully";
        };
        reader.readAsDataURL(file.originFileObj);
      }
    });
    let docname = "broadCastDocument";
    console.log("Check the broadcast details -----4 for document upload---", docname, fileList);

    setBroadcast({ ...broadcast, [docname]: fileList });
  };

  const chekMaxUpload = () => {
    let documentLength = broadcast.broadCastDocument.length;
    let imageLength = broadcast.broadCastImage.length;
    let docLength = imageLength + documentLength + countUploadDocument;
    if (status === "removed") {
      if (docLength - 1 >= 5) {
        setDisabledbtn(true);
      } else {
        setDisabledbtn(false);
      }
    } else {
      if (docLength > 5) {
        setDisabledbtn(true);
      } else {
        setDisabledbtn(false);
      }
    }
  };

  const checkDoucmentType = (fileType) => {
    const isImage = fileType.includes("image");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
  };

  const handleStepOneUpload = (value, name) => {
    console.log("-----handleStepOneUpload--", value, "---name---", name);
    chekMaxUpload();
    if (value.file.status === "removed") {
      deleteAttachments(value.file, name); //remove the uploaded attachement from
      showuploadError(true);
      chekMaxUpload(value.file.status);

    }
    let fileList = value.fileList;
    fileList.forEach(function (file, index) {
      if (!file.uid.includes("broadcastimg")) {
        let reader = new FileReader();
        reader.onload = (e) => {
          file.base64 = e.target.result;
          file.response = "Uploaded successfully";
        };
        reader.readAsDataURL(file.originFileObj);
      }
    });

    const uploadedFileName = fileList &&  fileList.name ? fileList.name : "";

    console.log("Check the broadcast details -----5 handleStepOneUpload upload---", uploadedFileName, fileList);
    console.log("Check the broadcast details -----5 handleStepOneUpload upload---", broadcast);

    if(uploadedFileName){

    }

    setBroadcast({ ...broadcast, [name]: fileList });
  };

  const handlePollImagePreview = async (file) => {
    if (file.uid.includes("broadcast")) {
      convertImgToBase64(file.url, function (base64Img) {
        console.log("Check the broadcast details -----6 handlePollImagePreview upload---", file.name);

        setBroadcast({
          ...broadcast,
          previewImage: base64Img,
          previewVisible: true,
          previewTitle: file.name,
        });
      });
    } else {
      file.preview = await getBase64(file.originFileObj);
      setBroadcast({
        ...broadcast,
        previewImage: file.url || file.preview,
        previewVisible: true,
        previewTitle:
          file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
      });
    }
  };

  function convertImgToBase64(url, callback, outputFormat) {
    var canvas = document.createElement("CANVAS");
    var ctx = canvas.getContext("2d");
    var img = new Image();
    img.crossOrigin = "Anonymous";
    img.onload = function () {
      canvas.height = img.height;
      canvas.width = img.width;
      ctx.drawImage(img, 0, 0);
      var dataURL = canvas.toDataURL(outputFormat || "image/png");
      callback.call(this, dataURL);
      // Clean up
      canvas = null;
    };
    img.src = url;
  }

  const handleCancel = (value) =>
    setBroadcast({ ...broadcast, previewVisible: false });

  const getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const handleOnRemove = (value, name) => {
    console.log("document delete ------value------", value, "------name----", name);
    // setPoll({ ...poll, pollImageValidation: true });
    deleteAttachments(value, name); //remove the uploaded attachement from
  };

  const handleBeforeDocUpload = (file, name) => {
    let xlsxfileExt = file.name.includes(".xls");
    let pptxfileExt = file.name.includes(".pptx");
    let pptfileExt = file.name.includes(".ppt");
    let docfileExt = file.name.includes(".doc");
    let csvfileExt = file.name.includes(".csv");

    const isValidDoc =
      file.type === "application/pdf" ||
      xlsxfileExt ||
      pptxfileExt ||
      docfileExt ||
      csvfileExt;
    if (!isValidDoc) {
      showuploadError(false);
      message.error(`${file.name} is not a supported format file type.`);
    } else {
      showuploadError(true);
    }

    const isLt10M = file.size / 1024 / 1024 < 10;
    if (isValidDoc) {
      if (!isLt10M && isValidDoc) {
        showuploadError(false);
        message.error("Document must smaller than 10MB!");
      } else {
        showuploadError(true);
      }
    }

    return isValidDoc && isLt10M;
  };

  const getDisabledHours = (value) => {
    var hours = [];
    if (moment().isSame(startDate, "day")) {
      for (var i = 0; i < moment().hour(); i++) {
        hours.push(i);
      }
    }
    return hours;
  };

  const getDisabledMinutes = (selectedHour) => {
    var minutes = [];
    if (moment().isSame(startDate, "day")) {
      if (selectedHour === moment().hour()) {
        for (var i = 0; i < moment().minute(); i++) {
          minutes.push(i);
        }
      }
    }
    return minutes;
  };

  const getDisabledSeconds = (selectedHour, selectedMinute) => {
    var second = [];
    if (
      selectedHour === moment().hour() &&
      selectedMinute == moment().minute()
    ) {
      for (var i = 0; i < moment().second(); i++) {
        second.push(i);
      }
    }
    return second;
  };

  const handleBeforeUpload = (file, name) => {
    console.log("handleBeforeUpload------- ", file, "------name----", name);
    const isImage =
      file.type === "image/jpeg" ||
      file.type === "image/png" ||
      file.type === "image/gif";

    const isLt10M = file.size / 1024 / 1024 < 10;

    if (!isImage) {
      showuploadError(false);
      message.error(`${file.name} is not a supported format file type.`);
    } else {
      showuploadError(true);
    }
    if (isImage) {
      if (!isLt10M && isImage) {
        showuploadError(false);
        message.error("Image must smaller than 5MB!");
      } else {
        showuploadError(true);
      }
    }

    return isImage && isLt10M;
  };

  const setLocation = (latitude, longitude, name) => {
    var lng = longitude.toString().substring(0, 9);
    var lat = latitude.toString().substring(0, 9);
    var obj = {
      location_latitude: parseFloat(lat),
      location_longitude: parseFloat(lng),
      address: name,
    };
    setBroadcast({ ...broadcast, address: name });
    setBroadcast({ ...broadcast, mapLoction: obj });
  };

  function getDocBase64(file) {
    var atb = btoa(file);
    var base64Blob = new Blob([atb]);
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(base64Blob);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  }

  const onUpdate = (e) => {
    e.preventDefault();
    if (
      uploadError &&
      broadcast.messageTittle &&
      broadcast.messageTittle.replace(/^\s*/, "").replace(/\s*$/, "").length >
        0 &&
      startTime &&
      broadcast.meesage &&
      startDate
    ) {
      // if (broadcast.broadCastDocument[0]) {
      //   getDocBase64(broadcast.broadCastDocument[0].url, function (res) {
      //     docuArray.push({ name: item.name, url: res });

      //     setDocArray([...docArray, docuArray]);

      //     updateBroadcast();
      //     showError(false);
      //   });
      // } else {

      // }
      updateBroadcast();
      showError(false);
    } else {
      showError(true);
    }
  };

  const postUpdateCall = (documentArray, imgArray) => {
  
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    let attachments = {};
    let isMap =
      broadcast.mapLoction?.location_latitude !== null &&
      broadcast.mapLoction?.location_longitude !== null && 
      broadcast.mapLoction?. address!==null;
    if (
      broadcast.broadCastDocument?.length === 0 &&
      broadcast.broadCastImage?.length === 0 &&
      isMap
    ) {
      attachments = {
        location: broadcast.mapLoction,
      };
    } else if (
      broadcast.broadCastDocument?.length > 0 &&
      broadcast.broadCastImage?.length > 0 &&
      !isMap
    ) {
      attachments = { documents: documentArray, images: imgArray };
    } else if (
      broadcast.broadCastDocument?.length > 0 &&
      broadcast.broadCastImage?.length === 0 &&
      !isMap
    ) {
      attachments = { documents: documentArray };
    } else if (
      broadcast.broadCastDocument?.length === 0 &&
      broadcast.broadCastImage?.length > 0 &&
      !isMap
    ) {
      attachments = { images: imgArray };
    } else if (
      broadcast.broadCastDocument?.length === 0 &&
      broadcast.broadCastImage?.length > 0 &&
      isMap
    ) {
      attachments = { images: imgArray, location: broadcast.mapLoction };
    } else if (
      broadcast.broadCastDocument?.length > 0 &&
      broadcast.broadCastImage?.length === 0 &&
      isMap
    ) {
      attachments = {
        documents: documentArray,
        location: broadcast.mapLoction,
      };
    } else {
      attachments = {
        documents: documentArray,
        images: imgArray,
        location: broadcast.mapLoction,
      };
    }
    let data = {};
    if (
      attachments.documents?.length > 0 ||
      attachments.images?.length > 0 ||
      attachments.location?.location_latitude
    ) {
      data = {
        title: broadcast.messageTittle,
        message: broadcast.meesage,
        attachments: attachments,
        scheduled_date: momentUtil.formatAsDDMMYYYY(startDate),
        scheduled_time: momentUtil.formatAsHHmmss(startTime),
        status_id: broadcast.statusId,
      };
    } else {
      data = {
        title: broadcast.messageTittle,
        message: broadcast.meesage,
        scheduled_date: momentUtil.formatAsDDMMYYYY(startDate),
        scheduled_time: momentUtil.formatAsHHmmss(startTime),
        status_id: broadcast.statusId,
      };
    }
    data.offset =  -(new Date().getTimezoneOffset());

    console.log("data",data)
    // console.log("------documentArray-----", "-----imgArray-----", imgArray);
    // return false;

        axios
          .put(BASE_URL + `broadcast/` + broadcast.brodcastId + `/`, data, options)
          .then(() => {
            const placement = "bottomLeft";
            api.info({
              description: BROADCAST_MESSAGE_UPDATE,
              placement,
            });
            setLoader(false);
            window.location = "/orgcc/broadcastmessages";
          })
          .catch((error) => {
            if (error.message.includes("400")) {
              setBroadcastError(true);
              setBroadcastErrorMessage(error.response.data.title);
            }
            setLoader(false);
          });
  };

  const updateBroadcast = () => {
    setLoader(true);
    let documentArray = [];
    let imgArray = [];
    if (
      broadcast.broadCastDocument.length > 0 &&
      broadcast.broadCastImage.length > 0
    ) {
      console.log("------", JSON.stringify(broadcast));
      broadcast.broadCastDocument.forEach((value) => {
        if (value.uid.includes("broadcastdoc")) {
          docArray.forEach((doc) => {
            if (doc.uid === value.uid) {
              documentArray.push(doc);
            }
          });
        } else {
          documentArray.push({ name: value.name, url: value.base64 });
        }
      });

      broadcast.broadCastImage?.forEach((item) => {
        if (item.uid.includes("broadcastimg")) {
          imageArray.forEach((img) => {
            if (img.uid === item.uid) {
              imgArray.push(img);
            }
          });
        } else {
          imgArray.push({ name: item.name, url: item.base64 });
        }
      });

      postUpdateCall(documentArray, imgArray);
    } else if (
      broadcast.broadCastDocument.length === 0 &&
      broadcast.broadCastImage.length > 0
    ) {
      broadcast.broadCastImage?.forEach((item) => {
        if (item.uid.includes("broadcastimg")) {
          imageArray.forEach((img) => {
            if (img.uid === item.uid) {
              imgArray.push(img);
            }
          });
        } else {
          imgArray.push({ name: item.name, url: item.base64 });
        }
      });
      postUpdateCall(documentArray, imgArray);
    } else if (
      broadcast.broadCastDocument.length > 0 &&
      broadcast.broadCastImage.length === 0
    ) {
      broadcast.broadCastDocument.forEach((value) => {
        if (value.uid.includes("broadcastdoc")) {
          docArray.forEach((doc) => {
            if (doc.uid === value.uid) {
              documentArray.push(doc);
            }
          });
        } else {
          documentArray.push({ name: value.name, url: value.base64 });
        }
        postUpdateCall(documentArray, imgArray);
      });
    } else {
      postUpdateCall(documentArray, imgArray);
    }
  };

  const onCancel = () => {
    window.location = "/orgcc/broadcastmessages";
  };

  const deleteAttachments = async (file, type) =>{
    setLoader(true);
    console.log("--broadCastImageArray---", file, type);
    let data = {}
    data =  type == "pollImageValidation" || type == "broadCastImage" ? 
    { broadcast_id: broadcast.brodcastId,
      broadcast_image: {
        name: file.name
      }
    } : { broadcast_id: broadcast.brodcastId,
      broadcast_document: {
        name: file.name
      }
    };
    console.log("data",data)

    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
          axios
          .post(BASE_URL + `deleteAttachment/`, data, options)
          .then((response) => {
            console.log("response",response)

            const placement = "bottomLeft";
            api.info({
              description: DELETE_ATTACHMENT_UPLOADED_FILE,
              placement,
            });
            setDisabledbtn(false);
            setLoader(false);
          })
          .catch((error) => {
            setDisabledbtn(false);
            console.log("error",error)
            if (error.message.includes("400")) {
              //setBroadcastError(true);
              //setBroadcastErrorMessage("Something went wrong while deleting uploaded files");
            }
            setLoader(false);
          });
  }

  return (
    <>
      {contextHolder}
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content>
                <Spin spinning={loader} size="large">
                  <Row className="py-3">
                    <h1 className="mb-0">{EDIT_BROADCAST_MESSAGE}</h1>
                  </Row>
                  <div className="mid-content">
                    <Row>
                      <Col span={24}>
                        <Form
                          className="input-md floating-label w-100"
                          name="validate_other"
                          form={form}
                        >
                          {error && (
                            <Form.Item>
                              <Alert
                                message={REQUIRED_MESSAGE}
                                type="error"
                                showIcon
                              ></Alert>
                            </Form.Item>
                          )}

                          {broadcastError && (
                            <Form.Item>
                              <Alert
                                message={broadcastErrorMessage}
                                type="error"
                                showIcon
                              ></Alert>
                            </Form.Item>
                          )}
                          <Col span={24}>
                            <Row>
                              <Col span={24} md={18} className="gutter-row">
                                <FloatLabel
                                  label="Message Title"
                                  value={broadcast.messageTittle}
                                >
                                  <Form.Item
                                    className="required"
                                    name="messageTittle"
                                    id="messageTittle"
                                    validateStatus="success"
                                  >
                                    <TextArea
                                      showCount
                                      className="input-control"
                                      id="messageTittle"
                                      name="messageTittle"
                                      htmlFor="helptext"
                                      autoComplete={"off"}
                                      value={broadcast.messageTittle}
                                      maxLength={80}
                                      onChange={(e) => {
                                        onBroadcastChange(e);
                                      }}
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                            </Row>
                          </Col>
                          <Col span={24}>
                            <Row>
                              <Col span={24} md={18} className="gutter-row">
                                <FloatLabel
                                  label="Message"
                                  value={broadcast.meesage}
                                >
                                  <Form.Item
                                    className="required"
                                    name="message"
                                    validateStatus="success"
                                  >
                                    <RichTextEditor
                                      value={broadcast.meesage}
                                      onChange={(content) =>
                                        onBroadMessageChange(content, "meesage")
                                      }
                                      id="message"
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                            </Row>
                          </Col>
                          <Col span={24} className="gutter-row">
                            <Row
                              className="align-items-center d-flex"
                              gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}
                            >
                              <Col
                                span={24}
                                md={9}
                                lg={6}
                                className="gutter-row"
                              >
                                <FloatLabel
                                  label="Select Date"
                                  value={startDate}
                                >
                                  <Form.Item
                                    className="required mb-0"
                                    name="startDate"
                                  >
                                    <DatePicker
                                      defaultValue={startDate}
                                      disabledDate={disabledDate}
                                      name="startDate"
                                      onChange={(date) =>
                                        onChangeStartDate(date, "startDate")
                                      }
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                              {broadcast.pastStartTimeError && (
                                <Alert
                                  message={PAST_TIME}
                                  type="error"
                                  showIcon
                                ></Alert>
                              )}
                              <Col
                                span={24}
                                md={9}
                                lg={6}
                                className="gutter-row"
                              >
                                <FloatLabel
                                  label="Select Time"
                                  value={startTime}
                                >
                                  <Form.Item
                                    className="required timepicker mb-0"
                                    name="time"
                                  >
                                    <></>
                                    <TimePicker
                                      value={startTime}
                                      onChange={(value) =>
                                        onChangeStartTime(value)
                                      }
                                      onBlurStartTime={(value) =>
                                        onBlurStartTime(value)
                                      }
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                              <Col span={24} className="6gutter-row mt-3">
                                {!uploadError && (
                                  <Form.Item>
                                    <Alert
                                      message={UPLOAD_FORMAT_ERROR}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  </Form.Item>
                                )}

                                <h6>{UPLOAD}</h6>
                                <span>{UPLOAD_NOTE}</span>
                                <Form.Item
                                  name="help text"
                                  className="upload-list mt-3"
                                >
                                  <Row>
                                    <UploadBroadcast
                                      file={broadcast.broadCastImage}
                                      handleStepOneUpload={handleStepOneUpload}
                                      handleBeforeUpload={handleBeforeUpload}
                                      handleOnRemove={handleOnRemove}
                                      handlePollImagePreview={
                                        handlePollImagePreview
                                      }
                                      handleCancel={handleCancel}
                                      broadcast={broadcast}
                                      uploadtitle={IMAGE}
                                      disabledbutton={disabledbtn}
                                      countDocument = {countUploadDocument}
                                    />

                                    <UploadBroadcast
                                      file={broadcast.broadCastDocument}
                                      handleStepOneUpload={
                                        handleStepDocumentUpload
                                      }
                                      handleBeforeUpload={handleBeforeDocUpload}
                                      handleOnRemove={handleOnRemove}
                                      handlePollImagePreview={
                                        handlePollImagePreview
                                      }
                                      handleCancel={handleCancel}
                                      broadcast={broadcast}
                                      uploadtitle={DOCUMENT}
                                      disabledbutton={disabledbtn}
                                      countDocument = {countUploadDocument}
                                    />
                                    <Location
                                      setLocation={setLocation}
                                      longitude={broadcast.location_longitude}
                                      latitude={broadcast.location_latitude}
                                      locationName={
                                        broadcast.mapLoction.address
                                      }
                                      isEdit={true}
                                    />
                                  </Row>
                                </Form.Item>
                              </Col>
                            </Row>
                            <div className="mt-5">
                              <Button
                                type="primary"
                                className="md mr-4"
                                onClick={onUpdate}
                              >
                                {UPDATE}
                              </Button>
                              <Button
                                type="secondary"
                                type="secondary"
                                htmlType="submit"
                                onClick={onCancel}
                              >
                                {CANCEL}
                              </Button>
                            </div>
                          </Col>
                        </Form>
                      </Col>
                    </Row>
                  </div>
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </>
  );
};

export default EditBroadcast;
