import React, { Component } from "react";
import { Modal } from "antd";
import "../modal/modal.scss";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import {
  DELETE,
  DELETE_HOTLINE,
  NO_TEXT_MODAL,
  YES_TEXT_MODAL,
} from "../../headings";
import { DELETE_CONFIRM_HOTLINE } from "../../messages";

class DeleteHotLineModal extends Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.deleteHotline();
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  deleteHotline() {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .delete(BASE_URL + "hotline/" + this.props.hotlineId, options)
      .then(() => {
        window.location = "/orgcc/hotlinedirectory";
      })
      .catch((error) => {});
  }

  render() {
    return (
      <>
        <a onClick={this.showModal}>{DELETE}</a>
        <Modal
          title={DELETE_HOTLINE}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={370}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
          }}
          okText={YES_TEXT_MODAL}
          cancelText={NO_TEXT_MODAL}
        >
          <p>{DELETE_CONFIRM_HOTLINE}</p>
        </Modal>
      </>
    );
  }
}

export default DeleteHotLineModal;
