import React, { useEffect, useState } from "react";
import { Bar } from "@reactchartjs/react-chart.js";

const BarChart = (props) => {
  const [data, setData] = useState({
    labels: [],
    datasets: [],
  });
  const [options, setOptions] = useState({
    title: {
      display: true,
      text: props.chartTitle,
    },
  });

  useEffect(() => {
    setOptions({
      title: {
        display: true,
        text: props.chartTitle,
      },
      legend: {
        onClick: null,
      },
      scales: {
        xAxes: [
          {
            title: {
              display: true,
              text: props.chartTitle,
            },
          },
        ],
        yAxes: [
          {
            title: {
              display: true,
              text: props.chartTitle,
            },
          },
        ],
      },
    });
    let finalData = {};
    let sets = [];
    let labelsData = [];
    let yAxisData = [];
    let recordData = [];
    let count = [];
    const colors = [
      "rgba(234, 80, 55)",
      "rgba(32, 50, 92)",
      "rgba(96,110,141,1)",
      "rgba(236,131,123,1)",
    ];
    props.data?.forEach((value) => {
      yAxisData.push({ [value.poll_option_id.toString()]: 0 });
      count.push(value.count);
    });
    props.data?.map((value, key) => {
      let finalYAxisData = [];
      yAxisData.map((item, index) => {
        if (JSON.parse(Object.keys(item)) === value.poll_option_id) {
          yAxisData[index] = { [value.poll_option_id]: value.count };
          finalYAxisData.push(value.count);
        } else {
          finalYAxisData.push(0);
        }
      });
      let set = {
        label: value.option_text,
        data: finalYAxisData,
        backgroundColor: colors[key],
        borderWidth: 0,
      };
      labelsData.push(value.option_text);
      sets.push(set);
      recordData = finalYAxisData;
    });
    finalData = {
      ...data,
      labels: labelsData,
      datasets: sets,
    };
    setData(finalData);
    props.sendBackData(labelsData, count);
  }, [props.data]);

  const random_hex_color_code = (string) => {
    var hash = 0;
    for (var i = 0; i < string.length; i++) {
      hash = string.charCodeAt(i) + ((hash << 5) - hash);
    }
    var colour = "#";
    for (var i = 0; i < 3; i++) {
      var value = (hash >> (i * 8)) & 0xff;
      colour += ("00" + value.toString(16)).substr(-2);
    }
    return colour;
  };

  return (
    <>
      <Bar id="barChart" data={data} options={options} />
    </>
  );
};

export default BarChart;
