import React from "react";
import { Link } from "react-router-dom";
import { Layout, Menu } from "antd";
const { Sider } = Layout;
import { MenuUnfoldOutlined, MenuFoldOutlined } from "@ant-design/icons";
import { HOTLINE_REPORTS, OPENLINE_REPORTS, POLL_RESULTS, BROADCAST_MESSAGES, HELP, INFO } from "../../headings";

class IdbSidebar extends React.Component {
  state = {
    collapsed: false,
  };

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    let defaultKey = "";
    if (this.props.data?.location) {
      if (this.props.data.location.pathname.includes("hotlinereports")) {
        defaultKey = "1";
      } else if (
        this.props.data.location.pathname.includes("openlinereports")
      ) {
        defaultKey = "2";
      }else if  (this.props.data.location.pathname.includes("polls")){
          defaultKey = "3";
      }else if  (this.props.data.location.pathname.includes("Broadcast")){
        defaultKey = "4";
      }else if  (this.props.data.location.pathname.includes("help")){
        defaultKey = "5";
      }else if  (this.props.data.location.pathname.includes("info")){
        defaultKey = "6";
      }
    }
    return (
      <>
        {React.createElement(
          this.state.collapsed ? MenuFoldOutlined : MenuUnfoldOutlined,
          {
            className: "trigger d-lg-none d-block",
            onClick: this.toggle,
          }
        )}
        <Sider
          className={
            this.state.collapsed
              ? "d-lg-block w-md-100 sider-width"
              : " d-none d-lg-block w-md-100 sider-width"
          }
          trigger={null}
          collapsible
          collapsed={this.state.collapsed}
        >
          <Menu theme="dark" mode="inline" defaultSelectedKeys={[defaultKey]}>
            <Menu.Item
              key="1"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-manage-org" />
                </span>
              }
            >
              <Link to="/idb/hotlinereports">{HOTLINE_REPORTS}</Link>
            </Menu.Item>
            <Menu.Item
              key="2"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-hotlines" />
                </span>
              }
            >
              {" "}
              <Link to="/idb/openlinereports">{OPENLINE_REPORTS}</Link>
            </Menu.Item>
            <Menu.Item
              key="3"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-polls" />
                </span>
              }
            >
              {" "}
              <Link to="/idb/pollsdirectory">{POLL_RESULTS}</Link>
            </Menu.Item>
            <Menu.Item
              key="4"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-broadcast" />
                </span>
              }
            >
              <Link to="/idb/broadcastmessages">{BROADCAST_MESSAGES}</Link>
            </Menu.Item>
            <Menu.Item
              key="5"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-help" />
                </span>
              }
            >
              <Link to="/idb/help">{HELP}</Link>
            </Menu.Item>
            <Menu.Item
              key="6"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-info" />
                </span>
              }
            >
              <Link to="/idb/info">{INFO}</Link>
            </Menu.Item>
          </Menu>
          <div className="btm-div">
            <Link to="/idb/info" className="px-2">
              {INFO}
            </Link>
            <Link to="/idb/help" className="px-2">
              {HELP}
            </Link>
          </div>
        </Sider>
      </>
    );
  }
}

export default IdbSidebar;
