import React, { useState, useEffect } from "react";
import {
  Layout,
  Row,
  Col,
  Input,
  Avatar,
  Form,
  DatePicker,
  Button,
  Select,
  Alert,
  Spin,
  notification,
} from "antd";
import { CheckCircleTwoTone } from "@ant-design/icons";
import { useHistory } from "react-router-dom";
import moment from "moment";
import EditSeededUsers from "./EditSeededUsers";
import AddNewSeededUser from "./AddNewSeededUser";
import DeleteModal from "../../components/modal/DeleteModal";
import { LoggedinHeader } from "../../containers";
import "../manageOrg/manageOrg.scss";
import AccSidebar from "../manageOrg/AccSidebar";
import FloatLabel from "./FloatLabels";
import AssignedUsers from "../manageOrg/AssignedUsers";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import authHeader from "../../services/auth-header";
import {
  DELETE,
  EDIT_ORGANIZATION,
  UPDATE,
  CANCEL,
  EDIT_SEEDED_USERS,
  ADD_SEEDED_USER,
  NO_SEEDED_USERS,
  SAVE_AS_DRAFT_BTN,
} from "../../headings";
import {
  MAX_CHAR_LIMIT,
  ORGANIZATION_ID_ALREADY_EXISTS,
  ORGANIZATION_NAME_ALREADY_EXISTS,
  REQUIRED_MESSAGE,
  STATUS_NOT_CHANGED,
} from "../../errors";
import { DATE, ORGANIZATION_ID, STATUS } from "../../labels";
import { STATUS_ACTIVE, STATUS_INACTIVE } from "../../values";
import {
  ADD_SEEDED_USER_NOTIFICATION,
  EDIT_SEEDED_USER_NOTIFICATION_PART1,
  EDIT_SEEDED_USER_NOTIFICATION_PART2,
  DELETE_SEEDED_USER_NOTIFICATION,
} from "../../messages";

const { Content } = Layout;

const EditOrg = (props) => {
  const [api, contextHolder] = notification.useNotification();
  const history = useHistory();
  const [form] = Form.useForm();
  const [loader, setLoader] = useState(false);
  const [statusClicked, setStatusClicked] = useState(false);
  const orgID = props.computedMatch?.params.id;
  const [error, showError] = useState(false);
  const [modalError, showModalError] = useState(false);
  const [emailValidationError, setEmailValidationError] = useState(false);
  const [mobileValidationError, setMobileValidationError] = useState(false);
  const [passwordValidationError, showPasswordValidationError] = useState(
    false
  );
  const [orgNameAlreadyExists, setOrgNameAlreadyExists] = useState("");
  const [orgIDAlreadyExists, setOrgIDAlreadyExists] = useState("");
  const [organizationName, setOrganizationName] = useState("");
  const [organizationIdentifier, setOrganizationIdentifier] = useState("");
  const [seededUserName, setSeededUserName] = useState("");
  const [seededUserEmail, setSeededUserEmail] = useState("");
  const [organizationDetails, setOrganizationDetails] = useState({
    name: "",
    identifier: "",
    org_id: "",
    status: "",
    date1: "",
  });
  const [seededUserDetails, setSeededUserDetails] = useState({
    firstName: "",
    lastName: "",
    status2: "",
    email: "",
    //  password: "",
    //date2: "",
    mobile: "",
    startDate:"",
    endDate:""
  });
  const [seededUsers, setSeededUsers] = useState([]);
  const [editVisible, setEditVisible] = useState(false);
  const [showAddSeededUser, setShowAddSeededUser] = useState(false);
  const [deleteClick, setDeleteClick] = useState(false);
  const [orgNameCount, setOrgNameCount] = useState(false);
  const [orgIdCount, setOrgIDCount] = useState(false);
  const [fNameCount, setFNameCount] = useState(false);
  const [lNameCount, setLNameCount] = useState(false);
  const [orgStatus, setOrgStatus] = useState("");

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    axios.get(BASE_URL + `getOrganization/${orgID}/`, options).then((res) => {
      const data = res?.data;
      setOrganizationDetails({
        name: data.organization.name,
        identifier: data.organization.identifier,
        status: data.organization.status_id.toString(),
        date1: [
          data.organization.start_date ? moment(data.organization.start_date, "YYYY-MM-DD") : "",
          data.organization.end_date ? moment(data.organization.end_date, "YYYY-MM-DD") : "",
        ],
      });
      setOrganizationName(data.organization.name);
      setOrganizationIdentifier(data.organization.identifier);
      setOrgStatus(data.organization.status_id.toString());
      form.setFieldsValue({
        name: data.organization.name,
        identifier: data.organization.identifier,
      });
      let appendedUsers = [];
      data.seeded_user.map((value) => {
        if (!value.soft_delete) {
          appendedUsers = [
            ...appendedUsers,
            {
              firstName: value.first_name,
              lastName: value.last_name,
              status2: value.status_id.toString(),
              email: value.email,
              startDate: moment(value.start_date, "YYYY-MM-DD"),
              endDate : moment(value.end_date).isValid() && moment(value.end_date, "YYYY-MM-DD"),
              mobile: value.mobile_number,
            },
          ];
        }
      });
      setLoader(false);
      setSeededUsers(appendedUsers);
    });
  }, [statusClicked]);

  const onDelete = () => {
    setDeleteClick(true);
  };

  const CheckOrganizationName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (e.target.value !== organizationName) {
      axios
        .post(
          BASE_URL + "checkOrganization/",
          { organization_name: organizationDetails.name },
          options
        )
        .then((res) => {
          if (res.data) {
            setOrgNameAlreadyExists(res.data[0]);
          }
        });
    } else {
      setOrgNameAlreadyExists(false);
    }
  };

  const CheckOrganizationID = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (e.target.value !== organizationIdentifier) {
      axios
        .post(
          BASE_URL + "checkOrganizationID/",
          { orgId: organizationDetails.identifier },
          options
        )
        .then((res) => {
          if (res.data) {
            setOrgIDAlreadyExists(res.data[0]);
          }
        });
    } else {
      setOrgIDAlreadyExists(false);
    }
  };

  const handleEditCancel = () => {
    setEditVisible(false);
  };

  const handleAddSeededUserCancel = () => {
    setShowAddSeededUser(false);
  };

  const handleEditOk = () => {
    if (
      seededUserDetails.firstName &&
      seededUserDetails.lastName &&
      seededUserDetails.status2 &&
      seededUserDetails.mobile &&
      seededUserDetails.startDate &&
      seededUserDetails.email
    ) {
      showModalError(false);
      if (!emailValidationError && !mobileValidationError) {
        editUser();
      }
    } else {
      showModalError(true);
    }
  };

  const editUser = () => {
    seededUsers.forEach((user) => {
      if (
        JSON.stringify(user) !== JSON.stringify(seededUserDetails) &&
        user.email === seededUserDetails.email
      ) {
        let index = seededUsers.indexOf(user);
        let users = JSON.parse(JSON.stringify(seededUsers));
        users[index] = seededUserDetails;
        const finalUsers = [];
        users.map((value) => {
          value = {
            ...value,
            startDate : moment(value.startDate, "YYYY-MM-DD"),
            endDate : moment(value.endDate).isValid() && moment(value.endDate, "YYYY-MM-DD")
          };
          finalUsers.push(value);
        });
        setSeededUsers(finalUsers);
      }
    });
    onSeededUserEditSubmit();
  };

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  const handleOrganizationOnChange = (e) => {
    if (e.target.id === "identifier" && e.target.value.length === 20) {
      setOrgIDCount(true);
    } else if (e.target.id === "name" && e.target.value.length === 80) {
      setOrgNameCount(true);
    } else {
      setOrgNameCount(false);
      setOrgIDCount(false);
    }
    setOrganizationDetails({
      ...organizationDetails,
      [e.target.id]: e.target.value,
    });
  };

  const handleOrganizationOnSelect = (value, name) => {
    setOrganizationDetails({ ...organizationDetails, [name]: value });
  };

  const handleOrganizationDatePickerChange = (date, name) => {
    setOrganizationDetails({ ...organizationDetails, [name]: date });
  };

  const handleSeededUserOnChange = (e) => {
    if (e.target.id === "firstName" && e.target.value.length === 50) {
      setFNameCount(true);
    } else if (e.target.id === "lastName" && e.target.value.length === 50) {
      setLNameCount(true);
    } else {
      setFNameCount(false);
      setLNameCount(false);
    }
    setSeededUserDetails({
      ...seededUserDetails,
      [e.target.id]: e.target.value,
    });
  };

  const handleMobile = (mobile) => {
    setSeededUserDetails({
      ...seededUserDetails,
      mobile: mobile,
    });
  };

  const handleSeededUserOnSelect = (value, name) => {
    setSeededUserDetails({ ...seededUserDetails, [name]: value });
  };

  const handleSeededUserDatePickerChange = (date, name) => {
    setSeededUserDetails({ ...seededUserDetails, [name]: date });
  };

  const validateEmailAddress = (e) => {
    var re = /\S+@\S+\.\S+/;
    if (!re.test(e.target.value)) {
      setEmailValidationError(true);
    } else {
      setEmailValidationError(false);
    }
  };

  const validateMobile = (e) => {
    var regex = /^\+\d$/;
    if (seededUserDetails.mobile.length > 10 || regex.test(e.target.value)) {
      setMobileValidationError(false);
    } else {
      setMobileValidationError(true);
    }
  };

  const validatePassword = (e) => {
    var re = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,}$/;
    if (!re.test(e.target.value)) {
      showPasswordValidationError(true);
    } else {
      showPasswordValidationError(false);
    }
  };

  const handleCancelDelete = () => {
    setDeleteClick(false);
  };

  const handleDeleteOrganization = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .delete(BASE_URL + `deleteOrganization/${orgID}/`, options)
      .then(() => {
        window.location = "/amcc/manageorganizations";
      });
  };

  const handleResetPasswordOk = () => {};

  const handleResetPasswordCancel = () => {};

  const onOrgEditSubmit = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      organization_id: orgID,
      name: organizationDetails.name,
      identifier: organizationDetails.identifier,
      start_date: organizationDetails.date1[0]
        ? moment(organizationDetails.date1[0]).format("DD-MM-YYYY")
        : "",
      end_date: organizationDetails.date1[1]
        ? moment(organizationDetails.date1[1]).format("DD-MM-YYYY")
        : "",
      status_id: 1,
    };
    const endDate = moment(organizationDetails.date1[1]).format("MM-DD-YYYY");
    // if (organizationDetails.status !== orgStatus) {
      // if (new Date() < new Date(endDate)) {
        if (organizationDetails.name && organizationDetails.identifier) {
          if (
            (orgNameAlreadyExists === "status: false" ||
              organizationDetails.name === organizationName) &&
            (orgIDAlreadyExists === "status: false" ||
              organizationDetails.identifier === organizationIdentifier)
          ) {
            showError(false);
            axios
              .put(BASE_URL + "saveOrganization/", data, options)
              .then(() => {
                window.location = "/amcc/manageorganizations";
              });
          }
        } else {
          showError(true);
        }
      // } else {
      //   const placement = "bottomLeft";
      //   api.info({
      //     description: STATUS_NOT_CHANGED,
      //     placement,
      //   });
      // }
    // } else {
    //   if (organizationDetails.name && organizationDetails.identifier) {
    //     showError(false);
    //     setLoader(true);
    //     axios.put(BASE_URL + "saveOrganization/", data, options).then(() => {
    //       window.location = "/amcc/manageorganizations";
    //     });
    //   } else {
    //     showError(true);
    //   }
    // }
  };

  const onOrgSaveAsDraft = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      organization_id: orgID,
      name: organizationDetails.name,
      identifier: organizationDetails.identifier,
      start_date: organizationDetails.date1[0]
        ? moment(organizationDetails.date1[0]).format("DD-MM-YYYY")
        : null,
      end_date: organizationDetails.date1[1]
        ? moment(organizationDetails.date1[1]).format("DD-MM-YYYY")
        : null,
      status_id: 2,
    };
    const endDate = moment(organizationDetails.date1[1]).format("MM-DD-YYYY");
    if (organizationDetails.status !== orgStatus) {
      if (new Date() < new Date(endDate)) {
        if (organizationDetails.name && organizationDetails.identifier) {
          if (
            (orgNameAlreadyExists === "status: false" ||
              organizationDetails.name === organizationName) &&
            (orgIDAlreadyExists === "status: false" ||
              organizationDetails.identifier === organizationIdentifier)
          ) {
            showError(false);
            axios
              .put(BASE_URL + "saveOrganization/", data, options)
              .then(() => {
                window.location = "/amcc/manageorganizations";
              });
          }
        } else {
          showError(true);
        }
      } else {
        const placement = "bottomLeft";
        api.info({
          description: STATUS_NOT_CHANGED,
          placement,
        });
      }
    } else {
      if (organizationDetails.name && organizationDetails.identifier) {
        showError(false);
        setLoader(true);
        axios.put(BASE_URL + "saveOrganization/", data, options).then(() => {
          window.location = "/amcc/manageorganizations";
        });
      } else {
        showError(true);
      }
    }
  };


  const onSeededUserEditSubmit = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = seededUserDetails.endDate ? {
      first_name: seededUserDetails.firstName,
      last_name: seededUserDetails.lastName,
      email: seededUserEmail,
      //email: seededUserDetails.email,
      mobile_number: seededUserDetails.mobile,
      status_id: parseInt(seededUserDetails.status2),
      start_date: seededUserDetails.startDate
        ? moment(seededUserDetails.startDate).format("DD-MM-YYYY")
        : "",
      end_date: seededUserDetails.endDate
        ? moment(seededUserDetails.endDate).format("DD-MM-YYYY")
        : "",
      organization_id: parseInt(orgID),
    } : {
      first_name: seededUserDetails.firstName,
      last_name: seededUserDetails.lastName,
      email: seededUserEmail,
      //email: seededUserDetails.email,
      mobile_number: seededUserDetails.mobile,
      status_id: parseInt(seededUserDetails.status2),
      start_date: seededUserDetails.startDate
        ? moment(seededUserDetails.startDate).format("DD-MM-YYYY")
        : "",
      organization_id: parseInt(orgID),
    };
    setLoader(true);
    axios.put(BASE_URL + "seededUser/", data, options).then(() => {
      const placement = "bottomLeft";
      api.info({
        description:
          EDIT_SEEDED_USER_NOTIFICATION_PART1 +
          seededUserName +
          EDIT_SEEDED_USER_NOTIFICATION_PART2,
        placement,
      });
      setLoader(false);
      setEditVisible(false);
    });
  };

  const cancelEditing = () => {
    if (props.location.state) {
      const path = `/amcc/orgdetails/${orgID}`;
      history.push(path);
    } else {
      window.location = "/amcc/manageorganizations";
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const handleEditSeededUser = (email) => {
    setEditVisible(true);
    showModalError(false);
    setFNameCount(false);
    setLNameCount(false);
    setEmailValidationError(false);
    setMobileValidationError(false);
    let editableUser = {};
    seededUsers.forEach((seededUser) => {
      if (seededUser.email === email) {
        editableUser = seededUser;
      }
    });
    setSeededUserDetails(editableUser);
    setSeededUserName(editableUser.firstName);
    setSeededUserEmail(editableUser.email);
  };

  const addSeededUser = () => {
    setSeededUserDetails({
      firstName: "",
      lastName: "",
      status2: "",
      email: "",
      // password: "",
      //date2: "",
      mobile: "",
      startDate:"",
      endDate:""
    });
    showModalError(false);
    setFNameCount(false);
    setLNameCount(false);
    setShowAddSeededUser(true);
  };

  const handleAddSeededUser = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const newSeededUser = seededUserDetails.endDate ? {
      email: seededUserDetails.email,
      mobile_number: "+" + seededUserDetails.mobile,
      //  password: seededUserDetails.password,
      is_seeded_user: 1,
      first_name: seededUserDetails.firstName,
      last_name: seededUserDetails.lastName,
      start_date: seededUserDetails.startDate
        ? moment(seededUserDetails.startDate).format("DD-MM-YYYY")
        : "",
      end_date: seededUserDetails.endDate
        ? moment(seededUserDetails.endDate).format("DD-MM-YYYY")
        : "",
      status_id: parseInt(seededUserDetails.status2),
      organization_id: parseInt(orgID),
    } : {
      email: seededUserDetails.email,
      mobile_number: "+" + seededUserDetails.mobile,
      //  password: seededUserDetails.password,
      is_seeded_user: 1,
      first_name: seededUserDetails.firstName,
      last_name: seededUserDetails.lastName,
      start_date: seededUserDetails.startDate
        ? moment(seededUserDetails.startDate).format("DD-MM-YYYY")
        : "",
      status_id: parseInt(seededUserDetails.status2),
      organization_id: parseInt(orgID),
    };
    const data = {
      seeded_users: [newSeededUser],
    };
    const appendedUsers = [
      ...seededUsers,
      {
        firstName: newSeededUser.first_name,
        lastName: newSeededUser.last_name,
        status2: newSeededUser.status_id.toString(),
        email: newSeededUser.email,
        startDate : moment(newSeededUser.start_date, "YYYY-MM-DD"),
        endDate : moment(newSeededUser.end_date).isValid() && moment(newSeededUser.end_date, "YYYY-MM-DD"),
        mobile: newSeededUser.mobile_number,
      },
    ];

    if (
      seededUserDetails.firstName &&
      seededUserDetails.lastName &&
      seededUserDetails.status2 &&
      seededUserDetails.mobile &&
      seededUserDetails.startDate &&
      seededUserDetails.email
    ) {
      showModalError(false);
      if (
        !emailValidationError &&
        !mobileValidationError &&
        !passwordValidationError
      ) {
        setSeededUsers(appendedUsers);
        setLoader(true);
        axios.post(BASE_URL + "seededUser/", data, options).then((res) => {
          const placement = "bottomLeft";
          api.info({
            description:
              seededUserDetails.firstName + ADD_SEEDED_USER_NOTIFICATION,
            placement,
          });
          setLoader(false);
          setShowAddSeededUser(false);
        });
      }
    } else {
      showModalError(true);
    }
  };

  const handleDeleteSeededUser = (email) => {
    let filteredUsers = seededUsers?.filter((user) => user?.email !== email);
    let userName = "";
    seededUsers.map((value) => {
      if (email === value.email) userName = value.firstName;
    });
    setSeededUsers(filteredUsers);
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.delete(BASE_URL + `seededUser/${email}`, options).then(() => {
      const placement = "bottomLeft";
      api.info({
        description: userName + DELETE_SEEDED_USER_NOTIFICATION,
        placement,
      });
    });
  };

  const { RangePicker } = DatePicker;

  return (
    <>
      {contextHolder}
      <Row className="inner-header">
        <LoggedinHeader amCCUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <AccSidebar />

            <Content>
              <Row className="py-3">
                <h1 className="mb-0">{EDIT_ORGANIZATION}</h1>
              </Row>

              <Spin spinning={loader} size="large">
                <div className="mid-content">
                  <Row>
                    <Col span={24}>
                      <Col
                        span={24}
                        className="d-flex justify-content-between align-items-center"
                      >
                        <h6 className="py-3 mb-0">{EDIT_ORGANIZATION}</h6>
                        <div>
                          <Button
                            type="link"
                            className="link pl-0 btn-link-primary ml-3 deleteOrg"
                            onClick={onDelete}
                          >
                            <span className="icon icon-delete" />
                            <span className="d-none d-md-inline-flex deleteOrg">
                              {DELETE}
                            </span>
                          </Button>
                          {deleteClick ? (
                            <DeleteModal
                              org={organizationName}
                              handleDelete={handleDeleteOrganization}
                              handleCancel={handleCancelDelete}
                              deleteOrg
                            />
                          ) : (
                            <></>
                          )}
                        </div>
                      </Col>
                      <div className="avatar-wrapper pt-2 pb-4">
                        <Avatar>{organizationName.charAt(0)}</Avatar>
                        <span className="md-name">{organizationName}</span>
                      </div>
                      <Form
                        className="input-md floating-label w-100"
                        form={form}
                      >
                        {error && (
                          <Form.Item>
                            <Alert
                              message={REQUIRED_MESSAGE}
                              type="error"
                              showIcon
                            ></Alert>
                          </Form.Item>
                        )}
                        <Col span={24}>
                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label="Organization Name"
                                value={organizationDetails.name}
                              >
                                <Form.Item
                                  className="required edit-input"
                                  name="name"
                                  id="name"
                                >
                                  <Input
                                    className="input-control"
                                    id="name"
                                    name="name"
                                    className="required"
                                    defaultValue={organizationDetails.name}
                                    value={organizationDetails.name}
                                    onChange={(e) =>
                                      handleOrganizationOnChange(e)
                                    }
                                    onBlur={(e) => CheckOrganizationName(e)}
                                    htmlFor="name"
                                    autoComplete="off"
                                    maxLength={80}
                                  />
                                  {orgNameAlreadyExists === "status: false" &&
                                    organizationDetails.name
                                      .replace(/^\s*/, "")
                                      .replace(/\s*$/, "").length > 0 &&
                                    organizationDetails.name.length > 0 && (
                                      <CheckCircleTwoTone
                                        className="successCheck"
                                        twoToneColor="#52c41a"
                                      />
                                    )}
                                  {orgNameAlreadyExists == "status: true" && (
                                    <Alert
                                      message={ORGANIZATION_NAME_ALREADY_EXISTS}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                                {orgNameCount && (
                                  <Form.Item>
                                    <Alert
                                      message={MAX_CHAR_LIMIT}
                                      type="error"
                                    ></Alert>
                                  </Form.Item>
                                )}
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={ORGANIZATION_ID}
                                value={organizationDetails.identifier}
                              >
                                <Form.Item
                                  className="required edit-input"
                                  name="identifier"
                                  id="identifier"
                                >
                                  <Input
                                    className="input-control edit-input"
                                    name="identifier"
                                    id="identifier"
                                    htmlFor="identifier"
                                    className="required"
                                    defaultValue={
                                      organizationDetails.identifier
                                    }
                                    value={organizationDetails.identifier}
                                    onChange={(e) =>
                                      handleOrganizationOnChange(e)
                                    }
                                    onBlur={(e) => CheckOrganizationID(e)}
                                    autoComplete="off"
                                    maxLength={20}
                                  />
                                  {orgIDAlreadyExists === "status: false" &&
                                    organizationDetails.identifier
                                      .replace(/^\s*/, "")
                                      .replace(/\s*$/, "").length > 0 &&
                                    organizationDetails.identifier.length >
                                      0 && (
                                      <CheckCircleTwoTone
                                        className="successCheck"
                                        twoToneColor="#52c41a"
                                      />
                                    )}
                                  {orgIDAlreadyExists == "status: true" && (
                                    <Alert
                                      message={ORGANIZATION_ID_ALREADY_EXISTS}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                                {orgIdCount && (
                                  <Form.Item>
                                    <Alert
                                      message={MAX_CHAR_LIMIT}
                                      type="error"
                                    ></Alert>
                                  </Form.Item>
                                )}
                              </FloatLabel>
                            </Col>
                            {/* <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={STATUS}
                                value={organizationDetails.status}
                              >
                                <Form.Item
                                  className="required edit-input"
                                  name="status"
                                >
                                  <>
                                    <Select
                                      className="input-control w-100 w-lg-100"
                                      value={organizationDetails.status}
                                      htmlFor="status"
                                      onSelect={(value) =>
                                        handleOrganizationOnSelect(
                                          value,
                                          "status"
                                        )
                                      }
                                    >
                                      <Option value="1">{STATUS_ACTIVE}</Option>
                                      <Option value="2">
                                        {STATUS_INACTIVE}
                                      </Option>
                                    </Select>
                                  </>
                                </Form.Item>
                              </FloatLabel>
                            </Col> */}
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel label={DATE} value="date1">
                                <Form.Item className="input-control datepicker-wrapper required">
                                  <RangePicker
                                    className="w-100 "
                                    name="date1"
                                    value={organizationDetails.date1}
                                    onChange={(date) =>
                                      handleOrganizationDatePickerChange(
                                        date,
                                        "date1"
                                      )
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>
                        <Form.Item>
                          <div className="steps-action mt-5">
                            <Button
                              type="primary"
                              htmlType="submit"
                              onClick={onOrgEditSubmit}
                            >
                              {UPDATE}
                            </Button>
                            <Button
                              type="secondary"
                              htmlType="submit"
                              className="ml-4"
                              onClick={onOrgSaveAsDraft}
                            >
                              {SAVE_AS_DRAFT_BTN}
                            </Button>
                            <Button
                              className="ml-4"
                              type="secondary"
                              onClick={cancelEditing}
                            >
                              {CANCEL}
                            </Button>
                          </div>
                        </Form.Item>
                      </Form>
                    </Col>
                  </Row>
                </div>

                <div className="mid-content mt-4">
                  <Row>
                    <Col
                      span={24}
                      md={12}
                      className="d-flex justify-content-between align-items-center"
                    >
                      <h6 className="py-3 mb-0">{EDIT_SEEDED_USERS}</h6>
                    </Col>
                    <Col
                      span={24}
                      md={12}
                      className="d-flex justify-content-left justify-content-md-end align-items-center my-3 my-md-0"
                    >
                      <Button
                        className="mr-4"
                        type="secondary"
                        onClick={() => addSeededUser()}
                      >
                        {ADD_SEEDED_USER}
                      </Button>
                    </Col>
                    {seededUsers.length > 0 ? (
                      <AssignedUsers
                        seededUsers={seededUsers}
                        statusOnClicked={statusOnClicked}
                        orgID={parseInt(orgID)}
                        handleEditUser={handleEditSeededUser}
                        handleDelete={handleDeleteSeededUser}
                        addSeededUser={addSeededUser}
                        viaEdit
                        seededUser
                      />
                    ) : (
                      <Col span={24} lg={18}>
                        <Row className="justify-content-center">
                          {NO_SEEDED_USERS}
                        </Row>
                      </Col>
                    )}
                    {editVisible ? (
                      <EditSeededUsers
                        error={modalError}
                        handleEditOk={handleEditOk}
                        handleEditCancel={handleEditCancel}
                        seededUsers={seededUsers}
                        seededUserName={seededUserName}
                        fNameCount={fNameCount}
                        lNameCount={lNameCount}
                        seededUserDetails={seededUserDetails}
                        disabledDate={disabledDate}
                        handleSeededUserOnChange={handleSeededUserOnChange}
                        handleSeededUserDatePickerChange={
                          handleSeededUserDatePickerChange
                        }
                        handleMobile={handleMobile}
                        handleSeededUserOnSelect={handleSeededUserOnSelect}
                        validateEmailAddress={validateEmailAddress}
                        emailValidationError={emailValidationError}
                        validateMobile={validateMobile}
                        mobileValidationError={mobileValidationError}
                        handleResetPasswordOk={handleResetPasswordOk}
                        handleResetPasswordCancel={handleResetPasswordCancel}
                        loader={loader}
                      />
                    ) : showAddSeededUser ? (
                      <AddNewSeededUser
                        error={modalError}
                        handleAddSeededUser={handleAddSeededUser}
                        handleAddSeededUserCancel={handleAddSeededUserCancel}
                        seededUsers={seededUsers}
                        fNameCount={fNameCount}
                        lNameCount={lNameCount}
                        seededUserDetails={seededUserDetails}
                        disabledDate={disabledDate}
                        handleSeededUserOnChange={handleSeededUserOnChange}
                        handleSeededUserDatePickerChange={
                          handleSeededUserDatePickerChange
                        }
                        handleMobile={handleMobile}
                        handleSeededUserOnSelect={handleSeededUserOnSelect}
                        validateEmailAddress={validateEmailAddress}
                        emailValidationError={emailValidationError}
                        validatePassword={validatePassword}
                        passwordValidationError={passwordValidationError}
                        validateMobile={validateMobile}
                        mobileValidationError={mobileValidationError}
                        loader={loader}
                      />
                    ) : (
                      <></>
                    )}
                  </Row>
                </div>
              </Spin>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default EditOrg;
