import React, { useState, useEffect } from "react";
import { Pie } from "@reactchartjs/react-chart.js";

const PieChart = (props) => {
  const [data, setData] = useState({
    labels: [],
    datasets: [],
  });

  const options = {
    title: {
      display: true,
      text: props.chartTitle,
    },
    legend: {
      onClick: null,
    },
  };

  useEffect(() => {
    let finalData = {};
    let labelsData = [];
    let set = [];
    let recordData = [];
    let colors = [
      "rgba(234, 80, 55)",
      "rgba(32, 50, 92)",
      "rgba(96,110,141,1)",
      "rgba(236,131,123,1)",
    ];
    let borderColors = [];
    let percentage = [];
    props.data.map((value) => {
      labelsData.push(value.option_text);
      recordData.push(
        parseInt(value.percentage.substring(0, value.percentage.length - 1))
      );

      borderColors.push("rgba(255, 255, 255)");
      percentage.push(value.percentage);
    });
    set = [
      {
        label: "",
        data: recordData,
        backgroundColor: colors,
        borderColor: borderColors,
        borderWidth: 0,
      },
    ];
    finalData = {
      ...data,
      labels: labelsData,
      datasets: set,
    };
    setData(finalData);
    props.sendBackData(labelsData, percentage);
  }, [props.data]);

  const random_hex_color_code = (string) => {
    var hash = 0;
    for (var i = 0; i < string.length; i++) {
      hash = string.charCodeAt(i) + ((hash << 5) - hash);
    }
    var colour = "#";
    for (var i = 0; i < 3; i++) {
      var value = (hash >> (i * 8)) & 0xff;
      colour += ("00" + value.toString(16)).substr(-2);
    }
    return colour;
  };

  return (
    <>
      <Pie id="pieChart" data={data} options={options} />
    </>
  );
};

export default PieChart;
