import React, { useEffect, useState } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Button,
  Select,
  Input,
  Switch,
  Alert,
  message,
  notification,
  Upload,
  Spin,
  Modal,
} from "antd";
import { useHistory, useLocation } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import styles from "./managePoll.module.scss";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import ReactStars from "react-rating-stars-component";
import MultiChoice from "./MultipleChoice";
import EditPreviewPoll from "./EditPreviewPoll";
import { CheckCircleTwoTone } from "@ant-design/icons";
import momentUtil from "../../utils/momentUtil";
import authHeader from "../../services/auth-header";
import axios from "axios";
import moment from "moment";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import Thermometer from "../../assets/images/thermometer.svg";
import Thumb from "../../assets/images/Thumb.svg";
import Smiley from "../../assets/images/emoji.svg";
import Steps from "../../assets/images/steps.svg";
import {
  VIEW_POLL,
  EDIT_POLL,
  POLL_TITLE,
  UPLOAD,
  DOCUMENT_IMAGE,
  PREVIEW,
  CANCEL,
  CLOSE_TEXT_MODAL,
} from "../../headings";
import { REQUIRED_MESSAGE, STAR_CHART_TYPE } from "../../errors";
import {
  DESCRIPTION,
  DISPLAY_RESULTS_AS,
  QUESTION,
  QUESTION_TYPE,
  RATING_TYPE,
  SHOW_RESULTS_POLL,
} from "../../labels";
import {
  BAR_CHART,
  LADDER,
  PIE_CHART,
  SELECT_MULTIPLE,
  SELECT_SINGLE,
  SMILEY,
  STARS,
  STAR_FORMAT,
  STAR_RATING,
  THERMOMETER,
  THUMBSUP,
} from "../../values";
import { EDIT_POLL_NOTIFICATION } from "../../messages";

const starsRating = {
  size: 40,
  count: 5,
  isHalf: false,
  value: 0,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  onChange: (newValue) => {},
};

const { TextArea } = Input;
const { Content } = Layout;
var CryptoJS = require("crypto-js");

const EditPoll = (props) => {
  const history = useHistory();
  const [attachmentName, setAttachmentName] = useState("");
  const [poll, setPoll] = useState({
    pollName: "",
    pollHelpText: "",
    pollDocument: [],
    showResult: false,
    pollChartType: "",
    pollQuestion: "",
    pollQuestionType: "",
    pollRatingType: "",
    pollRatingResponse: 0,
    pollStartDate: "",
    pollEndDate: "",
    pollStartTime: "",
    pollEndTime: "",
    pollResponse: [],
    pollOptionText: [],
    pollId: "",
    status_id: "",
  });
  const [loader, setLoader] = useState(false);
  const [pollError, setpollError] = useState("");
  const [error, showError] = useState(false);
  const [chartError, showChartError] = useState(false);
  const [previewPoll, setPreviewPoll] = useState(false);
  const [uploadError, showuploadError] = useState(true);
  const [isImage, setIsImage] = useState(false);
  const [form] = Form.useForm();
  let location = useLocation();
  const [api, contextHolder] = notification.useNotification();

  const pollID = props.computedMatch?.params.id;

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    axios.get(BASE_URL + `getPoll/${pollID}/`, options).then((res) => {
      const data = res.data;
      setPoll({
        ...poll,
        pollId: data.poll_id,
        pollName: data.title,
        pollHelpText: data.help_text,
        pollDocument:
          data.image !== null || data.document !== null
            ? [
                {
                  uid: "1",
                  name:
                    data.image_name !== null
                      ? data.image_name
                      : data.document_name !== null
                      ? data.document_name
                      : "",
                  status: "done",
                  url: data.image ? data.image : data.document,
                },
              ]
            : "",
        showResult: data.show_result_to_users,
        pollChartType: data.poll_result_chart_type.toString(),
        pollQuestion: data.question,
        pollQuestionType: data.poll_question_type.toString(),
        pollRatingType: data.poll_question_rating_type
          ? data.poll_question_rating_type.toString()
          : "",
        pollRatingResponse: data.poll_question_rating_type,
        pollStartDate: momentUtil.formatAsDDMMYYYY(data.start_date),
        pollEndDate: momentUtil.formatAsDDMMYYYY(data.end_date),
        pollStartTime: data.start_time,
        pollEndTime: data.end_time,
        status_id: data.status_id,
        pollResponse: data.option_text.map((value) => value.option_text),
        pollOptionText: data.option_text,
      });
      setAttachmentName(
        data.image_name !== null
          ? data.image_name
          : data.document_name !== null
          ? data.document_name
          : ""
      );
      const isImage =
        data.image
          ?.slice(data.image.lastIndexOf("."), data.image.length)
          .includes("jpg") ||
        data.image
          ?.slice(data.image.lastIndexOf("."), data.image.length)
          .includes("jpeg") ||
        data.image
          ?.slice(data.image.lastIndexOf("."), data.image.length)
          .includes("png");
      if (isImage) {
        setIsImage(true);
      } else {
        setIsImage(false);
      }
      setLoader(false);
      form.setFieldsValue({
        pollName: data.title,
        pollHelpText: data.help_text,
        pollQuestion: data.question,
      });
    });
  }, []);

  const setPollsDate = (date, name) => {
    setPoll({ ...poll, [name]: date });
  };

  const handleCancel = (value) => setPoll({ ...poll, previewVisible: false });

  const backtoEdit = (value) => {
    setPreviewPoll(false);
    setPoll({
      ...poll,
      pollName: value.pollName,
      pollHelpText: value.pollHelpText,
      pollDocument: value.pollDocument,
      showResult: value.showResult,
      pollChartType: value.pollChartType,
      pollQuestion: value.pollQuestion,
      pollQuestionType: value.pollQuestionType,
      pollRatingResponse: value.pollRatingResponse,
      pollStartDate: value.pollStartDate,
      pollEndDate: value.pollEndDate,
      pollResponse: value.pollResponse,
    });

    form.setFieldsValue({
      pollHelpText: value.pollHelpText,
      pollQuestion: value.pollQuestion,
    });
  };

  const onPreview = (e) => {
    e.preventDefault();
    if (
      uploadError &&
      poll.pollName &&
      poll.pollName.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      poll.pollHelpText &&
      poll.pollHelpText.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      poll.pollQuestionType &&
      poll.pollQuestionType.replace(/^\s*/, "").replace(/\s*$/, "").length >
        0 &&
      poll.pollQuestion &&
      poll.pollResponse.length > 0
    ) {
      if (poll.showResult || poll.pollQuestionType === "3") {
        if (!poll.pollChartType && poll.showResult) {
          showError(true);
          setPreviewPoll(false);
        } else if (poll.pollQuestionType === "3" && !poll.pollRatingType) {
          showError(true);
          setPreviewPoll(false);
        } else {
          showError(false);
          setPreviewPoll(true);
        }
      } else {
        showError(false);
        setPreviewPoll(true);
      }
    } else {
      showError(true);
      setPreviewPoll(false);
    }
  };

  function getBase64(file) {
    var atb = btoa(file);
    var base64Blob = new Blob([atb]);
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(base64Blob);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  }

  function convertImgToBase64(url, callback, outputFormat) {
    var canvas = document.createElement("CANVAS");
    var ctx = canvas.getContext("2d");
    var img = new Image();
    img.crossOrigin = "Anonymous";
    img.onload = function () {
      canvas.height = img.height;
      canvas.width = img.width;
      ctx.drawImage(img, 0, 0);
      var dataURL = canvas.toDataURL(outputFormat || "image/png");
      callback.call(this, dataURL);
      // Clean up
      canvas = null;
    };
    img.src = url;
  }

  const editPoll = () => {
    setLoader(true);
    const user = JSON.parse(AppStorage.get("user"));
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    let optionsData = [];
    poll.pollResponse.map((value) => optionsData.push({ option_text: value }));

    const finalOptions = optionsData.map((value) => {
      var option = Object.assign({}, value);
      poll.pollOptionText.map((item) => {
        if (item.option_text === value.option_text) {
          option.poll_option_id = item.poll_option_id;
          option.poll = poll.pollId;
        } else if (
          item.option_text !== value.option_text &&
          !option.poll_option_id
        ) {
          option.poll_option_id = "";
          option.poll = poll.pollId;
        }
      });
      return option;
    });
    let data = {};

    const startDate = moment.isMoment(poll.pollStartDate)
      ? momentUtil.formatAsDDMMYYYY(poll.pollStartDate)
      : poll.pollStartDate;
    const endDate = moment.isMoment(poll.pollEndDate)
      ? momentUtil.formatAsDDMMYYYY(poll.pollEndDate)
      : poll.pollEndDate;
    const startTime =
      poll.pollStartTime instanceof Date
        ? momentUtil.formatAsHHmmss(poll.pollStartTime)
        : poll.pollStartTime;
    const endTime =
      poll.pollEndTime instanceof Date
        ? momentUtil.formatAsHHmmss(poll.pollEndTime)
        : poll.pollEndTime;
    if (
      poll.pollDocument?.length === 0 ||
      poll.pollDocument[0].url?.length === 0
    ) {
      data = {
        update_poll: {
          title: poll.pollName,
          help_text: poll.pollHelpText,
          show_result_to_users: poll.showResult ? 1 : 0,
          poll_result_chart_type: poll.pollChartType
            ? parseInt(poll.pollChartType)
            : 2,
          poll_question_type: parseInt(poll.pollQuestionType),
          poll_question_rating_type: poll.pollRatingType
            ? parseInt(poll.pollRatingType)
            : "",
          question: poll.pollQuestion,
          start_date: startDate,
          start_time: startTime,
          end_date: endDate,
          end_time: endTime,
          status_id: poll.status_id,
          poll_id: poll.pollId,
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        },
        option: finalOptions,
      };
      axios
        .put(BASE_URL + "poll/" + poll.pollId + "/", data, options)
        .then(() => {
          setLoader(false);
          const placement = "bottomLeft";
          api.info({
            description: EDIT_POLL_NOTIFICATION,
            placement,
          });
          window.location = "/orgcc/pollsdirectory";
        });
    } else {
      if (poll.pollDocument[0]?.url) {
        if (isImage) {
          convertImgToBase64(poll.pollDocument[0]?.url, function (base64Img) {
            data = {
              update_poll: {
                title: poll.pollName,
                help_text: poll.pollHelpText,
                image: base64Img,
                image_name: attachmentName,
                show_result_to_users: poll.showResult ? 1 : 0,
                poll_result_chart_type: poll.pollChartType
                  ? parseInt(poll.pollChartType)
                  : 2,
                poll_question_type: parseInt(poll.pollQuestionType),
                poll_question_rating_type: poll.pollRatingType
                  ? parseInt(poll.pollRatingType)
                  : "",
                question: poll.pollQuestion,
                start_date: startDate,
                start_time: startTime,
                end_date: endDate,
                end_time: endTime,
                status_id: poll.status_id,
                poll_id: poll.pollId,
                created_by: parseInt(
                  CryptoJS.AES.decrypt(
                    user.user_details.org_user_id,
                    "anonymoose"
                  ).toString(CryptoJS.enc.Utf8)
                ),
              },
              option: finalOptions,
            };
            axios
              .put(BASE_URL + "poll/" + poll.pollId + "/", data, options)
              .then(() => {
                setLoader(false);
                const placement = "bottomLeft";
                api.info({
                  description: "Poll details have been changed successfully.",
                  placement,
                });
                window.location = "/orgcc/pollsdirectory";
              });
          });
        } else {
          getBase64(poll.pollDocument[0]?.url).then((res) => {
            data = {
              update_poll: {
                title: poll.pollName,
                help_text: poll.pollHelpText,
                document: res,
                document_name: attachmentName,
                show_result_to_users: poll.showResult ? 1 : 0,
                poll_result_chart_type: poll.pollChartType
                  ? parseInt(poll.pollChartType)
                  : 2,
                poll_question_type: parseInt(poll.pollQuestionType),
                poll_question_rating_type: poll.pollRatingType
                  ? parseInt(poll.pollRatingType)
                  : "",
                question: poll.pollQuestion,
                start_date: startDate,
                start_time: startTime,
                end_date: endDate,
                end_time: endTime,
                status_id: poll.status_id,
                poll_id: poll.pollId,
                created_by: parseInt(
                  CryptoJS.AES.decrypt(
                    user.user_details.org_user_id,
                    "anonymoose"
                  ).toString(CryptoJS.enc.Utf8)
                ),
              },
              option: finalOptions,
            };
            axios
              .put(BASE_URL + "poll/" + poll.pollId + "/", data, options)
              .then(() => {
                setLoader(false);
                const placement = "bottomLeft";
                api.info({
                  description: "Poll details have been changed successfully.",
                  placement,
                });
                window.location = "/orgcc/pollsdirectory";
              });
          });
        }
      } else {
        if (isImage) {
          data = {
            update_poll: {
              title: poll.pollName,
              help_text: poll.pollHelpText,
              image: poll.pollDocument.base64,
              image_name: attachmentName,
              show_result_to_users: poll.showResult ? 1 : 0,
              poll_result_chart_type: poll.pollChartType
                ? parseInt(poll.pollChartType)
                : 2,
              poll_question_type: parseInt(poll.pollQuestionType),
              poll_question_rating_type: poll.pollRatingType
                ? parseInt(poll.pollRatingType)
                : "",
              question: poll.pollQuestion,
              start_date: startDate,
              start_time: startTime,
              end_date: endDate,
              end_time: endTime,
              status_id: poll.status_id,
              poll_id: poll.pollId,
              created_by: parseInt(
                CryptoJS.AES.decrypt(
                  user.user_details.org_user_id,
                  "anonymoose"
                ).toString(CryptoJS.enc.Utf8)
              ),
            },
            option: finalOptions,
          };
        } else {
          data = {
            update_poll: {
              title: poll.pollName,
              help_text: poll.pollHelpText,
              document: poll.pollDocument.base64,
              document_name: attachmentName,
              show_result_to_users: poll.showResult ? 1 : 0,
              poll_result_chart_type: poll.pollChartType
                ? parseInt(poll.pollChartType)
                : 2,
              poll_question_type: parseInt(poll.pollQuestionType),
              poll_question_rating_type: poll.pollRatingType
                ? parseInt(poll.pollRatingType)
                : "",
              question: poll.pollQuestion,
              start_date: startDate,
              start_time: startTime,
              end_date: endDate,
              end_time: endTime,
              status_id: poll.status_id,
              poll_id: poll.pollId,
              created_by: parseInt(
                CryptoJS.AES.decrypt(
                  user.user_details.org_user_id,
                  "anonymoose"
                ).toString(CryptoJS.enc.Utf8)
              ),
            },
            option: finalOptions,
          };
        }
        axios
          .put(BASE_URL + "poll/" + poll.pollId + "/", data, options)
          .then(() => {
            setLoader(false);
            const placement = "bottomLeft";
            api.info({
              description: "Poll details have been changed successfully.",
              placement,
            });
            window.location = "/orgcc/pollsdirectory";
          });
      }
    }
  };

  const onChange = (checked, e) => {
    setPoll({ ...poll, [e.target.id]: checked });
  };

  const onCancel = () => {
    history.push("/orgcc/pollsdirectory");
  };

  const onCreateChange = (e) => {
    setPoll({ ...poll, [e.target.id]: e.target.value });
  };

  const checkChartType = (value) => {
    if (showResult && value === "3" && poll.pollChartType != "4") {
      showChartError(true);
    }
  };

  const handlePollOnSelect = (value, name) => {
    if (value === "3" && name === "pollQuestionType") {
      checkChartType(value);
    } else {
      showChartError(false);
    }
    if (name === "pollRatingType") {
      setPoll({
        ...poll,
        [name]: value,
        ["pollResponse"]: ["1", "2", "3", "4", "5"],
      });
    } else {
      setPoll({ ...poll, [name]: value });
    }
  };

  const setSingleChoiceResponse = (value) => {
    let name = "pollResponse";
    setPoll({ ...poll, [name]: value });
  };

  const handleStepOneUpload = (value, name) => {
    if (poll.status_id === 8 || poll.status_id === 5) {
      return null;
    } else {
      if (value.file.status === "removed") {
        showuploadError(true);
      }
      let fileList = value.fileList.slice(-1);
      checkDoucmentType(value.file.type);
      fileList.forEach(function (file, index) {
        let reader = new FileReader();
        reader.onload = (e) => {
          file.base64 = e.target.result;
        };
        reader.readAsDataURL(file.originFileObj);
      });
      setAttachmentName(value.file.name);
      setPoll({ ...poll, [name]: fileList });
    }
  };

  const checkDoucmentType = (fileType) => {
    const isImage = fileType?.includes("image");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
  };

  const handleOnRemove = () => {
    if (poll.status_id === 8 || poll.status_id === 5) {
      return null;
    }
  };

  function toImgDataURL(url, callback) {
    var xhr = new XMLHttpRequest();
    xhr.open("get", url);
    xhr.responseType = "blob";
    xhr.onload = function () {
      var fr = new FileReader();
      fr.onload = function () {
        callback(this.result);
      };

      fr.readAsDataURL(xhr.response); // async call
    };

    xhr.send();
  }

  const handleStepOnePreview = async (file) => {
    if (file.uid === "1") {
      convertImgToBase64(file.url, function (base64Img) {
        // await toImgDataURL(proxyUrl + file.url, function (dataURL) {
        setPoll({
          ...poll,
          previewImage: base64Img,
          previewVisible: true,
          previewTitle: file.name,
        });
      });
    } else {
      setPoll({
        ...poll,
        previewImage: file.base64,
        previewVisible: true,
        previewTitle: file.name,
      });
    }
  };

  const handleBeforeUpload = (file, name) => {
    if (poll.status_id === 8 || poll.status_id === 5) {
      return null;
    } else {
      let xlsxfileExt = file.name.includes(".xls");
      let pptxfileExt = file.name.includes(".pptx");
      let docfileExt = file.name.includes(".doc");
      let csvfileExt = file.name.includes(".csv");

      const isValidDoc =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/gif" ||
        file.type === "application/pdf" ||
        xlsxfileExt ||
        pptxfileExt ||
        docfileExt ||
        csvfileExt;

      if (!isValidDoc) {
        showuploadError(true);
        message.error(`${file.name} is not a supported format file type.`);
      } else {
        showuploadError(false);
      }
      const isImage =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/gif";
      const isDocument =
        file.type === "application/pdf" ||
        xlsxfileExt ||
        pptxfileExt ||
        docfileExt ||
        csvfileExt;
      const isLt5M = file.size / 1024 / 1024 < 5;
      const isLt10M = file.size / 1024 / 1024 < 10;
      if (isImage) {
        if (!isLt5M && isImage) {
          showuploadError(false);
          message.error("Image must smaller than 5MB!");
        } else {
          showuploadError(true);
        }
      }

      if (isDocument) {
        if (!isLt10M && isDocument) {
          showuploadError(false);
          message.error("Document must smaller than 10MB!");
        } else {
          showuploadError(true);
        }
      }
      return (isValidDoc && isLt5M) || isLt10M;
    }
  };

  const isPollNameDuplicate = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    if (e.target.value !== location.state.title) {
      axios
        .post(BASE_URL + "checkpoll/", { title: e.target.value }, options)
        .then((res) => {
          if (res.data) {
            setpollError(res.data[0]);
          }
        });
    }
  };

  return (
    <>
      {contextHolder}
      <React.Fragment>
        {!previewPoll ? (
          <React.Fragment>
            {" "}
            <Row className="inner-header">
              <LoggedinHeader amORGUser />
            </Row>
            <Row className="content-center pt-0">
              <Layout>
                <Layout>
                  <OrgSidebar amORGUser data={props} />
                  <Content>
                    <Spin spinning={loader} size="large">
                      <Form
                        className="input-md floating-label w-100"
                        name="validate_other"
                        initialValues={{
                          remember: true,
                        }}
                        form={form}
                      >
                        <Row className="py-3">
                          <h1 className="mb-0">
                            {poll.status_id === 8 || poll.status_id === 5
                              ? VIEW_POLL
                              : EDIT_POLL}
                          </h1>
                        </Row>

                        <div className="mid-content">
                          <Row>
                            <Col span={24}>
                              {error && (
                                <Form.Item>
                                  <Alert
                                    message={REQUIRED_MESSAGE}
                                    type="error"
                                    showIcon
                                  ></Alert>
                                </Form.Item>
                              )}
                              <Col span={24}>
                                <Row>
                                  <Col span={24} md={18} className="gutter-row">
                                    <FloatLabel
                                      label={POLL_TITLE}
                                      value={poll.pollName}
                                    >
                                      <TextArea
                                        showCount
                                        maxLength={50}
                                        htmlFor="polltitle"
                                        value={poll.pollName}
                                        id="pollName"
                                        onChange={(e) => onCreateChange(e)}
                                        onBlur={(e) => isPollNameDuplicate(e)}
                                        autoComplete="off"
                                        disabled={
                                          poll.status_id === 8 ||
                                          poll.status_id === 5
                                        }
                                        className={`required ${styles.sm_textarea}`}
                                      />

                                      {pollError === "status: false" &&
                                        poll.pollName
                                          .replace(/^\s*/, "")
                                          .replace(/\s*$/, "").length > 0 &&
                                        poll.pollName.length > 0 && (
                                          <CheckCircleTwoTone
                                            className="successCheck"
                                            twoToneColor="#52c41a"
                                          />
                                        )}
                                      {pollError == "status: true" && (
                                        <Alert
                                          message={POLL_ALREADY_EXISTS}
                                          type="error"
                                          showIcon
                                        ></Alert>
                                      )}
                                    </FloatLabel>
                                  </Col>
                                  <Col span={24} md={18} className="gutter-row">
                                    <FloatLabel
                                      label={DESCRIPTION}
                                      value={poll.pollHelpText}
                                    >
                                      <Form.Item
                                        className="required"
                                        name="pollHelpText"
                                        id="pollHelpText"
                                        validateStatus="success"
                                      >
                                        <Input
                                          className="input-control"
                                          htmlFor="helptext"
                                          id="pollHelpText"
                                          name="pollHelpText"
                                          value={poll.pollHelpText}
                                          disabled={
                                            poll.status_id === 8 ||
                                            poll.status_id === 5
                                          }
                                          autoComplete="off"
                                          maxLength={500}
                                          onChange={(e) => onCreateChange(e)}
                                        />
                                      </Form.Item>
                                    </FloatLabel>
                                  </Col>
                                  <Col span={24} className="gutter-row mt-3">
                                    {poll.pollDocument?.length === 0 && (
                                      <h6>{UPLOAD}</h6>
                                    )}
                                    <Form.Item
                                      name="upload"
                                      className="upload-list"
                                      valuePropName="fileList"
                                    >
                                      <>
                                        <Upload
                                          action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                          listType="picture"
                                          fileList={poll.pollDocument}
                                          onChange={(value) =>
                                            handleStepOneUpload(
                                              value,
                                              "pollDocument"
                                            )
                                          }
                                          beforeUpload={(value) =>
                                            handleBeforeUpload(
                                              value,
                                              "pollDocument"
                                            )
                                          }
                                          onRemove={() => handleOnRemove()}
                                          onPreview={(value) =>
                                            handleStepOnePreview(value)
                                          }
                                          className="upload-list-inline c-upload"
                                          accept="image/png, image/jpeg, image/gif, application/pdf, .doc, .csv, .pptx, application/vnd.ms-excel, .csv "
                                        >
                                          {poll.pollDocument.length === 0 ? (
                                            <Button
                                              type="link"
                                              className="link pl-0"
                                            >
                                              <span className="icon icon-upload pr-2"></span>{" "}
                                              {DOCUMENT_IMAGE}
                                            </Button>
                                          ) : null}
                                        </Upload>
                                        <Modal
                                          visible={poll.previewVisible}
                                          title={poll.previewTitle}
                                          centered
                                          onOk={(value) => handleCancel(value)}
                                          okButtonProps={{
                                            children: "Custom OK",
                                          }}
                                          cancelButtonProps={{
                                            style: { display: "none" },
                                          }}
                                          okText={CLOSE_TEXT_MODAL}
                                        >
                                          <img
                                            alt="image"
                                            style={{ width: "100%" }}
                                            src={poll.previewImage}
                                          />
                                        </Modal>
                                      </>
                                    </Form.Item>
                                  </Col>
                                </Row>
                              </Col>
                              <Col span={24} className="gutter-row">
                                <Row className="align-items-center d-flex">
                                  <Col span={24} md={8} lg={8} xl={6}>
                                    <Form.Item
                                      name="showresult"
                                      label={SHOW_RESULTS_POLL}
                                      className="mb-0"
                                    >
                                      <Switch
                                        id="showResult"
                                        checked={poll.showResult}
                                        disabled={
                                          poll.status_id === 8 ||
                                          poll.status_id === 5
                                        }
                                        onChange={(checked, e) =>
                                          onChange(checked, e)
                                        }
                                      />
                                    </Form.Item>
                                  </Col>
                                  {poll.showResult ? (
                                    <Col span={24} lg={6}>
                                      <FloatLabel
                                        label={DISPLAY_RESULTS_AS}
                                        value={poll.pollChartType}
                                      >
                                        <Form.Item
                                          className="required mb-0"
                                          name="result"
                                        >
                                          <>
                                            <Select
                                              className="input-control"
                                              htmlFor="result"
                                              className="required"
                                              disabled={
                                                poll.status_id === 8 ||
                                                poll.status_id === 5
                                              }
                                              value={
                                                poll.pollChartType === "2"
                                                  ? ""
                                                  : poll.pollChartType
                                              }
                                              onSelect={(value) =>
                                                handlePollOnSelect(
                                                  value,
                                                  "pollChartType"
                                                )
                                              }
                                            >
                                              <Select.Option value="1">
                                                {PIE_CHART}
                                              </Select.Option>
                                              <Select.Option value="3">
                                                {BAR_CHART}
                                              </Select.Option>
                                              <Select.Option value="4">
                                                {STAR_FORMAT}
                                              </Select.Option>
                                            </Select>
                                          </>
                                        </Form.Item>
                                      </FloatLabel>
                                      {chartError && (
                                        <Form.Item>
                                          <Alert
                                            message={STAR_CHART_TYPE}
                                            type="error"
                                            showIcon
                                          ></Alert>
                                        </Form.Item>
                                      )}
                                    </Col>
                                  ) : (
                                    <Col></Col>
                                  )}
                                </Row>
                              </Col>
                            </Col>
                          </Row>
                        </div>

                        <div className="mid-content mt-4">
                          <Row>
                            <Col span={24}>
                              <Col span={24}>
                                <Row className="d-block">
                                  <Col span={24} md={18} className="gutter-row">
                                    <Row
                                      gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}
                                    >
                                      <Col
                                        span={24}
                                        md={8}
                                        lg={8}
                                        className="mb-3 t-2 mb-y-0 gutter-row"
                                      >
                                        <FloatLabel
                                          label={QUESTION_TYPE}
                                          value={poll.pollQuestionType}
                                        >
                                          <Form.Item
                                            className="required"
                                            name="questiontype"
                                          >
                                            <>
                                              <Select
                                                className=""
                                                htmlFor="questiontype"
                                                className="required"
                                                id="pollQuestionType"
                                                disabled={
                                                  poll.status_id === 8 ||
                                                  poll.status_id === 5
                                                }
                                                value={poll.pollQuestionType}
                                                onSelect={(value) =>
                                                  handlePollOnSelect(
                                                    value,
                                                    "pollQuestionType"
                                                  )
                                                }
                                              >
                                                <Select.Option
                                                  value="1"
                                                  disabled={
                                                    poll.pollChartType !== "" &&
                                                    poll.pollChartType === "4"
                                                  }
                                                >
                                                  {SELECT_SINGLE}
                                                </Select.Option>
                                                <Select.Option
                                                  value="2"
                                                  disabled={
                                                    poll.pollChartType !== "" &&
                                                    poll.pollChartType === "4"
                                                  }
                                                >
                                                  {SELECT_MULTIPLE}
                                                </Select.Option>
                                                <Select.Option
                                                  value="3"
                                                  disabled={
                                                    poll.pollChartType !== "" &&
                                                    poll.pollChartType !== "4"
                                                  }
                                                >
                                                  {STAR_RATING}
                                                </Select.Option>
                                              </Select>
                                            </>
                                          </Form.Item>
                                        </FloatLabel>
                                      </Col>
                                      {poll.pollQuestionType === "3" ? (
                                        <Col
                                          span={24}
                                          md={10}
                                          lg={8}
                                          className="gutter-row"
                                        >
                                          <FloatLabel
                                            label={RATING_TYPE}
                                            value={poll.pollRatingType}
                                          >
                                            <Form.Item
                                              className="required mb-0"
                                              name="ratingtype"
                                            >
                                              <>
                                                <Select
                                                  className=""
                                                  htmlFor="ratingtype"
                                                  className="required"
                                                  id="pollRatingType"
                                                  value={poll.pollRatingType}
                                                  onSelect={(value) =>
                                                    handlePollOnSelect(
                                                      value,
                                                      "pollRatingType"
                                                    )
                                                  }
                                                >
                                                  <Select.Option value="1">
                                                    {STARS}
                                                  </Select.Option>
                                                  <Select.Option value="2">
                                                    {SMILEY}
                                                  </Select.Option>
                                                  <Select.Option value="3">
                                                    {THERMOMETER}
                                                  </Select.Option>
                                                  <Select.Option value="4">
                                                    {THUMBSUP}
                                                  </Select.Option>
                                                  <Select.Option value="5">
                                                    {LADDER}
                                                  </Select.Option>
                                                </Select>
                                              </>
                                            </Form.Item>
                                          </FloatLabel>
                                        </Col>
                                      ) : null}
                                    </Row>
                                  </Col>
                                  <Col span={24} md={18} className="gutter-row">
                                    <FloatLabel
                                      label={QUESTION}
                                      value={poll.pollQuestion}
                                    >
                                      <Form.Item
                                        className="required edit-input"
                                        name="pollQuestion"
                                        id="pollQuestion"
                                      >
                                        <Input
                                          className="input-control edit-input"
                                          name="pollQuestion"
                                          id="pollQuestion"
                                          htmlFor="pollQuestion"
                                          className="required"
                                          disabled={
                                            poll.status_id === 8 ||
                                            poll.status_id === 5
                                          }
                                          defaultValue={poll.pollQuestion}
                                          value={poll.pollQuestion}
                                          onChange={(e) => onCreateChange(e)}
                                          autoComplete="off"
                                          maxLength={500}
                                        />
                                      </Form.Item>
                                    </FloatLabel>
                                  </Col>
                                  <Col
                                    span={24}
                                    className={`mt-4 ${styles.rating_img_wrapper}`}
                                  >
                                    {poll.pollQuestionType === "3" &&
                                    poll.pollRatingType ? (
                                      poll.pollRatingType === "1" ? (
                                        <ReactStars
                                          {...starsRating}
                                          value={poll.pollRatingResponse}
                                          name="pollRatingResponse"
                                          disabled={poll.status_id === 8}
                                          onChange={(value) =>
                                            handlePollOnSelect(
                                              value,
                                              "pollRatingResponse"
                                            )
                                          }
                                          classNames="mt-3"
                                        />
                                      ) : poll.pollRatingType === "2" ? (
                                        <img src={Smiley} alt="send message" />
                                      ) : poll.pollRatingType === "3" ? (
                                        <img
                                          src={Thermometer}
                                          alt="send message"
                                        />
                                      ) : poll.pollRatingType === "4" ? (
                                        <img src={Thumb} alt="send message" />
                                      ) : poll.pollRatingType === "5" ? (
                                        <img
                                          className={`${styles.steps_img}`}
                                          src={Steps}
                                          alt="send message"
                                        />
                                      ) : null
                                    ) : poll.pollQuestionType === "1" ? (
                                      <div>
                                        <MultiChoice
                                          setSingleChoiceResponse={
                                            setSingleChoiceResponse
                                          }
                                          pollResponse={poll.pollResponse}
                                          disabled={
                                            poll.status_id === 8 ||
                                            poll.status_id === 5
                                          }
                                        />
                                      </div>
                                    ) : poll.pollQuestionType === "2" ? (
                                      <MultiChoice
                                        pollResponse={poll.pollResponse}
                                        setSingleChoiceResponse={
                                          setSingleChoiceResponse
                                        }
                                        disabled={
                                          poll.status_id === 8 ||
                                          poll.status_id === 5
                                        }
                                      />
                                    ) : null}
                                  </Col>
                                </Row>
                              </Col>

                              <div className="mt-5">
                                <Button
                                  type="primary"
                                  className="md mr-4"
                                  onClick={onPreview}
                                >
                                  {PREVIEW}
                                </Button>
                                <Button
                                  type="secondary"
                                  type="secondary"
                                  htmlType="submit"
                                  onClick={onCancel}
                                >
                                  {CANCEL}
                                </Button>
                              </div>
                            </Col>
                          </Row>
                        </div>
                      </Form>
                    </Spin>
                  </Content>
                </Layout>
              </Layout>
            </Row>
          </React.Fragment>
        ) : (
          <EditPreviewPoll
            poll={poll}
            setPollsDate={setPollsDate}
            backtoEdit={backtoEdit}
            editPoll={editPoll}
            showError={showError}
            attachmentName={attachmentName}
            data={props}
            error={error}
            loader={loader}
          />
        )}
      </React.Fragment>
    </>
  );
};

export default EditPoll;
