import React from "react";
import { useHistory } from "react-router-dom";

const ShowDetails = (props) => {
  const history = useHistory();
  const path = `/amcc/orgdetails/${props.record.organization_id}`;

  const showModal = () => {
    history.push(path);
  };

  return (
    <>
      {props.orgName ? (
        <a onClick={showModal}>{props.record.name}</a>
      ) : props.orgId ? (
        <a onClick={showModal}>{props.record.identifier}</a>
      ) : (
        <> </>
      )}
    </>
  );
};

export default ShowDetails;
