import moment from 'moment-timezone';
//#region Formats

const formatAsYYYYMMDDHHmmss = date => moment(date, "MM/DD/YYYY").format('YYYY-MM-DD[T]HH:mm:ss');
const formatAsHHmmss = date => moment(date).format('HH:mm:ss');
const formatAsMMMDDYYYY = date => moment(date).format('MM-DD-YYYY');
const formatAsHHmm = date => moment(date).format('HH:mm');
const formatAsYYYYMMDD = date => moment(date).format('YYYY-MM-DD');
const formatAsDDMMYYYY = date => moment(date).format('DD-MM-YYYY');


const format = date => moment(date).format();
export default{
    formatAsYYYYMMDDHHmmss,
    formatAsHHmmss,
    formatAsMMMDDYYYY,
    formatAsHHmm,
    formatAsYYYYMMDD,
    formatAsDDMMYYYY,
    format
};