import React from "react";
import { Popover, Button } from "antd";
import dotsicon from "../../assets/icons/dots-icon.svg";
import { DELETE_MESSAGE, CLEAR_CONVERSATION } from "../../headings";

const content = (props) => (
  <div>
    <ul>
      <li>
        <Button
          onClick={() => props.openModal()}
          disabled={props.convoMessages.length === 0}
        >
          {CLEAR_CONVERSATION}
        </Button>
      </li>
      <li>
        <Button
          onClick={() => props.showDeleteMessagesCheckboxes()}
          disabled={props.convoMessages.length === 0}
        >
          {DELETE_MESSAGE}
        </Button>
      </li>
    </ul>
  </div>
);

class MessagePopover extends React.Component {
  state = {
    visible: false,
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.hidePopup) {
      this.hide();
    }
  }

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
    this.props.setHidePopup(false);
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="switch-ico ico-grid-dots pt-2 pl-2">
            <img src={dotsicon} alt="DotIcon" />
          </span>
        </Popover>
      </>
    );
  }
}

export default MessagePopover;
