import React, { useState } from "react";
import {
  Row,
  Col,
  Form,
  Input,
  Button,
  DatePicker,
  Avatar,
  Divider,
  Select,
  Alert,
  Spin,
  Modal,
} from "antd";
import moment from "moment";
import FloatLabel from "./FloatLabels";
import AssignedUsers from "../manageOrg/AssignedUsers";
import "../form/form.scss";
import { CREATE_ORGANIZATION_ERROR, REQUIRED_MESSAGE } from "../../errors";
import {
  ADD_SEEDED_USER,
  BACK,
  CREATE_INVITE,
  OK_TEXT_MODAL,
  ORGANIZATION_DETAILS,
  SAVE_AS_DRAFT_BTN,
  SEEDED_USERS_DETAILS,
} from "../../headings";
import { DATE, ORGANIZATION_ID, ORGANIZATION_NAME, SAVE_AS_DRAFT, STATUS } from "../../labels";
import { STATUS_ACTIVE, STATUS_INACTIVE } from "../../values";
import CUGModal from "../../components/modal/CUGAttributesModal";

const CreateOrgStepThree = ({
  prev,
  loader,
  apiError,
  handleOk,
  handleCancel,
  stepOneOrganizationInfo,
  handleStepOneOnChange,
  handleStepOneOnSelect,
  handleStepOneDatePickerChange,
  createNewOrganization,
  seededUsers,
  showEditSeededUserForm,
  deleteUser,
  addSeededUserFromThirdStep,
  flexAttribute,
  CUGAttribute,
  saveOrganizationAsDefault
}) => {
  const [form] = Form.useForm();
  const { RangePicker } = DatePicker;
  const [error, showError] = useState(false);
  const [CUGModalvisible, setCUGModalvisible] = useState(false);

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const finish = (e) => {
    e.preventDefault();
    if (
      seededUsers &&
      stepOneOrganizationInfo.organizationName &&
      stepOneOrganizationInfo.organizationId &&
     // stepOneOrganizationInfo.status1 &&
      stepOneOrganizationInfo.s1Date
    ) {
      showError(false);
      createNewOrganization();
    } else {
      showError(true);
    }
  };

  const saveAsDraft = (e) =>{
    e.preventDefault();
    if (
      stepOneOrganizationInfo.organizationName &&
      stepOneOrganizationInfo.organizationId 
    ) {
      showError(false);
      saveOrganizationAsDefault();
    } else {
      showError(true);
    }

  }

  const stepThreePrev = (e) => {
    e.preventDefault();
    prev();
  };

  const handleEditUser = (userID) => {
    showEditSeededUserForm(userID, "viaStepThree");
  };

  const handleDelete = (userID) => {
    deleteUser(userID, "viaStepThree");
  };

  const setModalVisible = () => {
    setCUGModalvisible(true)
  }

  const handleCUGCancel = () =>{
    setCUGModalvisible(false)
  }

  const checkCUGAttribute = () =>{
    if(CUGAttribute.cug1 || CUGAttribute.cug2 || CUGAttribute.cug3 || CUGAttribute.cug4 || CUGAttribute.cug5 ||
      flexAttribute.flex1 || flexAttribute.flex2 || flexAttribute.flex3 || flexAttribute.flex4 || flexAttribute.flex5) 
    {
      return true;
    }
    else{
      return false;
    }
  }

  return (
    <>
      <h6 className="py-3 mb-0">{ORGANIZATION_DETAILS}</h6>
      <div className="avatar-wrapper pt-2 pb-4">
        <Avatar>{stepOneOrganizationInfo.organizationName?.charAt(0)}</Avatar>
        <span className="md-name">
          {stepOneOrganizationInfo.organizationName}
        </span>
      </div>
      <Form
        className="input-md floating-label w-100"
        name="validate_other"
        form={form}
      >
        {error && (
          <Form.Item>
            <Alert message={REQUIRED_MESSAGE} type="error" showIcon></Alert>
          </Form.Item>
        )}
        <Col span={24}>
          <Modal
            visible={apiError}
            onOk={handleOk}
            onCancel={handleCancel}
            centered
            width={500}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText={OK_TEXT_MODAL}
          >
            {" "}
            <p>{CREATE_ORGANIZATION_ERROR}</p>{" "}
          </Modal>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label={ORGANIZATION_NAME}
                value={stepOneOrganizationInfo.organizationName}
              >
                <Form.Item className="required" name="organizationName">
                  <Input
                    className="input-control"
                    htmlFor="organizationName"
                    id="organizationName"
                    defaultValue={stepOneOrganizationInfo.organizationName}
                    onChange={(e) => handleStepOneOnChange(e)}
                    value={stepOneOrganizationInfo.organizationName}
                    autoComplete="off"
                    disabled
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label={ORGANIZATION_ID}
                value={stepOneOrganizationInfo.organizationId}
              >
                <Form.Item
                  className="required"
                  name="organizationId"
                  validateStatus="success"
                >
                  <Input
                    className="input-control"
                    htmlFor="organizationId"
                    id="organizationId"
                    defaultValue={stepOneOrganizationInfo.organizationId}
                    onChange={(e) => handleStepOneOnChange(e)}
                    value={stepOneOrganizationInfo.organizationId}
                    autoComplete="off"
                    disabled
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
            {/* <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label={STATUS}
                value={stepOneOrganizationInfo.status1}
              >
                <Form.Item className="required" name="status1">
                  <>
                    <Select
                      className="input-control w-100 w-lg-100"
                      value={stepOneOrganizationInfo.status1}
                      htmlFor="status1"
                      onSelect={(value) =>
                        handleStepOneOnSelect(value, "status1")
                      }
                      disabled
                    >
                      <Option value="1">{STATUS_ACTIVE}</Option>
                      <Option value="2">{STATUS_INACTIVE}</Option>
                    </Select>
                  </>
                </Form.Item>
              </FloatLabel>
            </Col> */}
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel label={DATE} value="date">
                <Form.Item className="input-control datepicker-wrapper required">
                  <RangePicker
                    className="w-100 "
                    name="s1Date"
                    defaultValue={stepOneOrganizationInfo.s1Date}
                    value={stepOneOrganizationInfo.s1Date}
                    onChange={(date) =>
                      handleStepOneDatePickerChange(date, "s1Date")
                    }
                    disabledDate={disabledDate}
                    disabled
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
          </Row>
        </Col>
        
        <Form.Item>
        <Col span={24}>
          <div className="pt-3">
          {checkCUGAttribute() &&  <Button type="link" className="link pl-0" onClick={() => setModalVisible()}>
              Configured CUG and ORG Attribute
            </Button>}
          </div>
          {CUGModalvisible && <CUGModal 
              CUGModalvisible={CUGModalvisible}
              flexAttribute={flexAttribute}
              CUGAttribute={CUGAttribute}
              viewOnly={true}
              handleCancel={handleCUGCancel}
            />}
        </Col>
      </Form.Item>
        <Divider className="w-100" />
        <Spin spinning={loader} size="large">
          <Col
            span={24}
            className="d-flex justify-content-between align-items-center"
          >
            <h6 className="py-3 mb-0">{SEEDED_USERS_DETAILS}</h6>
            <div>
              {" "}
              <Button
                className="mr-4"
                type="secondary"
                onClick={() => addSeededUserFromThirdStep()}
              >
                {ADD_SEEDED_USER}
              </Button>
            </div>
          </Col>
          <Col span={24}>
            <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
              <AssignedUsers
                seededUsers={seededUsers}
                handleEditUser={handleEditUser}
                handleDelete={handleDelete}
                fromStepThree
              />
            </Row>
          </Col>
          <Form.Item>
            <div className="steps-action mt-5">
              <Button className="mr-4" type="secondary" onClick={stepThreePrev}>
                {BACK}
              </Button>
              <Button type="primary" className="mr-4" htmlType="submit" onClick={finish}>
                {CREATE_INVITE}
              </Button>

              <Button type="secondary" htmlType="submit" onClick={saveAsDraft}>
                {SAVE_AS_DRAFT_BTN}
              </Button>
            </div>
          </Form.Item>
        </Spin>
      </Form>
    </>
  );
};

export default CreateOrgStepThree;
