//Authentication service for AnonyMoose
import axios from "axios";
import { BASE_URL } from "../userconfig";
import * as AppStorage from "../utils/AppStorage";

var CryptoJS = require("crypto-js");

const options = {
  headers: {
    Accept: "application/json, text/plain",
    "Content-Type": "application/json;charset=UTF-8",
  },
};

class AuthService {
  async login(email, password, page) {
    const response = await axios.post(
      BASE_URL + "authenticate/",
      { email, password, page },
      options
    );
    if (response.data) {
      const data = response.data;
      const userFName = CryptoJS.AES.encrypt(
        data.user_details?.first_name,
        "anonymoose"
      ).toString();
      const userLName = CryptoJS.AES.encrypt(
        data.user_details?.last_name,
        "anonymoose"
      ).toString();
      const departmentId = CryptoJS.AES.encrypt(
        data.user_details?.department_id.toString(),
        "anonymoose"
      ).toString();
      const orgUserId = CryptoJS.AES.encrypt(
        data.user_details?.org_user_id.toString(),
        "anonymoose"
      ).toString();
      const endDate = CryptoJS.AES.encrypt(
        data.user_details?.end_date,
        "anonymoose"
      ).toString();
      const startDate = CryptoJS.AES.encrypt(
        data.user_details?.start_date,
        "anonymoose"
      ).toString();
      const userEmail = CryptoJS.AES.encrypt(
        data.user_data?.email,
        "anonymoose"
      ).toString();
      const userMobile = CryptoJS.AES.encrypt(
        data.user_data?.mobile_number,
        "anonymoose"
      ).toString();
      const orgId = CryptoJS.AES.encrypt(
        data.user_data?.organization_id.toString(),
        "anonymoose"
      ).toString();
      const userId = CryptoJS.AES.encrypt(
        data.user_data?.user_id.toString(),
        "anonymoose"
      ).toString();
      const amweb = CryptoJS.AES.encrypt(
        data.user_data?.is_am_web_user.toString(),
        "anonymoose"
      ).toString();
      const amsuper = CryptoJS.AES.encrypt(
        data.user_data?.is_super_user.toString(),
        "anonymoose"
      ).toString();
      const amcc = CryptoJS.AES.encrypt(
        data.user_data?.is_amcc_user.toString(),
        "anonymoose"
      ).toString();
      const amorgcc = CryptoJS.AES.encrypt(
        data.user_data?.is_org_cc_user.toString(),
        "anonymoose"
      ).toString();
      const amidb = CryptoJS.AES.encrypt(
        data.user_data?.is_idb_user.toString(),
        "anonymoose"
      ).toString();
      const amseeded = CryptoJS.AES.encrypt(
        data.user_data?.is_seeded_user.toString(),
        "anonymoose"
      ).toString();
      const amapp = CryptoJS.AES.encrypt(
        data.user_data?.is_app_user.toString(),
        "anonymoose"
      ).toString();
      const userData = {
        ...data,
        user_data: {
          ...data.user_data,
          email: userEmail,
          mobile_number: userMobile,
          organization_id: orgId,
          user_id: userId,
          is_am_web_user: amweb,
          is_amcc_user: amcc,
          is_super_user: amsuper,
          is_app_user: amapp,
          is_org_cc_user: amorgcc,
          is_idb_user: amidb,
          is_seeded_user: amseeded,
        },
        user_details: {
          ...data.user_details,
          first_name: userFName,
          last_name: userLName,
          department_id: departmentId,
          org_user_id: orgUserId,
          end_date: endDate,
          start_date: startDate,
        },
      };
      AppStorage.set("user", JSON.stringify(userData));
      AppStorage.set(
        "current",
        CryptoJS.AES.encrypt(JSON.stringify(password), "anonymoose").toString()
      );
    }
    return response.data;
  }

  logout() {
    let userType = AppStorage.get("userType");
    userType = CryptoJS.AES.decrypt(userType, "anonymoose").toString(
      CryptoJS.enc.Utf8
    );
    let redirectURL = "/";
    if (userType !== "AM CC") {
      redirectURL = "/org/login";
    } else {
      redirectURL = "/amcc/login";
    }
    AppStorage.removeAll();
    window.location = redirectURL;
  }
}

export default new AuthService();
