import React from "react";
import { Layout, Row, Col, Input, Spin, notification } from "antd";
import { LoggedinHeader } from "../../containers";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined } from "@ant-design/icons";
import HotlinePopover from "../table/HotlinePopover";
import { Link } from "react-router-dom";
import PermissionModal from "./../modal/ShowModal";
import OrgSidebar from "../../containers/OrgSidebar";
import moment from "moment";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import authHeader from "../../services/auth-header";
import * as AppStorage from "../../utils/AppStorage";
import {
  HOTLINE_NAME,
  END_DATE,
  START_DATE,
  PERMISSIONS,
  STATUS,
} from "../../labels";
import {
  STATUS_ACTIVE,
  STATUS_DRAFT,
  SCHEDULED,
  COMPLETED,
} from "../../values";
import { HOTLINE_DIRECTORY, SEARCH } from "../../headings";
import {
  STATUS_CHANGED_NOTIFICATION_PART1,
  STATUS_CHANGED_NOTIFICATION_PART2,
  STATUS_CHANGED_NOTIFICATION_PART3,
} from "../../messages";

var CryptoJS = require("crypto-js");
const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);
const { Content } = Layout;

class ShowAllHotlines extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      filterData: [],
      searchBy: "",
    };
  }

  componentDidMount() {
    let { hotlines } = this.props;
    
    hotlines?.forEach((hotline) => {
      hotline.start_date = hotline.start_date ? moment(hotline.start_date)
        .utc()
        .local()
        .format("MM / DD / YYYY") : '';
      hotline.end_date = hotline.end_date ? moment(hotline.end_date)
        .utc()
        .local()
        .format("MM / DD / YYYY") : '';
    });
    this.setState({ data: hotlines }, () => {
      this.onSearch("");
    });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.hotlines !== nextProps.hotlines) { 
      nextProps.hotlines?.forEach((hotline) => {
        hotline.start_date = hotline.start_date ? moment(hotline.start_date)
          .utc()
          .local()
          .format("MM / DD / YYYY") : '';
        hotline.end_date =  hotline.end_date ? moment(hotline.end_date)
          .utc()
          .local()
          .format("MM / DD / YYYY") : '';
      });
      this.setState({ data: nextProps.hotlines }, () => {
        this.onSearch(this.state.searchBy);
      });
    }
  }

  statusClicked = (record, via) => {
    const endDate = record.end_date.replaceAll(" / ", "/");
    if (new Date() < new Date(endDate)) {
      const user = JSON.parse(AppStorage.get("user"));
      const data = {
        help_text: record.help_text,
        hotline_icon_id: record.hotline_icon_id,
        hotline_id: record.hotline_id,
        name: record.name,
        start_date: moment(record.start_date.replaceAll(" / ", "-")).format(
          "DD-MM-YYYY"
        ),
        end_date: moment(endDate.replaceAll("/", "-")).format("DD-MM-YYYY"),
        status_id: via === "Active" ? 4 : 5,
        updated_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
      };
      const token = authHeader();
      const options = {
        headers: {
          Authorization: token,
          Accept: "application/json, text/plain",
          "Content-Type": "application/json;charset=UTF-8",
        },
      };
      axios
        .put(BASE_URL + "hotline/" + data.hotline_id, data, options)
        .then(() => {
          notification.config({
            placement: "bottomLeft",
          });
          notification.open({
            description:
              STATUS_CHANGED_NOTIFICATION_PART1 +
              record.name +
              STATUS_CHANGED_NOTIFICATION_PART2,
          });
          this.props.statusOnClicked();
        });
    } else {
      notification.config({
        placement: "bottomLeft",
      });
      notification.open({
        description:
          STATUS_CHANGED_NOTIFICATION_PART1 +
          record.name +
          STATUS_CHANGED_NOTIFICATION_PART3,
      });
    }
  };

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt, searchBy: value });
  };

  render() {
    const columns = [
      {
        title: HOTLINE_NAME,
        dataIndex: "name",
        key: "name",
        width: "22%",
        render: (name) => {
          return {
            children: name,
            props: {
              "data-title": "Hotline Name",
            },
          };
        },
      },
      {
        title: START_DATE,
        dataIndex: "start_date",
        key: "start_date",
        width: "22%",
        render: (start_date) => {
          let sdate = start_date ? start_date : '-'
          return {
            children: sdate,
            props: {
              "data-title": "Start Date",
            },
          };
        },
      },
      {
        title: END_DATE,
        dataIndex: "end_date",
        key: "end_date",
        width: "21%",
        render: (end_date) => {
          let edate = end_date ? end_date : '-'
          return {
            children: edate,
            props: {
              "data-title": "End Date",
            },
          };
        },
      },
      {
        title: PERMISSIONS,
        dataIndex: "",
        key: "x",
        width: "20%",
        render: (selectedRows) => {
          return {
            children: (
              <div>
                <PermissionModal selectedRows={selectedRows} />
              </div>
            ),
            props: {
              "data-title": "Permissions",
            },
          };
        },
      },
      {
        title: STATUS,
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) => {
          return {
            children:
              record.status_id === 4 ? (
                <div className="secondary-tag-rounded tag">{STATUS_DRAFT}</div>
              ) : record.status_id === 5 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {STATUS_ACTIVE}
                </div>
              ) : record.status_id === 7 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {SCHEDULED}
                </div>
              ) : (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {COMPLETED}
                </div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: (record) => {
          return {
            children: <HotlinePopover data={record} />,
            props: {
              "data-title": "Action",
            },
          };
        },
      },
    ];

    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={this.props.data} />
              <Content className="">
                <Row className="py-3">
                  <h1>{HOTLINE_DIRECTORY}</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder={SEARCH}
                            onSearch={this.onSearch}
                          />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/orgcc/createhotline">
                        <Buttons value="Create Hotline" />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Spin spinning={this.props.loader} size="large">
                  <Grid
                    rowKey="hotline_id"
                    columns={columns}
                    data={this.state.filterData}
                  />
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default ShowAllHotlines;
