import React from "react";
import { Avatar } from "antd";
import "../userInfo/userInfo.scss";
import { Popover, List } from "antd";
import ExpandMore from "../../assets/icons/expand-more.svg";
import AuthService from "./../../services/auth-service";
import { connect } from "react-redux";
import { withRouter, Link } from "react-router-dom";
import * as AppStorage from "../../utils/AppStorage";
import { AM_CC, AM_WEB, IDB, ORG_CC } from "../../values";
import { CHANGE_PASSWORD, EDIT_PROFILE, SIGN_OUT } from "../../headings";

var CryptoJS = require("crypto-js");

class AnonymooseUser extends React.Component {
  constructor(props) {
    super(props);
    this.handleLogoutClick = this.handleLogoutClick.bind(this);
    this.handleChangeUserRole = this.handleChangeUserRole.bind(this);
    this.state = { isLoggedIn: false };
  }

  handleLogoutClick() {
    AuthService.logout();
  }
  handleChangeUserRole(type) {
    if (type === "amweb") {
      window.location.href = "/amweb/hotlinemessages";
    } else if (type === "amcc") {
      window.location.href = "/amcc/manageorganizations";
    } else if (type === "amorgcc") {
      window.location.href = "/orgcc/manageemployees";
    } else if (type === "idb") {
      window.location.href = "/idb/hotlinereports";
    }
  }

  render() {
    const { amWEBUser, amCCUser, amORGUser, amIDBUser } = this.props?.userRole;
    if (amCCUser) {
      AppStorage.set(
        "userType",
        CryptoJS.AES.encrypt("AM CC", "anonymoose").toString()
      );
    } else if (amORGUser) {
      AppStorage.set(
        "userType",
        CryptoJS.AES.encrypt("Org CC", "anonymoose").toString()
      );
    } else if (amWEBUser) {
      AppStorage.set(
        "userType",
        CryptoJS.AES.encrypt("AM WEB", "anonymoose").toString()
      );
    } else if (amIDBUser) {
      AppStorage.set(
        "userType",
        CryptoJS.AES.encrypt("IDB", "anonymoose").toString()
      );
    }
    const type = AppStorage.get("userType");
    const userType = type
      ? CryptoJS.AES.decrypt(type, "anonymoose").toString(CryptoJS.enc.Utf8)
      : "";
    const {
      email,
      is_am_web_user,
      is_amcc_user,
      is_org_cc_user,
      is_app_user,
      is_idb_user,
      is_seeded_user,
    } = this.props?.user?.user_data || {};
    const { first_name, last_name } = this.props?.user?.user_details;
    const name = first_name + " " + last_name;
    const nameCapitalized = name.charAt(0).toUpperCase() + name.slice(1);
    const fullName = nameCapitalized.split(" ");
    const initials = fullName.shift().charAt(0) + fullName.pop().charAt(0);

    return (
      <>
        <Popover
          data-test="userInfo"
          className="test"
          placement="bottom"
          content={
            <List className="u-info" size="large" bordered>
              <List.Item className=" d-flex d-lg-none">
                <div className="d-flex flex-column ">
                  <span className="uname-top">{nameCapitalized}</span>
                  <span className="u-type">
                    {userType?.includes("Org") ? ORG_CC : userType}
                  </span>
                </div>
              </List.Item>
              <List.Item>
                <div className="email">
                  {email}
                  <br />
                  {/* {this.showLoggedinUserData()} */}
                  <a href="#">{EDIT_PROFILE}</a>
                </div>
              </List.Item>
              {amORGUser && is_am_web_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amweb")}>
                  {AM_WEB}
                </List.Item>
              ) : null}
              {amORGUser && is_amcc_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amcc")}>
                  {AM_CC}
                </List.Item>
              ) : null}
              {amORGUser && is_idb_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("idb")}>
                  {IDB}
                </List.Item>
              ) : null}
              {amWEBUser && is_org_cc_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amorgcc")}>
                  {ORG_CC}
                </List.Item>
              ) : null}
              {amWEBUser && is_amcc_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amcc")}>
                  {AM_CC}
                </List.Item>
              ) : null}
              {amWEBUser && is_idb_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("idb")}>
                  {IDB}
                </List.Item>
              ) : null}
              {amCCUser && is_am_web_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amweb")}>
                  {AM_WEB}
                </List.Item>
              ) : null}
              {amCCUser && is_org_cc_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amorgcc")}>
                  {ORG_CC}
                </List.Item>
              ) : null}
              {amCCUser && is_idb_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("idb")}>
                  {IDB}
                </List.Item>
              ) : null}
              {amIDBUser && is_am_web_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amweb")}>
                  {AM_WEB}
                </List.Item>
              ) : null}
              {amIDBUser && is_org_cc_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amorgcc")}>
                  {ORG_CC}
                </List.Item>
              ) : null}
              {amIDBUser && is_amcc_user ? (
                <List.Item onClick={() => this.handleChangeUserRole("amcc")}>
                  {AM_CC}
                </List.Item>
              ) : null}
              <Link to="/changepassword">
                <List.Item>{CHANGE_PASSWORD}</List.Item>
              </Link>
              <List.Item onClick={this.handleLogoutClick}>{SIGN_OUT}</List.Item>
            </List>
          }
          trigger="click"
        >
          <Avatar className="mr-2">{initials}</Avatar>
          <span className="d-none d-lg-flex flex-column">
            <span className="uname-top">{nameCapitalized}</span>
            <span className="u-type">
              {userType?.includes("Org") ? ORG_CC : userType}
            </span>
          </span>
          <span className="expand-arrow">
            <img src={ExpandMore} alt="Expand Arrow" className="pr-0 pr-md-4" />
          </span>
        </Popover>
      </>
    );
  }
}

function mapStateToProps(state) {
  const { isLoggedIn, user } = state.auth;
  return {
    isLoggedIn,
    user,
  };
}

export default withRouter(connect(mapStateToProps)(AnonymooseUser));
