import React from "react";
import { Link } from "react-router-dom";
import { Layout, Menu } from "antd";
const { Sider } = Layout;
import { MenuUnfoldOutlined, MenuFoldOutlined } from "@ant-design/icons";
import { MANAGE_ORGANIZATIONS } from "../../headings";

class AccSidebar extends React.Component {
  state = {
    collapsed: false,
  };

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    return (
      <>
        {React.createElement(
          this.state.collapsed ?MenuFoldOutlined : MenuUnfoldOutlined  ,
          {
            className: "trigger d-lg-none d-block",
            onClick: this.toggle,
          }
        )}
        <Sider
          className={
            this.state.collapsed
              ? "d-lg-block w-md-100 sider-width"
              : " d-none d-lg-block w-md-100 sider-width"
          }
          trigger={null}
          collapsible
          collapsed={this.state.collapsed}
        >
          <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]}>
            <Menu.Item
              key="1"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-manage-org" />
                </span>
              }
            >
              <Link to="/amcc/manageorganizations">{MANAGE_ORGANIZATIONS}</Link>
            </Menu.Item>
          </Menu>
        </Sider>
      </>
    );
  }
}

export default AccSidebar;
