import { POLL_FAIL, POLL_SUCCESS } from "../actions/types";

const deafaultState = {};
deafaultState.pollsdata = null;

export default function (state = deafaultState, action) {
  const { type, payload } = action;

  switch (type) {
    case POLL_SUCCESS:
      return {
        ...state,
        pollsdata: payload.pollsdata,
      };
    case POLL_FAIL:
      return {
        ...state,
        pollsdata: [],
      };
    default:
      return state;
  }
}
