import React, { useState } from "react";
import "antd/dist/antd.css";
import "./assets/scss/style.scss";
import "./assets/icons/icon-styles.css";
import { Provider, connect } from "react-redux";
import Routes from "./routes";
import store from "./services/stores";
import IdleTimer from "react-idle-timer";
import { Modal } from "antd";
import AuthService from "../src/services/auth-service";
import * as AppStorage from "../src/utils/AppStorage";
import { ABOUT_TO_LOGOUT_TEXT, LOGGED_OUT_TEXT } from "./messages";
import { OK_TEXT_MODAL } from "./headings";

function App() {
  const timeOutIdle = 1200000; // 20 minutes
  const timeOutLogout = 60000; // a minute
  var idleTimer = null;
  const [idleModal, showIdleModal] = useState(false);
  const [logoutModal, showLogoutModal] = useState(false);
  const [timedOut, setTimedOut] = useState(false);

  const onIdle = (e) => {
    if (AppStorage.get("user")) {
      const isTimedOut = timedOut;
      if (isTimedOut) {
        showIdleModal(false);
        showLogoutModal(true);
      } else {
        showIdleModal(true);
        idleTimer.reset();
        setTimedOut(true);
      }
    }
  };

  const handleIdleOk = () => {
    showIdleModal(false);
  };

  const handleIdleCancel = () => {
    showIdleModal(false);
  };

  const handleLogoutOk = () => {
    if (AppStorage.get("user")) {
      AuthService.logout();
    }
  };

  const handleLogoutCancel = () => {
    if (AppStorage.get("user")) {
      AuthService.logout();
    }
  };

  const onActive = (e) => {
    setTimedOut(false);
  };

  const onAction = (e) => {
    setTimedOut(false);
  };

  return (
    <Provider store={store}>
      <IdleTimer
        ref={(ref) => {
          idleTimer = ref;
        }}
        element={document}
        onIdle={onIdle}
        onAction={onAction}
        onActive={onActive}
        debounce={250}
        timeout={!timedOut ? timeOutIdle : timeOutLogout}
      />
      <div className="App">
        <div>
          <div className="content">
            <Modal
              visible={idleModal}
              onOk={handleIdleOk}
              onCancel={handleIdleCancel}
              centered
              width={350}
              okButtonProps={{
                children: "Custom OK",
              }}
              cancelButtonProps={{ style: { display: "none" } }}
              okText={OK_TEXT_MODAL}
            >
              <p>{ABOUT_TO_LOGOUT_TEXT}</p>
            </Modal>
            <Modal
              visible={logoutModal}
              onOk={handleLogoutOk}
              onCancel={handleLogoutCancel}
              centered
              width={350}
              okButtonProps={{
                children: "Custom OK",
              }}
              cancelButtonProps={{ style: { display: "none" } }}
              okText={OK_TEXT_MODAL}
            >
              <p>{LOGGED_OUT_TEXT}</p>
            </Modal>
            <Routes />
          </div>
        </div>
      </div>
    </Provider>
  );
}

export default App;
