import React, { useEffect, useState } from "react";
import HotlineDirectory from "./HotlineDirectory";
import ShowAllHotlines from "./ShowAllHotlines";
import { connect } from "react-redux";
import { getHotlinesAsync } from "../../actions/hotlines";

const HotlineContainer = (props) => {
  const [statusClicked, setStatusClicked] = useState(false);
  const [loader, setLoader] = useState(false);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getHotlinesAsync());
  }, [statusClicked]);

  useEffect(() => {
    if (props.hotlinesdata === null) {
      setLoader(true);
    } else if (props.hotlinesdata !== null) {
      setLoader(false);
    }
  }, [props.hotlinesdata]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  return !loader &&
    props.hotlinesdata !== null &&
    props.hotlinesdata.length === 0 ? (
    <HotlineDirectory data={props} />
  ) : (
    <ShowAllHotlines
      hotlines={props.hotlinesdata}
      statusOnClicked={statusOnClicked}
      data={props}
      loader={loader}
    />
  );
};

function mapStateToProps(state) {
  const { hotlinesdata } = state.hotlinedata;
  return {
    hotlinesdata,
  };
}

export default connect(mapStateToProps)(HotlineContainer);
