import React, { useState } from "react";
import {
  Row,
  Col,
  Form,
  Select,
  Input,
  Button,
  DatePicker,
  Alert,
  Radio,
  Checkbox,
  Divider,
} from "antd";
import FloatLabel from "./FloatLabels";
import moment from "moment";
import "../form/form.scss";
import authHeader from "../../services/auth-header";
import { CheckCircleTwoTone } from "@ant-design/icons";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import {
  MAX_CHAR_LIMIT,
  ORGANIZATION_ID_ALREADY_EXISTS,
  ORGANIZATION_NAME_ALREADY_EXISTS,
  REQUIRED_MESSAGE,
} from "../../errors";
import {
  DATE,
  EMAIL,
  MOBILE,
  ORGANIZATION_ID,
  ORGANIZATION_NAME,
  SAVE_AS_DRAFT,
  SEND_OTP,
  STATUS,
} from "../../labels";
import { STATUS_ACTIVE, STATUS_INACTIVE } from "../../values";
import CUGModal from "../../components/modal/CUGAttributesModal";
import { NEXT, SAVE_AS_DRAFT_BTN } from "../../headings";

const CreateOrgStepOne = ({
  next,
  stepOneOrganizationInfo,
  handleStepOneOnChange,
  handleStepOneOnSelect,
  handleStepOneDatePickerChange,
  orgIdCount,
  orgNameCount,
  setOrgNameAlreadyExists,
  setOrgIDAlreadyExists,
  orgIDAlreadyExists,
  orgNameAlreadyExists,
  setStepOneOrganization,
  flexAttribute,
  CUGAttribute,
  handleCUGChange,
  clearAttribute
}) => {
  const primaryRadioValue =
    stepOneOrganizationInfo.primary === 3
      ? 1
      : stepOneOrganizationInfo.primary === 4
      ? 2
      : stepOneOrganizationInfo.primary;
  const primaryCheckValue =
    stepOneOrganizationInfo.primary === 3
      ? true
      : stepOneOrganizationInfo.primary === 4
      ? true
      : false;
  const [form] = Form.useForm();
  const [error, showError] = useState(false);
  const [value, setValue] = useState(primaryRadioValue);
  const [checked, setChecked] = useState(false);
  const [CUGModalvisible, setCUGModalvisible] = useState(false);
  const CheckOrganizationName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL + "checkOrganization/",
        { organization_name: stepOneOrganizationInfo.organizationName },
        options
      )
      .then((res) => {
        if (res.data) {
          setOrgNameAlreadyExists(res.data[0]);
        }
      });
  };

  const CheckOrganizationID = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL + "checkOrganizationID/",
        { orgID: stepOneOrganizationInfo.organizationId },
        options
      )
      .then((res) => {
        if (res.data) {
          setOrgIDAlreadyExists(res.data[0]);
        }
      });
  };

  const saveAsDraft = (e) =>{
    e.preventDefault();
    if (
      stepOneOrganizationInfo.organizationName &&
      stepOneOrganizationInfo.organizationId 
    ) {
      if (
        orgNameAlreadyExists === "status: false" &&
        orgIDAlreadyExists === "status: false"
      ) {
        showError(false);
        next();
      }
    } else {
      showError(true);
    }

  }

  const stepOneNext = (e) => {
    e.preventDefault();
    if (
      stepOneOrganizationInfo.organizationName &&
      stepOneOrganizationInfo.organizationId &&
      //stepOneOrganizationInfo.status1 &&
      stepOneOrganizationInfo.s1Date
    ) {
      if (
        orgNameAlreadyExists === "status: false" &&
        orgIDAlreadyExists === "status: false"
      ) {
        showError(false);
        next();
      }
    } else {
      showError(true);
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const onChangeRadio = (e) => {
    let radioVal = 0;
    if (checked) {
      if (e.target.value === 1) {
        setValue(1);
        radioVal = 3;
      } else {
        setValue(2);
        radioVal = 4;
      }
    } else {
      if (e.target.value === 1) {
        setValue(1);
        radioVal = 1;
      } else {
        setValue(2);
        radioVal = 2;
      }
    }
    setStepOneOrganization({
      ...stepOneOrganizationInfo,
      primary: radioVal,
    });
  };

  const onChangeCheckbox = (e) => {
    setChecked(e.target.checked);
    let checkVal = 0;
    if (e.target.checked) {
      if (value === 1) {
        checkVal = 3;
      } else if (value === 2) {
        checkVal = 4;
      }
    } else {
      if (value === 1) {
        checkVal = 1;
      } else if (value === 2) {
        checkVal = 2;
      }
    }
    setStepOneOrganization({
      ...stepOneOrganizationInfo,
      primary: checkVal,
    });
  };

  const setModalVisible = () => {
    setCUGModalvisible(!CUGModalvisible)
  }

  const handleSave = () =>{
    setCUGModalvisible(false)
  }

  const handleCancel = () =>{
    setCUGModalvisible(false)
    clearAttribute()
  }

  const { RangePicker } = DatePicker;
  
  return (
    <Form
      className="input-md floating-label w-100"
      name="validate_other"
      form={form}
    >
      {error && (
        <Form.Item>
          <Alert message={REQUIRED_MESSAGE} type="error" showIcon></Alert>
        </Form.Item>
      )}
      <Col span={24}>
        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel
              label={ORGANIZATION_NAME}
              value={stepOneOrganizationInfo.organizationName}
            >
              <Form.Item className="required" name="organizationName">
                <Input
                  className="input-control"
                  id="organizationName"
                  htmlFor="organizationName"
                  defaultValue={stepOneOrganizationInfo.organizationName}
                  value={stepOneOrganizationInfo.organizationName}
                  onChange={(e) => handleStepOneOnChange(e)}
                  onBlur={(e) => CheckOrganizationName(e)}
                  autoComplete="off"
                  maxLength={80}
                />
                {orgNameAlreadyExists === "status: false" &&
                  stepOneOrganizationInfo.organizationName
                    .replace(/^\s*/, "")
                    .replace(/\s*$/, "").length > 0 &&
                  stepOneOrganizationInfo.organizationName.length > 0 && (
                    <CheckCircleTwoTone
                      className="successCheck"
                      twoToneColor="#52c41a"
                    />
                  )}
                {orgNameAlreadyExists == "status: true" && (
                  <Alert
                    message={ORGANIZATION_NAME_ALREADY_EXISTS}
                    type="error"
                    showIcon
                  ></Alert>
                )}
              </Form.Item>
              {orgNameCount && (
                <Form.Item>
                  <Alert message={MAX_CHAR_LIMIT} type="error"></Alert>
                </Form.Item>
              )}
            </FloatLabel>
          </Col>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel
              label={ORGANIZATION_ID}
              value={stepOneOrganizationInfo.organizationId}
            >
              <Form.Item
                className="required"
                name="organizationId"
                validateStatus="success"
              >
                <Input
                  className="input-control"
                  id="organizationId"
                  htmlFor="organizationId"
                  defaultValue={stepOneOrganizationInfo.organizationId}
                  value={stepOneOrganizationInfo.organizationId}
                  onChange={(e) => handleStepOneOnChange(e)}
                  onBlur={(e) => CheckOrganizationID(e)}
                  autoComplete="off"
                  maxLength={20}
                />
                {orgIDAlreadyExists === "status: false" &&
                  stepOneOrganizationInfo.organizationId
                    .replace(/^\s*/, "")
                    .replace(/\s*$/, "").length > 0 &&
                  stepOneOrganizationInfo.organizationId.length > 0 && (
                    <CheckCircleTwoTone
                      className="successCheck"
                      twoToneColor="#52c41a"
                    />
                  )}
                {orgIDAlreadyExists == "status: true" && (
                  <Alert
                    message={ORGANIZATION_ID_ALREADY_EXISTS}
                    type="error"
                    showIcon
                  ></Alert>
                )}
              </Form.Item>
              {orgIdCount && (
                <Form.Item>
                  <Alert message={MAX_CHAR_LIMIT} type="error"></Alert>
                </Form.Item>
              )}
            </FloatLabel>
          </Col>
          {/* <Col span={24} md={8} className="gutter-row">
            <FloatLabel label={STATUS} value={stepOneOrganizationInfo.status1}>
              <Form.Item className="required" name="status1">
                <>
                  <Select
                    className="input-control w-100 w-lg-100"
                    value={stepOneOrganizationInfo.status1}
                    htmlFor="status1"
                    onSelect={(value) =>
                      handleStepOneOnSelect(value, "status1")
                    }
                  >
                    <Option value="1">{STATUS_ACTIVE}</Option>
                    <Option value="2">{STATUS_INACTIVE}</Option>
                  </Select>
                </>
              </Form.Item>
            </FloatLabel>
          </Col> */}
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel label={DATE} value="date">
              <Form.Item className="input-control datepicker-wrapper required">
                <RangePicker
                  className="w-100"
                  name="s1Date"
                  value={stepOneOrganizationInfo.s1Date}
                  onChange={(date) =>
                    handleStepOneDatePickerChange(date, "s1Date")
                  }
                  disabledDate={disabledDate}
                />
              </Form.Item>
            </FloatLabel>
          </Col>
          <Col span={24} md={16} lg={8} className="gutter-row">
            <FloatLabel label="Primary Key" value="primarykey">
              <Form.Item className="">
                <div className="box-wht">
                  <span className="pt-3 px-3 d-flex font-weight-md6">
                    <Radio.Group
                      onChange={(e) => onChangeRadio(e)}
                      value={value}
                    >
                      <Radio value={1}>{EMAIL}</Radio>
                      <Radio value={2}>{MOBILE}</Radio>
                    </Radio.Group>
                  </span>
                  <span className="p-3 d-flex">
                    <Checkbox
                      className="font-13"
                      onChange={(e) => onChangeCheckbox(e)}
                      checked={primaryCheckValue}
                    >
                      {SEND_OTP}
                    </Checkbox>
                  </span>
                </div>
              </Form.Item>
            </FloatLabel>
          </Col>
        </Row>
      </Col>
      <Divider className="w-100"></Divider>
      <Form.Item>
        <Col span={24}>
          {/* <div className="font-weight-md6">Add Organization and CUG Attributes</div> */}
          <div className="pt-3">
            <Button type="link" className="link pl-0" onClick={() => setModalVisible()}>
                Configure CUG and ORG Attribute
            </Button>
           {CUGModalvisible && <CUGModal 
              CUGModalvisible={CUGModalvisible}
              flexAttribute={flexAttribute}
              CUGAttribute={CUGAttribute}
              handleCancel={handleCancel}
              handleSave={handleSave}
              handleCUGChange={handleCUGChange}
            />}
          </div>
        </Col>
      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit" className="mr-3" onClick={stepOneNext}>
          {NEXT}
        </Button>
        <Button type="secondary" htmlType="submit" onClick={saveAsDraft}>
          {SAVE_AS_DRAFT_BTN}
        </Button>
      </Form.Item>
    </Form>
  );
};

export default CreateOrgStepOne;
