import React, { useState, useEffect } from "react";
import { Row, Col, Form, Select, Input, Button, DatePicker, Alert } from "antd";
import FloatLabel from "./FloatLabels";
import AssignedUsers from "../manageOrg/AssignedUsers";
import "../form/form.scss";
import moment from "moment";
import "react-phone-input-2/lib/style.css";
import PhoneInput from "react-phone-input-2";
import {
  MAX_CHAR_LIMIT,
  REQUIRED_MESSAGE,
  VALID_EMAIL,
  VALID_MOBILE,
} from "../../errors";
import { DATE, EMAIL, FIRST_NAME, LAST_NAME, STATUS, START_DATE, END_DATE, SAVE_AS_DRAFT } from "../../labels";
import { STATUS_ACTIVE, STATUS_INACTIVE } from "../../values";
import { BACK, ADD_USER, UPDATE, NEXT, SAVE_AS_DRAFT_BTN } from "../../headings";

const CreateOrgStepTwo = ({
  next,
  prev,
  stepTwoOrganizationInfo,
  handleStepTwoOnChange,
  handleStepTwoOnSelect,
  handleMobile,
  handleStepTwoDatePickerChange,
  addUser,
  seededUsers,
  showAddUserForm,
  showAddSeededUserForm,
  showEditUserForm,
  showEditSeededUserForm,
  cancelAddUser,
  cancelEditUser,
  editUser,
  deleteUser,
  fNameCount,
  lNameCount,
}) => {
  const [error, showError] = useState(false);
  const [emailValidationError, showEmailValidationError] = useState(false);
  const [mobileValidationError, showMobileValidationError] = useState(false);
  const [passwordValidationError, showPasswordValidationError] = useState(
    false
  );

  useEffect(() => {
    if (showAddUserForm || showAddUserForm) {
      showError(false);
      showEmailValidationError(false);
      showMobileValidationError(false);
      showPasswordValidationError(false);
    }
  }, [showAddUserForm, showEditUserForm]);

  const stepTwoPrev = (e) => {
    e.preventDefault();
    prev();
  };

  const stepTwoNext = (e) => {
    e.preventDefault();
    next();
  };

  const saveAsDraft= (type) =>{
    if (stepTwoOrganizationInfo.email ) {
      showError(false);
      if (
        !emailValidationError &&
        !mobileValidationError &&
        !passwordValidationError
      ) {
        addUser(type);
      }
    } else {
      showError(true);
    }
  }

  

  const addSeededUser = (type) => {
    if (
      stepTwoOrganizationInfo.firstName &&
      stepTwoOrganizationInfo.lastName &&
      stepTwoOrganizationInfo.status2 &&
      stepTwoOrganizationInfo.mobile &&
      stepTwoOrganizationInfo.startDate &&
      stepTwoOrganizationInfo.email
    ) {
      showError(false);
      if (
        !emailValidationError &&
        !mobileValidationError &&
        !passwordValidationError
      ) {
        addUser(type);
      }
    } else {
      showError(true);
    }
  };

  const editSeededUser = (type) => {
    if (
      stepTwoOrganizationInfo.firstName &&
      stepTwoOrganizationInfo.lastName &&
      stepTwoOrganizationInfo.status2 &&
      stepTwoOrganizationInfo.mobile &&
      stepTwoOrganizationInfo.startDate &&
      stepTwoOrganizationInfo.email
    ) {
      showError(false);
      if (
        !emailValidationError &&
        !mobileValidationError &&
        !passwordValidationError
      ) {
        editUser(type);
      }
    } else {
      showError(true);
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const handleEditUser = (userID) => {
    showEditSeededUserForm(userID, "viaStepTwo");
  };

  const handleDelete = (userID) => {
    deleteUser(userID, "viaStepTwo");
  };

  const validateEmailAddress = (e) => {
    var re = /\S+@\S+\.\S+/;
    if (!re.test(e.target.value) || e.target.value.length < 6) {
      showEmailValidationError(true);
    } else {
      showEmailValidationError(false);
    }
  };

  const validateMobile = () => {
    if (stepTwoOrganizationInfo.mobile.length > 10) {
      showMobileValidationError(false);
    } else {
      showMobileValidationError(true);
    }
  };

  const validatePassword = (e) => {
    var re = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,}$/;
    if (!re.test(e.target.value)) {
      showPasswordValidationError(true);
    } else {
      showPasswordValidationError(false);
    }
  };

  const { RangePicker } = DatePicker;

  return seededUsers.length === 0 || showAddUserForm || showEditUserForm ? (
    <Form className="input-md floating-label w-100" name="validate_other">
      {error && (
        <Form.Item>
          <Alert message={REQUIRED_MESSAGE} type="error" showIcon></Alert>
        </Form.Item>
      )}
      <Col span={24}>
        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel
              label={FIRST_NAME}
              value={stepTwoOrganizationInfo.firstName}
            >
              <Form.Item name="firstName" className="required">
                <Input
                  id="firstName"
                  className="input-control"
                  defaultValue={stepTwoOrganizationInfo.firstName}
                  htmlFor="firstName"
                  value={stepTwoOrganizationInfo.firstName}
                  onChange={(e) => handleStepTwoOnChange(e)}
                  autoComplete="off"
                  maxLength={50}
                />
              </Form.Item>
              {fNameCount && (
                <Form.Item>
                  <Alert message={MAX_CHAR_LIMIT} type="error"></Alert>
                </Form.Item>
              )}
            </FloatLabel>
          </Col>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel
              label={LAST_NAME}
              value={stepTwoOrganizationInfo.lastName}
            >
              <Form.Item name="lastName" className="required">
                <Input
                  className="input-control"
                  id="lastName"
                  htmlFor="lastName"
                  className="required"
                  defaultValue={stepTwoOrganizationInfo.lastName}
                  value={stepTwoOrganizationInfo.lastName}
                  onChange={(e) => handleStepTwoOnChange(e)}
                  autoComplete="off"
                  maxLength={50}
                />
              </Form.Item>
              {lNameCount && (
                <Form.Item>
                  <Alert message={MAX_CHAR_LIMIT} type="error"></Alert>
                </Form.Item>
              )}
            </FloatLabel>
          </Col>
          {/* <Col span={24} md={8} className="gutter-row">
            <FloatLabel
              label="Temporary Password"
              value={stepTwoOrganizationInfo.password}
            >
              <Form.Item name="password" className="pwd required">
                <Input.Password
                  className="input-control"
                  htmlFor="password"
                  className="required"
                  id="password"
                  defaultValue={stepTwoOrganizationInfo.password}
                  value={stepTwoOrganizationInfo.password}
                  onChange={(e) => handleStepTwoOnChange(e)}
                  onBlur={(e) => validatePassword(e)}
                  autoComplete="new-password"
                />
                {passwordValidationError && (
                  <Alert
                    message="Please enter a valid password.
                    It should be atlast 8 characters long with atleast 1 each of Capital letter, Small letter, Numeric and Special character."
                    type="error"
                    showIcon
                  ></Alert>
                )}
              </Form.Item>
            </FloatLabel>
          </Col> */}
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel label={EMAIL} value={stepTwoOrganizationInfo.email}>
              <Form.Item name="email" className="required">
                <Input
                  className="input-control"
                  id="email"
                  htmlFor="email"
                  className="required"
                  defaultValue={stepTwoOrganizationInfo.email}
                  value={stepTwoOrganizationInfo.email}
                  onChange={(e) => handleStepTwoOnChange(e)}
                  onBlur={(e) => validateEmailAddress(e)}
                  autoComplete="off"
                />
                {emailValidationError && (
                  <Alert message={VALID_EMAIL} type="error" showIcon></Alert>
                )}
              </Form.Item>
            </FloatLabel>
          </Col>
          {/* <Col span={24} md={8} className="gutter-row">
            <FloatLabel label={DATE} value="date">
              <Form.Item className="input-control datepicker-wrapper required">
                <RangePicker
                  className="new w-100"
                  name="s2Date"
                  className="required"
                  value={stepTwoOrganizationInfo.s2Date}
                  onChange={(date) =>
                    handleStepTwoDatePickerChange(date, "s2Date")
                  }
                  disabledDate={disabledDate}
                />
              </Form.Item>
            </FloatLabel>
          </Col> */}
          <Col span={24} md={4} className="gutter-row">
              <FloatLabel label={START_DATE} value="date">
                <Form.Item
                  className="input-control datepicker-wrapper required"
                >
                  <DatePicker
                    className="w-100 "
                    name="startDate"
                    value={stepTwoOrganizationInfo.startDate}
                    onChange={(date) =>
                      handleStepTwoDatePickerChange(date, "startDate")
                    }
                    disabledDate={disabledDate}
                  />
                </Form.Item>
              </FloatLabel>
          </Col>
          <Col span={24} md={4} className="gutter-row">
            <FloatLabel label={END_DATE} value="date">
              <Form.Item
                className="input-control datepicker-wrapper"
              >
                <DatePicker
                  className="w-100 "
                  name="endDate"
                  value={stepTwoOrganizationInfo.endDate}
                  onChange={(date) =>
                    handleStepTwoDatePickerChange(date, "endDate")
                  }
                  disabledDate={disabledDate}
                />
              </Form.Item>
            </FloatLabel>
          </Col>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel label={STATUS} value={stepTwoOrganizationInfo.status2}>
              <Form.Item className="required" name="status2">
                <>
                  <Select
                    className="input-control w-100 w-lg-50"
                    value={stepTwoOrganizationInfo.status2}
                    htmlFor="status2"
                    className="required"
                    onSelect={(value) =>
                      handleStepTwoOnSelect(value, "status2")
                    }
                  >
                    <Option value="1">{STATUS_ACTIVE}</Option>
                    <Option value="2">{STATUS_INACTIVE}</Option>
                  </Select>
                </>
              </Form.Item>
            </FloatLabel>
          </Col>
          <Col span={24} md={8} className="gutter-row phone-field">
            <FloatLabel label="Mobile" value={stepTwoOrganizationInfo.mobile}>
              <Form.Item name="mobile" className="required">
                <PhoneInput
                  country={"us"}
                  id="mobile"
                  htmlFor="mobile"
                  value={stepTwoOrganizationInfo.mobile}
                  onChange={handleMobile}
                  onBlur={validateMobile}
                  autoComplete="off"
                />
                {mobileValidationError && (
                  <Alert message={VALID_MOBILE} type="error" showIcon></Alert>
                )}
              </Form.Item>
            </FloatLabel>
          </Col>
        </Row>
      </Col>
      <Form.Item>
        <div className="steps-action mt-5">
          {showAddUserForm ? (
            <>
              <Button
                className="mr-4"
                type="secondary"
                onClick={() => cancelAddUser()}
              >
                {BACK}
              </Button>
              <Button
                type="primary"
                onClick={() => addSeededUser("viaAddUserButton")}
              >
                {ADD_USER}
              </Button>
            </>
          ) : showEditUserForm ? (
            <>
              <Button
                className="mr-4"
                type="secondary"
                onClick={() => cancelEditUser()}
              >
                {BACK}
              </Button>
              <Button type="primary" onClick={() => editSeededUser()}>
                {UPDATE}
              </Button>
            </>
          ) : null}
        </div>
      </Form.Item>
      <Form.Item>
        {showAddUserForm || showEditUserForm ? null : (
          <Button className="mr-4" type="secondary" onClick={stepTwoPrev}>
            {BACK}
          </Button>
        )}
        {showAddUserForm || showEditUserForm ? null : (
          <Button
            type="primary"
            htmlType="submit"
            className="mr-3"
            onClick={() => addSeededUser("viaNext")}
          >
            {NEXT}
          </Button>
        )}
         {showAddUserForm || showEditUserForm ? null : (
          <Button type="secondary"  htmlType="submit" onClick={()=>saveAsDraft(("viaNext"))}>
                {SAVE_AS_DRAFT_BTN}
          </Button>
        )}
      </Form.Item>
    </Form>
  ) : (
    <>
      <AssignedUsers
        seededUsers={seededUsers}
        showAddSeededUserForm={showAddSeededUserForm}
        fromStepTwo
        handleEditUser={handleEditUser}
        handleDelete={handleDelete}
      />
      <Form>
        <Form.Item>
          <div className="steps-action mt-5">
            <Button className="mr-4" type="secondary" onClick={stepTwoPrev}>
              {BACK}
            </Button>
            <Button type="primary" className="mr-4" htmlType="submit" onClick={stepTwoNext}>
              {NEXT}
            </Button>
            
          </div>
        </Form.Item>
      </Form>
    </>
  );
};

export default CreateOrgStepTwo;
