import React from "react";
import { Popover } from "antd";
import ExtendEndDate from "../../components/modal/ExtendEndDate";
import dotsicon from "../../assets/icons/dots-icon.svg";
import { Link } from "react-router-dom";
import {
  EDIT,
  SHOW_FINAL_RESULT,
  SHOW_INTRIM_RESULT,
  VIEW,
} from "../../headings";
import DeletePollModal from "../modal/DeletePollModal";

const content = (data, extendPollDate) => (
  <div>
    <ul>
      <Link
        to={{ pathname: `/orgcc/duplicatepoll/${data.poll_id}`, state: data }}
      >
        <li>
          <a href={`/orgcc/duplicatepoll/${data.poll_id}`}>Create Duplicate</a>{" "}
        </li>{" "}
      </Link>
      {data.poll_id != 1 && data.poll_id != 2 ? (
        <Link to={{ pathname: `/orgcc/editpoll/${data.poll_id}`, state: data }}>
          <li>
            <a href={`/orgcc/editpoll/${data.poll_id}`}>
              {data.status_id === 8 || data.status_id === 5 ? VIEW : EDIT}
            </a>{" "}
          </li>
        </Link>
      ) : null}
      {data.status_id === 8 ? (
        <Link to={{ pathname: "/orgcc/pollresponse", state: data }}>
          <li>
            <a href="/orgcc/pollresponse">{SHOW_FINAL_RESULT}</a>{" "}
          </li>{" "}
        </Link>
      ) : null}
      {data.status_id === 5 ? (
        <Link to={{ pathname: "/orgcc/pollresponse", state: data }}>
          <li>
            <a href="/orgcc/pollresponse">{SHOW_INTRIM_RESULT}</a>{" "}
          </li>{" "}
        </Link>
      ) : null}

      {(data.status_id === 5 || data.status_id === 4 || data.status_id === 7) &&
      data.poll_id != 1 &&
      data.poll_id != 2 ? (
        <li>
          <ExtendEndDate extendPollDate={extendPollDate} pollsData={data} />
        </li>
      ) : null}

      {data.poll_id != 1 &&
      data.poll_id != 2 &&
      (data.status_id === 4 || data.status_id === 7) ? (
        <li>
          <DeletePollModal pollId={data.poll_id} />
        </li>
      ) : null}
    </ul>
  </div>
);

class OpenlinePopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props.data, this.props.extendPollDate)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="ico-grid-dots">
            <img src={dotsicon} alt="DotIcon" />
          </span>
        </Popover>
      </>
    );
  }
}

export default OpenlinePopover;
