import React, { useEffect, useState } from "react";
import CreateHelp from "./CreateHelp";
import Help from "./Help";
import { connect } from "react-redux";
import { getHelpAsync } from "../../actions/help";

const HelpContainer = (props) => {
  const [loader, setLoader] = useState(false);
  const [statusClicked, setStatusClicked] = useState(false);

  useEffect(() => {
    const { dispatch } = props;
    setLoader(false);
    dispatch(getHelpAsync());
    setLoader(true);
  }, [statusClicked]);

  useEffect(() => {
    if (props.helpsdata === null) {
      setLoader(true);
    } else if (props.helpsdata !== null) {
      setLoader(false);
    }
  }, [props.helpsdata]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  return !loader && props.helpsdata !== null && props.helpsdata.length === 0 ? (
    <CreateHelp data={props} />
  ) : (
    <Help
      helps={props.helpsdata}
      data={props}
      loader={loader}
      statusOnClicked={statusOnClicked}
    />
  );
};

function mapStateToProps(state) {
  const { helpsdata } = state.help;
  return {
    helpsdata,
  };
}

export default connect(mapStateToProps)(HelpContainer);
