import UserService from '../services/user.service';

import{
    INFO_SUCCESS,
    INFO_FAIL,
    SET_MESSAGE,
    CREATEINFO_SUCCESS,
    CREATEINFO_FAIL,
    CREATEINFOSET_MESSAGE
    } from "./types";

    export const getInfoAsync=(search)=>(dispatch)=>{
        return UserService.getInfos(search).then((data)=>{
            dispatch({
                type:INFO_SUCCESS,
                payload:{infosdata:data.data},
                
            });    
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:INFO_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };

    export const createInfoAsync=(data)=>(dispatch)=>{
        return UserService.createInfo(data).then((data)=>{
            
            dispatch({
                type:CREATEINFO_SUCCESS,
                payload:{createinfodata:data.data},
                
            });    
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:CREATEINFO_FAIL,
            });
            
            dispatch({
                type:CREATEINFOSET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };    