import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Select,
  Form,
  Input,
  Divider,
  Upload,
  Image,
  Spin,
  Checkbox,
  Modal,
} from "antd";
import axios from "axios";
import moment from "moment";
import Send from "../../assets/icons/send.svg";
import { DeleteOutlined } from "@ant-design/icons";
import Note from "./Note";
import Activity from "./Activity";
import Assigned from "./Assigned";
import Location from "../broadcastMessages/Location";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import * as AppStorage from "../../utils/AppStorage";
import { Scrollbars } from "react-custom-scrollbars";
import AttachmentPopover from "./AttachmentPopover";
import "./hotlineMessages.scss";
import { isMobile } from "mobile-device-detect";
import { STATUS } from "../../labels";
import PdfIcon from "../../assets/icons/file-pdf-icon.png";
import MessagePopover from "../table/MessagePopover";
import DeleteMessagePopover from "../table/DeleteMessagePopover";
import {
  LAST_READ,
  URGENCY_LEVEL,
  SEND_MESSAGE_TEXT,
  CLEAR_CONVERSATION_HISTORY,
  CLEAR_TEXT_MODAL,
  CANCEL_MODAL,
} from "../../headings";
import {
  STATUS_CLOSED,
  STATUS_NEW,
  STATUS_OPEN,
  URGENCY_LEVEL_HIGH,
  URGENCY_LEVEL_LOW,
  URGENCY_LEVEL_MEDIUM,
} from "../../values";
import { CLEAR_CONVERSATION_TEXT } from "../../messages";

var CryptoJS = require("crypto-js");
const { TextArea } = Input;
const { Option } = Select;
const obj_status = [
  { id: 1, name: STATUS_NEW },
  { id: 2, name: STATUS_OPEN },
  { id: 3, name: STATUS_CLOSED },
];

const obj_urgencyLevel = [
  { id: 1, name: URGENCY_LEVEL_LOW },
  { id: 2, name: URGENCY_LEVEL_MEDIUM },
  { id: 3, name: URGENCY_LEVEL_HIGH },
];

const Messages = ({
  conversation,
  urgencyLevel,
  status,
  onChangeUrgencyLevel,
  onChangeStatus,
  searchString,
  newState,
  setCounter,
  socket,
  chatdiv,
}) => {
  const [form] = Form.useForm();
  let userID = "";
  if (AppStorage.get("user")) {
    const user = JSON.parse(AppStorage.get("user"));
    userID = parseInt(
      CryptoJS.AES.decrypt(
        user?.user_details?.org_user_id,
        "anonymoose"
      ).toString(CryptoJS.enc.Utf8)
    );
  }
  const [convoMessages, setConvoMessages] = useState([]);
  const [showMap, setShowMap] = useState(false);
  const [latLng, setLatLng] = useState({ lat: 0, lng: 0 });
  const [message, setMessage] = useState("");
  const [showAttachmentSection, setShowAttachmentSection] = useState(false);
  const [convMsg, setConvMsg] = useState({});
  const [isImage, setIsImage] = useState(false);
  const [timeExpired, setTimeExpired] = useState({});
  const [documentFile, setDocumentFile] = useState({
    document: "",
    documentValidation: true,
    previewVisible: false,
    previewImage: "",
    previewTitle: "",
  });
  const [imageFile, setImageFile] = useState({
    image: "",
    imageValidation: true,
    previewVisible: false,
    previewImage: "",
    previewTitle: "",
  });
  const [attachmentName, setAttachmentName] = useState("");
  const [displayNote, setDisplayNote] = useState({
    noteDivClicked: false,
    myClass: "",
  });
  const [uploadError, showUploadError] = useState(true);
  const [modal, setModal] = useState(false);
  const [messagesIds, setMessagesIds] = useState([]);
  const [checkedMessages, setCheckedMessages] = useState([]);
  const [mapLocation, setMapLocation] = useState({
    latitude: "",
    longitude: "",
    address: "",
  });
  const [displayActivity, setDisplayActivity] = useState({
    activityDivClicked: false,
    myClass: "",
  });

  const [displayAssigne, setDisplayAssigne] = useState({
    assigneDivClicked: false,
    myClass: "",
  });
  const [showDeleteCheckboxes, setShowDeleteCheckboxes] = useState(false);
  const [hideDiv, sethideDiv] = useState(false);
  const [searchBy, setSearchBy] = useState("");
  const [loader, setLoader] = useState(false);
  const [hidePopup, setHidePopup] = useState(false);
  const [deleteMessagePopup, setHideDeleteMessagePopup] = useState(false);

  useEffect(() => {
    setSearchBy(searchString);
  }, [searchString, conversation.conversation_id]);

  useEffect(() => {
    getHotlineMessages();
    var activity = document.getElementById("activityId");
    var note = document.getElementById("noteId");
    var assigne = document.getElementById("assigneId");
    if (displayAssigne.myClass != "") {
      setDisplayAssigne({ myClass: "", assigneDivClicked: false });
      assigne.classList.remove("selected");
    }
    if (displayNote.myClass != "") {
      setDisplayNote({ myClass: "", noteDivClicked: false });
      note.classList.remove("selected");
    }
    if (displayActivity.myClass != "") {
      setDisplayActivity({ myClass: "", activityDivClicked: false });
      activity.classList.remove("selected");
    }
    const messagevalue =
      convMsg[conversation.conversation_id] !== undefined
        ? convMsg[conversation.conversation_id]
        : "";
    setMessage(messagevalue);
    setMessagesIds([]);
    setCheckedMessages([]);
    setShowDeleteCheckboxes(false);
    setTimeExpired({});
  }, [conversation.conversation_id]);

  useEffect(() => {
    connect();
  }, [newState]);

  useEffect(() => {
    if (convoMessages.length > 0) {
      if (searchBy.length > 1) {
        let count = 0;
        convoMessages.map((value) => {
          if (value.message.includes("</mark>")) {
            count += 1;
          }
          return count;
        });
        var highlightedMessage = document.getElementsByTagName("mark")[
          count - 1
        ];
        if (highlightedMessage) {
          highlightedMessage.scrollIntoView(false, { block: "end" });
        }
      } else {
        var lastMessage = document.getElementsByClassName("hotline_message")[
          document.getElementsByClassName("hotline_message").length - 1
        ];
        if (lastMessage) {
          lastMessage.scrollIntoView(false, { block: "end" });
        }
      }
    }
  }, [convoMessages, searchString]);

  const getHotlineMessages = async () => {
    setLoader(true);
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    let convID = "";
    if (conversation.conversation_id.includes("<mark")) {
      convID = conversation.conversation_id
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
    } else {
      convID = conversation.conversation_id;
    }
    var data = {
      conversation_id: convID,
      user_id: parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      ),
    };

    axios
      .post(BASE_URL + "hotlineConvoMessages/", data, options)
      .then((res) => {
        setConvoMessages(res.data);
        setLoader(false);
      });
  };

  const handleChange = (e) => {
    setMessage(e.target.value);
    setConvMsg({ ...convMsg, [e.target.name]: e.target.value });
  };

  const openAttachmentSection = () => {
    setShowAttachmentSection(!showAttachmentSection);
  };

  const closeAttachmentSection = () => {
    setShowAttachmentSection(false);
  };

  const setLocation = (latitude, longitude, name) => {
    var obj = {
      latitude: latitude,
      longitude: longitude,
      address: name,
    };
    setMessage("$$$L#o#c#a#t#i#o#n$$$");
    setMapLocation(obj);
  };

  const handleOk = () => {
    setModal(false);
    clearConversation();
  };

  const handleClose = () => {
    setModal(false);
  };

  const handleUploadDocument = (value) => {
    if (value.file.status === "removed") {
      showUploadError(true);
    }
    let fileList = value.fileList.slice(-5);
    checkDoucmentType(value.file.type);
    fileList.forEach(function (file, index) {
      let reader = new FileReader();
      reader.onload = (e) => {
        file.base64 = e.target.result;
      };
      reader.readAsDataURL(file.originFileObj);
    });
    setAttachmentName(value.file.name);
    if (value.file.status !== "removed") {
      setMessage("$$$D#o#c#u#m#e#n#t$$$");
    }
    if (fileList.length === 0) {
      setMessage("");
    }
    setDocumentFile({ ...documentFile, document: fileList });
  };

  const handleUploadImage = (value) => {
    if (value.file.status === "removed") {
      showUploadError(true);
    }
    let fileList = value.fileList.slice(-5);
    checkDoucmentType(value.file.type);
    fileList.forEach(function (file, index) {
      let reader = new FileReader();
      reader.onload = (e) => {
        file.base64 = e.target.result;
      };
      reader.readAsDataURL(file.originFileObj);
    });
    setAttachmentName(value.file.name);
    if (value.file.status !== "removed") {
      setMessage("$$$I#m#a#g#e$$$");
    }
    if (fileList.length === 0) {
      setMessage("");
    }
    setImageFile({ ...imageFile, image: fileList });
  };

  const checkDoucmentType = (fileType) => {
    const isImage = fileType.includes("image");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
  };

  const handlePreview = async (file) => {
    file.preview = await getBase64(file.originFileObj);
    if (isImage) {
      setImageFile({
        ...imageFile,
        previewImage: file.url || file.preview,
        previewVisible: true,
        previewTitle:
          file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
      });
    } else {
      setDocumentFile({
        ...documentFile,
        previewImage: file.url || file.preview,
        previewVisible: true,
        previewTitle:
          file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
      });
    }
  };

  const handleCancel = () => {
    if (isImage) {
      setImageFile({ ...imageFile, previewVisible: false });
    } else {
      setDocumentFile({ ...documentFile, previewVisible: false });
    }
  };

  const getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const handleOnRemove = () => {
    if (isImage) {
      setImageFile({ ...imageFile, imageValidation: true });
    } else {
      setDocumentFile({ ...documentFile, documentValidation: true });
    }
  };

  const handleBeforeUpload = (file) => {
    let xlsxfileExt = file.name.includes(".xls");
    let pptxfileExt = file.name.includes(".pptx");
    let docfileExt = file.name.includes(".doc");
    let csvfileExt = file.name.includes(".csv");

    const isValidDoc =
      file.type === "image/jpeg" ||
      file.type === "image/png" ||
      file.type === "image/gif" ||
      file.type === "application/pdf" ||
      xlsxfileExt ||
      pptxfileExt ||
      docfileExt ||
      csvfileExt;
    if (!isValidDoc) {
      showUploadError(false);
      message.error(`${file.name} is not a supported format file type.`);
    } else {
      showUploadError(true);
    }
    const isImage =
      file.type === "image/jpeg" ||
      file.type === "image/png" ||
      file.type === "image/gif";
    const isDocument =
      file.type === "application/pdf" ||
      xlsxfileExt ||
      pptxfileExt ||
      docfileExt ||
      csvfileExt;
    const isLt5M = file.size / 1024 / 1024 < 5;
    const isLt10M = file.size / 1024 / 1024 < 10;
    if (isImage) {
      if (!isLt5M && isImage) {
        showUploadError(false);
        message.error("Image must smaller than 5MB!");
      } else {
        showUploadError(true);
      }
    }

    if (isDocument) {
      if (!isLt10M && isDocument) {
        showUploadError(false);
        message.error("Document must smaller than 10MB!");
      } else {
        showUploadError(true);
      }
    }
    return (isValidDoc && isLt5M) || isLt10M;
  };

  const keyDown = (e) => {
    if ((e.altKey && e.keyCode === 13) || (e.ctrlKey && e.keyCode === 13)) {
      var textarea = document.querySelector(".input-control");
      textarea.value = textarea.value + "\r\n";
    }
    if (!e.altKey && !e.shiftKey && !e.ctrlKey && e.keyCode === 13) {
      e.preventDefault();
      onSendMessageClick();
    }
  };

  const onSendMessageClick = () => {
    const token = authHeader();
    let convID = "";
    if (conversation.conversation_id.includes("<mark")) {
      convID = conversation.conversation_id
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
    } else {
      convID = conversation.conversation_id;
    }
    const user = JSON.parse(AppStorage.get("user"));
    if (message.length > 0) {
      const options = {
        headers: {
          Authorization: token,
          Accept: "application/json, text/plain",
          "Content-Type": "application/json;charset=UTF-8",
        },
      };
      const attachmentData = [];
      let attachmentObject = {};
      if (documentFile.document?.length > 0) {
        documentFile.document?.map((value) => {
          attachmentObject = {
            name: attachmentName,
            attach: value.base64,
          };
          attachmentData.push(attachmentObject);
        });
      } else if (imageFile.image?.length > 0) {
        imageFile.image?.map((value) => {
          attachmentObject = {
            name: attachmentName,
            attach: value.base64,
          };
          attachmentData.push(attachmentObject);
        });
      } else {
        attachmentData.push({ name: "", attach: "" }, { name: "", attach: "" });
      }

      var data = {
        user_id: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
        hotline_id: conversation.hotline_id,
        conversation_id: convID,
        message: message,
        created_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
        attachment_type:
          documentFile.document?.length > 0
            ? "document"
            : imageFile.image?.length > 0
            ? "image"
            : mapLocation.latitude !== ""
            ? "location"
            : "",
        latitude: mapLocation.latitude,
        longitude: mapLocation.longitude,
        address: mapLocation.address,
        attachments: attachmentData,
      };
      // Sending request for first message through API
      axios
        .post(BASE_URL + "receiveHotlineMessage/", data, options)
        .then((res) => {
          if (res.status === 201) {
            if (res.data.status_id === 1 || res.data.status_id === 3) {
              onChangeStatus(2, convID);
            }
            if (
              documentFile.document.length > 0 ||
              imageFile.image.length > 0 ||
              mapLocation.latitude !== ""
            ) {
              setDocumentFile({
                document: "",
                documentValidation: true,
                previewVisible: false,
                previewImage: "",
                previewTitle: "",
              });
              setImageFile({
                image: "",
                imageValidation: true,
                previewVisible: false,
                previewImage: "",
                previewTitle: "",
              });
              setMapLocation({
                latitude: "",
                longitude: "",
                address: "",
              });
            }
            setSearchBy("");
            setCounter(res);
            setMessage("");
            setConvMsg({ ...convMsg, [convID]: "" });
            socket.send(JSON.stringify(res.data));
          }
        });
    }
    form.setFieldsValue({ chatArea: "" });
  };

  const showMapModal = (lat, lng) => {
    setLatLng({ lat: lat, lng: lng });
    setShowMap(true);
  };

  const deleteLocation = () => {
    setMessage("");
    setMapLocation({
      latitude: "",
      longitude: "",
      address: "",
    });
  };

  const connect = () => {
    setConvoMessages((convoMessages) => {
      let arrayOf = [];
      // Condition to pass the message to particular conv
      if (
        newState.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "") ===
        conversation.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "")
      ) {
        let filteredDataArray = convoMessages.filter(
          (item) =>
            item.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "") ===
            newState.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "")
        );
        if (newState.message.includes("This message was deleted")) {
          const ids = newState.message.substring(
            newState.message.lastIndexOf(".") + 1,
            newState.message.length
          );
          const messageIds = ids.split(",");
          filteredDataArray = filteredDataArray.filter(
            (filter) =>
              !messageIds.includes(
                filter.user_hotline_conversation_id.toString()
              )
          );
          arrayOf = [...filteredDataArray, newState];
        } else {
          arrayOf = [...filteredDataArray, newState];
        }
      } else {
        arrayOf = [...convoMessages];
      }
      return arrayOf;
    });
  };

  const notesClicked = (e) => {
    var notes = document.getElementById("noteId");
    if (isMobile) {
      sethideDiv(true);
    }
    if (displayActivity.myClass != "") {
      setDisplayActivity({ myClass: "", activityDivClicked: false });
    }
    if (displayAssigne.myClass != "") {
      setDisplayAssigne({ myClass: "", assigneDivClicked: false });
    }
    if (displayNote.myClass === "") {
      notes.classList.add("selected");
      setDisplayNote({ myClass: "rgt-panel", noteDivClicked: true });
    } else {
      notes.classList.remove("selected");
      setDisplayNote({ myClass: "", noteDivClicked: false });
    }
    var activity = document.getElementById("activityId");
    activity.classList.remove("selected");
    var assigne = document.getElementById("assigneId");
    assigne.classList.remove("selected");
  };

  const activityClicked = (e) => {
    var activity = document.getElementById("activityId");
    if (isMobile) {
      sethideDiv(true);
    }
    if (displayNote.myClass != "") {
      setDisplayNote({ myClass: "", noteDivClicked: false });
    }
    if (displayAssigne.myClass != "") {
      setDisplayAssigne({ myClass: "", assigneDivClicked: false });
    }
    if (displayActivity.myClass === "") {
      activity.classList.add("selected");
      setDisplayActivity({ myClass: "rgt-panel", activityDivClicked: true });
    } else {
      activity.classList.remove("selected");
      setDisplayActivity({ myClass: "", activityDivClicked: false });
    }
    var notes = document.getElementById("noteId");
    notes.classList.remove("selected");
    var assigne = document.getElementById("assigneId");
    assigne.classList.remove("selected");
  };

  const assigneClicked = (e) => {
    var assigne = document.getElementById("assigneId");
    if (isMobile) {
      sethideDiv(true);
    }
    if (displayNote.myClass != "") {
      setDisplayNote({ myClass: "", noteDivClicked: false });
    }
    if (displayActivity.myClass != "") {
      setDisplayActivity({ myClass: "", activityDivClicked: false });
    }
    if (displayAssigne.myClass === "") {
      assigne.classList.add("selected");
      setDisplayAssigne({ myClass: "rgt-panel", assigneDivClicked: true });
    } else {
      assigne.classList.remove("selected");
      setDisplayAssigne({ myClass: "", assigneDivClicked: false });
    }
    var activity = document.getElementById("activityId");
    activity.classList.remove("selected");
    var notes = document.getElementById("noteId");
    notes.classList.remove("selected");
  };

  const showDeleteMessagesCheckboxes = () => {
    setShowDeleteCheckboxes(true);
    setHidePopup(true);
  };

  const hideDeleteMessagesCheckboxes = () => {
    setShowDeleteCheckboxes(false);
    setHidePopup(true);
  };

  const deleteMessage = (type, message_ids) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      user_hotline_message_id: message_ids,
      type: type === "deleteForMe" ? "delete_by_user_id" : "delete_for_all",
      user_id: parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      ),
    };
    axios.post(BASE_URL + "deleteHotlineMessage/", data, options).then(() => {
      getHotlineMessages();
      setHidePopup(true);
      setMessagesIds([]);
      setCheckedMessages([]);
      setShowDeleteCheckboxes(false);
      if (type !== "deleteForMe") {
        var dataForReceiveAPI = {
          user_id: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
          hotline_id: conversation.hotline_id,
          conversation_id: conversation.conversation_id,
          message: `This message was deleted.${message_ids}`,
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
          address: "",
          attachment_type: "",
          latitude: "",
          longitude: "",
          attachments: [
            {
              name: "",
              attach: "",
            },
            {
              name: "",
              attach: "",
            },
          ],
        };
        axios
          .post(BASE_URL + "receiveHotlineMessage/", dataForReceiveAPI, options)
          .then((response) => {
            if (response.status === 201) {
              socket.send(JSON.stringify(response.data));
            }
          });
      }
    });
  };

  const clearConversation = () => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      user_id: parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      ),
      hotline_conversation_id: conversation.conversation_id,
    };
    axios
      .post(BASE_URL + "clearHotlineConversation/", data, options)
      .then(() => {
        setConvoMessages([]);
        setMessagesIds([]);
        setCheckedMessages([]);
      });
  };

  const createMarkup = (html) => {
    return { __html: html };
  };

  const openModal = () => {
    setModal(true);
  };

  const onChangeCheckbox = (e, value, msg) => {
    if (e.target.checked) {
      setMessagesIds([...messagesIds, value]);
      setCheckedMessages([...checkedMessages, msg]);
      if (msg.is_responder_message) {
        const currentDateTime = new Date();
        const messageTime = new Date(msg.created_on);
        const EIGHT_MINS = 480000;
        const diff = currentDateTime - messageTime;
        console.log("currentDateTime: ", currentDateTime);
        console.log("messageTime: ", messageTime);
        console.log("diff: ", diff);
        if (diff > EIGHT_MINS) {
          console.log("IF");
          setTimeExpired({ ...timeExpired, [value]: true });
        } else {
          console.log("ELSE");
          const time = EIGHT_MINS - diff;
          console.log("ELSE TIME: ", time);
          setTimeExpired({ ...timeExpired, [value]: false });
          setTimeout(() => {
            console.log("TIMEOUT");
            setTimeExpired({ ...timeExpired, [value]: true });
          }, time);
        }
      }
    } else {
      setMessagesIds(messagesIds.filter((id) => id !== value));
      setCheckedMessages(
        checkedMessages.filter(
          (msg) => msg.user_hotline_conversation_id.toString() !== value
        )
      );
      delete timeExpired[value];
    }
  };

  if (searchString) {
    if (conversation.responder_conversation_name.includes("</mark>")) {
      conversation.responder_conversation_name = conversation.responder_conversation_name
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
      conversation.responder_conversation_name = conversation.responder_conversation_name.replace(
        new RegExp(searchString, "gi"),
        (match) =>
          `<mark style="background: #2769AA; color: white;">${match}</mark>`
      );
    }
    if (conversation.hotline_name.includes("</mark>")) {
      conversation.hotline_name = conversation.hotline_name
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
      conversation.hotline_name = conversation.hotline_name.replace(
        new RegExp(searchString, "gi"),
        (match) =>
          `<mark style="background: #2769AA; color: white;">${match}</mark>`
      );
    }
    if (conversation.conversation_id?.toString().includes("</mark>")) {
      conversation.conversation_id = conversation.conversation_id
        ?.toString()
        .replace(/(<([^>]+)>)/gi, "");
      conversation.conversation_id = conversation.conversation_id
        ?.toString()
        .replace(
          new RegExp(searchString, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
    }
    convoMessages.map((item) => {
      if (item.message.includes("</mark>")) {
        item.message = item.message?.toString().replace(/(<([^>]+)>)/gi, "");
        item.message = item.message.replace(
          new RegExp(searchString, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
      } else {
        item.message = item.message.replace(/(?:\r\n|\r|\n)/g, "<br />");
        item.message = item.message.replace(
          new RegExp(searchString, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
      }
      return {
        ...item,
        message: item.message,
      };
    });
  } else {
    conversation.responder_conversation_name = conversation.responder_conversation_name
      ?.toString()
      .replace(/(<([^>]+)>)/gi, "");
    conversation.hotline_name = conversation.hotline_name
      ?.toString()
      .replace(/(<([^>]+)>)/gi, "");
    conversation.conversation_id = conversation.conversation_id
      ?.toString()
      .replace(/(<([^>]+)>)/gi, "");
    convoMessages.map((item) => {
      item.message = item.message
        ?.toString()
        .replace(/(?:\r\n|\r|\n)/g, "<br />");
      return {
        ...item,
        message: item.message,
      };
    });
  }

  let messageLength = convoMessages.length - conversation.unread_messages_count;

  return (
    <Col
      span={24}
      lg={18}
      className={
        chatdiv ? "chat-section d-lg-flex" : "chat-section d-lg-flex d-none"
      }
      data-test="messages"
    >
      <Col span={24} className="h-100">
        <Row className="top-filter-pan p-2 p-lg-4 d-flex align-items-center">
          <Col span={24} lg={10}>
            <div className="">
              <h6
                dangerouslySetInnerHTML={createMarkup(
                  conversation.responder_conversation_name
                )}
              />
              <p
                style={{ margin: 0, display: "inline", float: "left" }}
                dangerouslySetInnerHTML={createMarkup(
                  conversation.hotline_name
                )}
              />
              &nbsp;|&nbsp;
              <p
                style={{ margin: 0, display: "inline", float: "center" }}
                dangerouslySetInnerHTML={createMarkup(
                  conversation.conversation_id
                )}
              />
            </div>
          </Col>
          <Col span={24} lg={14}>
            <div className="d-flex justify-content-start justify-content-lg-end pt-3 pt-sm-1 pt-md-0">
              <div className="selection-panel pr-3">
                <label>{URGENCY_LEVEL}</label>
                <Select
                  bordered={false}
                  value={urgencyLevel}
                  onChange={(event) =>
                    onChangeUrgencyLevel(
                      event,
                      conversation.conversation_id
                        ?.toString()
                        .replace(/(<([^>]+)>)/gi, "")
                    )
                  }
                >
                  {obj_urgencyLevel.map((u) => (
                    <Option value={u.id}>{u.name}</Option>
                  ))}
                </Select>
              </div>
              <div className="selection-panel pr-3">
                <label>{STATUS}</label>
                <Select
                  bordered={false}
                  value={status}
                  onChange={(event) =>
                    onChangeStatus(
                      event,
                      conversation.conversation_id
                        ?.toString()
                        .replace(/(<([^>]+)>)/gi, "")
                    )
                  }
                >
                  {obj_status.map((s) =>
                    (status === 2 || status === 3) && s.id === 1 ? (
                      <Option value={s.id} disabled>
                        {s.name}
                      </Option>
                    ) : (
                      <Option value={s.id}>{s.name}</Option>
                    )
                  )}
                </Select>
              </div>
              <div className="icon-row d-flex align-items-center">
                <Col span={8}>
                  <span
                    id="noteId"
                    className="icon icon-create"
                    onClick={(e) => notesClicked(e)}
                  ></span>
                  {/* add selected class to activr icon */}
                </Col>
                <Col span={8}>
                  <span
                    id="activityId"
                    className="icon icon-activity"
                    onClick={(e) => activityClicked(e)}
                  ></span>
                </Col>
                <Col span={8}>
                  <span
                    id="assigneId"
                    className="icon icon-assign"
                    onClick={(e) => assigneClicked(e)}
                  ></span>
                </Col>
               
              </div>
              {checkedMessages.length > 0 && (
                  <DeleteMessagePopover
                    deleteMessage={deleteMessage}
                    setHideDeleteMessagePopup={setHideDeleteMessagePopup}
                    deleteMessagePopup={deleteMessagePopup}
                    userID={userID}
                    convoMessages={convoMessages}
                    timeExpired={timeExpired}
                    showDeleteCheckboxes={showDeleteCheckboxes}
                    messagesIds={messagesIds}
                    checkedMessages={checkedMessages}
                    hideDeleteMessagesCheckboxes={hideDeleteMessagesCheckboxes}
                    hotline
                  />
                )}
              {checkedMessages.length === 0 && (
                <MessagePopover
                  showDeleteMessagesCheckboxes={showDeleteMessagesCheckboxes}
                  openModal={openModal}
                  setHidePopup={setHidePopup}
                  hidePopup={hidePopup}
                  convoMessages={convoMessages}
                  showDeleteCheckboxes={showDeleteCheckboxes}
                />
              )}
            </div>
          </Col>
        </Row>
        {/* filter end */}

        <Spin spinning={loader} size={"medium"}>
          <Row className="row-hgt">
            <div
              className={
                displayNote.noteDivClicked ||
                displayActivity.activityDivClicked ||
                displayAssigne.assigneDivClicked
                  ? hideDiv
                    ? "w-75 d-none"
                    : "w-75"
                  : "w-100"
              }
            >
              {/* add class w-75 when click on right side Note, Activity and Assign icon and remove once panel closed*/}
              <div className="msg_main">
                <Modal
                  title={CLEAR_CONVERSATION_HISTORY}
                  visible={modal}
                  onOk={handleOk}
                  onCancel={handleClose}
                  centered
                  width={350}
                  okButtonProps={{
                    children: "Custom OK",
                  }}
                  cancelButtonProps={{
                    children: "Custom cancel",
                  }}
                  okText={CLEAR_TEXT_MODAL}
                  cancelText={CANCEL_MODAL}
                >
                  <p>{CLEAR_CONVERSATION_TEXT}</p>
                </Modal>
                <Scrollbars autoHeightMax={450} universal={true}>
                  <Col span={24} className="msg-history p-4">
                    {convoMessages.length > 0 &&
                      convoMessages.map((msg, index) => (
                        <div>
                          {messageLength === index ? (
                            <Divider plain>{LAST_READ}</Divider>
                          ) : null}
                          {msg.message.includes(
                            "Urgency Level has been changed to"
                          ) ||
                          msg.message.includes("Status has been changed to") ? (
                            <Divider plain>{msg.message}</Divider>
                          ) : null}
                          {msg.message.includes(
                            "Urgency Level has been changed to"
                          ) ||
                          msg.message.includes(
                            "Status has been changed to"
                          ) ? null : (
                            <div
                              className={
                                msg.is_responder_message
                                  ? "outgoing-msg hotline_message mb-3"
                                  : "incoming-msg hotline_message mb-2"
                              }
                            >
                              <div
                                className={
                                  msg.is_responder_message
                                    ? "sent-msg"
                                    : "received-msg"
                                }
                              >
                                <div className="d-flex align-items-center">
                                  {showDeleteCheckboxes &&
                                    !msg.message.includes(
                                      "This message was deleted"
                                    ) && (
                                      <>
                                        <Checkbox
                                          className="font-13 pr-2"
                                          onChange={(e) =>
                                            onChangeCheckbox(
                                              e,
                                              msg.user_hotline_conversation_id?.toString(),
                                              msg
                                            )
                                          }
                                        ></Checkbox>
                                      </>
                                    )}
                                  {msg.image_url?.length > 0 ? (
                                    <>
                                      {" "}
                                      <Image
                                        width={100}
                                        src={msg.image_url}
                                        className="pr-3"
                                        placeholder={msg.image_name}
                                        alt={msg.image_name}
                                      />
                                      {msg.image_name}
                                    </>
                                  ) : msg.document_url?.length > 0 ? (
                                    <a href={msg.document_url} target="_blank">
                                      {msg.document_url.substring(
                                        msg.document_url.length - 3
                                      ) === "pdf" ? (
                                        <>
                                          {" "}
                                          <img src={PdfIcon}></img>
                                          {msg.document_name}
                                        </>
                                      ) : (
                                        <>
                                          {" "}
                                          <span className="icon icon-doc"></span>
                                          {msg.document_name}
                                        </>
                                      )}
                                    </a>
                                  ) : msg.longitude !== null ? (
                                    <>
                                      {" "}
                                      <span
                                        className="icon icon-location"
                                        onClick={() =>
                                          showMapModal(
                                            msg.latitude,
                                            msg.longitude
                                          )
                                        }
                                      >
                                        {msg.address}
                                      </span>
                                      {showMap ? (
                                        <Location
                                          setLocation={setLocation}
                                          viaMessages
                                          viaModal
                                          setShowMap={setShowMap}
                                          latitude={latLng.lat}
                                          longitude={latLng.lng}
                                        />
                                      ) : null}
                                    </>
                                  ) : msg.message.includes(
                                      "This message was deleted"
                                    ) ? (
                                    <i>
                                      {msg.message.substring(
                                        0,
                                        msg.message.lastIndexOf(".")
                                      )}
                                    </i>
                                  ) : (
                                    <p
                                      ellipsis
                                      // className="w-75 pr-2 mb-1"
                                      dangerouslySetInnerHTML={createMarkup(
                                        msg.message
                                      )}
                                    />
                                  )}
                                </div>
                                <span className="time-date">
                                  {" "}
                                  {moment(msg.created_on).isSame(
                                    moment(),
                                    "day"
                                  )
                                    ? moment(msg.created_on)
                                        .utc()
                                        .local()
                                        .format("hh:mm A")
                                    : moment(msg.created_on)
                                        .utc()
                                        .local()
                                        .format("MM:DD:YY | hh:mm A ")}
                                  {msg.is_responder_message &&
                                    ` | ${msg.first_name} ${msg.last_name}`}
                                </span>
                              </div>
                            </div>
                          )}
                        </div>
                      ))}
                  </Col>
                </Scrollbars>
              </div>
              <Col
                span={24}
                className={
                  displayNote.noteDivClicked ||
                  displayActivity.activityDivClicked ||
                  displayAssigne.assigneDivClicked
                    ? "send-msg w-75"
                    : "send-msg w-100"
                }
              >
                {/* add class w-75 when click on right side Note, Activity and Assign icon and toggle with w-100 once panel closed*/}
                <div className="type-msg">
                  <AttachmentPopover
                    openAttachmentSection={openAttachmentSection}
                    closeAttachmentSection={closeAttachmentSection}
                    showAttachmentSection={showAttachmentSection}
                    setLocation={setLocation}
                    handleUploadImage={handleUploadImage}
                    handleUploadDocument={handleUploadDocument}
                    handleBeforeUpload={handleBeforeUpload}
                    handleOnRemove={handleOnRemove}
                    handlePreview={handlePreview}
                    documentFile={documentFile}
                    imageFile={imageFile}
                    message={message}
                    mapLocation={mapLocation}
                  />
                  <Form
                    className="input-md w-100"
                    name="validate-other"
                    form={form}
                  >
                    {documentFile.document.length > 0 ? (
                      <Upload
                        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                        listType="picture"
                        fileList={documentFile.document}
                        onPreview={(value) => handlePreview(value)}
                        onRemove={() => handleOnRemove()}
                        onChange={(value) => handleUploadDocument(value)}
                        beforeUpload={(value) => handleBeforeUpload(value)}
                      ></Upload>
                    ) : imageFile.image.length > 0 ? (
                      <Upload
                        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                        listType="picture"
                        fileList={imageFile.image}
                        onPreview={(value) => handlePreview(value)}
                        onRemove={() => handleOnRemove()}
                        onChange={(value) => handleUploadImage(value)}
                        beforeUpload={(value) => handleBeforeUpload(value)}
                      ></Upload>
                    ) : mapLocation.longitude !== "" ? (
                      <>
                        {" "}
                        <span
                          className="icon icon-location"
                          onClick={() => showMapModal()}
                        ></span>
                        {mapLocation.address}{" "}
                        <DeleteOutlined onClick={() => deleteLocation()} />
                      </>
                    ) : (
                      <TextArea
                        className="input-control"
                        id="chatArea"
                        name={conversation.conversation_id}
                        placeholder={SEND_MESSAGE_TEXT}
                        onChange={(e) => handleChange(e)}
                        value={message}
                        onKeyDown={keyDown}
                        maxLength={10000}
                        disabled={status === 3}
                      />
                    )}
                  </Form>
                  <span
                    className="ml-3 send"
                    onClick={() => onSendMessageClick()}
                  >
                    <img src={Send} alt="send message" />
                  </span>
                </div>
              </Col>
            </div>
            <div className="w-25 d-flex justify-content-end width-sm-100">
              {/* Converstaion Notes */}
              {displayNote.noteDivClicked ? (
                <Note
                  class={displayNote.myClass}
                  conversation_id={conversation.conversation_id
                    ?.toString()
                    .replace(/(<([^>]+)>)/gi, "")}
                  notesClicked={() => notesClicked()}
                />
              ) : null}
              {displayActivity.activityDivClicked ? (
                <Activity
                  class={displayActivity.myClass}
                  conversation_id={conversation.conversation_id
                    ?.toString()
                    .replace(/(<([^>]+)>)/gi, "")}
                  activityClicked={() => activityClicked()}
                />
              ) : null}
              {displayAssigne.assigneDivClicked ? (
                <Assigned
                  class={displayAssigne.myClass}
                  hotline_id={conversation.hotline_id}
                  assigneClicked={() => assigneClicked()}
                />
              ) : null}
            </div>
          </Row>
        </Spin>
      </Col>
    </Col>
  );
};

export default Messages;
