import React from "react";
import { Layout, Row, Col, Divider, Button } from "antd";
import { LoggedinHeader } from "../../containers";
import CreateInfoImg from "../../assets/images/create-info.svg";
import OrgSidebar from "../../containers/OrgSidebar";
import { CREATE_INFO } from "../../headings";
import { useHistory } from "react-router-dom";
import { INFO_TEXT } from "../../messages";

const { Content } = Layout;

const CreateInfo = (props) => {
  const history = useHistory();
  return (
    <React.Fragment>
      {" "}
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={props.data} />
            <Content data-test="Broadcast" className="">
              <Row className="py-3">
                <h1 className="mb-0">{CREATE_INFO}</h1>
              </Row>
              <div className="mid-content d-flex justify-content-center align-items-center">
                <Row>
                  <Col span={24}>
                    <div className="c-vector-area d-flex justify-content-center">
                      <img src={CreateInfoImg} alt="Create Info" />
                    </div>
                    <p className="text-center pt-3">{INFO_TEXT}</p>
                    <Row className="d-flex justify-content-center">
                      <Divider className="mt-1" />
                    </Row>
                    <Col
                      span={24}
                      className="d-flex align-items-center justify-content-center"
                    >
                      <Button
                        onClick={() => {
                          history.push("/orgcc/createinfo");
                        }}
                        type="primary"
                      >
                        {CREATE_INFO}
                      </Button>
                    </Col>
                  </Col>
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </React.Fragment>
  );
};

export default CreateInfo;
