import React, { Component } from "react";
import { Modal, Button, Form, Col, Input } from "antd";
import FloatLabel from "../form/FloatLabels";
import "../modal/modal.scss";
import {
  CANCEL_MODAL,
  RESET_INVITE,
  RESET_PASSWORD,
  RESET_PASSWORD_FOR,
  RESET_PASSWORD_LINK,
} from "../../headings";
import { CONFIRM_PASSWORD, NEW_PASSWORD } from "../../labels";
import {ENTER_NEW_PASSWORD, CONFIRM_YOUR_PASSWORD} from "../../errors";

class ResetPwdModal extends Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.props.handleResetPasswordOk();
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
    this.props.handleResetPasswordCancel();
  };

  render() {
    return (
      <>
        <Button type="link" className="link pl-0 mt-4" onClick={this.showModal}>
          <span>{RESET_PASSWORD_LINK}</span>
        </Button>
        <Modal
          title={RESET_PASSWORD}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={370}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText={RESET_INVITE}
          cancelText={CANCEL_MODAL}
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              <div className="mb-4 subtext-md">
                {RESET_PASSWORD_FOR}{" "}
                <span className="font-weight-md">{this.props.user}</span>
              </div>
              <FloatLabel label={NEW_PASSWORD} value="">
                <Form.Item
                  name="newpwd"
                  className="pwd"
                  rules={[
                    {
                      required: true,
                      message: {ENTER_NEW_PASSWORD},
                    },
                  ]}
                >
                  <Input.Password
                    className="input-control"
                    htmlFor="newpwd"
                    className="required"
                    value=""
                  />
                </Form.Item>
              </FloatLabel>
              <FloatLabel label={CONFIRM_PASSWORD} value="">
                <Form.Item
                  name="confirmpwd"
                  className="pwd"
                  rules={[
                    {
                      required: true,
                      message: {CONFIRM_YOUR_PASSWORD},
                    },
                  ]}
                >
                  <Input.Password
                    className="input-control"
                    htmlFor="confirmpwd"
                    className="required"
                    value=""
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default ResetPwdModal;
