import React from "react";
import { Popover } from "antd";
import DeleteBroadcast from "../../components/modal/DeleteBroadcast";
import dotsicon from "../../assets/icons/dots-icon.svg";
import { Link } from "react-router-dom";
import { EDIT } from "../../headings";

const content = (test) => (
  <div>
    <ul>
      {test.status_id === "7" ? (
        <Link
          to={{
            pathname: `/orgcc/editbroadcast/${test.broadcast_id}`,
            state: test,
          }}
        >
          <li>
            <a href={`/orgcc/editbroadcast/${test.broadcast_id}`}>{EDIT}</a>{" "}
          </li>
        </Link>
      ) : null}
      <li>
        <DeleteBroadcast broadcastId={test.broadcast_id} />
      </li>
    </ul>
  </div>
);

class BroadcastPopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props.data)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="ico-grid-dots">
            <img src={dotsicon} alt="DotIcon" className="pr-4" />
          </span>
        </Popover>
      </>
    );
  }
}

export default BroadcastPopover;
