import React, { useEffect, useState } from "react";
import ManageBroadcast from "./ManageBroadcast";
import { connect } from "react-redux";
import { getBroadCastMessages } from "../../actions/broadCast";
import BroadcastViewMessages from "./BroadcastViewMessages";

const BroadcastViewContainer = (props) => {
  const dafaultValue = {status_id : [9]}

  const [loader, setLoader] = useState(false);
  const [filter, setFilter] = useState(dafaultValue);
  const [search, setSearch] = useState("");
  const [sort, setSort] = useState("DESC");


  useEffect(() => {
    const { dispatch } = props;
    console.log("filter", filter)
    dispatch(getBroadCastMessages(filter, search, sort));
  }, [filter, search, sort]);

  useEffect(() => {
    if (props.broadcastmessagedata === null) {
      setLoader(true);
    } else if (props.broadcastmessagedata !== null) {
      setLoader(false);
    }
  }, [props.broadcastmessagedata]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  const setSearchValue = (value) => {
    setSearch(value);
  };

  const setFilterOptions = (value) => {
    setFilter(value);
    setLoader(true);
  };

  const setSortOptions = (value) => {
    setSort(value);
    setLoader(true);

  };

  return (
    <BroadcastViewMessages
      broadcastmessages={props.broadcastmessagedata}
      statusOnClicked={statusOnClicked}
      setFilterOptions={setFilterOptions}
      setSearchValue={setSearchValue}
      setSortOptions={setSortOptions}
      data={props}
      loader={loader}
    />
  );
};

function mapStateToProps(state) {
  const { broadcastmessagedata } = state.broadcastMessages;
  return {
    broadcastmessagedata,
  };
}

export default connect(mapStateToProps)(BroadcastViewContainer);
