import React, { useState, useEffect } from "react";
import { Popover, Row, Col, Button, Divider, Checkbox } from "antd";
import axios from "axios";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import * as AppStorage from "../../utils/AppStorage";
import { highlightText } from "../../utils/HighlightTextOpenline";
import "./FilterPopover.scss";
import { Scrollbars } from "react-custom-scrollbars";
import {
  STATUS_CLOSED,
  STATUS_NEW,
  STATUS_OPEN,
  URGENCY_LEVEL_HIGH,
  URGENCY_LEVEL_LOW,
  URGENCY_LEVEL_MEDIUM,
} from "../../values";
import {
  APPLY,
  CANCEL,
  FILTER,
  OPENLINE,
  OPENLINES,
  RESET,
  URGENCY_LEVEL,
} from "../../headings";
import { STATUS } from "../../labels";

var CryptoJS = require("crypto-js");
export const Content = (props) => {
  const [status, setStatus] = useState([]);
  const [urgencyLevel, setUrgencyLevel] = useState([]);
  const [openline, setOpenline] = useState([]);
  const [openlinesData, setOpenlinesData] = useState([]);
  const [oldUrgency, setOldUrgency] = useState([]);
  const [oldOpenline, setOldOpenline] = useState([]);
  const [oldStatus, setOldStatus] = useState([]);
  const [selectedOldUrgency, setSelectedOldUrgency] = useState([]);
  const [selectedOldOpenline, setSelectedOldOpenline] = useState([]);
  const [selectedOldStatus, setSelectedOldStatus] = useState([]);
  const [selectedUrgency, setSelectedUrgency] = useState([]);
  const [selectedOpenline, setSelectedOpenline] = useState([]);
  const [selectedStatus, setSelectedStatus] = useState([]);

  useEffect(() => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .get(
        BASE_URL +
          "getUserAllOpenlines/" +
          parseInt(
            CryptoJS.AES.decrypt(user.user_data.user_id, "anonymoose").toString(
              CryptoJS.enc.Utf8
            )
          ),
        options
      )
      .then((res) => {
        let data = [];
        res.data.map((value) =>
          data.push({
            label: value.openline.name,
            value: value.openline.openline_id,
          })
        );
        setOpenlinesData(data);
      });
  }, []);

  useEffect(() => {
    if (props.removeFilter.type !== undefined && !props.visible) {
      onApplySubmit(false);
    }
  }, [urgencyLevel, status, openline]);

  useEffect(() => {
    if (props.removeFilter.type === "urgencyLevel") {
      const removeUrgency = selectedUrgency.filter(
        (it) => it.value !== props.removeFilter.value
      );
      const rmUrg = urgencyLevel.filter(
        (ur) => ur !== props.removeFilter.value
      );
      setUrgencyLevel(rmUrg);
      setSelectedUrgency(removeUrgency);
    }
    if (props.removeFilter.type === "urgencyOpenline") {
      const removeOpenline = selectedOpenline.filter(
        (it) => it.value !== props.removeFilter.value
      );
      const rmOpen = openline.filter((ur) => ur !== props.removeFilter.value);
      setOpenline(rmOpen);
      setSelectedOpenline(removeOpenline);
    }
    if (props.removeFilter.type === "urgencyStatus") {
      const removeStatus = selectedStatus.filter(
        (it) => it.value !== props.removeFilter.value
      );
      const rmStatus = status.filter((ur) => ur !== props.removeFilter.value);
      setStatus(rmStatus);
      setSelectedStatus(removeStatus);
    }
  }, [props.removeFilter]);

  const urgencyOptions = [
    { label: URGENCY_LEVEL_LOW, value: 1 },
    { label: URGENCY_LEVEL_MEDIUM, value: 2 },
    { label: URGENCY_LEVEL_HIGH, value: 3 },
  ];
  const statusOptions = [
    { label: STATUS_NEW, value: 1 },
    { label: STATUS_OPEN, value: 2 },
    { label: STATUS_CLOSED, value: 3 },
  ];
  const openlineOptions = openlinesData;

  const onCancel = (e) => {
    setUrgencyLevel(oldUrgency);
    setStatus(oldStatus);
    setOpenline(oldOpenline);
    setSelectedUrgency(selectedOldUrgency);
    setSelectedStatus(selectedOldStatus);
    setSelectedOpenline(selectedOldOpenline);
    props.hide();
  };

  const onApplySubmit = (hide) => {
    let allCheckedOnes = {
      ...(status.length > 0 && { status_id: status }),
      ...(urgencyLevel.length > 0 && { urgency_level_id: urgencyLevel }),
      ...(openline.length > 0 && { openline_id: openline }),
    };
    setOldStatus(status);
    setOldUrgency(urgencyLevel);
    setOldOpenline(openline);
    setSelectedOldOpenline(selectedOpenline);
    setSelectedOldStatus(selectedStatus);
    setSelectedOldUrgency(selectedUrgency);
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const searchBy = AppStorage.get("searchBy");
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const baseUrl =
      BASE_URL +
      "openlineResponderConversation/?user_id=" +
      parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      );
    const finalInput = {
      ...allCheckedOnes,
      openline_id:
        allCheckedOnes.openline_id && allCheckedOnes.openline_id.toString(),
      status_id:
        allCheckedOnes.status_id && allCheckedOnes.status_id.toString(),
      urgency_level_id:
        allCheckedOnes.urgency_level_id &&
        allCheckedOnes.urgency_level_id.toString(),
    };
    const finalUrl =
      props.searchString === ""
        ? baseUrl + "&filter=" + JSON.stringify(finalInput)
        : baseUrl +
          "&filter=" +
          JSON.stringify(finalInput) +
          "&search=" +
          props.searchString;

    let filteredData = [];
    axios.get(finalUrl, options).then((res) => {
      if (searchBy?.length > 0) {
        // highlight the text found during search.
        filteredData = highlightText(res.data, searchBy);
      } else {
        filteredData = res.data;
      }
      props.loadFilteredConversations(
        filteredData,
        "apply",
        selectedUrgency,
        selectedStatus,
        selectedOpenline
      );
    });
    if (hide) {
      props.hide();
    }
  };

  function handleStatus(checkedValues) {
    let selected = selectedStatus;
    if (checkedValues.length > 0) {
      checkedValues.map((val) => {
        if (!selected.find((it) => it.value === val)) {
          selected.push(statusOptions.find((opt) => opt.value === val));
        } else {
          selected = selected.filter((it) => it.value === val);
        }
      });
    } else {
      selected = [];
    }
    setSelectedStatus(selected);
    setStatus(checkedValues);
  }

  function handleOpenline(checkedValues) {
    let selected = selectedOpenline;
    if (checkedValues.length > 0) {
      checkedValues.map((val) => {
        if (!selected.find((it) => it.value === val)) {
          selected.push(openlineOptions.find((opt) => opt.value === val));
        } else {
          selected = selected.filter((it) => it.value === val);
        }
      });
    } else {
      selected = [];
    }
    setSelectedOpenline(selected);
    setOpenline(checkedValues);
  }

  function handleUrgencyLevel(checkedValues) {
    let selected = selectedUrgency;
    if (checkedValues.length > 0) {
      checkedValues.map((val) => {
        if (!selected.find((it) => it.value === val)) {
          selected.push(urgencyOptions.find((opt) => opt.value === val));
        } else {
          selected = selected.filter((it) => it.value === val);
        }
      });
    } else {
      selected = [];
    }
    setSelectedUrgency(selected);
    setUrgencyLevel(checkedValues);
  }

  const handleReset = () => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const searchBy = AppStorage.get("searchBy");
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const baseUrl =
      BASE_URL +
      "openlineResponderConversation/?user_id=" +
      parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      );

    let filteredData = [];
    axios.get(baseUrl, options).then((res) => {
      if (searchBy?.length > 0) {
        // highlight the text found during search.
        filteredData = highlightText(res.data, searchBy);
      } else {
        filteredData = res.data;
      }
      props.loadFilteredConversations(filteredData, "reset", [], [], []);
      setUrgencyLevel([]);
      setSelectedUrgency([]);
      setStatus([]);
      setSelectedStatus([]);
      setSelectedOpenline([]);
      setOpenline([]);
    });
    props.hide();
  };

  return (
    <div>
      <Col span={24} style={{ width: "325px" }}>
        <div className="">
          <Row className="filter-wrapper align-items-center px-3 py-2">
            <Col span="12">
              <h6>{FILTER}</h6>
            </Col>
            <Col span="12" className="d-flex justify-content-end">
              <Button
                type="link"
                className="link pl-0 pt-0"
                onClick={() => handleReset()}
              >
                {RESET}
              </Button>
            </Col>
          </Row>

          <Divider className="mt-0 mb-1 w-100" />
          <Scrollbars
            autoHeight
            autoHeightMin={0}
            autoHeightMax={450}
            thumbMinSize={30}
            universal={true}
          >
            <Row className="filter-wrapper align-items-center px-3 py-2">
              <div className="w-100 mb-4">
                <Col span="24" className="filter-type mb-3">
                  <h6>{URGENCY_LEVEL}</h6>
                </Col>
                <Col span="24">
                  <Checkbox.Group
                    className="d-flex flex-column flex-gap"
                    name="urgencyLevel"
                    options={urgencyOptions}
                    value={urgencyLevel}
                    onChange={handleUrgencyLevel}
                  />
                </Col>
              </div>

              <div className="w-100 mb-4">
                <Col span="24" className="filter-type mb-3">
                  <h6>{STATUS}</h6>
                </Col>
                <Col span="24">
                  <Checkbox.Group
                    className="d-flex flex-column flex-gap"
                    name="status"
                    value={status}
                    options={statusOptions}
                    onChange={handleStatus}
                  />
                </Col>
              </div>
              <div className="w-100 mb-4">
                <Col span="24" className="filter-type mb-3">
                  <h6>{OPENLINE}</h6>
                </Col>
                <Col span="24">
                  <Checkbox.Group
                    className="d-flex flex-column flex-gap"
                    name="openline"
                    options={openlineOptions}
                    value={openline}
                    onChange={handleOpenline}
                  />
                </Col>
              </div>
            </Row>
          </Scrollbars>
        </div>
        <Row>
          <Col span="24" className="btn-row actions">
            <Button
              type="primary"
              className="mr-2"
              onClick={() => onApplySubmit(true)}
            >
              {APPLY}
            </Button>
            <Button type="secondary" onClick={onCancel}>
              {CANCEL}
            </Button>
          </Col>
        </Row>
      </Col>
    </div>
  );
};

class FilterPopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };
  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={
            <Content
              hide={this.hide}
              searchString={this.props.searchString}
              removeFilter={this.props.removeFilter}
              loadFilteredConversations={this.props.loadFilteredConversations}
              visible={this.state.visible}
            />
          }
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="icon icon-filter"></span>
        </Popover>
      </>
    );
  }
}

export default FilterPopover;
