import React, { useState } from "react";
import { Modal, Checkbox, Col, Row, Button } from "antd";
import "../modal/modal.scss";
import { ASSIGN, CANCEL, DONE, HOTLINE } from "../../headings";
import { ASSIGN_HOTLINE_TEXT_PART2 } from "../../messages";

const AssignHotlineModal = ({
  handleCheck,
  selectedRow,
  stepOneHotLineValues,
  permissions,
  getCheckValues,
}) => {
  const [visible, setVisible] = useState(false);

  const showModal = async () => {
    setVisible(true);
  };

  const handleOk = (e) => {
    setVisible(false);
    getCheckValues();
  };

  const handleCancel = (e) => {
    setVisible(false);
  };

  return (
    <>
      <Button type="primary" onClick={() => showModal()}>
        {ASSIGN}
      </Button>
      <Modal
        className="modal-checkbox"
        visible={visible}
        onOk={() => handleOk()}
        onCancel={() => handleCancel()}
        centered
        width={590}
        okButtonProps={{
          children: "Custom OK",
        }}
        cancelButtonProps={{
          children: "Custom cancel",
          className: "ant-btn-secondary",
        }}
        okText={DONE}
        cancelText={CANCEL}
      >
        <div>
          {ASSIGN_HOTLINE_TEXT_PART1}{" "}
          <b>{stepOneHotLineValues.directoryName}</b> {HOTLINE}
          <br /> <span className="font-wgt-md"> </span>{" "}
        </div>
        <div>
          <Row className="mt-2 mt-lg-5">
            <Col span={6}></Col>
            <Col span={14} className="small text-center">
              {ASSIGN_HOTLINE_TEXT_PART2}
            </Col>
          </Row>
          {selectedRow &&
            selectedRow.map((item) => {
              let per = permissions.find((p) => p.user_id === item.user_id);
              return (
                <Row className="mt-3 d-flex justify-content-center">
                  <Col span={12}>{`${item.firstname} ${item.lastname}`}</Col>
                  <Col span={10}>
                    <Checkbox
                      checked={per && per.can_respond === 1 ? true : false}
                      disabled
                      onChange={(e) => handleCheck(item)}
                    ></Checkbox>
                  </Col>
                </Row>
              );
            })}
        </div>
      </Modal>
    </>
  );
};

export default AssignHotlineModal;
