import React, { Component } from "react";
import { Modal } from "antd";
import "../modal/modal.scss";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import {
  DELETE,
  NO_TEXT_MODAL,
  YES_TEXT_MODAL,
  DELETE_EMPLOYEE,
} from "../../headings";
import {
  DELETE_CONFIRM_EMPLOYEE,
  DELETE_CONFIRM_HOTLINE_ASSOCIATED_EMPLOYEE,
  DELETE_CONFIRM_OPENLINE_ASSOCIATED_EMPLOYEE,
  DELETE_CONFIRM_ASSOCIATED_EMPLOYEE,
} from "../../messages";

class DeleteEmployeeModal extends Component {
  state = {
    visible: false,
    isEmployeeAssociated: "",
    hotlineAssociated: false,
    openlineAssociated: false,
    hotlineData: [],
    openlineData: [],
    data: [],
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
    this.checkifEmployee();
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.deleteEmployee();
  };

  handleForceOk = () => {
    this.setState({
      visible: false,
    });
    this.deleteEmployee();
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  checkifEmployee = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    this.props.setLoader(true);
    axios
      .get(BASE_URL + "checkEmployeeDelete/" + this.props.userId + "/", options)
      .then((res) => {
        this.props.setLoader(false);
        if (
          res.data.assigned_hotlines.length === 0 &&
          res.data.assigned_openlines.length === 0
        ) {
          this.setState({ isEmployeeAssociated: false });
        } else {
          this.setState({ isEmployeeAssociated: true, data: res.data });
          if (res.data.assigned_hotlines.length > 0) {
            this.setState({
              hotlineAssociated: true,
              hotlineData: res.data.assigned_hotlines,
            });
          }
          if (res.data.assigned_openlines.length > 0) {
            this.setState({
              openlineAssociated: true,
              openlineData: res.data.assigned_openlines,
            });
          }
          this.setState({ data: res.data });
        }
      })
      .catch((error) => {
        this.setState({ isEmployeeAssociated: "" });
      });
  };

  deleteEmployee = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (this.state.isEmployeeAssociated) {
      const data = { ...this.state.data, user_id: this.props.userId };
      axios
        .post(BASE_URL + "deleteEmployee/", data, options)
        .then(() => {
          window.location = "/orgcc/manageemployees";
        })
        .catch((error) => {});
    } else {
      const data = {
        assigned_hotlines: [],
        assigned_openlines: [],
        user_id: this.props.userId,
      };
      axios
        .post(BASE_URL + "deleteEmployee/", data, options)
        .then(() => {
          window.location = "/orgcc/manageemployees";
        })
        .catch((error) => {});
    }
  };

  render() {
    return (
      <>
        <a onClick={this.showModal}>{DELETE}</a>
        <Modal
          title={DELETE_EMPLOYEE}
          visible={
            this.state.visible &&
            this.state.isEmployeeAssociated !== "" &&
            !this.state.isEmployeeAssociated
          }
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={370}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
          }}
          okText={YES_TEXT_MODAL}
          cancelText={NO_TEXT_MODAL}
        >
          <p>{DELETE_CONFIRM_EMPLOYEE}</p>
        </Modal>
        <Modal
          title={DELETE_EMPLOYEE}
          visible={this.state.visible && this.state.isEmployeeAssociated}
          onOk={this.handleForceOk}
          onCancel={this.handleCancel}
          centered
          width={370}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
          }}
          okText={YES_TEXT_MODAL}
          cancelText={NO_TEXT_MODAL}
        >
          <p>
            {this.state.hotlineAssociated && !this.state.openlineAssociated
              ? DELETE_CONFIRM_HOTLINE_ASSOCIATED_EMPLOYEE
              : !this.state.hotlineAssociated && this.state.openlineAssociated
              ? DELETE_CONFIRM_OPENLINE_ASSOCIATED_EMPLOYEE
              : this.state.hotlineAssociated && this.state.openlineAssociated
              ? DELETE_CONFIRM_ASSOCIATED_EMPLOYEE
              : ""}
            <br />
            {this.state.hotlineData.length > 0
              ? this.state.hotlineData.map((value) => (
                  <>
                    <p>{value.name}</p>
                    <br />
                  </>
                ))
              : this.state.openlineData.length > 0
              ? this.state.openlineData.map((value) => (
                  <>
                    <p>{value.name}</p>
                    <br />
                  </>
                ))
              : null}
            {DELETE_CONFIRM_EMPLOYEE}
          </p>
        </Modal>
      </>
    );
  }
}

export default DeleteEmployeeModal;
