import {
  HOTLINE_FAIL,
  HOTLINE_SUCCESS,
  HOTLINEASSIGN_SUCCESS,
  HOTLINEASSIGN_FAIL,
} from "../actions/types";

const deafaultState = {};
deafaultState.hotlinesdata = null;
deafaultState.assignhotlinedata = [];

export default function (state = deafaultState, action) {
  const { type, payload } = action;

  switch (type) {
    case HOTLINE_SUCCESS:
      return {
        ...state,
        hotlinesdata: payload.hotlinesdata,
      };
    case HOTLINE_FAIL:
      return {
        ...state,
        hotlinesdata: [],
      };
    case HOTLINEASSIGN_SUCCESS:
      return {
        ...state,
        assignhotlinedata: payload.assignhotlinedata,
      };
    case HOTLINEASSIGN_FAIL:
      return {
        ...state,
        assignhotlinedata: [],
      };
    default:
      return state;
  }
}
