import React, { Component } from "react";
import { Modal, Col, Row, Spin } from "antd";
import "../modal/modal.scss";
import OpenlinePermission from "../table/OpenlinePermission";
import SearchEmployee from "../autocomplete/SearchEmployee";
import { connect } from "react-redux";
import { getManageEmpAsync } from "../../actions/manageEmp";
import authHeader from "../../services/auth-header";
import axios from "axios";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import { CANCEL, DONE, SHOW } from "../../headings";
import {
  ASSIGN_HOTLINE_TEXT_PART2,
  HIT_ENTER_TO_ADD,
  OPENLINE_PERMISSION_TEXT_PART1,
} from "../../messages";

var CryptoJS = require("crypto-js");
class OpenlinePermissionModal extends Component {
  state = {
    visible: false,
    assignedOpenLines: "",
    hotLineName: "",
    assignedEmp: "",
    loader: false,
  };

  showModal = () => {
    this.setState({
      visible: true,
      employeeData: [],
      hotLineName: this.props.selectedRows.name,
      openlineId: this.props.selectedRows.openline_id,
      loader: true,
    });
    const { dispatch } = this.props;
    dispatch(getManageEmpAsync({}, ""));
    this.getAssignedEmployee();
  };

  getAssignedEmployee = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .get(
        BASE_URL +
          "getOpenlineAssignEmpl/" +
          this.props.selectedRows.openline_id,
        options
      )
      .then((res) => {
        this.setState({
          assignedOpenLines: res.data,
          loader: false,
        });
      });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });

    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(BASE_URL + "openlineAssignEmpl/", this.state.assignedEmp, options)
      .then((res) => {
        this.getAssignedEmployee();
        this.setState({
          assignedEmp: "",
        });
      })
      .catch((error) => {});
  };

  handleCheck = (record) => {
    let assignedOpenLines = [...this.state.assignedOpenLines];
    let index = assignedOpenLines.findIndex(
      (el) => el.user_id === record.user_id
    );
    assignedOpenLines[index] = {
      ...assignedOpenLines[index],
      can_respond: !record.can_respond,
    };
    this.setState({ assignedOpenLines });

    let assignedEmp = [...this.state.assignedEmp];
    let aindex = assignedEmp.findIndex((el) => el.user_id === record.user_id);
    assignedEmp[aindex] = {
      ...assignedEmp[aindex],
      can_respond: !record.can_respond,
    };
    this.setState({ assignedEmp });
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  deleteRecord = (value) => {
    var obj = {
      user_id: value.user.user_id,
    };
    const newArray =
      this.state.assignedEmp &&
      this.state.assignedEmp.filter(function (element) {
        return element.user_id !== value.user.user_id;
      });
    this.setState({
      assignedEmp: newArray,
    });
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
      data: JSON.stringify({ user_id: value.user.user_id }),
    };
    axios
      .delete(
        BASE_URL + "getOpenlineAssignEmpl/" + value.openline.openline_id,
        options
      )
      .then(() => {
        this.getAssignedEmployee();
      })
      .catch((error) => {});
  };

  addUser = (value, options) => {
    const user = JSON.parse(AppStorage.get("user"));

    const { assignedOpenLines, assignedEmp } = this.state;
    const x = {
      user: {
        user_id: parseInt(options.user_id),
        first_name: options.value,
      },
      openline: {
        openline_id: this.state.openlineId,
      },
      can_respond: true,
      can_view_reports: false,
    };
    const y = {
      openline_id: this.state.openlineId,
      user_id: parseInt(options.user_id),
      can_respond: true,
      can_view_reports: false,
      created_by: parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      ),
    };

    this.setState({
      assignedOpenLines: [...assignedOpenLines, x],
      assignedEmp: [...assignedEmp, y],
    });
  };

  componentDidMount() {}

  componentDidUpdate(prevProps) {
    let { manageEmpData } = this.props;
    var newArray = [];
    if (prevProps.manageEmpData != manageEmpData) {
      for (var i = 0; i < manageEmpData.length; i++) {
        newArray.push({
          value: manageEmpData[i]?.first_name.concat(
            " ",
            manageEmpData[i]?.last_name
          ),
          user_id: manageEmpData[i]?.user_id,
        });
      }
      this.setState({ employeeData: newArray });
    }
  }

  render() {
    return (
      <>
        <a onClick={this.showModal}>{SHOW}</a>
        <Modal
          className="modal-checkbox"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={590}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
            className: "ant-btn-secondary",
          }}
          okText={DONE}
          cancelText={CANCEL}
        >
          <div>
            {OPENLINE_PERMISSION_TEXT_PART1} {this.state.hotLineName}
          </div>

          <Row className="mt-2 mt-lg-5 mb-3 thead-outer">
            <Col span={10}>{ASSIGN_HOTLINE_TEXT_PART2}</Col>
            {/* <Col span={5}>View Reports</Col> */}
          </Row>
          <Spin spinning={this.state.loader} size="large">
            <OpenlinePermission
              data={this.state.assignedOpenLines}
              deleteRecord={this.deleteRecord}
              handleCheck={this.handleCheck}
            />
          </Spin>
          <Row className="mb-2 autocomplete">
            <Col span={24}>
              <SearchEmployee
                searchData={this.state.employeeData}
                addAssignUser={this.addUser}
                data={this.state.assignedOpenLines}
              />
              <small>{HIT_ENTER_TO_ADD}</small>
            </Col>
          </Row>
        </Modal>
      </>
    );
  }
}

function mapStateToProps(state) {
  const { assignhotlines } = state.assignedHotline;
  const { manageEmpData } = state.manageEmp;
  const { message } = state.message;
  return {
    assignhotlines,
    manageEmpData,
    message,
  };
}

export default connect(mapStateToProps)(OpenlinePermissionModal);
