import React from "react";
import { Layout, Row, Col, Input, notification, Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageOrg/manageOrg.scss";
import AccSidebar from "./AccSidebar";
import ShowDetails from "../../containers/ShowDetails";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { Link } from "react-router-dom";
import { AudioOutlined } from "@ant-design/icons";
import Pencil from "../../assets/icons/pencil.svg";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import authHeader from "../../services/auth-header";
import moment from "moment";
import {
  ACTIONS,
  END_DATE,
  ORGANIZATION_ID,
  ORGANIZATION_NAME,
  START_DATE,
  STATUS,
} from "../../labels";
import { STATUS_ACTIVE, STATUS_INACTIVE, DRAFT, SCHEDULED, COMPLETED, DELETED } from "../../values";
import { CREATE_ORGANIZATION, ORGANIZATIONS, SEARCH } from "../../headings";
import {
  STATUS_CHANGED_NOTIFICATION_PART1,
  STATUS_CHANGED_NOTIFICATION_PART2,
  STATUS_CHANGED_NOTIFICATION_PART3,
} from "../../messages";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);

const { Content } = Layout;

class ShowAllOrganizations extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      searchBy: "",
      filterData: [],
    };
  }

  componentDidMount() {
    let { organizations } = this.props;
    organizations?.forEach((org) => {
      org.start_date = moment(org.start_date)
        .utc()
        .local()
        .format("MM / DD / YYYY");
      org.end_date = moment(org.end_date)
        .utc()
        .local()
        .format("MM / DD / YYYY");
    });
    this.setState({ data: organizations }, () => {
      this.onSearch("");
    });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.organizations !== nextProps.organizations) {
      nextProps.organizations.forEach((org) => {
        org.start_date = org.start_date ? moment(org.start_date)
          .utc()
          .local()
          .format("MM / DD / YYYY") : "";
        org.end_date = org.end_date ? moment(org.end_date)
          .utc()
          .local()
          .format("MM / DD / YYYY") : "";
      });
      this.setState({ data: nextProps.organizations }, () => {
        this.onSearch(this.state.searchBy);
      });
    }
  }

  statusClicked = (record, via) => {
    const endDate = record.end_date.replaceAll(" / ", "/");
    if (new Date() < new Date(endDate)) {
      const data = {
        name: record.name,
        identifier: record.identifier,
        organization_id: record.organization_id,
        start_date: moment(record.start_date.replaceAll(" / ", "-")).format(
          "DD-MM-YYYY"
        ),
        end_date: moment(endDate.replaceAll("/", "-")).format("DD-MM-YYYY"),
        status_id: via === "Active" ? 2 : 1,
      };
      const token = authHeader();
      const options = {
        headers: {
          Authorization: token,
          Accept: "application/json, text/plain",
          "Content-Type": "application/json;charset=UTF-8",
        },
      };
      axios.put(BASE_URL + "saveOrganization/", data, options).then(() => {
        notification.config({
          placement: "bottomLeft",
        });
        notification.open({
          description:
            STATUS_CHANGED_NOTIFICATION_PART1 +
            record.name +
            STATUS_CHANGED_NOTIFICATION_PART2,
        });
        this.props.statusOnClicked();
      });
    } else {
      notification.config({
        placement: "bottomLeft",
      });
      notification.open({
        description:
          STATUS_CHANGED_NOTIFICATION_PART1 +
          record.name +
          STATUS_CHANGED_NOTIFICATION_PART3,
      });
    }
  };

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt, searchBy: value });
  };

  render() {
    const columns = [
      {
        title: ORGANIZATION_NAME,
        dataIndex: "",
        key: "name",
        width: "22%",
        render: (record) => {
          return {
            children: <ShowDetails record={record} orgName />,
            props: {
              "data-title": "Organization Name",
            },
          };
        },
      },
      {
        title: ORGANIZATION_ID,
        dataIndex: "",
        key: "identifier",
        width: "20%",
        render: (record) => {
          return {
            children: <ShowDetails record={record} orgId />,
            props: {
              "data-title": "Organization Id",
            },
          };
        },
      },
      {
        title: START_DATE,
        dataIndex: "start_date",
        key: "start_date",
        width: "20%",
        render: (start_date) => {
          return {
            children: start_date,
            props: {
              "data-title": "Start date",
            },
          };
        },
      },
      {
        title: END_DATE,
        dataIndex: "end_date",
        key: "end_date",
        width: "20%",
        render: (start_date) => {
          return {
            children: start_date,
            props: {
              "data-title": "End date",
            },
          };
        },
      },
      {
        title: STATUS,
        dataIndex: "",
        key: "status_id",
        width: "10%",

        render: (record) => {
          return {
            children:
              record.status_id === 1 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {STATUS_ACTIVE}
                </div>
              ) :  record.status_id === 2  ?(
                <div className="secondary-tag-rounded tag">
                  {DRAFT}
                </div>
              ) : record.status_id === 3 ? (
                <div className="secondary-tag-rounded tag">
                    {SCHEDULED}
                </div>
              ): record.status_id === 4 ? (
                <div className="secondary-tag-rounded tag">
                  {COMPLETED}
                </div>
              ) : (
                <div className="secondary-tag-rounded tag">
                  {DELETED}
                </div>

              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
      {
        title: ACTIONS,
        dataIndex: "",
        key: "",
        width: "10%",
        render: (record) => {
          const path = `/amcc/editorg/${record.organization_id}`;
          return {
            children: (
              <Link to={path}>
                <img src={Pencil} alt="Actions" />
              </Link>
            ),
            props: {
              "data-title": "Actions",
            },
          };
        },
      },
    ];
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amCCUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <AccSidebar />
              <Content className="">
                <Row className="py-3">
                  <h1>{ORGANIZATIONS}</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder={SEARCH}
                            onSearch={this.onSearch}
                          />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/amcc/createorg">
                        <Buttons value={CREATE_ORGANIZATION} />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Spin spinning={this.props.loader} size="large">
                  <Grid
                    key="manageOrg"
                    rowKey="identifier"
                    columns={columns}
                    data={this.state.filterData}
                  />
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default ShowAllOrganizations;
