import React from "react";
import { Popover, Button, Upload, Modal } from "antd";
import { ADD_DOCUMENT, ADD_IMAGE, CANCEL_MODAL } from "../../headings";
import Location from "../broadcastMessages/Location";
import PlusIcon from "../../assets/icons/plus_icon.png";

const content = ({
  state,
  props,
  showImageModal,
  showDocumentModal,
  handleOk,
}) => (
  <div>
    <ul className="mb-0 px-2">
      <li>
        <Location
          setLocation={props.setLocation}
          documents={props.documentFile.document}
          images={props.imageFile.image}
          viaMessages
        />
      </li>
      <li>
        <Button
          type="link"
          className="link pl-0"
          disabled={
            props.documentFile.document.length > 0 ||
            props.mapLocation.longitude !== ""
          }
          onClick={() => showImageModal()}
        >
          <span className="icon icon-image"></span>
          {ADD_IMAGE}
        </Button>
        <Modal
          visible={state.showImageModal}
          centered
          onOk={(value) => handleOk(value)}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{ style: { display: "none" } }}
          okText={CANCEL_MODAL}
        >
          {props.message.length === 0 ? (
            <Upload
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
              fileList={[]}
              onChange={(value) => props.handleUploadImage(value)}
              beforeUpload={(value) => props.handleBeforeUpload(value)}
              onPreview={(value) => props.handlePreview(value)}
              onRemove={() => props.handleOnRemove()}
              multiple={true}
              className="upload-list-inline c-upload"
              accept="image/png, image/jpeg, image/gif"
            >
              <Button type="link" className="link pl-0">
                {ADD_IMAGE}
              </Button>
            </Upload>
          ) : (
            <Upload
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
              fileList={props.imageFile.image}
              onChange={(value) => props.handleUploadImage(value)}
              beforeUpload={(value) => props.handleBeforeUpload(value)}
              onPreview={(value) => props.handlePreview(value)}
              onRemove={() => props.handleOnRemove()}
              multiple={true}
              className="upload-list-inline c-upload"
              accept="image/png, image/jpeg, image/gif"
            >
              <Button type="link" className="link pl-0">
                {ADD_IMAGE}
              </Button>
            </Upload>
          )}
        </Modal>
      </li>
      <li>
        <Button
          type="link"
          className="link pl-0"
          disabled={
            props.imageFile.image.length > 0 ||
            props.mapLocation.longitude !== ""
          }
          onClick={() => showDocumentModal()}
        >
          <span className="icon icon-doc"></span>
          {ADD_DOCUMENT}
        </Button>
        <Modal
          visible={state.showDocumentModal}
          centered
          onOk={(value) => handleOk(value)}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{ style: { display: "none" } }}
          okText={CANCEL_MODAL}
        >
          {props.message.length === 0 ? (
            <Upload
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
              fileList={[]}
              onChange={(value) => props.handleUploadDocument(value)}
              beforeUpload={(value) => props.handleBeforeUpload(value)}
              onPreview={(value) => props.handlePreview(value)}
              onRemove={() => props.handleOnRemove()}
              multiple={true}
              className="upload-list-inline c-upload"
              accept="application/pdf, .doc, .csv, .pptx, application/vnd.ms-excel"
            >
              <Button type="link" className="link pl-0">
                {ADD_DOCUMENT}
              </Button>
            </Upload>
          ) : (
            <Upload
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
              fileList={props.documentFile.document}
              onChange={(value) => props.handleUploadDocument(value)}
              beforeUpload={(value) => props.handleBeforeUpload(value)}
              onPreview={(value) => props.handlePreview(value)}
              onRemove={() => props.handleOnRemove()}
              multiple={true}
              className="upload-list-inline c-upload"
              accept="application/pdf, .doc, .csv, .pptx, application/vnd.ms-excel"
            >
              <Button type="link" className="link pl-0">
                {ADD_DOCUMENT}
              </Button>
            </Upload>
          )}
        </Modal>
      </li>
    </ul>
  </div>
);

class AttachmentPopover extends React.Component {
  state = {
    showDocumentModal: false,
    showImageModal: false,
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (
      this.props !== nextProps &&
      (nextProps.documentFile.document.length > 0 ||
        nextProps.imageFile.image.length > 0)
    ) {
      this.handleOk();
    }
  }

  showDocumentModal = () => {
    this.setState({ showDocumentModal: !this.state.showDocumentModal });
  };

  showImageModal = () => {
    this.setState({ showImageModal: !this.state.showImageModal });
  };

  handleOk = () => {
    this.setState({ showDocumentModal: false, showImageModal: false });
  };

  handleCancel = () => {
    this.setState({ showDocumentModal: false, showImageModal: false });
  };

  showHidePopover = () => {
    this.props.openAttachmentSection();
  };

  render() {
    const data = {
      state: this.state,
      props: this.props,
      showDocumentModal: this.showDocumentModal,
      showImageModal: this.showImageModal,
      handleOk: this.handleOk,
      handleCancel: this.handleCancel,
    };
    return (
      <>
        <Popover
          placement="bottom"
          content={content(data)}
          trigger="click"
          visible={this.props.showAttachmentSection}
          onVisibleChange={this.props.openAttachmentSection}
        >
          <span className="add-icon-wrapper">
            <img src={PlusIcon} alt="add attachment" className="pr-4" />
          </span>
        </Popover>
      </>
    );
  }
}

export default AttachmentPopover;
