import React from "react";
import { Layout, Row, Col, Input, Spin, notification } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageEmp/manageEmp.scss";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import OpenlinePermissionModal from "../modal/OpenlinePermissionModal";
import OrgSidebar from "../../containers/OrgSidebar";
import moment from "moment";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import authHeader from "../../services/auth-header";
import OpenlinePopover from "../table/OpenlinePopover";
import {
  STATUS_CHANGED_NOTIFICATION_PART1,
  STATUS_CHANGED_NOTIFICATION_PART2,
  STATUS_CHANGED_NOTIFICATION_PART3,
} from "../../messages";
import {
  END_DATE,
  OPENLINE_NAME,
  PERMISSIONS,
  START_DATE,
  STATUS,
} from "../../labels";
import {
  STATUS_ACTIVE,
  STATUS_DRAFT,
  SCHEDULED,
  COMPLETED,
} from "../../values";
import { OPENLINE_DIRECTORY, SEARCH } from "../../headings";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);
const { Content } = Layout;

class ShowAllOpenlines extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
    };
  }

  componentDidMount() {
    let { openlines } = this.props;
    openlines?.forEach((openline) => {
      openline.start_date = openline.start_date ? moment(openline.start_date)
        .utc()
        .local()
        .format("MM / DD / YYYY") : '';
      openline.end_date = openline.end_date ? moment(openline.end_date)
        .utc()
        .local()
        .format("MM / DD / YYYY") : '';
    });
    this.setState({ data: openlines }, () => {
      this.onSearch("");
    });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.openlines !== nextProps.openlines) {
      nextProps.openlines?.forEach((openline) => {
        openline.start_date = openline.start_date ? moment(openline.start_date)
          .utc()
          .local()
          .format("MM / DD / YYYY") : '';
        openline.end_date = openline.end_date ? moment(openline.end_date)
          .utc()
          .local()
          .format("MM / DD / YYYY") : '';
      });
      this.setState({ data: nextProps.openlines }, () => {
        this.onSearch("");
      });
    }
  }

  statusClicked = (record, via) => {
    const endDate = record.end_date.replaceAll(" / ", "/");
    if (new Date() < new Date(endDate)) {
      const data = {
        status_id: via === "Active" ? 4 : 5,
      };
      const token = authHeader();
      const options = {
        headers: {
          Authorization: token,
          Accept: "application/json, text/plain",
          "Content-Type": "application/json;charset=UTF-8",
        },
      };
      axios
        .put(
          BASE_URL + "changeOpenlineStatus/" + record.openline_id + "/",
          data,
          options
        )
        .then(() => {
          notification.config({
            placement: "bottomLeft",
          });
          notification.open({
            description:
              STATUS_CHANGED_NOTIFICATION_PART1 +
              record.name +
              STATUS_CHANGED_NOTIFICATION_PART2,
          });
          this.props.statusOnClicked();
        });
    } else {
      notification.config({
        placement: "bottomLeft",
      });
      notification.open({
        description:
          STATUS_CHANGED_NOTIFICATION_PART1 +
          record.name +
          STATUS_CHANGED_NOTIFICATION_PART3,
      });
    }
  };

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });
  };

  render() {
    const columns = [
      {
        title: OPENLINE_NAME,
        dataIndex: "name",
        key: "name",
        width: "22%",
        render: (name) => {
          return {
            children: name,
            props: {
              "data-title": "Openline Name",
            },
          };
        },
      },
      {
        title: START_DATE,
        dataIndex: "start_date",
        key: "start_date",
        width: "22%",
        render: (start_date) => {
          let sdate = start_date ? start_date : '-'
          return {
            children: sdate,
            props: {
              "data-title": "Start Date",
            },
          };
        },
      },
      {
        title: END_DATE,
        dataIndex: "end_date",
        key: "end_date",
        width: "21%",
        render: (end_date) => {
          let edate = end_date ? end_date : '-'
          return {
            children: edate,
            props: {
              "data-title": "End Date",
            },
          };
        },
      },
      {
        title: PERMISSIONS,
        dataIndex: "",
        key: "x",
        width: "20%",
        render: (selectedRows) => {
          return {
            children: (
              <div>
                <OpenlinePermissionModal selectedRows={selectedRows} />
              </div>
            ),
            props: {
              "data-title": "Permissions",
            },
          };
        },
      },
      {
        title: STATUS,
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) => {
          return {
            children:
              record.status_id === 4 ? (
                <div className="secondary-tag-rounded tag">{STATUS_DRAFT}</div>
              ) : record.status_id === 5 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {STATUS_ACTIVE}
                </div>
              ) : record.status_id === 7 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {SCHEDULED}
                </div>
              ) : (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {COMPLETED}
                </div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: (record) => {
          return {
            children: <OpenlinePopover data={record} />,
            props: {
              "data-title": "Action",
            },
          };
        },
      },
    ];
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={this.props.data} />
              <Content className="">
                <Row className="py-3">
                  <h1>{OPENLINE_DIRECTORY}</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder={SEARCH}
                            onSearch={this.onSearch}
                          />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/orgcc/createopenline">
                        <Buttons value="Create Openline" />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Spin spinning={this.props.loader} size="large">
                  <Grid
                    rowKey="openline_id"
                    columns={columns}
                    data={this.state.filterData}
                  />
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default ShowAllOpenlines;
