import React from "react";
import { Link, NavLink } from "react-router-dom";
import { Layout, Row, Col, Input, Divider, Breadcrumb } from "antd";
import { LoggedinHeader } from "../../containers";
import Grid from "../table/Table";
import OrgSidebar from "../../containers/OrgSidebar";
import { connect } from "react-redux";
import { getPollAsync } from "../../actions/poll";
import { CREATE_POLL, POLLS, OOTB_POLL, SEARCH } from "../../headings";
import { OOTB_TEXT } from "../../messages";

const { Search } = Input;
const { Content } = Layout;

class ExistingPolls extends React.Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: "Poll Name",
        dataIndex: "title",
        key: "name",
        width: "22%",
        render: (name) => {
          return {
            children: name,
            props: {
              "data-title": "Poll Name",
            },
          };
        },
      },
    ];
    this.state = {
      columns: columns,
      data: [],
    };
  }

  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(getPollAsync());
  }

  componentDidUpdate(prevProps) {
    let { pollsdata } = this.props;

    if (prevProps.pollsdata !== pollsdata) {
      this.setState(
        {
          data: pollsdata,
        },
        () => {
          this.onSearch("");
        }
      );
    }
  }
  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });
  };

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }
  render() {
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0" data-test="manageEmp">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={this.props} />
              <Content className="">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/pollsdirectory">
                      <Breadcrumb.Item>{POLLS}</Breadcrumb.Item>
                    </NavLink>
                    <NavLink to="/createpoll">
                      <Breadcrumb.Item>{CREATE_POLL}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{OOTB_POLL}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1>{OOTB_TEXT}</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder={SEARCH}
                            onSearch={this.onSearch}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </Col>
                <div className="mid-content">
                  <Grid
                    rowKey="hotline_id"
                    columns={this.state.columns}
                    data={this.state.filterData}
                  />
                  {/* <Row>
                    <Col span={24}>
                      <h6 className="mb-4">Poll Name</h6>
                      <Row className="my-2">
                      <Link to="/">Impact of Microaggression</Link>
                      </Row>
                      <Divider className="w-100" />
                      <Row className="my-2">
                        <Link to="/">Microaggressions – do we have it?</Link>
                      </Row>
                      <Divider className="w-100" />
                    </Col>
                  </Row> */}
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

function mapStateToProps(state) {
  const { pollsdata } = state.poll;
  return {
    pollsdata,
  };
}

export default connect(mapStateToProps)(ExistingPolls);
