import React from "react";
import { Layout, Row, Col, Button } from "antd";
import { LoggedinHeader } from "../../containers";
const { Content } = Layout;
import { Link, useHistory } from "react-router-dom";
import styles from "./manageEmp.module.scss";
import { ADD_EMPLOYEE, BACK } from "../../headings";
import OrgSidebar from "../../containers/OrgSidebar";
import {
  ADD_EMPLOYEE_TEXT_PART1,
  ADD_EMPLOYEE_TEXT_PART2,
  ADD_EMPLOYEE_TEXT_PART3,
  ADD_EMPLOYEE_TEXT_PART4,
} from "../../messages";

const AddEmployee = (props) => {
  const history = useHistory();
  const onCancel = () => {
    history.push("/orgcc/manageemployees");
  };
  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content className="w-100">
                <Row className="py-3">
                  <h1 className="mb-0">{ADD_EMPLOYEE}</h1>
                  <div className="d-block w-100 pl-3 pl-lg-0 pt-1">
                    {ADD_EMPLOYEE_TEXT_PART1}
                  </div>
                </Row>
                <div className="mid-content mt-0 mt-md-4 no-bg px-3 px-md-0">
                  <Row>
                    <Col span={24} className="mt-5">
                      <Row
                        className="d-flex justify-content-center"
                        gutter={{ xs: 8, sm: 32, md: 32, lg: 100 }}
                      >
                        <Col className="gutter-row" span={24} md={8} xl={7}>
                          <div
                            className={`d-flex flex-column ${styles.box_bordered}`}
                          >
                            <span
                              className={`icon-integrate ${styles.icon}`}
                            ></span>
                            <div className="font-weight-bold text-center">
                              {ADD_EMPLOYEE_TEXT_PART2}
                            </div>
                          </div>
                        </Col>
                        <Col className="gutter-row" span={24} md={8} xl={7}>
                          <Link to="/orgcc/importemployee">
                            <div
                              className={`d-flex flex-column ${styles.box_bordered}`}
                            >
                              <span
                                className={`icon-csv ${styles.icon}`}
                              ></span>
                              <div className="font-weight-bold text-center">
                                {ADD_EMPLOYEE_TEXT_PART3}
                              </div>
                            </div>
                          </Link>
                        </Col>
                        <Col className="gutter-row" span={24} md={8} xl={7}>
                          <Link to="/orgcc/createemployee">
                            <div
                              className={`d-flex flex-column ${styles.box_bordered}`}
                            >
                              <span
                                className={`icon-add-user ${styles.icon}`}
                              ></span>
                              <div className="font-weight-bold text-center">
                                {ADD_EMPLOYEE_TEXT_PART4}
                              </div>
                            </div>
                          </Link>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </div>
                <Button type="primary" onClick={onCancel}>
                  {BACK}
                </Button>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default AddEmployee;
