import {
    INFO_FAIL,INFO_SUCCESS,CREATEINFO_SUCCESS,CREATEINFO_FAIL
 } from "../actions/types";
   
  const deafaultState = {};
  deafaultState.infosdata = null;
  deafaultState.createinfodata =null;
  
  export default function (state = deafaultState, action) {
    const { type, payload } = action;
    switch (type) {
      case INFO_SUCCESS:
        return {
          ...state,
          infosdata: payload.infosdata,
        };
      case INFO_FAIL:
        return {
          ...state,
          infosdata: [],
        };
        case CREATEINFO_SUCCESS:
            return {
              ...state,
              createinfodata: payload.createinfodata,
            };
          case CREATEINFO_FAIL:
            return {
              ...state,
              createinfodata: [],
            };  
      default:
        return state;
    }
  }
  