import React, { Component } from "react";
import { Modal, Button, Form, Row, Col, DatePicker, Alert } from "antd";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import "../modal/modal.scss";
import {
  CANCEL_MODAL,
  POLL,
  PUBLISH,
  PUBLISH_POLL,
  SCHEDULE,
} from "../../headings";
import "date-fns";
import momentUtil from "../../utils/momentUtil";
import { TimePicker } from "@progress/kendo-react-dateinputs";
import { POLL_DATES_TEXT } from "../../messages";
import { SELECT_DATE, PAST_TIME, VALID_DATE, END_TIME } from "../../errors";
import { SELECT_END_DATE, SELECT_START_DATE } from "../../labels";

class DuplicateDateTimeModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      dateError: false,
      pastStartTimeError: false,
      pastEndTimeError: false,
      endTimeError: false,
      startDate: "",
      startTime: new Date(),
      start_time: new Date(),
      endDate: "",
      endTime: new Date(),
      end_time: new Date(),
    };
  }

  componentDidMount() {
    let {
      pollStartDate,
      pollStartTime,
      pollEndDate,
      pollEndTime,
      pollId,
    } = this.props.pollData;
    const sdate = pollStartDate.split("-")[0];
    const smonth = pollStartDate.split("-")[1];
    const syear = pollStartDate.split("-")[2];
    const edate = pollEndDate.split("-")[0];
    const emonth = pollEndDate.split("-")[1];
    const eyear = pollEndDate.split("-")[2];

    this.setState({
      startDate: moment(pollStartDate, "DD-MM-YYYY"),
      endDate: moment(pollEndDate, "DD-MM-YYYY"),
      startTime: new Date(
        syear + "-" + smonth + "-" + sdate + "T" + pollStartTime
      ),
      start_time: new Date(
        syear + "-" + smonth + "-" + sdate + "T" + pollStartTime
      ),
      endTime: new Date(eyear + "-" + emonth + "-" + edate + "T" + pollEndTime),
      end_time: new Date(
        eyear + "-" + emonth + "-" + edate + "T" + pollEndTime
      ),
    });
    const finalStartDate = new Date(smonth + "-" + sdate + "-" + syear);
    const finalEndDate = new Date(emonth + "-" + edate + "-" + eyear);
    const currentDate = new Date();
    if (pollId != 1 && pollId != 2) {
      if (finalStartDate < currentDate || finalEndDate < currentDate) {
        this.setState({
          dateError: true,
        });
      }
    }
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    if (!this.state.startDate || !this.state.endDate) {
      this.props.showError(true);
    } else {
      this.props.showError(false);
      this.setState({
        visible: false,
      });
      this.props.onPublish();
    }
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  disabledStartDate = (current) => {
    return current && current < moment().startOf("day");
  };

  disabledEndDate = (current) => {
    if (this.props.pollData.pollStartDate) {
      return (
        current &&
        current < moment(this.props.pollData.pollStartDate, "DD-MM-YYYY")
      );
    } else {
      return current && current < moment().startOf("day");
    }
  };

  range = (start, end) => {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  };

  onChangeStartDate = (date) => {
    this.props.handlePollStartDatePickerChange(date, "pollStartDate");
    this.setState({
      dateError: false,
    });
  };

  onChangeEndDate = (date) => {
    this.props.handlePollEndDatePickerChange(date, "pollEndDate");
    this.setState({
      dateError: false,
    });
  };

  onChangeStartTime = (event) => {
    const startTime = event.target.value;
    const formattedStartDate = momentUtil.formatAsDDMMYYYY(
      this.state.startDate
    );
    const edate = formattedStartDate.split("-")[0];
    const emonth = formattedStartDate.split("-")[1];
    const eyear = formattedStartDate.split("-")[2];
    let minutes = startTime.getMinutes();
    let hours = startTime.getHours();
    if (minutes.toString().length === 1) {
      minutes = "0" + minutes;
    }
    if (hours.toString().length === 1) {
      hours = "0" + hours;
    }
    if (hours.toString() === "0") {
      hours = "00";
    }
    const extractedTime = hours + ":" + minutes + ":" + "00";
    const final_startTime = new Date(
      eyear + "-" + emonth + "-" + edate + "T" + extractedTime
    );
    this.setState({ pastStartTimeError: false, startTime: final_startTime });
  };

  onBlurStartTime = (event) => {
    const currentTime = new Date();
    if (
      currentTime > this.state.startTime &&
      this.state.startTime.getTime() !== this.state.start_time.getTime()
    ) {
      this.setState({
        pastStartTimeError: true,
        startTime: this.state.start_time,
      });
    } else {
      this.setState({ pastStartTimeError: false });
      this.props.handlePollStartTimePickerChange(
        this.state.startTime,
        "pollStartTime"
      );
    }
  };

  onChangeEndTime = (event) => {
    const endTime = event.target.value;
    const formattedEndDate = momentUtil.formatAsDDMMYYYY(this.state.endDate);
    const edate = formattedEndDate.split("-")[0];
    const emonth = formattedEndDate.split("-")[1];
    const eyear = formattedEndDate.split("-")[2];
    let minutes = endTime.getMinutes();
    let hours = endTime.getHours();
    if (minutes.toString().length === 1) {
      minutes = "0" + minutes;
    }
    if (hours.toString().length === 1) {
      hours = "0" + hours;
    }
    if (hours.toString() === "0") {
      hours = "00";
    }
    const extractedTime = hours + ":" + minutes + ":" + "00";
    const final_endTime = new Date(
      eyear + "-" + emonth + "-" + edate + "T" + extractedTime
    );
    this.setState({ pastEndTimeError: false, endTime: final_endTime });
  };

  onBlurEndTime = (event) => {
    const currentTime = new Date();
    if (
      currentTime > this.state.endTime &&
      this.state.endTime?.valueOf() !== this.state.end_time?.valueOf()
    ) {
      this.setState({
        pastEndTimeError: true,
        endTime: this.state.end_time,
      });
    } else if (this.state.endTime < this.state.startTime) {
      this.setState({
        endTimeError: true,
        endTime: this.state.end_time,
      });
    } else {
      this.setState({ pastEndTimeError: false });
      this.props.handlePollEndTimePickerChange(
        this.state.endTime,
        "pollEndTime"
      );
    }
  };

  render() {
    return (
      <>
        <Button type="primary" className="md mr-4" onClick={this.showModal}>
          {SCHEDULE}
        </Button>
        <Modal
          title={PUBLISH_POLL}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={520}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText={PUBLISH}
          cancelText={CANCEL_MODAL}
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              {this.props.error && (
                <Form.Item>
                  <Alert message={SELECT_DATE} type="error" showIcon></Alert>
                </Form.Item>
              )}
              {this.state.dateError && (
                <Form.Item>
                  <Alert message={VALID_DATE} type="error" showIcon></Alert>
                </Form.Item>
              )}
              <div className="mb-4 subtext-md">
                {POLL_DATES_TEXT}
                <span className="pr-1 pr-1 font-weight-md6">
                  {" "}
                  {this.props.pollData.pollName}
                </span>
                {POLL}
              </div>
              <FloatLabel label={SELECT_START_DATE} value="sDate">
                <Form.Item
                  name="sDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col span="24" lg={12} className="gutter-row">
                      <DatePicker
                        defaultValue={this.state.startDate}
                        disabledDate={this.disabledStartDate}
                        onChange={(date) => this.onChangeStartDate(date)}
                        className="w-100"
                        name="sDate"
                        className="required"
                      />
                      {this.state.pastStartTimeError && (
                        <Alert
                          message={PAST_TIME}
                          type="error"
                          showIcon
                        ></Alert>
                      )}
                    </Col>
                    <Col span="24" lg={12}>
                      <TimePicker
                        value={this.state.startTime}
                        onChange={this.onChangeStartTime}
                        onBlur={this.onBlurStartTime}
                      />
                    </Col>
                  </Row>
                </Form.Item>
              </FloatLabel>
              <FloatLabel label={SELECT_END_DATE} value="eDate">
                <Form.Item
                  name="eDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col span="24" lg={12} className="gutter-row">
                      {this.state.endDate !== "" ? (
                        <>
                          {" "}
                          <DatePicker
                            defaultValue={this.state.endDate}
                            disabledDate={this.disabledEndDate}
                            onChange={(date) => this.onChangeEndDate(date)}
                            className="w-100"
                            name="eDate"
                            className="required"
                          />
                        </>
                      ) : (
                        <DatePicker
                          defaultValue={this.state.endDate}
                          disabledDate={this.disabledEndDate}
                          onChange={(date) => this.onChangeEndDate(date)}
                          className="w-100"
                          name="eDate"
                          className="required"
                        />
                      )}
                      {this.state.pastEndTimeError && (
                        <Alert
                          message={PAST_TIME}
                          type="error"
                          showIcon
                        ></Alert>
                      )}
                      {this.state.endTimeError && (
                        <Alert message={END_TIME} type="error" showIcon></Alert>
                      )}
                    </Col>
                    <Col span="24" lg={12}>
                      <TimePicker
                        value={this.state.endTime}
                        onChange={this.onChangeEndTime}
                        onBlur={this.onBlurEndTime}
                      />
                    </Col>
                  </Row>
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default DuplicateDateTimeModal;
