import React, { Component } from "react";
import { Modal, Button } from "antd";
import "../modal/modal.scss";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import {
  CANCEL_MODAL,
  DELETE,
  DELETE_TEXT_MODAL,
  YES_TEXT_MODAL,
  NO_TEXT_MODAL,
  OK_TEXT_MODAL,
} from "../../headings";
import {
  ABOUT_TO_DELETE_ORGANIZATION_PART1,
  ABOUT_TO_DELETE_ORGANIZATION_PART2,
  ATLEAST_ONE_SEEDED_USER,
  DELETE_CONFIRM,
} from "../../messages";

class DeleteModal extends Component {
  state = { visible: false, show: false };

  componentDidMount() {
    if (this.props.deleteOrg) {
      this.showModal();
    }
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  showBothModals = () => {
    this.setState({
      visible: true,
      show: true,
    });
  };

  handleOnOk = () => {
    this.setState({
      show: false,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    if (this.props.seededUser) {
      if (this.props.viaEdit) {
        this.props.handleDelete(this.props.user.email);
      } else {
        this.props.handleDelete(this.props.user.userId);
      }
    } else if (this.props.deleteOrg) {
      this.props.handleDelete(this.props.org);
    }
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
    if (this.props.deleteOrg) this.props.handleCancel();
  };

  deleteHotline() {
    const { history } = this.props;
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .delete(BASE_URL + "hotline/" + this.props.hotlineId, options)
      .then(() => {
        history.push("/orgcc/hotlinedirectory");
      })
      .catch((error) => {});
  }

  render() {
    return (
      <>
        {!this.props.deleteOrg && !this.props.data.manageEmp ? (
          <Button onClick={this.showBothModals}>{DELETE}</Button>
        ) : this.props.data && this.props.data.manageEmp ? (
          <a onClick={this.showModal}>{DELETE}</a>
        ) : (
          <></>
        )}

        {this.props.deleteOrg ? (
          <Modal
            visible={this.state.visible}
            onOk={this.handleCancel}
            onCancel={this.handleOk}
            centered
            width={700}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{
              children: "Custom cancel",
            }}
            okText={CANCEL_MODAL}
            cancelText={DELETE_TEXT_MODAL}
          >
            <p>
              {ABOUT_TO_DELETE_ORGANIZATION_PART1}{" "}
              <strong>{this.props.org}</strong>.{" "}
              {ABOUT_TO_DELETE_ORGANIZATION_PART2}{" "}
              <strong>{this.props.org}</strong>?
            </p>
          </Modal>
        ) : !this.props.deleteOrg &&
          this.props.viaEdit &&
          this.props.seededUsers.length > 1 ? (
          <Modal
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            centered
            width={370}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{
              children: "Custom cancel",
            }}
            okText={YES_TEXT_MODAL}
            cancelText={NO_TEXT_MODAL}
          >
            <p>
              {DELETE_CONFIRM} {this.props.user.firstName}?
            </p>
          </Modal>
        ) : !this.props.viaEdit && this.props.seededUser ? (
          <Modal
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            centered
            width={370}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{
              children: "Custom cancel",
            }}
            okText={YES_TEXT_MODAL}
            cancelText={NO_TEXT_MODAL}
          >
            <p>
              {DELETE_CONFIRM} {this.props.user.firstName}?
            </p>
          </Modal>
        ) : !this.props.deleteOrg &&
          this.props.viaEdit &&
          this.props.seededUsers.length === 1 ? (
          <Modal
            visible={this.state.show}
            onOk={this.handleOnOk}
            centered
            width={370}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText={OK_TEXT_MODAL}
          >
            <p>{ATLEAST_ONE_SEEDED_USER}</p>
          </Modal>
        ) : (
          <Modal
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            centered
            width={370}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{
              children: "Custom cancel",
            }}
            okText={OK_TEXT_MODAL}
            cancelText={CANCEL_MODAL}
          >
            <p>{DELETE_CONFIRM}</p>
          </Modal>
        )}
      </>
    );
  }
}

export default DeleteModal;
