import React from "react";
import LoggedinHeader from "./LoggedinHeader";
import AmWebSidebar from "../components/hotlineMessages/AmWebSidebar";
import OrgSidebar from "./OrgSidebar";
import AccSidebar from "../components/manageOrg/AccSidebar";
import IdbSidebar from "../components/reports/IdbSidebar";
import { Row, Layout } from "antd";
import * as AppStorage from "../utils/AppStorage";
import { PERMISSION_DENIED } from "../messages";

var CryptoJS = require("crypto-js");
const { Content } = Layout;
class PermissionDenied extends React.Component {
  render() {
    const type = AppStorage.get("userType");
    const userType = type
      ? CryptoJS.AES.decrypt(type, "anonymoose").toString(CryptoJS.enc.Utf8)
      : "";
    return (
      <>
        <Row className="inner-header">
          {userType === "AM WEB" ? (
            <LoggedinHeader amWEBUser />
          ) : userType === "AM CC" ? (
            <LoggedinHeader amCCUser />
          ) : userType === "Org CC" ? (
            <LoggedinHeader amORGUser />
          ) : userType === "IDB" ? (
            <LoggedinHeader amIDBUser />
          ) : null}
        </Row>
        <Row className="content-center">
          <Layout>
            <Layout>
              {userType === "AM WEB" ? (
                <AmWebSidebar data={this.props} />
              ) : userType === "Org CC" ? (
                <OrgSidebar data={this.props} />
              ) : userType === "AM CC" ? (
                <AccSidebar />
              ) : userType === "IDB" ? (
                <IdbSidebar />
              ) : null}

              <Content className="">
                <Row className="py-3" style={{ justifyContent: "center" }}>
                  <h1 className="mb-0">{PERMISSION_DENIED}</h1>
                </Row>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default PermissionDenied;
