import { LOGIN_SUCCESS, LOGIN_FAIL, LOGOUT } from "./types";
import AuthService from "../services/auth-service";

export const login = (email, password, page) => (dispatch) => {
  page = email === "admin@anonymoose.co" ? "is_amcc_user" : page;
  return AuthService.login(email, password, page).then(
    (data) => {
      dispatch({
        type: LOGIN_SUCCESS,
        payload: {
          user: data,
          current: password,
        },
      });

      return Promise.resolve();
    },
    (error) => {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      dispatch({
        type: LOGIN_FAIL,
      });

      // dispatch({
      //   type: SET_MESSAGE,
      //   payload: message,
      // });

      return Promise.reject();
    }
  );
};

export const logout = () => (dispatch) => {
  AuthService.logout();

  dispatch({
    type: LOGOUT,
  });
};
