import React, { useState } from "react";
import { Layout, Row, Col, Button, Input, Divider, Spin, Table } from "antd";
import { LoggedinHeader } from "../../containers";
import IdbSidebar from "../reports/IdbSidebar";
import { Link, useHistory } from "react-router-dom";
import { CREATE, INFO } from "../../headings";
import Grid from "../table/Table";
const { Content } = Layout;

const { Search } = Input;

const InfoView = (props) => {
  const history = useHistory();
  const [search, setSearch] = useState("");
  const loader = props.loader;
  const handleOnSearch = () => {
    props.setSearchValue(search);
  };
  const columns = [

    {
      title: "Info Name",
      dataIndex: "title",
      key: "title",
      width: "22%",
      render: (name, record) => {
        return {
          children:
            (
              <h6 className="font-weight-md6 mb-1">
                <Link
                  to={{
                    pathname: `/idb/infodetails`,
                    state: record,
                  }}
                >
                  {record.title}
                </Link>
              </h6>)
          ,
          props: {
            "data-title": "title",
          },
        };
      },
    }
  ];

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amIDBUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <IdbSidebar amIDBUser data={props.data} />
              <Content>
                <Row className="py-3 justify-content-between">
                  <h1 className="mb-0">{INFO}</h1>
                </Row>

                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            onChange={(e) => {
                              setSearch(e.target.value);
                            }}
                            onBlur={handleOnSearch}
                            placeholder="Search"
                          />
                        </Col>
                        <Col
                          span={24}
                          lg={6}
                          className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                        ></Col>
                      </Row>
                    </Col>

                    {/* <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Button
                        onClick={() => {
                          history.push("/orgcc/createinfo");
                        }}
                        type="primary"
                      >
                        {CREATE}
                      </Button>
                    </Col> */}
                  </Row>
                </Col>

                <Spin spinning={loader} size="large">
                  <Grid
                    rowKey="info_id"
                    columns={columns}
                    data={props.infos}
                  />
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default InfoView;
