import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import ProtectedRoute from "./ProtectedRoute";
import OrganizationContainer from "./components/manageOrg/OrganizationContainer";
import ForgotPassword from "./components/login/ForgotPassword";
import ResetPassword from "./components/login/ResetPassword";
import Login from "./components/login/Login";
import AMCCLogin from "./components/login/AmccLogin";
import ChangePassword from "./components/form/ChangePassword";
import HotlineCreation from "./components/hotlineCreation/HotlineCreation";
import HotlineContainer from "./components/hotlineDirectory/HotlineContainer";
import AssignHotlineModal from "./components/modal/AssignHotlineModal";
import HotlineMessages from "./components/hotlineMessages/HotlineMessages";
import OpenlineMessages from "./components/openlineMessages/OpenlineMessages";
import PermissionDenied from "./containers/PermissionDenied";
import * as AppStorage from "../src/utils/AppStorage";
import CreateOrgStepper from "./components/form/CreateOrgStepper";
import OrgDetails from "./components/form/OrgDetails";
import EditOrg from "./components/form/EditOrg";
import EditProfile from "./components/form/EditProfile";
import EditHotLine from "./components/hotlineDirectory/EditHotLine";
import AddEmployee from "./components/manageEmp/AddEmployee";
import ImportFile from "./components/manageEmp/ImportFile";
import CreateManually from "./components/manageEmp/CreateManually";
import CreatePoll from "./components/managePolls/CreatePoll";
import PreviewPoll from "./components/managePolls/PreviewPoll";
import ManageBroadcast from "./components/broadcastMessages/ManageBroadcast";
import CreateBroadcast from "./components/broadcastMessages/CreateBroadcast";
import EditBroadcast from "./components/broadcastMessages/EditBroadcast";
import BroadcastMessages from "./components/broadcastMessages/BroadcastMessages";
import BroadcastContainer from "./components/broadcastMessages/BroadcastContainer";
import PollsContainer from "./components/managePolls/PollsContainer";
import PollResponse from "./components/managePolls/PollResponse";
import EditPoll from "./components/managePolls/EditPoll";
import ExistingPolls from "./components/managePolls/ExistingPolls";
import DuplicatePoll from "./components/managePolls/DuplicatePoll";
import EditEmployee from "./components/manageEmp/EditEmployee";
import EmployeesContainer from "./components/manageEmp/EmployeesContainer";
import Reports from "./components/reports/Reports";
import OpenlineContainer from "./components/openlineDirectory/OpenlineContainer";
import OpenlineCreation from "./components/openlineCreation/OpenlineCreation";
import EditOpenline from "./components/openlineDirectory/EditOpenline";
import CreateHelpForm from "./components/help/CreateHelpForm";
import HelpContainer from "./components/help/HelpContainer";
import CreateInfoForm from "./components/info/CreateInfoForm";
import InfoDetails from "./components/info/InfoDetails";
import InfoContainer from "./components/info/InfoContainer";
import BroadcastViewContainer from "./components/broadcastMessages/BroadcastViewContainer"
import PollContainerView from "./components/managePolls/PollContainerView";
import InfoContainerView from "./components/info/InfoContainerView";
import InfoDetailsView from "./components/info/InfoDetailsView";
import HelpContainerView from "./components/help/HelpContainerView";
import PollResponseView from "./components/managePolls/PollResponseView";
var CryptoJS = require("crypto-js");
function Routes(props) {
  const isAuthenticated = AppStorage.get("user");
  const commonForAllUsers = JSON.parse(AppStorage.get("user"));

  let isamcc = false;
  let isorgcc = false;
  let isamweb = false;
  let isidb = false;
  if (!isAuthenticated) {
    isamcc = true;
    isorgcc = true;
    isamweb = true;
    isidb = true;
  }
  if (
    isAuthenticated &&
    commonForAllUsers &&
    commonForAllUsers.user_data != "null"
  ) {
    isamcc =
      CryptoJS.AES.decrypt(
        commonForAllUsers.user_data.is_amcc_user,
        "anonymoose"
      ).toString(CryptoJS.enc.Utf8) === "true";
    isorgcc =
      CryptoJS.AES.decrypt(
        commonForAllUsers.user_data.is_org_cc_user,
        "anonymoose"
      ).toString(CryptoJS.enc.Utf8) === "true";
    isamweb =
      CryptoJS.AES.decrypt(
        commonForAllUsers.user_data.is_am_web_user,
        "anonymoose"
      ).toString(CryptoJS.enc.Utf8) === "true";
    isidb =
      CryptoJS.AES.decrypt(
        commonForAllUsers.user_data.is_idb_user,
        "anonymoose"
      ).toString(CryptoJS.enc.Utf8) === "true";
  }
  const url = window.location.pathname;

  return (
    <BrowserRouter>
      <Switch>
        <Route path="/editprofile" component={EditProfile} />
        <Route path="/managebroadcast" component={ManageBroadcast} />
        <ProtectedRoute path="/changepassword" component={ChangePassword} />
        <Route exact={true} path="/amcc/login" component={AMCCLogin} />
        <Route exact={true} path="/org/login" component={Login} />
        <Route exact={true} path="/forgotpassword" component={ForgotPassword} />
        <Route exact={true} path="/resetpassword" component={ResetPassword} />
        {isorgcc &&
        (url === "/orgcc/createhotline" ||
          url === "/orgcc/edithotline" ||
          url === "/orgcc/editopenline" ||
          url === "/orgcc/manageemployees" ||
          url === "/orgcc/assignhotline" ||
          url === "/orgcc/hotlinedirectory" ||
          url === "/orgcc/createopenline" ||
          url === "/orgcc/openlinedirectory" ||
          url === "/orgcc/pollsdirectory" ||
          url === "/orgcc/createpoll" ||
          url.includes("/orgcc/editpoll") ||
          url === "/orgcc/previewpoll" ||
          url === "/orgcc/pollresponse" ||
          url === "/orgcc/existingpolls" ||
          url.includes("/orgcc/duplicatepoll") ||
          url === "/orgcc/addemployee" ||
          url === "/orgcc/createemployee" ||
          url === "/orgcc/help" ||
          url === "/orgcc/createhelp" ||
          url === "/orgcc/edithelp" ||
          url === "/orgcc/info" ||
          url === "/orgcc/infodetails" ||
          url === "/orgcc/createinfo" ||
          url === "/orgcc/editinfo" ||
          url === "/orgcc/importemployee" ||
          url.includes("/orgcc/editemployee") ||
          url === "/orgcc/broadcastmessages" ||
          url === "/orgcc/createbroadcast" ||
          url.includes("/orgcc/editbroadcast") ||
          url === "/" ||
          url === "/amcc/login" ||
          url === "/org/login") ? (
          <Switch>
            {/* ORGCC ROUTING */}
            <ProtectedRoute
              exact={true}
              path="/orgcc/createhotline"
              amorg
              component={HotlineCreation}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/manageemployees"
              amorg
              component={EmployeesContainer}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/importemployee"
              amorg
              component={ImportFile}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/addemployee"
              amorg
              component={AddEmployee}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/createemployee"
              amorg
              component={CreateManually}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/assignhotline"
              amorg
              component={AssignHotlineModal}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/hotlinedirectory"
              amorg
              component={HotlineContainer}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/edithotline"
              amorg
              component={EditHotLine}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/createopenline"
              amorg
              component={OpenlineCreation}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/openlinedirectory"
              amorg
              component={OpenlineContainer}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/editopenline"
              amorg
              component={EditOpenline}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/pollsdirectory"
              amorg
              component={PollsContainer}
            />

            <ProtectedRoute
              exact={true}
              path="/orgcc/previewpoll"
              amorg
              component={PreviewPoll}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/createpoll"
              amorg
              component={CreatePoll}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/pollresponse"
              amorg
              component={PollResponse}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/editpoll/:id"
              amorg
              component={EditPoll}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/existingpolls"
              amorg
              component={ExistingPolls}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/createhelp"
              amorg
              component={CreateHelpForm}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/help"
              amorg
              component={HelpContainer}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/info"
              amorg
              component={InfoContainer}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/createinfo"
              amorg
              component={CreateInfoForm}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/editinfo"
              amorg
              component={CreateInfoForm}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/edithelp"
              amorg
              component={CreateHelpForm}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/infodetails"
              amorg
              component={InfoDetails}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/duplicatepoll/:id"
              amorg
              component={DuplicatePoll}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/editemployee/:id"
              amorg
              component={EditEmployee}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/broadcastmessages"
              amorg
              component={BroadcastContainer}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/createbroadcast"
              amorg
              component={CreateBroadcast}
            />
            <ProtectedRoute
              exact={true}
              path="/orgcc/editbroadcast/:id"
              amorg
              component={EditBroadcast}
            />
          </Switch>
        ) : isamweb &&
          (url === "/amweb/hotlinemessages" ||
            url === "/org/login" ||
            url === "/amcc/login" ||
            url === "/" ||
            url === "/amweb/openlinemessages" ||
            url === "/hotlinereports" ||
            url === "/openlinereports" ||
            url === "/orgcc/help" ||
            url === "/orgcc/createhelp" ||
            url === "/orgcc/edithelp" ||
            url === "/orgcc/info" ||
            url === "/orgcc/infodetails" ||
            url === "/orgcc/createinfo" ||
            url === "/orgcc/editinfo") ? (
          <>
            {" "}
            {/* AMWEB ROUTING */}
            <Switch>
              <ProtectedRoute
                exact={true}
                path="/amweb/hotlinemessages"
                amweb
                component={HotlineMessages}
              />
              <ProtectedRoute
                exact={true}
                path="/amweb/openlinemessages"
                amweb
                component={OpenlineMessages}
              />
              <ProtectedRoute
                exact={true}
                path="/orgcc/createhelp"
                amweb
                component={CreateHelpForm}
              />
              <ProtectedRoute
                exact={true}
                path="/orgcc/edithelp"
                amweb
                component={CreateHelpForm}
              />
              <ProtectedRoute
                exact={true}
                path="/orgcc/help"
                amweb
                component={HelpContainer}
              />
              <ProtectedRoute
                exact={true}
                path="/orgcc/info"
                amweb
                component={InfoContainer}
              />
              <ProtectedRoute
                exact={true}
                path="/orgcc/createinfo"
                amweb
                component={CreateInfoForm}
              />
              <ProtectedRoute
                exact={true}
                path="/orgcc/editinfo"
                amorg
                component={CreateInfoForm}
              />
              <ProtectedRoute
                exact={true}
                path="/orgcc/infodetails"
                amweb
                component={InfoDetails}
              />
            </Switch>
          </>
        ) : isamcc &&
          (url === "/amcc/manageorganizations" ||
            url === "/amcc/createorg" ||
            url === "/" ||
            url.includes("/amcc/editorg") ||
            url.includes("/amcc/orgdetails") ||
            url === "/org/login" ||
            url === "/amcc/login") ? (
          <>
            {" "}
            {/* AMCC ROUTING */}
            <Switch>
              <ProtectedRoute
                exact={true}
                path="/amcc/orgdetails/:id"
                amcc
                component={OrgDetails}
              />
              <ProtectedRoute
                exact={true}
                path="/amcc/manageorganizations"
                amcc
                component={OrganizationContainer}
              />
              <ProtectedRoute
                exact={true}
                path="/amcc/createorg"
                amcc
                component={CreateOrgStepper}
              />
              <ProtectedRoute
                exact={true}
                path="/amcc/editorg/:id"
                amcc
                component={EditOrg}
              />
            </Switch>{" "}
          </>
        ) : isidb &&
          (url === "/org/login" ||
            url === "/amcc/login" ||
            url === "/" ||
            url === "/idb/hotlinereports" ||
            url === "/idb/openlinereports" ||
            url === "/idb/pollsdirectory" ||
            url === "/idb/broadcastmessages" ||
            url === "/orgcc/help" ||
            url === "/orgcc/createhelp" ||
            url === "/orgcc/edithelp" ||
            url === "/orgcc/info" ||
            url === "/orgcc/infodetails" ||
            url === "/orgcc/createinfo" ||
            url === "/orgcc/editinfo") ? (
          <Switch>
            {" "}
            <ProtectedRoute
              exact={true}
              path="/idb/hotlinereports"
              amidb
              component={Reports}
            />
            <ProtectedRoute
              exact={true}
              path="/idb/openlinereports"
              amidb
              component={Reports}
            />{" "}
            <ProtectedRoute
              exact={true}
              path="/idb/help"
              amidb
              component={HelpContainerView}
            />
            <ProtectedRoute
              exact={true}
              path="/idb/info"
              amidb
              component={InfoContainerView}
            />
            <ProtectedRoute
              exact={true}
              path="/idb/infodetails"
              amidb
              component={InfoDetailsView}
            />
            <ProtectedRoute
              exact={true}
              path="/idb/pollsdirectory"
              amidb
              component={PollContainerView}
            />
            <ProtectedRoute
              exact={true}
              path="/idb/pollresponse"
              amidb
              component={PollResponseView}
            />
            <ProtectedRoute
              exact={true}
              path="/idb/broadcastmessages"
              amidb
              component={BroadcastViewContainer}
            />
            
          </Switch>
        ) : (
          isAuthenticated && <Route component={PermissionDenied} />
        )}
      </Switch>
    </BrowserRouter>
  );
}

export default Routes;