import {
    OPENLINE_FAIL,
    OPENLINE_SUCCESS
  } from "../actions/types";
  
  const deafaultState = {};
  deafaultState.openlinesdata = null;
  
  export default function (state = deafaultState, action) {
    const { type, payload } = action;
  
    switch (type) {
      case OPENLINE_SUCCESS:
        return {
          ...state,
          openlinesdata: payload.openlinesdata,
        };
      case OPENLINE_FAIL:
        return {
          ...state,
          openlinesdata: [],
        };
      default:
        return state;
    }
  }
  