import React, { Component } from "react";
import { Modal } from "antd";
import "../modal/modal.scss";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import { DELETE_HELP, NO_TEXT_MODAL, YES_TEXT_MODAL } from "../../headings";
import { DELETE_CONFIRM_HELP } from "../../messages";

class DeleteHelp extends Component {
  state = { visible: true };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.deleteHelp();
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  deleteHelp() {
    if (this.props.help) {
      const token = authHeader();

      const options = {
        headers: {
          Authorization: token,
          Accept: "application/json, text/plain",
          "Content-Type": "application/json;charset=UTF-8",
        },
      };
      axios
        .delete(BASE_URL + "deletehelp/" + this.props.help.help_id, options)
        .then(() => {
          window.location = "/orgcc/help";
        })
        .catch((error) => {});
    }
  }

  render() {
    return (
      <>
        <Modal
          title={DELETE_HELP}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={370}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
          }}
          okText={YES_TEXT_MODAL}
          cancelText={NO_TEXT_MODAL}
        >
          <p>{DELETE_CONFIRM_HELP}</p>
        </Modal>
      </>
    );
  }
}

export default DeleteHelp;
