import React from "react";
import { TreeSelect } from "antd";
import {
  MAKE_APP_USER,
  MAKE_SUPER_USER,
  MAKE_CONTROL_CENTER,
  MAKE_IDB_USER,
} from "../../values";
import { SELECT_ROLE } from "../../labels";

const { SHOW_PARENT } = TreeSelect;
const treeData = [
  {
    title: MAKE_APP_USER,
    value: "0",
    key: "0",
    disabled: true,
  },
  {
    title: MAKE_SUPER_USER,
    value: "1",
    key: "1",
  },
  {
    title: MAKE_CONTROL_CENTER,
    value: "2",
    key: "2",
  },
  {
    title: MAKE_IDB_USER,
    value: "3",
    key: "3",
  },
];

class MultiRoleSelection extends React.Component {
  
  render() {
    const tProps = {
     // defaultValue: this.props.viaEdit ? this.props.roles : ["0"],
      value:this.props.roles,
      treeData,
      onChange: this.props.handleRoleOnSelect,
      treeCheckable: true,
      showCheckedStrategy: SHOW_PARENT,
      placeholder: SELECT_ROLE,
      style: {
        width: "100%",
      }
    };
    return <TreeSelect {...tProps} />;
  }
}

export default MultiRoleSelection;
