import UserService from '../services/user.service';

import{
    POLL_SUCCESS,
    POLL_FAIL,
    SET_MESSAGE
    } from "./types";

    export const getPollAsync=(filterStatus, search)=>(dispatch)=>{
        return UserService.getPolls(filterStatus,search).then((data)=>{
            dispatch({
                type:POLL_SUCCESS,
                payload:{pollsdata:data.data},
                
            });    
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:POLL_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };

    