import React  from "react";
import {
  Modal,
  Collapse,
  Row,
  Col,
  Input
} from "antd";
import { Scrollbars } from "react-custom-scrollbars";

const { Panel } = Collapse;

const CUGModal = ({
  CUGModalvisible, 
  flexAttribute, 
  CUGAttribute, 
  viewOnly=false, 
  handleCancel, 
  handleSave, 
  handleCUGChange}) => {
    
    return (
      <>
        <Modal
          title="Add Employees"
          centered
          visible={CUGModalvisible}
          width={800}
          onOk={() => handleSave()}
          onCancel={() => handleCancel()}
          okButtonProps={{
            children: "Custom OK",
            disabled: viewOnly ? true : false
          }}
          cancelButtonProps={{
            children: "Custom cancel",
          }}
          okText="Save"
          cancelText={viewOnly ? "Close" : "Cancel"}
        >
          <div className="font-weight-normal">
            Please create all employees for your organization who are eligible
            to use the app
          </div>
          <h6 className="mt-3 mb-1">Choose Attributes</h6>
          <small>
            You can choose from following organizational attribute as well as
            from CUG attribute to build sample form.
          </small>
          <Row>
            <Col span={24}>
              <Collapse
                defaultActiveKey={[1]}
                className="mt-3"
              >
                <Panel header="Add CUG Attributes" key="1">
                    <Col className="pr-4">
                      <h6 className="font-weight-bold">CUG Attributes</h6>
                      <p className="pr-3">
                        Description will go here... Lorem ipsum is a placeholder
                        text commonly used to demonstrate the visual form of a
                        document or a typeface.
                      </p>
                     <div>
                        <h6 className="mt-3 mb-1">
                          Enter Attributes{" "}
                          <small className="pl-1">(Max 5)</small>
                        </h6>
                        <small>
                          Attributes like
                          <b className="pl-1 pr-1">
                            Gender, Age, Race, Location, Designation
                          </b>
                          etc.
                        </small>
                        <Col span={24} className="gutter-row mt-3">
                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            <Col span={24} md={8} className="mb-2">
                              <Input
                                placeholder="CUG Attribute 1"
                                allowClear
                                value={CUGAttribute.cug1}
                                id="cug1"
                                onChange={(e) =>handleCUGChange(e, 'cug')}
                                disabled={viewOnly}
                                maxLength={50}
                              />
                            </Col>
                            <Col span={24} md={8} className="mb-2">
                              <Input
                                placeholder="CUG Attribute 2"
                                allowClear
                                value={CUGAttribute.cug2}
                                id="cug2"
                                onChange={(e) =>handleCUGChange(e, 'cug')}
                                disabled={viewOnly}
                                maxLength={50}
                              />
                            </Col>
                            <Col span={24} md={8} className="mb-2">
                              <Input
                                placeholder="CUG Attribute 3"
                                allowClear
                                value={CUGAttribute.cug3}
                                id="cug3"
                                onChange={(e) =>handleCUGChange(e, 'cug')}
                                disabled={viewOnly}
                                maxLength={50}
                              />
                            </Col>

                            <Col span={24} md={8} className="mb-2">
                              <Input
                                placeholder="CUG Attribute 4"
                                allowClear
                                value={CUGAttribute.cug4}
                                id="cug4"
                                onChange={(e) =>handleCUGChange(e, 'cug')}
                                disabled={viewOnly}
                                maxLength={50}
                              />
                            </Col>
                            <Col span={24} md={8} className="mb-2">
                              <Input
                                placeholder="CUG Attribute 5"
                                allowClear
                                value={CUGAttribute.cug5}
                                id="cug5"
                                onChange={(e) =>handleCUGChange(e, 'cug')}
                                disabled={viewOnly}
                                maxLength={50}
                              />
                            </Col>
                          </Row>
                        </Col>
                      </div>
                    </Col>
                </Panel>
                <Panel header="Add Org Attributes" key="2">
                  {/* <Scrollbars
                    autoHeight
                    autoHeightMin={0}
                    autoHeightMax={350}
                    thumbMinSize={30}
                    universal={true}
                  > */}
                    <Col>
                      <h6 className="font-weight-bold">
                         Flexible Attributes
                      </h6>
                      <p className="pr-3">
                        Description will go here... Lorem ipsum is a placeholder
                        text commonly used to demonstrate the visual form of a
                        document or a typeface.
                      </p>
                      <h6 className="mt-3 mb-1">
                        Enter Flexible Attributes <small className="pl-1">(Max 5)</small>
                      </h6>
                      <small>
                        Attributes like
                        <b className="pl-1 pr-1">
                          Experience, Date of Birth, National Identification Number
                        </b>
                        etc.
                      </small>
                      <Col span={24} className="gutter-row mt-2 pr-3">
                        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                          <Col span={24} md={8} className="mb-2">
                            <Input
                              placeholder="Flexible Attribute 1"
                              allowClear
                              value={flexAttribute.flex1}
                              id="flex1"
                              onChange={(e) =>handleCUGChange(e, 'flex')}
                              disabled={viewOnly}
                              maxLength={50}
                            />
                          </Col>
                          <Col span={24} md={8} className="mb-2">
                            <Input
                              placeholder="Flexible Attribute 2"
                              allowClear
                              value={flexAttribute.flex2}
                              id="flex2"
                              onChange={(e) =>handleCUGChange(e, 'flex')}
                              disabled={viewOnly}
                              maxLength={50}
                            />
                          </Col>
                          <Col span={24} md={8} className="mb-2">
                            <Input
                              placeholder="Flexible Attribute 3"
                              allowClear
                              value={flexAttribute.flex3}
                              id="flex3"
                              onChange={(e) =>handleCUGChange(e, 'flex')}
                              disabled={viewOnly}
                              maxLength={50}
                            />
                          </Col>

                          <Col span={24} md={8} className="mb-2">
                            <Input
                              placeholder="Flexible Attribute 4"
                              allowClear
                              value={flexAttribute.flex4}
                              id="flex4"
                              onChange={(e) =>handleCUGChange(e, 'flex')}
                              disabled={viewOnly}
                              maxLength={50}
                            />
                          </Col>
                          <Col span={24} md={8} className="mb-2">
                            <Input
                              placeholder="Flexible Attribute 5"
                              allowClear
                              value={flexAttribute.flex5}
                              id="flex5"
                              onChange={(e) =>handleCUGChange(e, 'flex')}
                              disabled={viewOnly}
                              maxLength={50}
                            />
                          </Col>
                        </Row>
                      </Col>
                    </Col>
                    {/*</Scrollbars> */}
                </Panel>
              </Collapse>
            </Col>
          </Row>
        </Modal>
      </>
    );
  }
export default CUGModal;