import React, { useState } from "react";
import {
  Layout,
  Row,
  Col,
  Button,
  Tooltip,
  Radio,
  Checkbox,
  Breadcrumb,
  Spin,
  Upload,
  Modal,
} from "antd";
import { NavLink } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import DateTimeModal from "../modal/DateTimeModal";
import styles from "./managePoll.module.scss";
import OrgSidebar from "../../containers/OrgSidebar";
import ReactStars from "react-rating-stars-component";
import authHeader from "../../services/auth-header";
import axios from "axios";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import { PaperClipOutlined } from "@ant-design/icons";
import momentUtil from "../../utils/momentUtil";
import Thermometer from "../../assets/images/thermometer.svg";
import Thumb from "../../assets/images/Thumb.svg";
import Smiley from "../../assets/images/emoji.svg";
import Steps from "../../assets/images/steps.svg";
import { BACK, PREVIEW_POLL, POLLS, CLOSE_TEXT_MODAL } from "../../headings";

const starsRating = {
  size: 40,
  count: 5,
  isHalf: false,
  value: 0,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  onChange: (newValue) => {},
};

const { Content } = Layout;
var CryptoJS = require("crypto-js");
const PreviewPoll = ({
  poll,
  setPollsDate,
  backtoEdit,
  isImage,
  data,
  attachmentName,
  showError,
  error,
}) => {
  const [loader, setLoader] = useState(false);
  const [pollState, setPollState] = useState({
    pollName: poll.pollName,
    pollHelpText: poll.pollHelpText,
    pollDocument: poll.pollDocument,
    showResult: poll.showResult,
    pollChartType: poll.pollChartType,
    pollQuestion: poll.pollQuestion,
    pollQuestionType: poll.pollQuestionType,
    pollRatingResponse: poll.pollRatingResponse,
    pollStartDate: poll.pollStartDate,
    pollEndDate: poll.pollEndDate,
    attachmentName: poll.attachmentName,
    pollStartTime: poll.pollStartTime,
    pollEndTime: poll.pollEndTime,
    pollResponse: poll.pollResponse,
    pollImageValidation: poll.pollImageValidation,
    previewVisible: poll.previewVisible,
    previewImage: poll.previewImage,
    previewTitle: poll.previewTitle,
    pollRatingType: poll.pollRatingType,
  });

  const handlePollStartDatePickerChange = (date, name) => {
    setPollsDate(date, name);
  };

  const handlePollEndDatePickerChange = (date, name) => {
    setPollsDate(date, name);
  };

  const handlePollStartTimePickerChange = (time, name) => {
    setPollsDate(time, name);
  };

  const handlePollEndTimePickerChange = (time, name) => {
    setPollsDate(time, name);
  };

  const onBackCreatePoll = () => {
    backtoEdit(poll);
  };

  const handlePollImagePreview = async (file) => {
    if (
      file.name?.includes("jpeg") ||
      file.name?.includes("jpg") ||
      file.name?.includes("png") ||
      file.name?.includes("gif") ||
      file.name?.includes("jfif") ||
      file.name?.includes("tiff")
    ) {
      file.preview = await getBase64(file.originFileObj);
      setPollState({
        ...pollState,
        previewImage: file.url || file.preview,
        previewVisible: true,
        previewTitle:
          file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
      });
    } else {
      return false;
    }
  };

  const getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const handleCancel = () =>
    setPollState({ ...pollState, previewVisible: false });

  const onPublish = () => {
    const user = JSON.parse(AppStorage.get("user"));
    const token = authHeader();
    setLoader(true);
    const optionsData = [];
    let data = {};
    poll.pollResponse.map((value) => {
      optionsData.push({ option_text: value });
    });
    if (isImage) {
      data = {
        create_poll: {
          title: poll.pollName,
          help_text: poll.pollHelpText,
          image:
            poll.pollDocument && poll.pollDocument[0]
              ? poll.pollDocument[0].base64
              : "",
          show_result_to_users: poll.showResult ? 1 : 0,
          poll_result_chart_type: poll.pollChartType
            ? parseInt(poll.pollChartType)
            : 2,
          image_name: attachmentName,
          poll_question_type: parseInt(poll.pollQuestionType),
          question: poll.pollQuestion,
          poll_question_rating_type: poll.pollRatingType
            ? parseInt(poll.pollRatingType)
            : "",
          start_date: momentUtil.formatAsDDMMYYYY(poll.pollStartDate),
          start_time: momentUtil.formatAsHHmmss(poll.pollStartTime),
          end_date: momentUtil.formatAsDDMMYYYY(poll.pollEndDate),
          end_time: momentUtil.formatAsHHmmss(poll.pollEndTime),
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        },
        option: optionsData,
      };
    } else if (poll.pollDocument?.length === 0) {
      data = {
        create_poll: {
          title: poll.pollName,
          help_text: poll.pollHelpText,
          show_result_to_users: poll.showResult ? 1 : 0,
          poll_result_chart_type: poll.pollChartType
            ? parseInt(poll.pollChartType)
            : 2,
          poll_question_type: parseInt(poll.pollQuestionType),
          poll_question_rating_type: poll.pollRatingType
            ? parseInt(poll.pollRatingType)
            : "",
          question: poll.pollQuestion,
          start_date: momentUtil.formatAsDDMMYYYY(poll.pollStartDate),
          start_time: momentUtil.formatAsHHmmss(poll.pollStartTime),
          end_date: momentUtil.formatAsDDMMYYYY(poll.pollEndDate),
          end_time: momentUtil.formatAsHHmmss(poll.pollEndTime),
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        },
        option: optionsData,
      };
    } else {
      data = {
        create_poll: {
          title: poll.pollName,
          help_text: poll.pollHelpText,
          document:
            poll.pollDocument && poll.pollDocument[0]
              ? poll.pollDocument[0].base64
              : "",
          document_name: attachmentName,
          show_result_to_users: poll.showResult ? 1 : 0,
          poll_result_chart_type: poll.pollChartType
            ? parseInt(poll.pollChartType)
            : 2,
          poll_question_type: parseInt(poll.pollQuestionType),
          poll_question_rating_type: poll.pollRatingType
            ? parseInt(poll.pollRatingType)
            : "",
          question: poll.pollQuestion,
          start_date: momentUtil.formatAsDDMMYYYY(poll.pollStartDate),
          start_time: momentUtil.formatAsHHmmss(poll.pollStartTime),
          end_date: momentUtil.formatAsDDMMYYYY(poll.pollEndDate),
          end_time: momentUtil.formatAsHHmmss(poll.pollEndTime),
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        },
        option: optionsData,
      };
    }
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .post(BASE_URL + "poll/", data, options)
      .then(() => {
        setLoader(false);
        window.location = "/orgcc/pollsdirectory";
      })
      .catch((error) => {
        setLoader(false);
      });
  };

  return (
    <>
      <React.Fragment>
        <React.Fragment>
          {" "}
          <Row className="inner-header">
            <LoggedinHeader amORGUser />
          </Row>
          <Row className="content-center pt-0">
            <Layout>
              <Layout>
                <OrgSidebar amORGUser data={data} />
                <Content>
                  <Row className="pt-3">
                    <Breadcrumb separator=">">
                      <NavLink to="/orgcc/pollsdirectory">
                        <Breadcrumb.Item>{POLLS}</Breadcrumb.Item>
                      </NavLink>
                      <Breadcrumb.Item>{PREVIEW_POLL}</Breadcrumb.Item>
                    </Breadcrumb>
                  </Row>
                  <Row className="py-3 justify-content-between">
                    <h1 className="mb-0">{PREVIEW_POLL}</h1>

                    <Col
                      span={24}
                      lg={18}
                      className="d-flex align-items-center justify-content-end"
                    ></Col>
                  </Row>
                  <Spin spinning={loader} size="large">
                    <Col
                      span={24}
                      className={`mb-3 px-3 px-md-0 ${styles.poll_info}`}
                    >
                      <h6>
                        {poll.pollName}
                        <Tooltip placement="top" title={poll.pollHelpText}>
                          <span className="pl-2 icon icon-info-circle"></span>
                        </Tooltip>
                      </h6>
                    </Col>

                    <div className="mid-content">
                      <Row>
                        <Col span={24}>
                          <p className="font-weight-md6">{poll.pollQuestion}</p>
                          <Row className="preview-que" disabled={true}>
                            <div
                              className={`d-flex flex-column flex-gap chk-opt ${styles.rating_img_wrapper}`}
                            >
                              {poll.pollQuestionType === "1" ? (
                                poll.pollResponse.map((value) => (
                                  <Radio>{value}</Radio>
                                ))
                              ) : poll.pollQuestionType === "2" ? (
                                poll.pollResponse.map((value) => (
                                  <Checkbox>{value}</Checkbox>
                                ))
                              ) : poll.pollQuestionType === "3" ? (
                                poll.pollRatingType === "1" ? (
                                  <ReactStars
                                    {...starsRating}
                                    value={poll.pollRatingResponse}
                                    name="pollRatingResponse"
                                    onChange={(value) =>
                                      handlePollOnSelect(
                                        value,
                                        "pollRatingResponse"
                                      )
                                    }
                                    classNames="mt-3"
                                  />
                                ) : poll.pollRatingType === "2" ? (
                                  <img src={Smiley} alt="send message" />
                                ) : poll.pollRatingType === "3" ? (
                                  <img src={Thermometer} alt="send message" />
                                ) : poll.pollRatingType === "4" ? (
                                  <img src={Thumb} alt="send message" />
                                ) : poll.pollRatingType === "5" ? (
                                  <img
                                    className={`${styles.steps_img}`}
                                    src={Steps}
                                    alt="send message"
                                  />
                                ) : null
                              ) : null}
                            </div>
                          </Row>
                          <Row className="mt-2">
                            <div class="ant-upload-list-item">
                              <div class="ant-upload-list-item-info">
                                <span class="ant-upload-span">
                                  <div class="ant-upload-text-icon">
                                    <PaperClipOutlined />
                                  </div>
                                  <span
                                    class="ant-upload-list-item-name"
                                    title="add-user-doc"
                                  >
                                    <Upload
                                      listType="picture"
                                      fileList={pollState.pollDocument}
                                      onPreview={(value) =>
                                        handlePollImagePreview(value)
                                      }
                                    ></Upload>
                                    <Modal
                                      visible={pollState.previewVisible}
                                      title={pollState.previewTitle}
                                      centered
                                      onOk={(value) => handleCancel(value)}
                                      okButtonProps={{
                                        children: "Custom OK",
                                      }}
                                      cancelButtonProps={{
                                        style: { display: "none" },
                                      }}
                                      okText={CLOSE_TEXT_MODAL}
                                    >
                                      <img
                                        alt="image"
                                        style={{ width: "100%" }}
                                        src={pollState.previewImage}
                                      />
                                    </Modal>
                                  </span>
                                  <span class="ant-upload-list-item-card-actions">
                                    <button
                                      type="button"
                                      class="ant-btn"
                                    ></button>
                                  </span>
                                </span>
                              </div>
                            </div>
                          </Row>
                        </Col>
                      </Row>
                    </div>

                    <div className="mt-5 px-3">
                      <Button
                        type="secondary"
                        type="secondary"
                        htmlType="submit"
                        className="mr-4"
                        onClick={onBackCreatePoll}
                      >
                        {BACK}
                      </Button>
                      <DateTimeModal
                        pollData={poll}
                        handlePollStartDatePickerChange={
                          handlePollStartDatePickerChange
                        }
                        handlePollEndDatePickerChange={
                          handlePollEndDatePickerChange
                        }
                        handlePollStartTimePickerChange={
                          handlePollStartTimePickerChange
                        }
                        handlePollEndTimePickerChange={
                          handlePollEndTimePickerChange
                        }
                        onPublish={onPublish}
                        showError={showError}
                        error={error}
                      />
                    </div>
                  </Spin>
                </Content>
              </Layout>
            </Layout>
          </Row>
        </React.Fragment>
      </React.Fragment>
    </>
  );
};

export default PreviewPoll;
