import UserService from '../services/user.service';

import{
    MANAGEORG_FAIL,
    MANAGEORG_SUCCESS,
    SET_MESSAGE
    } from "./types";

    export const getManageOrgAsync=()=>(dispatch)=>{
        return UserService.getOrganizationData().then((data)=>{
            dispatch({
                type:MANAGEORG_SUCCESS,
                payload:{manageOrgData:data.data},
            });    
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:MANAGEORG_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };