import React, { Component } from "react";
import { Modal, Button, Row, Tooltip } from "antd";
import "../modal/modal.scss";
import { ADD_ICON, CANCEL_MODAL, ADD_TEXT_MODAL } from "../../headings";
import {
  CYBER_BULLYING,
  DISCRIMINATION,
  FIGHT,
  RACISM,
  SEXUAL_HARRASMENT,
  SLAP,
  VIOLENCE,
} from "../../values";

class AddIcoModal extends Component {
  state = { visible: false, iconContent: "", ImageId: "" };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.props.addIcon(this.state.iconContent, this.state.ImageId);
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  onSelectIcon = (e) => {
    this.setState({
      iconContent: e.currentTarget.innerHTML,
      ImageId: e.currentTarget.id,
    });
  };

  render() {
    return (
      <>
        <Button type="link" className="link pl-0" onClick={this.showModal}>
          <span className="icon icon-add-filled" />
          {ADD_ICON}
        </Button>
        <Modal
          className="modal-icon"
          title={ADD_ICON}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={500}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
            className: "ant-btn-secondary",
          }}
          okText={ADD_TEXT_MODAL}
          cancelText={CANCEL_MODAL}
        >
          <Row className="py-4" data-test="AddIcon">
            <div
              className={
                this.state.ImageId === "1"
                  ? "col-3 col-lg-2 selected"
                  : "col-3 col-lg-2"
              }
              id={1}
              onClick={this.onSelectIcon}
            >
              <Tooltip title={SEXUAL_HARRASMENT}>
                <span className="icon icon-sexual-harassment-1" />
              </Tooltip>
            </div>

            <div
              className={
                this.state.ImageId === "2"
                  ? "col-3 col-lg-2 selected"
                  : "col-3 col-lg-2"
              }
              id={2}
              onClick={this.onSelectIcon}
            >
              <Tooltip title={VIOLENCE}>
                <span className="icon icon-violence" />
              </Tooltip>
            </div>
            <div
              className={
                this.state.ImageId === "3"
                  ? "col-3 col-lg-2 selected"
                  : "col-3 col-lg-2"
              }
              id={3}
              onClick={this.onSelectIcon}
            >
              <Tooltip title={RACISM}>
                <span className="icon icon-racism" />
              </Tooltip>
            </div>
            <div
              className={
                this.state.ImageId === "4"
                  ? "col-3 col-lg-2 selected"
                  : "col-3 col-lg-2"
              }
              id={4}
              onClick={this.onSelectIcon}
            >
              <Tooltip title={CYBER_BULLYING}>
                <span className="icon icon-cyber-bullying" />
              </Tooltip>
            </div>
            <div
              className={
                this.state.ImageId === "5"
                  ? "col-3 col-lg-2 selected"
                  : "col-3 col-lg-2"
              }
              id={5}
              onClick={this.onSelectIcon}
            >
              <Tooltip title={FIGHT}>
                <span className="icon icon-fight" />
              </Tooltip>
            </div>
            <div
              className={
                this.state.ImageId === "6"
                  ? "col-3 col-lg-2 selected"
                  : "col-3 col-lg-2"
              }
              id={6}
              onClick={this.onSelectIcon}
            >
              <Tooltip title={DISCRIMINATION}>
                <span className="icon icon-discrimination" />
              </Tooltip>
            </div>
            <div
              className={
                this.state.ImageId === "7"
                  ? "col-3 col-lg-2 selected"
                  : "col-3 col-lg-2"
              }
              id={7}
              onClick={this.onSelectIcon}
            >
              <Tooltip title={SLAP}>
                <span className="icon icon-slap" />
              </Tooltip>
            </div>
          </Row>
        </Modal>
      </>
    );
  }
}

export default AddIcoModal;
