import React from "react";
import { Form, Checkbox, Input, Button, DatePicker, Upload, Modal } from "antd";
import "../form/form.scss";
import { BACK, CLOSE_TEXT_MODAL, CREATE, SAVE_AS_DRAFT_BTN } from "../../headings";
import { SAVE_AS_DRAFT } from "../../labels";
const { TextArea } = Input;

const CreateOpenlineStepThree = ({
  prev,
  createNewHotLine,
  stepOneOpenLine,
  stepTwoOpenLine,
  handleStepOnePreview,
  handleCancel,
}) => {
  const [form] = Form.useForm();
  const stepThreeBack = (e) => {
    e.preventDefault();
    prev();
  };
  const generateHotLine = (e) => {
    createNewHotLine();
  };

  const assignedUserNames = stepTwoOpenLine.selectedRows.map(
    (element) => element.first_name + " " + element.last_name
  );

  const { RangePicker } = DatePicker;

  return (
    <Form
      data-test="createHotlineStepThree"
      className="input-md w-md-100 w-75"
      name="validate_other"
      form={form}
    >
      <Form.Item name="openelineName">
        <>
          <TextArea
            showCount
            disabled={true}
            maxLength={80}
            value={stepOneOpenLine.openelineName}
            id={"openelineName"}
            className="input-control textarea-input"
            htmlFor="openelineName"
          />
        </>
      </Form.Item>
      <Form.Item name="description">
        <>
          <TextArea
            disabled={true}
            className="input-control textarea-md"
            value={stepOneOpenLine.description}
            id={"description"}
            htmlFor="description"
          />
        </>
      </Form.Item>

      <Form.Item className="input-control datepicker-wrapper">
        <>
          <RangePicker
            className="datepicker w-100 w-lg-50 mr-0 mr-lg-5"
            name="sdate"
            value={stepOneOpenLine.sDate}
            disabled
          />
        </>
      </Form.Item>
      <div className="d-block d-lg-flex">
        {/* <div className="w-100 w-lg-50 mr-0 mr-lg-5">
          <Form.Item name="status">
            <>
              <Checkbox
                className="font-13"
                checked={stepOneOpenLine.status}
                disabled
              >
                {SAVE_AS_DRAFT}
              </Checkbox>
            </>
          </Form.Item>
        </div> */}
        <div className="w-100 w-lg-50 mr-0 mr-lg-5">
          <Form.Item name="userNames">
            <>
              <Input
                className="input-control"
                htmlFor="assigned to"
                id={assignedUserNames}
                disabled
                value={assignedUserNames.join(", ")}
              />
            </>
          </Form.Item>
        </div>
      </div>
      <Form.Item className="w-100 w-lg-50">
        <Form.Item className="upload-control required">
          <Upload
            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
            listType="picture"
            disabled
            fileList={stepOneOpenLine.file}
            onPreview={(value) => handleStepOnePreview(value)}
          ></Upload>

          <Modal
            className="modal-openline"
            visible={stepOneOpenLine.previewVisible}
            title={stepOneOpenLine.previewTitle}
            centered
            onOk={(value) => handleCancel(value)}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText={CLOSE_TEXT_MODAL}
          >
            <img
              alt="example"
              style={{ width: "100%" }}
              src={stepOneOpenLine.previewImage}
            />
          </Modal>

          {/* <div className="ant-upload-list ant-upload-list-picture">
            <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
              <div className="ant-upload-list-item-info d-inline-flex w-100">
                <span className="d-inline-flex align-items-center">
                  <a className="ant-upload-list-item-thumbnail" href="#" rel="">
                    {ReactHtmlParser(stepOneOpenLine.iconHtml)}
                  </a>

                  <span
                    className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                    title="women safety"
                  >
                    {getIconText(stepOneOpenLine.hotlineIconId)}
                  </span>
                </span>
              </div>
            </div>
          </div> */}
        </Form.Item>
      </Form.Item>

      <div className="steps-action mt-5">
        <Button
          type="secondary"
          className="mr-4"
          type="secondary"
          htmlType="submit"
          onClick={stepThreeBack}
          data-test="stepThreePrev"
        >
          {BACK}
        </Button>
        <Button
          type="primary"
          className="md mr-4"
          htmlType="submit"
          onClick={generateHotLine}
          data-test="stepThreeNext"
        >
          {CREATE}
        </Button>
        <Button
          type="secondary"
          onClick={() => createNewHotLine(true)}
        >
          {SAVE_AS_DRAFT_BTN}
        </Button>
      </div>
    </Form>
  );
};

export default CreateOpenlineStepThree;
