import React, { Component } from "react";
import { Modal, Button, Form, Col, Row, DatePicker, Alert } from "antd";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import "../modal/modal.scss";
import "date-fns";
import { TimePicker } from "@progress/kendo-react-dateinputs";
import {
  PUBLISH_POLL,
  PUBLISH,
  SCHEDULE,
  POLL,
  CANCEL_MODAL,
} from "../../headings";
import { POLL_DATES_TEXT } from "../../messages";
import { SELECT_END_DATE, SELECT_START_DATE } from "../../labels";
import { PAST_TIME, SELECT_DATE } from "../../errors";

class DatetimeModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      startTimeError: false,
      endTimeError: false,
      startTime: new Date(),
      endTime: new Date(),
    };
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    if (
      !this.props.pollData.pollStartDate ||
      !this.props.pollData.pollEndDate
    ) {
      this.props.showError(true);
    } else {
      this.props.showError(false);
      this.setState({
        visible: false,
      });
      this.props.onPublish();
    }
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  disabledStartDate = (current) => {
    return current && current < moment().startOf("day");
  };

  disabledEndDate = (current) => {
    if (this.props.pollData.pollStartDate) {
      return current && current < moment(this.props.pollData.pollStartDate);
    } else {
      return current && current < moment().startOf("day");
    }
  };

  range = (start, end) => {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  };

  onChangeStartDate = (date) => {
    this.props.handlePollStartDatePickerChange(date, "pollStartDate");
  };

  onChangeEndDate = (date) => {
    this.props.handlePollEndDatePickerChange(date, "pollEndDate");
  };

  onChangeStartTime = (event) => {
    const startTime = event.target.value;
    this.setState({ startTimeError: false, startTime: startTime });
  };

  onBlurStartTime = (event) => {
    const currentTime = new Date();
    if (currentTime > this.state.startTime) {
      this.setState({
        startTimeError: true,
        startTime: this.props.pollData.pollStartTime,
      });
    } else {
      this.setState({ startTimeError: false });
      this.props.handlePollStartTimePickerChange(
        this.state.startTime,
        "pollStartTime"
      );
    }
  };

  onChangeEndTime = (event) => {
    const endTime = event.target.value;
    this.setState({ endTimeError: false, endTime: endTime });
  };

  onBlurEndTime = (event) => {
    const currentTime = new Date();
    if (currentTime > this.state.endTime) {
      this.setState({
        endTimeError: true,
        endTime: this.props.pollData.pollEndTime,
      });
    } else {
      this.setState({ endTimeError: false });
      this.props.handlePollEndTimePickerChange(
        this.state.endTime,
        "pollEndTime"
      );
    }
  };

  render() {
    return (
      <>
        <Button type="primary" className="md mr-4" onClick={this.showModal}>
          {SCHEDULE}
        </Button>
        <Modal
          title={PUBLISH_POLL}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={520}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText={PUBLISH}
          cancelText={CANCEL_MODAL}
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              {" "}
              {this.props.error && (
                <Form.Item>
                  <Alert message={SELECT_DATE} type="error" showIcon></Alert>
                </Form.Item>
              )}
              <div className="mb-4 subtext-md">
                {POLL_DATES_TEXT}
                <span className="pr-1 pr-1 font-weight-md6">
                  {" "}
                  {this.props.pollData.pollName}
                </span>
                {POLL}
              </div>
              <FloatLabel label={SELECT_START_DATE} value="sDate">
                <Form.Item
                  name="sDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col span="24" lg={12} className="gutter-row">
                      <DatePicker
                        value={this.props.pollData.pollStartDate}
                        disabledDate={this.disabledStartDate}
                        onChange={(date) => this.onChangeStartDate(date)}
                        className="w-100"
                        name="sDate"
                        className="required mb-3"
                      />
                      {this.state.startTimeError && (
                        <Alert
                          message={PAST_TIME}
                          type="error"
                          showIcon
                        ></Alert>
                      )}
                    </Col>
                    <Col span="24" lg={12}>
                      <TimePicker
                        value={this.state.startTime}
                        onChange={this.onChangeStartTime}
                      />
                    </Col>
                  </Row>
                </Form.Item>
              </FloatLabel>
              <FloatLabel label={SELECT_END_DATE} value="eDate">
                <Form.Item
                  name="eDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col span="24" lg={12} className="gutter-row">
                      <DatePicker
                        value={this.props.pollData.pollEndDate}
                        disabledDate={this.disabledEndDate}
                        onChange={(date) => this.onChangeEndDate(date)}
                        className="w-100"
                        name="eDate"
                        className="required mb-3"
                      />
                      {this.state.endTimeError && (
                        <Alert
                          message={PAST_TIME}
                          type="error"
                          showIcon
                        ></Alert>
                      )}
                    </Col>
                    <Col span="24" lg={12}>
                      <TimePicker
                        value={this.state.endTime}
                        onChange={this.onChangeEndTime}
                      />
                    </Col>
                  </Row>
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default DatetimeModal;
