import React, { useEffect, useState } from "react";
import {
  Form,
  Checkbox,
  Input,
  Button,
  DatePicker,
  Alert,
  Row,
  Layout,
  Upload,
  Spin,
  Modal,
} from "antd";
import ImgCrop from "antd-img-crop";
import * as AppStorage from "../../utils/AppStorage";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { CheckCircleTwoTone } from "@ant-design/icons";
import FloatLabel from "../form/FloatLabels";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import { useLocation } from "react-router-dom";
import "../form/form.scss";
import { BASE_URL } from "../../userconfig";
import {
  CANCEL,
  EDIT_OPENLINE,
  UPDATE,
  UPLOAD_IMAGE,
  STATUS_SET_ACTIVE,
  STATUS_SET_COMPLETE,
  STATUS_SET_SCHEDULED,
  SAVE_AS_DRAFT_BTN
} from "../../headings";
import {
  CYBER_BULLYING,
  DISCRIMINATION,
  FIGHT,
  RACISM,
  SEXUAL_HARRASMENT,
  SLAP,
  VIOLENCE,
} from "../../values";
import { DATE, DESCRIPTION, DIRECTORY_NAME, SAVE_AS_DRAFT } from "../../labels";
import {
  ENTER_DESCRIPTION,
  ENTER_DIRECTORY_NAME,
  OPENLINE_ALREADY_EXISTS,
  REQUIRED_MESSAGE,
  SELECT_DATE,
} from "../../errors";

var CryptoJS = require("crypto-js");
const { TextArea } = Input;
const { Content } = Layout;

const EditOpenline = (props) => {
  const [loader, setLoader] = useState(false);
  const [openLineData, setOpenLineData] = useState({
    end_date: "",
    help_text: "",
    openline_id: "",
    name: "",
    start_date: "",
    openline_image: "",
    openline_image_name: "",
    statusMessage: "",
    status: false,
    iconHtml: "",
    ImgfileList: [],
  });
  const [error, showError] = useState(false);
  const [form] = Form.useForm();
  const [openlineErrorUpdated, setopenlineErrorUpdated] = useState("");
  let location = useLocation();
  const [nameError, setNameError] = useState(false);

  useEffect(() => {
    let dates;
    let imageFile = [];
    if(location.state.start_date && location.state.end_date){
      const sDate = moment(location.state.start_date).format("YYYY-MM-DD");
      const eDate = moment(location.state.end_date).format("YYYY-MM-DD");
      dates = [moment(sDate, "YYYY/MM/DD"), moment(eDate, "YYYY/MM/DD")];
    }
    if(location.state.openline_image_name && location.state.openline_image){
      imageFile = [
        {
          uid: "1",
          name: location.state.openline_image_name,
          status: "done",
          url: location.state.openline_image,
        },
      ]
    }

    setOpenLineData({
      end_date: "",
      help_text: location.state.help_text,
      openline_id: location.state.openline_id,
      name: location.state.name,
      start_date: dates,
      openline_image: location.state.openline_image,
      openline_image_name: location.state.openline_image_name,
      status: location.state.status_id === 4,
      ImgfileList: imageFile,
    });
    form.setFieldsValue({
      name: location.state.name,
      help_text: location.state.help_text,
    });
  }, []);

  const handleStepOneOnChange = (e) => {
    setOpenLineData({ ...openLineData, [e.target.id]: e.target.value });
  };

  const handleStepOneDatePickerChange = (date, name) => {
    const currentDate = moment();
    if (
      date &&
      date[0]?.diff(currentDate, "days") === 0 &&
      !openLineData.status
    ) {
      setOpenLineData({
        ...openLineData,
        [name]: date,
        statusMessage: STATUS_SET_ACTIVE,
      });
    } else if (
      date &&
      date[0]?.diff(currentDate, "days") > 0 &&
      !openLineData.status
    ) {
      setOpenLineData({
        ...openLineData,
        [name]: date,
        statusMessage: STATUS_SET_SCHEDULED,
      });
    } else {
      setOpenLineData({
        ...openLineData,
        [name]: date,
        statusMessage: "",
      });
    }
  };

  const onChangeCheckbox = (e) => {
    if (e.target.checked) {
      setOpenLineData({
        ...openLineData,
        status: e.target.checked,
        statusMessage: "",
      });
    } else {
      const currentDate = moment();
      const date = openLineData.start_date;
      if (date && date[0]?.diff(currentDate, "days") === 0) {
        setOpenLineData({
          ...openLineData,
          status: e.target.checked,
          statusMessage: STATUS_SET_ACTIVE,
        });
      } else if (date && date[0]?.diff(currentDate, "days") > 0) {
        setOpenLineData({
          ...openLineData,
          status: e.target.checked,
          statusMessage: STATUS_SET_SCHEDULED,
        });
      }
    }
  };

  const handleStepOneOnSelect = (value, name) => {
    setOpenLineData({ ...openLineData, [name]: value });
  };

  function convertImgToBase64(url, callback, outputFormat) {
    var canvas = document.createElement("CANVAS");
    var ctx = canvas.getContext("2d");
    var img = new Image();
    img.crossOrigin = "Anonymous";
    img.onload = function () {
      canvas.height = img.height;
      canvas.width = img.width;
      ctx.drawImage(img, 0, 0);
      var dataURL = canvas.toDataURL(outputFormat || "image/png");
      callback.call(this, dataURL);
      // Clean up
      canvas = null;
    };
    img.src = url;
  }

  const editOpenline = (e) => {
    if (
      openLineData.name &&
      openLineData.name.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      openLineData.help_text &&
      openLineData.help_text.replace(/^\s*/, "").replace(/\s*$/, "").length >
        0 &&
      openLineData.openline_image.length > 0 &&
      openLineData.start_date
    ) {
      setLoader(true);
      showError(false);
      e.preventDefault();
      const user = JSON.parse(AppStorage.get("user"));

      let data = {};
      if (openLineData.ImgfileList[0].uid === "1") {
        convertImgToBase64(openLineData.openline_image, function (base64Img) {
          data = {
            end_date: moment(openLineData.start_date[1]).format("DD-MM-YYYY"),
            help_text: openLineData.help_text,
            openline_id: openLineData.openline_id,
            openline_image: base64Img,
            name: openLineData.name,
            openline_image_name: openLineData.openline_image_name,
            start_date: moment(openLineData.start_date[0]).format("DD-MM-YYYY"),
            status_id: 1,
            updated_by: parseInt(
              CryptoJS.AES.decrypt(
                user.user_details.org_user_id,
                "anonymoose"
              ).toString(CryptoJS.enc.Utf8)
            ),
            created_by: parseInt(
              CryptoJS.AES.decrypt(
                user.user_details.org_user_id,
                "anonymoose"
              ).toString(CryptoJS.enc.Utf8)
            ),
          };

          if (
            openlineErrorUpdated == "status: false" ||
            openLineData.name === location.state.name
          ) {
            updateOpenLine(data);
          }
        });
      } else {
        data = {
          end_date: moment(openLineData.start_date[1]).format("DD-MM-YYYY"),
          help_text: openLineData.help_text,
          openline_id: openLineData.openline_id,
          openline_image: openLineData.ImgfileList[0].base64,
          name: openLineData.name,
          openline_image_name: openLineData.openline_image_name,
          start_date: moment(openLineData.start_date[0]).format("DD-MM-YYYY"),
          status_id: openLineData.status ? 4 : 1,
          updated_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        };
        if (
          openlineErrorUpdated == "status: false" ||
          openLineData.name === location.state.name
        ) {
          updateOpenLine(data);
        }
      }
    } else {
      showError(true);
    }
  };

  const onClickSaveAsDraft = () =>{
    if (openLineData.name && 
      openLineData.name.replace(/^\s*/, "").replace(/\s*$/, "").length > 0)
    {
      showError(false);
      setNameError(false);
      const user = JSON.parse(AppStorage.get("user"));

      let data = {
        end_date: openLineData.start_date && openLineData.start_date[1] ? moment(openLineData.start_date[1]).format("DD-MM-YYYY"):null,
        help_text: openLineData.help_text,
        openline_id: openLineData.openline_id,
        openline_image: openLineData.ImgfileList.length > 0 ? openLineData.ImgfileList[0].base64 : null,
        name: openLineData.name,
        openline_image_name: openLineData.openline_image_name,
        start_date: openLineData.start_date && openLineData.start_date[0] ? moment(openLineData.start_date[0]).format("DD-MM-YYYY"):null,
        status_id: 4,
        updated_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
        created_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
      };
      if (
        openlineErrorUpdated == "status: false" ||
        openLineData.name === location.state.name
      ) {
        updateOpenLine(data);
      }
    }
    else{
      showError(false);
      setNameError(true)
    }
  }

  const cancelHotline = (e) => {
    window.location = "/orgcc/openlinedirectory";
  };

  const updateOpenLine = (data) => { console.log("data : ", data)
    const token = authHeader();
    let openLineId = openLineData.openline_id;
    delete openLineData.hotline_id;
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .put(BASE_URL + "openline/" + openLineId + "/", data, options)
      .then((res) => {
        if (res.status === 200) {
          setLoader(false);
          window.location = "/orgcc/openlinedirectory";
        }
      });
  };

  const handleStepOneUpload = (value, name) => {
    let fileList = value.fileList.slice(-1);
    let filename = value.file.name;
    fileList.forEach(function (file, index) {
      let reader = new FileReader();
      reader.onload = (e) => {
        file.base64 = e.target.result;
        file.response = "Uploaded successfully";
      };
      reader.readAsDataURL(file.originFileObj);
    });
    // setOpenLineData({ ...openLineData, [name]: fileList });
    setOpenLineData({
      ...openLineData,
      ImgfileList: fileList,
      openline_image: fileList,
      openline_image_name: filename,
    });

    if (value.file.status === "removed") {
      setOpenLineData({
        ...openLineData,
        ImgfileList: [],
        openline_image: "",
        openline_image_name: "",
      });
    }
  };

  const handleStepOnePreview = async (file) => {
    if (file.uid === "1") {
      convertImgToBase64(file.url, function (base64Img) {
        // await toImgDataURL(proxyUrl + file.url, function (dataURL) {
        setOpenLineData({
          ...openLineData,
          previewImage: base64Img,
          previewVisible: true,
          previewTitle: file.name,
        });
      });
    } else {
      setOpenLineData({
        ...openLineData,
        previewImage: file.base64,
        previewVisible: true,
        previewTitle: file.name,
      });
    }
  };

  const handleOnRemove = (value, name) => {
    setOpenLineData({ ...openLineData, fileList: [] });
  };

  const handleCancel = (value) =>
    setOpenLineData({ ...openLineData, previewVisible: false });

  function toImgDataURL(url, callback) {
    var xhr = new XMLHttpRequest();
    xhr.open("get", url);
    xhr.responseType = "blob";
    xhr.onload = function () {
      var fr = new FileReader();
      fr.onload = function () {
        callback(this.result);
      };

      fr.readAsDataURL(xhr.response); // async call
    };

    xhr.send();
  }

  const getIconText = (id) => {
    switch (id) {
      case "1":
        return SEXUAL_HARRASMENT;
      case "2":
        return VIOLENCE;
      case "3":
        return RACISM;
      case "4":
        return CYBER_BULLYING;
      case "5":
        return FIGHT;
      case "6":
        return DISCRIMINATION;
      case "7":
        return SLAP;
      default:
        return "";
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const CheckOpenlineName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (e.target.value !== location.state.name) {
      axios
        .post(
          BASE_URL + "checkOpenline/",
          { openline_name: e.target.value },
          options
        )
        .then((res) => {
          if (res.data) {
            setopenlineErrorUpdated(res.data[0]);
          }
        });
    } else {
      setopenlineErrorUpdated(false);
    }
  };

  

  const { RangePicker } = DatePicker;

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0" data-test="manageEmp">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={props} />
            <Content className="">
              <Row className="py-3">
                <h1>{EDIT_OPENLINE}</h1>
              </Row>
              <Spin spinning={loader} size="large">
                <div className="mid-content mt-4">
                  <Form
                    data-test="createHotlineStepOne"
                    className="input-md w-md-100 w-75 floating-label"
                    name="validate_other"
                    form={form}
                  >
                    {error && (
                      <Form.Item>
                        <Alert
                          message={REQUIRED_MESSAGE}
                          type="error"
                          showIcon
                        ></Alert>
                      </Form.Item>
                    )}
                    {nameError && (
                      <Form.Item>
                        <Alert message="Please fill openline name" type="error" showIcon></Alert>
                      </Form.Item>
                    )}
                    <FloatLabel
                      label={DIRECTORY_NAME}
                      value={openLineData.name}
                    >
                      <Form.Item
                        className="required"
                        name="name"
                        id="name"
                        rules={[
                          {
                            required: true,
                            message: ENTER_DIRECTORY_NAME,
                          },
                        ]}
                      >
                        <TextArea
                          showCount
                          maxLength={80}
                          value={openLineData.name}
                          onChange={(e) => handleStepOneOnChange(e)}
                          onBlur={(e) => CheckOpenlineName(e)}
                          name="name"
                          id="name"
                          className="input-control textarea-input"
                          htmlFor="name"
                          autoComplete="off"
                        />
                        {openlineErrorUpdated == "status: false" &&
                          openLineData.name
                            .replace(/^\s*/, "")
                            .replace(/\s*$/, "").length > 0 &&
                          openLineData.name.length > 0 && (
                            <CheckCircleTwoTone
                              className="successCheck"
                              twoToneColor="#52c41a"
                            />
                          )}
                        {openlineErrorUpdated == "status: true" && (
                          <Alert
                            message={OPENLINE_ALREADY_EXISTS}
                            type="error"
                            showIcon
                          ></Alert>
                        )}
                      </Form.Item>
                    </FloatLabel>
                    <FloatLabel
                      label={DESCRIPTION}
                      value={openLineData.help_text}
                    >
                      <Form.Item
                        className="required"
                        name="help_text"
                        id="help_text"
                        rules={[
                          {
                            required: true,
                            message: ENTER_DESCRIPTION,
                          },
                        ]}
                      >
                        <>
                          <TextArea
                            className="input-control textarea-input"
                            value={openLineData.help_text}
                            onChange={(e) => handleStepOneOnChange(e)}
                            name="help_text"
                            id="help_text"
                            htmlFor="help_text"
                            autoComplete="off"
                          />
                        </>
                      </Form.Item>
                    </FloatLabel>
                    {openLineData.statusMessage?.length > 0 ? (
                      <>{openLineData.statusMessage}</>
                    ) : null}
                    <FloatLabel label={DATE} value="start_date">
                      <Form.Item className="input-control datepicker-wrapper">
                        <RangePicker
                          className="datepicker w-100 w-lg-50 mr-0 mr-lg-5 required"
                          name="start_date"
                          value={openLineData.start_date}
                          disabledDate={disabledDate}
                          onChange={(date) =>
                            handleStepOneDatePickerChange(date, "start_date")
                          }
                          rules={[
                            {
                              required: true,
                              message: SELECT_DATE,
                            },
                          ]}
                        />
                      </Form.Item>
                    </FloatLabel>
                    {/* <Form.Item name="status">
                      <>
                        <Checkbox
                          className="font-13"
                          onChange={(e) => onChangeCheckbox(e)}
                          checked={openLineData.status}
                        >
                          {SAVE_AS_DRAFT}
                        </Checkbox>
                      </>
                    </Form.Item> */}
                    <Form.Item className="">
                      <Form.Item
                        className="upload-control required"
                        name="upload"
                        valuePropName="fileList"
                      >
                        <ImgCrop rotate modalOk="Upload">
                          <Upload
                            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                            listType="picture"
                            fileList={openLineData.ImgfileList}
                            onChange={(value) =>
                              handleStepOneUpload(value, "openline_image")
                            }
                            onPreview={(value) => handleStepOnePreview(value)}
                            handleOnRemove={handleOnRemove}
                            accept="image/png, image/jpeg, image/gif"
                          >
                            {(openLineData.openline_image === null || openLineData.openline_image.length === 0) && (
                              <Button type="link" className="link pl-0">
                                <span className="icon icon-upload pr-2"></span>{" "}
                                {UPLOAD_IMAGE}
                              </Button>
                            )}
                          </Upload>
                        </ImgCrop>
                        <Modal
                          visible={openLineData.previewVisible}
                          title={openLineData.previewTitle}
                          centered
                          onOk={(value) => handleCancel(value)}
                          okButtonProps={{
                            children: "Custom OK",
                          }}
                          cancelButtonProps={{ style: { display: "none" } }}
                          okText="Close"
                        >
                          <img
                            alt="example"
                            style={{ width: "100%" }}
                            src={openLineData.previewImage}
                          />
                        </Modal>

                        {getIconText(openLineData.openline_id) && (
                          <div className="ant-upload-list ant-upload-list-picture">
                            <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
                              <div className="ant-upload-list-item-info d-inline-flex w-100">
                                <span className="d-inline-flex align-items-center">
                                  <a className="ant-upload-list-item-thumbnail">
                                    {ReactHtmlParser(openLineData.iconHtml)}
                                  </a>

                                  <span
                                    className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                                    title={getIconText(
                                      openLineData.openline_id
                                    )}
                                  >
                                    {getIconText(openLineData.openline_id)}
                                  </span>
                                </span>
                              </div>
                            </div>
                          </div>
                        )}
                      </Form.Item>
                    </Form.Item>
                    <div className="steps-action mt-5">
                      <Button
                        type="primary"
                        htmlType="submit"
                        onClick={editOpenline}
                        data-test="stepOneNext"
                        className="mr-4"
                      >
                        {UPDATE}
                      </Button>
                      <Button
                        type="primary"
                        htmlType="submit"
                        onClick={cancelHotline}
                        data-test="stepOneNext"
                        className="mr-4"
                      >
                        {CANCEL}
                      </Button>
                      <Button
                        type="secondary"
                        onClick={onClickSaveAsDraft}
                      >
                        {SAVE_AS_DRAFT_BTN}
                      </Button>
                    </div>
                  </Form>
                </div>
              </Spin>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};
export default EditOpenline;
