import React, { useEffect } from "react";
import Logo from "../../src/assets/images/logo.svg";
import { Row, Col } from "antd";
import { connect } from "react-redux";
import { login } from "../actions/auth";
import AnonymooseUser from "../components/userInfo/UserInfo";
import * as AppStorage from "../utils/AppStorage";

var CryptoJS = require("crypto-js");
const LoggedinHeader = (props) => {
  useEffect(() => {
    const { dispatch } = props;
    const type = AppStorage.get("userType");
    const userType = type
      ? CryptoJS.AES.decrypt(type, "anonymoose").toString(CryptoJS.enc.Utf8)
      : "";
    let page = "";
    if (userType === "AM CC") {
      page = "is_amcc_user";
    } else if (userType === "AM WEB") {
      page = "is_am_web_user";
    } else if (userType === "Org CC") {
      page = "is_org_cc_user";
    } else if (userType === "Org CC") {
      page = "is_org_cc_user";
    } else if (userType === "IDB") {
      page = "is_idb_user";
    }
    const password = props.current.includes('"')
      ? props.current.slice(1, -1)
      : props.current;
    // call authenticate api after 10 minutes.
    setInterval(() => {
      if (props.user?.user_data?.email) {
        dispatch(login(props.user?.user_data?.email, password, page));
      }
    }, 600000);
  });

  return (
    <>
      <Col span={18} md={18} lg={12}>
        <img
          src={Logo}
          alt="Anonymoose Logo"
          className="pl-md-0 pr-0 pr-md-4 pt-0 pt-md-0 pt-lg-0 logo"
        />
      </Col>
      <Col
        span={6}
        md={6}
        lg={12}
        className="d-flex justify-content-end av-wrapper"
      >
        <AnonymooseUser userRole={props} />
      </Col>
    </>
  );
};

function mapStateToProps(state) {
  const { isLoggedIn, user, current } = state.auth;
  const { message } = state.message;
  return {
    isLoggedIn,
    user,
    current,
    message,
  };
}
export default connect(mapStateToProps)(LoggedinHeader);
