import React from "react";
import { Layout, Row, Col, Input, Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined, DownOutlined } from "@ant-design/icons";
import ManageEmpPopover from "../table/ManageEmpPopover";
import { Link } from "react-router-dom";
import OrgSidebar from "../../containers/OrgSidebar";
import { ADD_EMPLOYEE, MANAGE_EMPLOYEES, SEARCH } from "../../headings";
import MultiSelection from "./MultiSelection";
import moment from "moment";
import {
  ACCESS,
  FIRST_NAME,
  LAST_NAME,
  MOBILE,
  STATUS,
  ACTIONS,
  CREATED_ON
} from "../../labels";
import {
  IDB_USER,
  ORG_CC,
  STATUS_ACTIVE,
  STATUS_INACTIVE,
  SUPER_USER,
  APP_USER,
  AM_WEB,
  COMPLETED,
  SCHEDULED,
  DELETED,
  DRAFT
} from "../../values";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);

const { Content } = Layout;

class ShowAllEmployees extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
    };
  }

  componentDidMount() {
    let { employees } = this.props;
    employees?.forEach((emp) => {
      emp.created_on = moment(hotline.created_on)
        .utc()
        .local()
        .format("MM/DD/YYYY HH:mm");
    });
    this.setState({ data: employees });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.employees !== nextProps.employees) {
      nextProps.employees?.forEach((emp) => {
        emp.created_on = moment(emp.created_on)
          .utc()
          .local()
          .format("MM/DD/YYYY HH:mm")
      });
      this.setState({ data: nextProps.employees });
    }
  }

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    this.props.setSearchValue(value);
  };

  handlePollStatusOnSelect = (value) => {
    let newArray = value.map((item) => parseInt(item));
    const data = {
      is_app_user: newArray.includes(3) ? 1 : 0,
      is_am_web_user: newArray.includes(1) ? 1 : 0,
      is_org_cc_user: newArray.includes(2) ? 1 : 0,
      is_super_user: newArray.includes(5) ? 1 : 0,
      is_idb_user: newArray.includes(4) ? 1 : 0,
    };
    this.props.setFilterOptions(data);
  };

  render() {
    const { loader } = this.props;
    const columns = [
      {
        title: FIRST_NAME,
        dataIndex: "first_name",
        key: "first_name",
        width: "20%",
        render: (firstname) => {
          return {
            children: firstname,
            props: {
              "data-title": "First Name",
            },
          };
        },
        sorter: (a, b) => { return a.first_name.localeCompare(b.first_name)},
      },
      {
        title: LAST_NAME,
        dataIndex: "last_name",
        key: "last_name",
        width: "20%",
        render: (lastname) => {
          return {
            children: lastname,
            props: {
              "data-title": "Last Name",
            },
          };
        },
        sorter: (a, b) => { return a.last_name.localeCompare(b.last_name)},
      },
      {
        title: MOBILE,
        dataIndex: "mobile_number",
        key: "mobile_number",
        width: "20%",
        render: (mobNumber) => {
          return {
            children: mobNumber,
            props: {
              "data-title": "Mobile Number",
            },
          };
        },
      },
      {
        title: ACCESS,
        dataIndex: "",
        key: "x",
        render: (record) => {
          let test = "";
          let property = "";
          let flag = false;
          if (record.is_am_web_user) {
            property = AM_WEB;
            flag = true;
          }
          if (record.is_org_cc_user) {
            property = ORG_CC;
            flag = true;
          }
          if (record.is_idb_user) {
            property = IDB_USER;
            flag = true;
          }
          if (record.is_super_user) {
            property = SUPER_USER;
            flag = true;
          }
          if (record.is_app_user) {
            if (!flag) {
              property = APP_USER;
            }
          }

          if (test.length === 0) {
            test += property;
          } else if (property !== APP_USER) {
            if (test.includes("APP")) {
              test = property;
            } else {
              test += ", " + property;
            }
          }

          return {
            children: test,
            props: {
              "data-title": "Access",
            },
          };
        },
        width: "20%",
      },
      {
        title: CREATED_ON,
        dataIndex: "created_on",
        key: "created_on",
        width: "20%",
        render: (createdOn) => {
          return {
            children: createdOn,
            props: {
              "data-title": "Created On",
            },
          };
        },
        sorter: (a, b) => { return moment(a.created_on).unix() - moment(b.created_on).unix()}
      },
      {
        title: STATUS,
        dataIndex: "",
        key: "x",
        width: "12%",
        render: (record) => {
          return {
            children:
              record.status_id === 1 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {STATUS_ACTIVE}
                </div>
              ) : record.status_id === 2 ? ( 
                <div className="primary-tag-rounded tag">
                 <span className="circle mr-1"></span>
                  {DRAFT}
                </div>
              ) : record.status_id === 3 ? (
                <div className="primary-tag-rounded tag">
                 <span className="circle mr-1"></span>
                  {SCHEDULED}
                </div>
              ) : record.status_id === 4 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {COMPLETED}
                </div>
              ) :  (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {DELETED}
                 </div>) ,
            props: {
              "data-title": "Status",
            },
          };
        },
       // sorter: (a, b) => a.status_id - b.status_id,
      },
      {
        title: ACTIONS,
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) => {
          return {
            children: <ManageEmpPopover data={record} setLoader={this.props.setLoader} />,
            props: {
              "data-title": "Actions",
            },
          };
        },
      },
    ];
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0" data-test="manageEmp">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={this.props.data} />
              <Content className="">
                <Row className="py-3">
                  <h1>{MANAGE_EMPLOYEES}</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder={SEARCH}
                            onSearch={this.onSearch}
                          />
                        </Col>
                        <Col
                          span={24}
                          lg={9}
                          className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                        >
                          <MultiSelection
                            handlePollStatusOnSelect={
                              this.handlePollStatusOnSelect
                            }
                          />
                          <DownOutlined className="down-arrow-ico multiselect-arrow sm-arrow" />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/orgcc/addemployee">
                        <Buttons value={ADD_EMPLOYEE} />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Spin spinning={loader} size="large">
                  <Grid
                    rowKey="unique_identifier"
                    columns={columns}
                    data={this.state.data}
                  />
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default ShowAllEmployees;
