import React from "react";
import { Table, Button, Row, Col, Alert, Spin } from "antd";
import AssignHotlineModal from "../modal/AssignHotlineModal";
import { getManageEmpAsync } from "../../actions/manageEmp";
import Search from "antd/lib/input/Search";
import { connect } from "react-redux";
import "../table/table.scss";
import { BACK, NEXT, SEARCH_USER, SAVE_AS_DRAFT_BTN } from "../../headings";
import { ASSIGN_USERS_HOTLINES } from "../../errors";
import {
  IDB_USER,
  ORG_CC,
  SUPER_USER,
  APP_USER,
  AM_WEB,
  STATUS_ACTIVE,
  STATUS_INACTIVE,
} from "../../values";
import { ACCESS, FIRST_NAME, LAST_NAME, MOBILE, STATUS } from "../../labels";

class CreateOpenlineStepTwo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      employeeData: "",
      showAssigntoModal: "false",
      showAlert: false,
      loader: false,
    };
  }

  componentDidMount() {
    const { dispatch } = this.props;
    this.setState({ loader: true });
    dispatch(getManageEmpAsync({}, ""));
  }

  componentDidUpdate(prevProps) {
    let { manageEmpData } = this.props;
    if (prevProps.manageEmpData != manageEmpData) {
      this.setState(
        {
          employeeData: manageEmpData,
          loader: false,
        },
        () => {
          this.onSearch("");
        }
      );
    }
  }

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { employeeData } = this.state;
    const filterDt = this.filterIt(employeeData, value);
    this.setState({ filterData: filterDt });
  };

  start = () => {
    this.setState({
      showAssigntoModal: true,
    });
  };

  stepTwoPrev = () => {
    this.props.prev();
  };

  stepTwoNext = () => {
    if (this.props.stepTwoOpenLine.chekedValues.length !== 0) {
      this.props.next();
    } else {
      this.setState({ showAlert: true });
    }
  };

  render() {
    const { selectedRowKeys } = this.props.stepTwoOpenLine;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.props.handleStepTwoOnSelect,
    };

    const columns = [
      {
        title: FIRST_NAME,
        dataIndex: "first_name",
        key: "first_name",
        width: "22%",
      },
      {
        title: LAST_NAME,
        dataIndex: "last_name",
        key: "last_name",
        width: "22%",
      },
      {
        title: MOBILE,
        dataIndex: "mobile_number",
        key: "mobile_number",
        width: "21%",
      },
      {
        title: ACCESS,
        dataIndex: "",
        key: "x",
        render: (record) => {
          let test = "";
          let property = "";
          let flag = false;
          if (record.is_am_web_user) {
            property = AM_WEB;
            flag = true;
          }
          if (record.is_org_cc_user) {
            property = ORG_CC;
            flag = true;
          }
          if (record.is_idb_user) {
            property = IDB_USER;
            flag = true;
          }
          if (record.is_super_user) {
            property = SUPER_USER;
            flag = true;
          }
          if (record.is_app_user) {
            if (!flag) {
              property = APP_USER;
            }
          }

          if (test.length === 0) {
            test += property;
          } else if (property !== APP_USER) {
            if (test.includes("APP")) {
              test = property;
            } else {
              test += ", " + property;
            }
          }
          return {
            children: <span>{test}</span>,
            props: {
              "data-title": "Access",
            },
          };
        },
        width: "20%",
      },
      {
        title: STATUS,
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) => {
          return {
            children:
              record.status_id === 1 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {STATUS_ACTIVE}
                </div>
              ) : (
                <div className="secondary-tag-rounded tag">
                  {STATUS_INACTIVE}
                </div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
    ];
    return (
      <div>
        <Col data-test="createHotlineStepTwo" span={24}>
          <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
            <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
              <Row>
                <Col span={24} lg={9} className="search-box">
                  <Search
                    placeholder={SEARCH_USER}
                    onSearch={this.onSearch}
                  ></Search>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
        {this.state.showAlert && (
          <Alert message={ASSIGN_USERS_HOTLINES} type="error" showIcon />
        )}
        <Spin spinning={this.state.loader} size="large">
          <div id="no-more-tables">
            <Table
              rowSelection={rowSelection}
              columns={columns}
              dataSource={this.state.filterData}
              rowKey="unique_identifier"
            />
          </div>

          <div className="steps-action mt-5">
            <Button
              className="mr-4"
              type="secondary"
              onClick={() => this.stepTwoPrev()}
              data-test="steptwoPrev"
            >
              {BACK}
            </Button>
            <Button
              className="md mr-4"
              type="primary"
              onClick={() => this.stepTwoNext()}
              data-test="steptwoNext"
            >
              {NEXT}
            </Button>
            <Button
              type="secondary"
              onClick={() => this.props.createOpenline(true)}
            >
              {SAVE_AS_DRAFT_BTN}
            </Button>
          </div>
        </Spin>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { manageEmpData } = state.manageEmp;
  const { message } = state.message;
  return {
    manageEmpData,
    message,
  };
}
export default connect(mapStateToProps)(CreateOpenlineStepTwo);
