import UserService from '../services/user.service';

import{
    OPENLINE_FAIL,
    OPENLINE_SUCCESS,
    SET_MESSAGE
    } from "./types";

    export const getOpelineAsync=()=>(dispatch)=>{
        return UserService.getOpenlines().then((data)=>{
            dispatch({
                type:OPENLINE_SUCCESS,
                payload:{openlinesdata:data.data},
                
            });    
            
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:OPENLINE_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };

    