import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { Layout, Row, Col, Breadcrumb, Button, Alert, Spin, Modal } from "antd";
import { NavLink } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import UploadFiles from "./UploadFiles";
import { ADD_EMPLOYEE, CANCEL, UPLOAD, OK_TEXT_MODAL } from "../../headings";
import {
  IMPORT_CSV,
  DOWNLOAD_SAMPLE_FILE,
  CSV_FILE_UPLOADED,
} from "../../messages";
import { UPLOAD_CSV_FILE } from "../../errors";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import authHeader from "../../services/auth-header";

const { Content } = Layout;
const ImportFiles = (props) => {
  const [error, showError] = useState(false);
  const [loader, setLoader] = useState(false);
  const [fileUploaded, setFileUploaded] = useState(false);
  const [uploadMessage, SetUploadMessage] = useState("");
  const [uploaderror, Setuploaderror] = useState("");

  const [file, setFile] = useState("");
  const history = useHistory();

  const onCancel = () => {
    history.push("/orgcc/addemployee");
  };

  const handleOk = () => {
    window.location = "/orgcc/manageemployees";
  };

  const handleCancel = () => {
    window.location = "/orgcc/manageemployees";
  };

  const onChange = (e) => {
    let fileList = e.fileList.slice(-1);
    fileList.forEach(function (file, index) {
      let reader = new FileReader();
      reader.onload = (e) => {
        file.base64 = e.target.result;
        file.response = "Uploaded successfully";
      };
      reader.readAsDataURL(file.originFileObj);
    });
    setFile(fileList);
  };

  const onUpload = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": `multipart/form-data`,
      },
    };
    if (file) {
      const formData = new FormData();
      formData.append("file", file[0].originFileObj);
      showError(false);
      setLoader(true);
      axios
        .post(BASE_URL + "uploadCsv/", formData, options)
        .then(() => {
          setFileUploaded(true);
        })
        .catch((error) => {
          setLoader(false);
          setFileUploaded(false);
          if (error.message.includes("400")) {
            Setuploaderror(true);
            SetUploadMessage(error.response.data);
          }
        });
    } else {
      showError(true);
    }
  };
  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content className="w-100">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/addemployee">
                      <Breadcrumb.Item>{ADD_EMPLOYEE}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{IMPORT_CSV}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">{IMPORT_CSV} </h1>
                </Row>
                <div className="mid-content mt-4">
                  <Spin spinning={loader} size="large">
                    {error && (
                      <Alert
                        message={UPLOAD_CSV_FILE}
                        type="error"
                        showIcon
                      ></Alert>
                    )}
                    {uploaderror && (
                      <Alert
                        message={uploadMessage}
                        type="error"
                        showIcon
                      ></Alert>
                    )}
                    <Row className="d-flex justify-content-center align-items-center">
                      <Col span={20} className="mt-3 dragdrop">
                        <UploadFiles onChange={onChange} file={file} />
                      </Col>
                      <Col
                        span={24}
                        className="justify-content-center d-flex mt-5"
                      >
                        <Button
                          type="link"
                          href="https://amstagingresources.s3-us-west-1.amazonaws.com/Sample_Employees.csv"
                          className="link pl-0"
                        >
                          <span className="icon icon-download-icon" />
                          {DOWNLOAD_SAMPLE_FILE}
                        </Button>
                      </Col>
                    </Row>
                    <div className="mt-3 btn-row">
                      <Button
                        type="primary"
                        className="mr-4 mb-4"
                        onClick={onUpload}
                      >
                        {UPLOAD}
                      </Button>
                      <Button
                        type="secondary"
                        className="md"
                        onClick={onCancel}
                      >
                        {CANCEL}
                      </Button>
                    </div>
                    <Modal
                      visible={fileUploaded}
                      onOk={handleOk}
                      onCancel={handleCancel}
                      centered
                      width={350}
                      okButtonProps={{
                        children: "Custom OK",
                      }}
                      cancelButtonProps={{ style: { display: "none" } }}
                      okText={OK_TEXT_MODAL}
                    >
                      <p>{CSV_FILE_UPLOADED}</p>
                    </Modal>
                  </Spin>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default ImportFiles;
