import React, { useState } from "react";
import { Layout, Row, Col, Divider, Button } from "antd";
import { LoggedinHeader } from "../../containers";
import CreateBroadcast from "../../assets/images/manage-broadcast.svg";
import OrgSidebar from "../../containers/OrgSidebar";

const { Content } = Layout;

const ManageBroadcast = () => {
  return (
    <React.Fragment>
        <React.Fragment>
          {" "}
          <Row className="inner-header">
            <LoggedinHeader amORGUser />
          </Row>
          <Row className="content-center pt-0">
            <Layout>
              <Layout>
                <OrgSidebar amORGUser />
                <Content data-test="Broadcast" className="">

                  <Row className="py-3">
                    <h1 className="mb-0">Manage Broadcast</h1>
                  </Row>
                  <div className="mid-content d-flex justify-content-center align-items-center">
                    <Row>
                      <Col span={24}>
                        <div className="c-vector-area d-flex justify-content-center">
                          <img
                            src={CreateBroadcast}
                            alt="Create Broadcast"
                          />
                        </div>
                        <p className="text-center pt-3">
                        Create broadcasts and publish them for all employees from here. 
                        </p>
                        <Row className="d-flex justify-content-center">
                          <Divider className="mt-1" />
                        </Row>
                        <Col
                          span={24}
                          className="d-flex align-items-center justify-content-center"
                        >
                          <Button
                            type="primary"
                          >
                            Create Broadcast
                          </Button>
                        </Col>
                      </Col>
                    </Row>
                  </div>
                </Content>
              </Layout>
            </Layout>
          </Row>
        </React.Fragment>

    </React.Fragment>
  );
};

export default ManageBroadcast;
