import React, { Component } from "react";
import { Row, Col, Button, Divider, Typography, Slider } from "antd";
import NoteModal from "../modal/NoteModal";
import AddedNotes from "./AddedNotes";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import { NOTES } from "../../headings";

const { Paragraph } = Typography;

class Note extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rows: 1,
      notes: [],
      conversationId: "",
    };
    this.getNotes = this.getNotes.bind(this);
  }

  onChange = (rows) => {
    this.setState({ rows });
  };

  componentDidMount() {
    this.setState({ conversationId: this.props.conversation_id });
    this.getNotes(this.props.conversation_id);
  }

  getNotes(conversationId) {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
      params: { conversation_id: conversationId },
    };

    axios.get(BASE_URL + "getNotes/", options).then((res) => {
      this.setState({
        notes: res.data,
      });
    });
  }

  render() {
    const { notes } = this.state;
    return (
      <>
        <div id="rgt-panel" className={this.props.class} data-test="Notes">
          <p>
            <Row>
              <Col span={24}>
                <Row className="filter-wrapper align-items-center justify-content-between px-3 pt-3">
                  <Col span="12">
                    <h6>{NOTES}</h6>
                  </Col>
                  <Button
                    onClick={this.showModal}
                    type="link"
                    className="link pl-0 pt-0 align-items-center d-flex"
                  >
                    <span
                      className="icon icon-close small"
                      onClick={(e) => this.props.notesClicked(e)}
                    />
                  </Button>
                </Row>
                <Divider className="my-2 w-100" />
                <Col span="24" className="px-3">
                  <NoteModal
                    convoId={this.props.conversation_id}
                    getNoteData={this.getNotes}
                  />
                  <Row className="added-notes">
                    <AddedNotes addedNote={notes} />
                  </Row>
                </Col>
              </Col>
            </Row>
          </p>
        </div>
      </>
    );
  }
}

export default Note;
