import React from "react";
import { Layout, Row, Col, Divider, Button } from "antd";
import { LoggedinHeader } from "../../containers";
import "./manageOrg.scss";
import WorkChart from "../../assets/images/work-chart.svg";
import AccSidebar from "../manageOrg/AccSidebar";
import CreateOrgStepper from "../form/CreateOrgStepper";
import { CREATE_ORGANIZATION, MANAGE_ORGANIZATIONS } from "../../headings";
import { CREATE_NEW_ORGANIZATION_TEXT } from "../../messages";
const { Content } = Layout;

const CreateOrg = () => {
  const [showResult, setShowResult] = React.useState(false);

  const showBtn = () => {
    setShowResult(true);
  };
  return (
    <React.Fragment>
      {!showResult === true ? (
        <React.Fragment>
          {" "}
          <Row className="inner-header">
            <LoggedinHeader amCCUser />
          </Row>
          <Row className="content-center pt-0">
            <Layout>
              <Layout>
                <AccSidebar />
                <Content className="">
                  <Row className="py-3">
                    <h1 className="mb-0">{MANAGE_ORGANIZATIONS}</h1>
                  </Row>
                  <div className="mid-content d-flex justify-content-center align-items-center">
                    <Row>
                      <Col span={24}>
                        <div className="c-vector-area">
                          <img
                            src={WorkChart}
                            alt="Create Organization"
                            className="pr-4"
                          />
                        </div>
                        <p className="text-center pt-3">
                          {CREATE_NEW_ORGANIZATION_TEXT}
                        </p>
                        <Row className="d-flex justify-content-center">
                          <Divider className="mt-1" />
                        </Row>
                        <Col
                          span={24}
                          className="d-flex align-items-center justify-content-center"
                        >
                          <Button onClick={showBtn} type="primary">
                            {CREATE_ORGANIZATION}
                          </Button>
                        </Col>
                      </Col>
                    </Row>
                  </div>
                </Content>
              </Layout>
            </Layout>
          </Row>
        </React.Fragment>
      ) : (
        <CreateOrgStepper />
      )}
    </React.Fragment>
  );
};

export default CreateOrg;
