import UserService from '../services/user.service';

import{
    HOTLINE_FAIL,
    HOTLINE_SUCCESS,
    SET_MESSAGE,
    HOTLINEASSIGN_FAIL,
    HOTLINEASSIGN_SUCCESS
    } from "./types";

    export const getHotlinesAsync=()=>(dispatch)=>{
        return UserService.getHotlines().then((data)=>{
            dispatch({
                type:HOTLINE_SUCCESS,
                payload:{hotlinesdata:data.data},
                
            });    
            
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:HOTLINE_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };

    export const getAssignHotlinesAsync=(hotLineId)=>(dispatch)=>{
        return UserService.getAssignHotlines(hotLineId).then((data)=>{
            dispatch({
                type:HOTLINEASSIGN_SUCCESS,
                payload:{assignhotlinedata:data.data},
                
            });    
            
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:HOTLINEASSIGN_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };