import React, { useState } from "react";
import { Layout, Row, Col, Popover, Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import dotsicon from "../../assets/icons/dots-icon.svg";
import DeleteModal from "../../components/modal/DeleteHelp";
import ReactHtmlParser from "react-html-parser";
import { Link } from "react-router-dom";
import { DELETE, EDIT, HELP } from "../../headings";
import IdbSidebar from "../reports/IdbSidebar";
const { Content } = Layout;

const content = (props) => {
  const [deleteModal, setdeleteModal] = useState(false);
  return (
    <div>
      <ul>
        <Link
          to={{
            pathname: "/orgcc/edithelp",
            state: {
              help:
                props && props.helps && props.helps[0] ? props.helps[0] : {},
            },
          }}
        >
          <li>
            <a href={"/orgcc/edithelp"}>{EDIT}</a>
          </li>
        </Link>
        <Link
          onClick={() => {
            setdeleteModal(true);
          }}
        >
          <li>
            <a>{DELETE}</a>
            {deleteModal ? (
              <DeleteModal
                help={
                  props && props.helps && props.helps[0] ? props.helps[0] : {}
                }
              />
            ) : (
              ""
            )}
          </li>
        </Link>
      </ul>
    </div>
  );
};

const HelpView = (props) => {
  const [isVisible, setIsVisible] = useState(false);
  const { loader } = props;
  const handleVisibleChange = () => {
    setIsVisible(!isVisible);
  };

  return (
    <React.Fragment>
      {" "}
      <Row className="inner-header">
        <LoggedinHeader amIDBUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <IdbSidebar amIDBUser data={props.data} />
            <Content>
              <Row className="py-3 justify-content-between">
                <h1 className="mb-0">{HELP}</h1>
              </Row>
              <div className="mid-content">
                <Spin spinning={loader} size="large">
                  <Row>
                    <Col span={22} className="editor-data">
                      <h6 className="font-weight-md mb-1">
                        {props.helps && props.helps[0] && props.helps[0].title
                          ? props.helps[0].title
                          : ""}
                      </h6>
                    </Col>
                    {/* <Col span={2} className="d-flex justify-content-end">
                      <Popover
                        placement="bottom"
                        content={content(props)}
                        trigger="click"
                        visible={isVisible}
                        onVisibleChange={handleVisibleChange}
                      >
                        <span className="ico-grid-dots">
                          <img src={dotsicon} alt="DotIcon" className="pr-4" />
                        </span>
                      </Popover>
                    </Col> */}
                    <p className="pt-2 mt-3">
                      {props.helps && props.helps[0] && props.helps[0].message
                        ? ReactHtmlParser(props.helps[0].message)
                        : ""}
                    </p>
                  </Row>
                </Spin>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </React.Fragment>
  );
};

export default HelpView;
