import React, { useEffect, useState } from "react";
import ShowAllOrganizations from "./ShowAllOrganizations";
import CreateOrg from "./CreateOrg";
import { connect } from "react-redux";
import { getManageOrgAsync } from "../../actions/manageOrg";

const OrganizationContainer = (props) => {
  const [statusClicked, setStatusClicked] = useState(false);
  const [loader, setLoader] = useState(false);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getManageOrgAsync());
  }, [statusClicked]);

  useEffect(() => {
    if (props.manageOrgData === null) {
      setLoader(true);
    } else if (props.manageOrgData !== null) {
      setLoader(false);
    }
  }, [props.manageOrgData]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  return !loader &&
    props.manageOrgData !== null &&
    props.manageOrgData.length === 0 ? (
    <CreateOrg />
  ) : (
    <ShowAllOrganizations
      organizations={props.manageOrgData}
      statusOnClicked={statusOnClicked}
      loader={loader}
    />
  );
};

function mapStateToProps(state) {
  const { manageOrgData } = state.manageOrg;
  return {
    manageOrgData,
  };
}

export default connect(mapStateToProps)(OrganizationContainer);
