import React from "react";
import { Layout, Menu } from "antd";
import { Link } from "react-router-dom";
const { Sider } = Layout;
import { MenuUnfoldOutlined, MenuFoldOutlined } from "@ant-design/icons";
import {
  MANAGE_EMPLOYEES,
  HOTLINES,
  OPENLINES,
  HELP,
  INFO,
  BROADCAST_MESSAGES,
  POLLS,
} from "../headings";

class OrgSidebar extends React.Component {
  state = {
    collapsed: false,
  };

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    let defaultKey = "";
    if (this.props.data?.location) {
      if (this.props.data.location.pathname.includes("employee")) {
        defaultKey = "4";
      } else if (this.props.data.location.pathname.includes("hotline")) {
        defaultKey = "1";
      } else if (this.props.data.location.pathname.includes("openline")) {
        defaultKey = "2";
      } else if (this.props.data.location.pathname.includes("poll")) {
        defaultKey = "3";
      } else if (this.props.data.location.pathname.includes("broadcast")) {
        defaultKey = "5";
      } else if (this.props.data.location.pathname.includes("help")) {
        defaultKey = "6";
      } else if (this.props.data.location.pathname.includes("info")) {
        defaultKey = "7";
      }
    }

    return (
      <>
        {React.createElement(
          this.state.collapsed ? MenuFoldOutlined : MenuUnfoldOutlined,
          {
            className: "trigger d-lg-none d-block",
            onClick: this.toggle,
          }
        )}
        <Sider
          className={
            this.state.collapsed
              ? " d-lg-block w-md-100 sider-width"
              : "d-none d-lg-block w-md-100 sider-width"
          }
          trigger={null}
          collapsible
          collapsed={this.state.collapsed}
        >
          <Menu theme="dark" mode="inline" defaultSelectedKeys={[defaultKey]}>
            <Menu.Item
              key="1"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-hotlines" />
                </span>
              }
            >
              {" "}
              <Link to="/orgcc/hotlinedirectory">{HOTLINES}</Link>
            </Menu.Item>
            <Menu.Item
              key="2"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-openlines" />
                </span>
              }
            >
              {" "}
              <Link to="/orgcc/openlinedirectory">{OPENLINES}</Link>
            </Menu.Item>
            <Menu.Item
              key="3"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-polls" />
                </span>
              }
            >
              {" "}
              <Link to="/orgcc/pollsdirectory">{POLLS}</Link>
            </Menu.Item>
            <Menu.Item
              key="4"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-manage-emp" />
                </span>
              }
            >
              {" "}
              <Link to="/orgcc/manageemployees">{MANAGE_EMPLOYEES}</Link>
            </Menu.Item>
            <Menu.Item
              key="5"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-broadcast" />
                </span>
              }
            >
              <Link to="/orgcc/broadcastmessages">{BROADCAST_MESSAGES}</Link>
            </Menu.Item>
            <Menu.Item
              key="6"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-help" />
                </span>
              }
            >
              <Link to="/orgcc/help">{HELP}</Link>
            </Menu.Item>
            <Menu.Item
              key="7"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-info" />
                </span>
              }
            >
              <Link to="/orgcc/info">{INFO}</Link>
            </Menu.Item>
          </Menu>
          <div className="btm-div">
            <Link to="/orgcc/info" className="px-2">
              {INFO}
            </Link>
            <Link to="/orgcc/help" className="px-2">
              {HELP}
            </Link>
          </div>
        </Sider>
      </>
    );
  }
}

export default OrgSidebar;
