import React from "react";
import { Row, Col } from "antd";
import { NO_CONVERSATION_INITIATED } from "../../messages";

const NoHotlineMessages = (props) => (
  <Col
    span={24}
    lg={18}
    className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
  >
    <Row className="justify-content-center my-3">
      <img
        src={props.CreateHotline}
        alt="No Hotlines Selected"
        style={{ opacity: "0.8" }}
      />
    </Row>
    <h5>{NO_CONVERSATION_INITIATED}</h5>
    {/* <small>Select a hotline to view</small> */}
  </Col>
);

export default NoHotlineMessages;
