import React, { useState } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Button,
  Select,
  Input,
  Switch,
  Breadcrumb,
  Alert,
  message,
} from "antd";
import { NavLink, useHistory } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import styles from "./managePoll.module.scss";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import UploadImage from "./UploadImage";
import ReactStars from "react-rating-stars-component";
import MultiChoice from "./MultipleChoice";
import PreviewPoll from "./PreviewPoll";
import authHeader from "../../services/auth-header";
import { CheckCircleTwoTone } from "@ant-design/icons";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import Thermometer from "../../assets/images/thermometer.svg";
import Thumb from "../../assets/images/Thumb.svg";
import Smiley from "../../assets/images/emoji.svg";
import Steps from "../../assets/images/steps.svg";

import {
  DESCRIPTION,
  DISPLAY_RESULTS_AS,
  QUESTION,
  QUESTION_TYPE,
  RATING_TYPE,
  SHOW_RESULTS_POLL,
} from "../../labels";
import {
  CANCEL,
  CREATE_POLL,
  POLLS,
  POLL_TITLE,
  PREVIEW,
  UPLOAD,
} from "../../headings";
import {
  POLL_ALREADY_EXISTS,
  REQUIRED_MESSAGE,
  STAR_CHART_TYPE,
  ADD_OPTIONS,
} from "../../errors";
import {
  BAR_CHART,
  LADDER,
  PIE_CHART,
  SELECT_MULTIPLE,
  SELECT_SINGLE,
  SMILEY,
  STARS,
  STAR_FORMAT,
  STAR_RATING,
  THERMOMETER,
  THUMBSUP,
} from "../../values";

const starsRating = {
  size: 40,
  count: 5,
  isHalf: false,
  value: 0,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  emptyIcon: <i class="far fa-smile"></i>,
  fullIcon: <i class="fa fa-smile"></i>,
  onChange: (newValue) => {},
};

const { TextArea } = Input;
const { Content } = Layout;

const CreatePoll = (props) => {
  const history = useHistory();

  const [poll, setPoll] = useState({
    pollName: "",
    pollHelpText: "",
    pollDocument: "",
    showResult: false,
    pollChartType: "",
    pollQuestion: "",
    pollQuestionType: "",
    pollRatingResponse: 0,
    pollStartDate: "",
    pollEndDate: "",
    attachmentName: "",
    pollStartTime: new Date(),
    pollEndTime: new Date(),
    pollResponse: [],
    pollImageValidation: true,
    previewVisible: false,
    previewImage: "",
    previewTitle: "",
    pollRatingType: "",
  });
  const [pollError, setpollError] = useState("");
  const [error, showError] = useState(false);
  const [previewPoll, setPreviewPoll] = useState(false);
  const [chartError, showChartError] = useState(false);
  const [uploadError, showuploadError] = useState(true);
  const [isImage, setIsImage] = useState(false);
  const [attachmentName, setAttachmentName] = useState("");
  const [form] = Form.useForm();
  const [optionsError, setOptionsError] = useState(false);

  const onPreview = (e) => {
    e.preventDefault();
    if (
      uploadError &&
      poll.pollName &&
      poll.pollName.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      poll.pollHelpText &&
      poll.pollHelpText.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      poll.pollQuestionType &&
      poll.pollQuestionType.replace(/^\s*/, "").replace(/\s*$/, "").length >
        0 &&
      poll.pollQuestion
      //poll.pollResponse.length > 0
    ) {
      if (poll.pollResponse.length === 0) {
        setOptionsError(true);
        setPreviewPoll(false);
        showError(false);
      } else if (poll.showResult || poll.pollQuestionType === "3") {
        if (!poll.pollChartType && poll.showResult) {
          showError(true);
          setPreviewPoll(false);
          setOptionsError(false);
        } else if (poll.pollQuestionType === "3" && !poll.pollRatingType) {
          showError(true);
          setPreviewPoll(false);
          setOptionsError(false);
        } else {
          showError(false);
          setPreviewPoll(true);
          setOptionsError(false);
        }
      } else {
        showError(false);
        setPreviewPoll(true);
        setOptionsError(false);
      }
    } else {
      showError(true);
      setPreviewPoll(false);
      setOptionsError(false);
    }
  };

  const onChange = (checked, e) => {
    setPoll({ ...poll, [e.target.id]: checked });
  };

  const onCancel = () => {
    history.push("/orgcc/pollsdirectory");
  };

  const onCreateChange = (e) => {
    setPoll({ ...poll, [e.target.id]: e.target.value });
  };

  const checkChartType = (value) => {
    if (showResult && value === "3" && poll.pollChartType != "4") {
      showChartError(true);
    }
  };

  const handlePollOnSelect = (value, name) => {
    if (value === "3" && name === "pollQuestionType") {
      checkChartType(value);
    } else {
      showChartError(false);
    }
    if (name === "pollRatingType") {
      setPoll({
        ...poll,
        [name]: value,
        ["pollResponse"]: ["1", "2", "3", "4", "5"],
      });
    } else {
      setPoll({ ...poll, [name]: value });
    }
  };

  const setPollsDate = (date, name) => {
    setPoll({ ...poll, [name]: date });
  };

  const backtoEdit = (value) => {
    setPreviewPoll(false);
    setPoll({
      ...poll,
      pollName: value.pollName,
      pollHelpText: value.pollHelpText,
      pollDocument: value.pollDocument,
      showResult: value.showResult,
      pollChartType: value.pollChartType,
      pollQuestion: value.pollQuestion,
      pollQuestionType: value.pollQuestionType,
      pollRatingResponse: value.pollRatingResponse,
      pollStartDate: value.pollStartDate,
      pollEndDate: value.pollEndDate,
      pollResponse: value.pollResponse,
    });

    form.setFieldsValue({
      pollHelpText: value.pollHelpText,
      pollQuestion: value.pollQuestion,
    });
  };

  const setSingleChoiceResponse = (value) => {
    let name = "pollResponse";
    setPoll({ ...poll, [name]: value });
  };

  const handleStepOneUpload = (value, name) => {
    if (value.file.status === "removed") {
      showuploadError(true);
    }
    let fileList = value.fileList.slice(-1);
    checkDoucmentType(value.file.type);
    fileList.forEach(function (file, index) {
      let reader = new FileReader();
      reader.onload = (e) => {
        file.base64 = e.target.result;
        file.response = "Uploaded successfully";
      };
      reader.readAsDataURL(file.originFileObj);
    });
    setAttachmentName(value.file.name);
    setPoll({ ...poll, [name]: fileList });
  };

  const checkDoucmentType = (fileType) => {
    const isImage = fileType.includes("image");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
  };

  const handlePollImagePreview = async (file) => {
    if (
      file.name?.includes("jpeg") ||
      file.name?.includes("jpg") ||
      file.name?.includes("png") ||
      file.name?.includes("gif") ||
      file.name?.includes("jfif") ||
      file.name?.includes("tiff")
    ) {
      file.preview = await getBase64(file.originFileObj);
      setPoll({
        ...poll,
        previewImage: file.url || file.preview,
        previewVisible: true,
        previewTitle:
          file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
      });
    } else {
      return false;
    }
  };

  const handleCancel = (value) => setPoll({ ...poll, previewVisible: false });

  const getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const handleOnRemove = (value, name) => {
    setPoll({ ...poll, pollImageValidation: true });
  };

  const handleBeforeUpload = (file, name) => {
    let xlsxfileExt = file.name.includes(".xls");
    let pptxfileExt = file.name.includes(".pptx");
    let docfileExt = file.name.includes(".doc");
    let csvfileExt = file.name.includes(".csv");

    const isValidDoc =
      file.type === "image/jpeg" ||
      file.type === "image/png" ||
      file.type === "image/gif" ||
      file.type === "application/pdf" ||
      xlsxfileExt ||
      pptxfileExt ||
      docfileExt ||
      csvfileExt;
    if (!isValidDoc) {
      showuploadError(false);
      message.error(`${file.name} is not a supported format file type.`);
    } else {
      showuploadError(true);
    }
    const isImage =
      file.type === "image/jpeg" ||
      file.type === "image/png" ||
      file.type === "image/gif";
    const isDocument =
      file.type === "application/pdf" ||
      xlsxfileExt ||
      pptxfileExt ||
      docfileExt ||
      csvfileExt;
    const isLt5M = file.size / 1024 / 1024 < 5;
    const isLt10M = file.size / 1024 / 1024 < 10;
    if (isImage) {
      if (!isLt5M && isImage) {
        showuploadError(false);
        message.error("Image must smaller than 5MB!");
      } else {
        showuploadError(true);
      }
    }

    if (isDocument) {
      if (!isLt10M && isDocument) {
        showuploadError(false);
        message.error("Document must smaller than 10MB!");
      } else {
        showuploadError(true);
      }
    }
    return (isValidDoc && isLt5M) || isLt10M;
  };

  const isPollNameDuplicate = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .post(BASE_URL + "checkpoll/", { title: e.target.value }, options)
      .then((res) => {
        if (res.data) {
          setpollError(res.data[0]);
        }
      });
  };

  return (
    <React.Fragment>
      {!previewPoll ? (
        <React.Fragment>
          {" "}
          <Row className="inner-header">
            <LoggedinHeader amORGUser />
          </Row>
          <Row className="content-center pt-0">
            <Layout>
              <Layout>
                <OrgSidebar amORGUser data={props} />
                <Content>
                  <Row className="pt-3">
                    <Breadcrumb separator=">">
                      <NavLink to="/orgcc/pollsdirectory">
                        <Breadcrumb.Item>{POLLS}</Breadcrumb.Item>
                      </NavLink>
                      <Breadcrumb.Item>{CREATE_POLL}</Breadcrumb.Item>
                    </Breadcrumb>
                  </Row>
                  <Row className="py-3">
                    <h1 className="mb-0">{CREATE_POLL}</h1>
                  </Row>
                  <div className="mid-content">
                    <Row>
                      <Col span={24}>
                        <Form
                          className="input-md floating-label w-100"
                          name="validate_other"
                          form={form}
                        >
                          {error && (
                            <Form.Item>
                              <Alert
                                message={REQUIRED_MESSAGE}
                                type="error"
                                showIcon
                              ></Alert>
                            </Form.Item>
                          )}
                          {optionsError && (
                            <Form.Item>
                              <Alert
                                message={ADD_OPTIONS}
                                type="error"
                                showIcon
                              ></Alert>
                            </Form.Item>
                          )}
                          <Col span={24}>
                            <Row>
                              <Col span={24} md={18} className="gutter-row">
                                <FloatLabel
                                  label={POLL_TITLE}
                                  value={poll.pollName}
                                >
                                  <TextArea
                                    showCount
                                    maxLength={50}
                                    htmlFor="polltitle"
                                    value={poll.pollName}
                                    id="pollName"
                                    onChange={(e) => onCreateChange(e)}
                                    onBlur={(e) => isPollNameDuplicate(e)}
                                    autoComplete="off"
                                    className={`required ${styles.sm_textarea}`}
                                  />

                                  {pollError === "status: false" &&
                                    poll.pollName
                                      .replace(/^\s*/, "")
                                      .replace(/\s*$/, "").length > 0 &&
                                    poll.pollName.length > 0 && (
                                      <CheckCircleTwoTone
                                        className="successCheck"
                                        twoToneColor="#52c41a"
                                      />
                                    )}
                                  {pollError == "status: true" && (
                                    <Alert
                                      message={POLL_ALREADY_EXISTS}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </FloatLabel>
                              </Col>
                              <Col span={24} md={18} className="gutter-row">
                                <FloatLabel
                                  label={DESCRIPTION}
                                  value={poll.pollHelpText}
                                >
                                  <Form.Item
                                    className="required"
                                    name="pollHelpText"
                                    id="pollHelpText"
                                    validateStatus="success"
                                  >
                                    <Input
                                      className="input-control"
                                      htmlFor="helptext"
                                      id="pollHelpText"
                                      name="pollHelpText"
                                      value={poll.pollHelpText}
                                      autoComplete="off"
                                      maxLength={500}
                                      onChange={(e) => onCreateChange(e)}
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                              <Col span={24} className="gutter-row mt-3">
                                <h6>{UPLOAD}</h6>
                                <Form.Item
                                  name="upload"
                                  className="upload-list"
                                >
                                  <UploadImage
                                    file={poll.pollDocument}
                                    handleStepOneUpload={handleStepOneUpload}
                                    handleBeforeUpload={handleBeforeUpload}
                                    handleOnRemove={handleOnRemove}
                                    handlePollImagePreview={
                                      handlePollImagePreview
                                    }
                                    handleCancel={handleCancel}
                                    poll={poll}
                                  />
                                </Form.Item>
                              </Col>
                            </Row>
                          </Col>
                          <Col span={24} className="gutter-row">
                            <Row>
                              <Col
                                span={24}
                                md={8}
                                lg={8}
                                xl={6}
                                className="mb-3 t-2 mb-y-0"
                              >
                                <Form.Item
                                  name="showresult"
                                  label={SHOW_RESULTS_POLL}
                                  className="mb-0"
                                >
                                  <Switch
                                    id="showResult"
                                    checked={poll.showResult}
                                    onChange={(checked, e) =>
                                      onChange(checked, e)
                                    }
                                  />
                                </Form.Item>
                              </Col>
                              {poll.showResult ? (
                                <Col span={24} md={10} lg={8}>
                                  <FloatLabel
                                    label={DISPLAY_RESULTS_AS}
                                    value={poll.pollChartType}
                                  >
                                    <Form.Item
                                      className="required mb-0"
                                      name="result"
                                    >
                                      <>
                                        <Select
                                          className="input-control"
                                          htmlFor="result"
                                          className="required"
                                          value={poll.pollChartType}
                                          onSelect={(value) =>
                                            handlePollOnSelect(
                                              value,
                                              "pollChartType"
                                            )
                                          }
                                        >
                                          <Select.Option value="1">
                                            {PIE_CHART}
                                          </Select.Option>
                                          <Select.Option value="3">
                                            {BAR_CHART}
                                          </Select.Option>
                                          <Select.Option value="4">
                                            {STAR_FORMAT}
                                          </Select.Option>
                                        </Select>
                                      </>
                                    </Form.Item>
                                  </FloatLabel>
                                  {chartError && (
                                    <Form.Item>
                                      <Alert
                                        message={STAR_CHART_TYPE}
                                        type="error"
                                        showIcon
                                      ></Alert>
                                    </Form.Item>
                                  )}
                                </Col>
                              ) : (
                                <Col></Col>
                              )}
                            </Row>
                          </Col>
                        </Form>
                      </Col>
                    </Row>
                  </div>

                  <div className="mid-content mt-4">
                    <Row>
                      <Col span={24}>
                        <Form
                          className="input-md floating-label w-100"
                          name="validate_other"
                        >
                          <Col span={24}>
                            <Row className="d-block">
                              <Col span={24} md={18} className="gutter-row">
                                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                                  <Col
                                    span={24}
                                    md={8}
                                    lg={8}
                                    className="mb-3 t-2 mb-y-0 gutter-row"
                                  >
                                    <FloatLabel
                                      label={QUESTION_TYPE}
                                      value={poll.pollQuestionType}
                                    >
                                      <Form.Item
                                        className="required mb-0"
                                        name="questiontype"
                                      >
                                        <>
                                          <Select
                                            className=""
                                            htmlFor="questiontype"
                                            className="required"
                                            id="pollQuestionType"
                                            value={poll.pollQuestionType}
                                            onSelect={(value) =>
                                              handlePollOnSelect(
                                                value,
                                                "pollQuestionType"
                                              )
                                            }
                                          >
                                            <Select.Option
                                              value="1"
                                              disabled={
                                                poll.pollChartType !== "" &&
                                                poll.pollChartType === "4"
                                              }
                                            >
                                              {SELECT_SINGLE}
                                            </Select.Option>
                                            <Select.Option
                                              value="2"
                                              disabled={
                                                poll.pollChartType !== "" &&
                                                poll.pollChartType === "4"
                                              }
                                            >
                                              {SELECT_MULTIPLE}
                                            </Select.Option>
                                            <Select.Option
                                              value="3"
                                              disabled={
                                                poll.pollChartType !== "" &&
                                                poll.pollChartType !== "4"
                                              }
                                            >
                                              {STAR_RATING}
                                            </Select.Option>
                                          </Select>
                                        </>
                                      </Form.Item>
                                    </FloatLabel>
                                  </Col>

                                  {poll.pollQuestionType === "3" ? (
                                    <Col
                                      span={24}
                                      md={10}
                                      lg={8}
                                      className="gutter-row"
                                    >
                                      <FloatLabel
                                        label={RATING_TYPE}
                                        value={poll.pollRatingType}
                                      >
                                        <Form.Item
                                          className="required mb-0"
                                          name="ratingtype"
                                        >
                                          <>
                                            <Select
                                              className=""
                                              htmlFor="ratingtype"
                                              className="required"
                                              id="pollRatingType"
                                              value={poll.pollRatingType}
                                              onSelect={(value) =>
                                                handlePollOnSelect(
                                                  value,
                                                  "pollRatingType"
                                                )
                                              }
                                            >
                                              <Select.Option value="1">
                                                {STARS}
                                              </Select.Option>
                                              <Select.Option value="2">
                                                {SMILEY}
                                              </Select.Option>
                                              <Select.Option value="3">
                                                {THERMOMETER}
                                              </Select.Option>
                                              <Select.Option value="4">
                                                {THUMBSUP}
                                              </Select.Option>
                                              <Select.Option value="5">
                                                {LADDER}
                                              </Select.Option>
                                            </Select>
                                          </>
                                        </Form.Item>
                                      </FloatLabel>
                                    </Col>
                                  ) : null}
                                </Row>
                              </Col>
                              <Col span={24} md={18} className="gutter-row">
                                <FloatLabel
                                  label={QUESTION}
                                  value={poll.pollQuestion}
                                >
                                  <Form.Item
                                    className="required"
                                    name="pollQuestion"
                                    id="pollQuestion"
                                    validateStatus="success"
                                  >
                                    <Input
                                      className="input-control"
                                      htmlFor="pollQuestion"
                                      id="pollQuestion"
                                      name="pollQuestion"
                                      defaultValue={poll.pollQuestion}
                                      value={poll.pollQuestion}
                                      autoComplete="off"
                                      maxLength={500}
                                      onChange={(e) => onCreateChange(e)}
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                              <Col
                                span={24}
                                md={12}
                                className={`mt-4 ${styles.rating_img_wrapper}`}
                              >
                                {poll.pollQuestionType === "3" &&
                                poll.pollRatingType ? (
                                  poll.pollRatingType === "1" ? (
                                    <ReactStars
                                      {...starsRating}
                                      value={poll.pollRatingResponse}
                                      name="pollRatingResponse"
                                      onChange={(value) =>
                                        handlePollOnSelect(
                                          value,
                                          "pollRatingResponse"
                                        )
                                      }
                                      classNames="mt-3"
                                    />
                                  ) : poll.pollRatingType === "2" ? (
                                    <img
                                      className="font-size"
                                      src={Smiley}
                                      alt="send message"
                                    />
                                  ) : poll.pollRatingType === "3" ? (
                                    <img
                                      className="font-size"
                                      src={Thermometer}
                                      alt="send message"
                                    />
                                  ) : poll.pollRatingType === "4" ? (
                                    <img
                                      className="font-size"
                                      src={Thumb}
                                      alt="send message"
                                    />
                                  ) : poll.pollRatingType === "5" ? (
                                    <img
                                      className={`font-size ${styles.steps_img}`}
                                      src={Steps}
                                      alt="send message"
                                    />
                                  ) : null
                                ) : poll.pollQuestionType === "1" ? (
                                  <div>
                                    <MultiChoice
                                      setSingleChoiceResponse={
                                        setSingleChoiceResponse
                                      }
                                      pollResponse={poll.pollResponse}
                                    />
                                  </div>
                                ) : poll.pollQuestionType === "2" ? (
                                  <MultiChoice
                                    pollResponse={poll.pollResponse}
                                    setSingleChoiceResponse={
                                      setSingleChoiceResponse
                                    }
                                  />
                                ) : null}
                              </Col>
                            </Row>
                          </Col>

                          <div className="mt-5">
                            <Button
                              type="secondary"
                              htmlType="submit"
                              onClick={onCancel}
                              className="mr-4"
                            >
                              {CANCEL}
                            </Button>
                            <Button
                              type="primary"
                              className="md"
                              onClick={onPreview}
                            >
                              {PREVIEW}
                            </Button>
                          </div>
                        </Form>
                      </Col>
                    </Row>
                  </div>
                </Content>
              </Layout>
            </Layout>
          </Row>
        </React.Fragment>
      ) : (
        <PreviewPoll
          poll={poll}
          setPollsDate={setPollsDate}
          backtoEdit={backtoEdit}
          showError={showError}
          isImage={isImage}
          attachmentName={attachmentName}
          data={props}
          error={error}
        />
      )}
    </React.Fragment>
  );
};

export default CreatePoll;
