import React from "react";
import Grid from "../table/Table";
import { Row, Col, Button, notification } from "antd";
import GridPopover from "../table/Popover";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import moment from "moment";
import authHeader from "../../services/auth-header";
import { EMAIL, FIRST_NAME, LAST_NAME, MOBILE, STATUS } from "../../labels";
import { STATUS_ACTIVE, STATUS_INACTIVE } from "../../values";
import { ADD_SEEDED_USER } from "../../headings";
import {
  STATUS_CHANGED_NOTIFICATION_PART1,
  STATUS_CHANGED_NOTIFICATION_PART2,
} from "../../messages";

class AssignedUsers extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
    };
  }

  componentDidMount() {
    let { seededUsers } = this.props;
    this.setState({ data: seededUsers });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (
      this.props.seededUsers !== nextProps.seededUsers &&
      nextProps.seededUsers.length > 0
    ) {
      this.setState({ data: nextProps.seededUsers });
    }
  }

  statusClicked = (record, via) => {
    const data = moment(record.endDate).isValid()
      ? {
          first_name: record.firstName,
          last_name: record.lastName,
          email: record.email,
          mobile_number: record.mobile,
          status_id: via === "Active" ? 2 : 1,
          start_date: record.startDate
            ? moment(record.startDate).format("DD-MM-YYYY")
            : "",
          end_date: record.endDate
            ? moment(record.endDate).format("DD-MM-YYYY")
            : "",
          organization_id: this.props.orgID,
        }
      : {
          first_name: record.firstName,
          last_name: record.lastName,
          email: record.email,
          mobile_number: record.mobile,
          status_id: via === "Active" ? 2 : 1,
          start_date: record.startDate
            ? moment(record.startDate).format("DD-MM-YYYY")
            : "",
          organization_id: this.props.orgID,
        };
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.put(BASE_URL + "seededUser/", data, options).then(() => {
      notification.config({
        placement: "bottomLeft",
      });
      notification.open({
        description:
          STATUS_CHANGED_NOTIFICATION_PART1 +
          record.firstName +
          STATUS_CHANGED_NOTIFICATION_PART2,
      });
      this.props.statusOnClicked();
    });
  };

  render() {
    const columns = [
      {
        title: FIRST_NAME,
        dataIndex: "firstName",
        key: "firstName",
        width: "22%",
        render: (firstName) => {
          return {
            children: firstName,
            props: {
              "data-title": "First Name",
            },
          };
        },
      },
      {
        title: LAST_NAME,
        dataIndex: "lastName",
        key: "lastName",
        width: "22%",
        render: (lastName) => {
          return {
            children: lastName,
            props: {
              "data-title": "Last Name",
            },
          };
        },
      },
      {
        title: MOBILE,
        dataIndex: "mobile",
        key: "mobile",
        width: "22%",
        render: (mobile) => {
          return {
            children: mobile.includes("+") ? mobile : "+" + mobile,
            props: {
              "data-title": "Mobile",
            },
          };
        },
      },
      {
        title: EMAIL,
        dataIndex: "email",
        key: "email",
        width: "21%",
        render: (email) => {
          return {
            children: email,
            props: {
              "data-title": "Email",
            },
          };
        },
      },
      {
        title: STATUS,
        dataIndex: "",
        key: "status2",
        width: "10%",
        render: (record) => {
          return {
            children:
              record.status2 === 1 || record.status2 === "1" ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {STATUS_ACTIVE}
                </div>
              ) : (
                <div className="secondary-tag-rounded tag">
                  {STATUS_INACTIVE}
                </div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: (record) => {
          return (
            <GridPopover
              handleEditUser={this.props.handleEditUser}
              handleDelete={this.props.handleDelete}
              user={record}
              seededUsers={this.props.seededUsers}
              seededUser
              viaEdit={this.props.viaEdit}
            />
          );
        },
      },
    ];
    return (
      <div className="w-100" data-test="addSeededuser">
        {this.props.fromStepTwo ? (
          <Col className="d-flex align-items-center justify-content-end">
            <Button
              className="mr-4"
              type="secondary"
              onClick={() =>
                this.props.fromStepTwo
                  ? this.props.showAddSeededUserForm()
                  : this.props.addSeededUser()
              }
            >
              {ADD_SEEDED_USER}
            </Button>
          </Col>
        ) : null}
        <Grid
          rowKey="unique_identifier"
          columns={columns}
          data={this.state.data}
        />
      </div>
    );
  }
}

export default AssignedUsers;
