import React from "react";
import { Popover, Button } from "antd";
import DeleteModal from "../../components/modal/DeleteModal";
import dotsicon from "../../assets/icons/dots-icon.svg";
import { EDIT } from "../../headings";

const content = (props) => (
  <div>
    <ul>
      <li>
        {" "}
        {props.seededUser ? (
          props.viaEdit ? (
            <Button onClick={() => props.handleEditUser(props.user.email)}>
              {EDIT}
            </Button>
          ) : (
            <Button onClick={() => props.handleEditUser(props.user.userId)}>
              {EDIT}
            </Button>
          )
        ) : (
          <a href="">{EDIT}</a>
        )}
      </li>

      <li>
        {props.seededUser ? (
          <DeleteModal
            user={props.user}
            handleDelete={props.handleDelete}
            seededUsers={props.seededUsers}
            seededUser
            data={props}
            viaEdit={props.viaEdit}
          />
        ) : (
          <DeleteModal data={props} />
        )}
      </li>
    </ul>
  </div>
);

class GridPopover extends React.Component {
  state = {
    visible: false,
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props !== nextProps && this.state.visible) {
      this.hide();
    }
  }

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="ico-grid-dots">
            <img src={dotsicon} alt="DotIcon" className="pr-4" />
          </span>
        </Popover>
      </>
    );
  }
}

export default GridPopover;
