import React from "react";
import { TreeSelect } from "antd";
import { AM_WEB, ORG_CC, APP_USER, IDB_USER, SUPER_USER } from "../../values";
import { APPLY_FILTER } from "../../headings";

const { SHOW_PARENT } = TreeSelect;
const treeData = [
  {
    title: AM_WEB,
    value: "1",
    key: "1",
  },
  {
    title: ORG_CC,
    value: "2",
    key: "2",
  },
  {
    title: APP_USER,
    value: "3",
    key: "3",
  },
  {
    title: IDB_USER,
    value: "4",
    key: "4",
  },
  {
    title: SUPER_USER,
    value: "5",
    key: "5",
  },
];

class MultiSelection extends React.Component {
  state = {
    value: "",
  };

  onChange = (value) => {
    this.setState({ value });
  };

  render() {
    const tProps = {
      treeData,
      onChange: this.props.handlePollStatusOnSelect,
      treeCheckable: true,
      showCheckedStrategy: SHOW_PARENT,
      placeholder: APPLY_FILTER,
      style: {
        width: "100%",
      },
    };
    return <TreeSelect {...tProps} />;
  }
}

export default MultiSelection;
