import React, { useState } from "react";
import { Layout, Steps, Row, Col, Breadcrumb, message, Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import "../hotlineCreation/hotlineCreation.scss";
import CreateOpenlineStepOne from "../openlineForm/CreateOpenlineStepOne";
import CreateOpenlineStepTwo from "../openlineForm/CreateOpenlineStepTwo";
import CreateOpenlineStepThree from "../openlineForm/CreateOpenlineStepThree";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { NavLink } from "react-router-dom";
import OrgSidebar from "../../containers/OrgSidebar";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import {
  CYBER_BULLYING,
  DISCRIMINATION,
  FIGHT,
  OPENLINE_CREATE_STEP1,
  OPENLINE_CREATE_STEP2,
  OPENLINE_CREATE_STEP3,
  RACISM,
  SEXUAL_HARRASMENT,
  SLAP,
  VIOLENCE,
} from "../../values";
import {
  CREATE_OPENLINE_DIRECTORY,
  OPENLINE_DIRECTORY,
  CREATE_OPENLINE_DIRECTORY_ENTRIES,
  STATUS_SET_ACTIVE,
  STATUS_SET_COMPLETE,
  STATUS_SET_SCHEDULED,
} from "../../headings";

const { Content } = Layout;

const { Step } = Steps;

const steps = [
  {
    title: OPENLINE_CREATE_STEP1,
    content: "First-content",
  },
  {
    title: OPENLINE_CREATE_STEP2,
    content: "Second-content",
  },
  {
    title: OPENLINE_CREATE_STEP3,
    content: "Last-content",
  },
];

var CryptoJS = require("crypto-js");
const OpenlineCreation = (props) => {
  const [current, setCurrent] = useState(0);
  const [imageError, setImageError] = useState(false);
  const [loader, setLoader] = useState(false);
  const [stepOneOpenLine, setStepOneOpenLine] = useState({
    openelineName: "",
    description: "",
    status: false,
    statusMessage: "",
    sDate: "",
    tDate: "",
    hotlineIconId: "",
    file: "",
    previewVisible: false,
    previewImage: "",
    previewTitle: "",
    imagename: "",
  });
  const [hotlineErrorUpdated, setHotlineErrorUpdated] = useState("");

  const [stepTwoOpenLine, setStepTwoOpenLine] = useState({
    selectedRows: "",
    selectedRowKeys: [],
    chekedValues: [],
  });

  const next = () => {
    setCurrent(current + 1);
  };

  const prev = () => {
    setCurrent(current - 1);
  };

  const handleStepOneOnChange = (e) => {
    setStepOneOpenLine({ ...stepOneOpenLine, [e.target.id]: e.target.value });
  };

  const handleStepOneOnSelect = (value, name) => {
    setStepOneOpenLine({ ...stepOneOpenLine, [name]: value });
  };

  const onChangeCheckbox = (e) => {
    if (e.target.checked) {
      setStepOneOpenLine({
        ...stepOneOpenLine,
        status: e.target.checked,
        statusMessage: "",
      });
    } else {
      const currentDate = moment();
      const date = stepOneOpenLine.sDate;
      if (date && date[0]?.diff(currentDate, "days") === 0) {
        setStepOneOpenLine({
          ...stepOneOpenLine,
          status: e.target.checked,
          statusMessage: STATUS_SET_ACTIVE,
        });
      } else if (date && date[0]?.diff(currentDate, "days") > 0) {
        setStepOneOpenLine({
          ...stepOneOpenLine,
          status: e.target.checked,
          statusMessage: STATUS_SET_SCHEDULED,
        });
      }
    }
  };

  const handleBeforeUpload = (value) => {
    if (!value.type.includes("image")) {
      setImageError(true);
      message.error(
        `The file that you attemped to upload ${value.name} is not supported`
      );
      return false;
    } else {
      setImageError(false);
    }
    return value.type;
  };

  const handleStepOneUpload = (value, name) => {
    let fileList = value.fileList.slice(-1);
    let filename = value.file.name;
    fileList.forEach(function (file, index) {
      let reader = new FileReader();
      reader.onload = (e) => {
        file.base64 = e.target.result;
        file.response = "Uploaded successfully";
      };
      reader.readAsDataURL(file.originFileObj);
    });
    setStepOneOpenLine({
      ...stepOneOpenLine,
      [name]: fileList,
      imagename: filename,
    });
  };

  const handleReaderloaded = (readerEvt) => {
    let binaryString = readerEvt.target.result;
    setStepOneOpenLine({ ...stepOneOpenLine, file: btoa(binaryString) });
  };

  const handleStepOnePreview = async (file) => {
    file.preview = await getBase64(file.originFileObj);
    setStepOneOpenLine({
      ...stepOneOpenLine,
      previewImage: file.url || file.preview,
      previewVisible: true,
      previewTitle:
        file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
    });
  };

  const handleCancel = (value) =>
    setStepOneOpenLine({ ...stepOneOpenLine, previewVisible: false });

  const setNameValidHandle = (value) => {
    setHotlineErrorUpdated(value);
  };

  const getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const handleStepOneDatePickerChange = (date, name) => {
    const currentDate = moment();
    if (
      date &&
      date[0]?.diff(currentDate, "days") === 0 &&
      !stepOneOpenLine.status
    ) {
      setStepOneOpenLine({
        ...stepOneOpenLine,
        [name]: date,
        statusMessage: STATUS_SET_ACTIVE,
      });
    } else if (
      date &&
      date[0]?.diff(currentDate, "days") > 0 &&
      !stepOneOpenLine.status
    ) {
      setStepOneOpenLine({
        ...stepOneOpenLine,
        [name]: date,
        statusMessage: STATUS_SET_SCHEDULED,
      });
    } else {
      setStepOneOpenLine({
        ...stepOneOpenLine,
        [name]: date,
        statusMessage: "",
      });
    }
  };

  const addIcon = (values, id) => {
    setStepOneOpenLine({
      ...stepOneOpenLine,
      hotlineIconId: id,
      iconHtml: values,
    });
  };

  const getIconText = (id) => {
    switch (id) {
      case "1":
        return SEXUAL_HARRASMENT;
      case "2":
        return VIOLENCE;
      case "3":
        return RACISM;
      case "4":
        return CYBER_BULLYING;
      case "5":
        return FIGHT;
      case "6":
        return DISCRIMINATION;
      case "7":
        return SLAP;
      default:
        return "";
    }
  };

  const handleStepTwoOnSelect = (selectedRowKeys, selectedRows) => {
    let permissions = [];
    const user = JSON.parse(AppStorage.get("user"));
    let difference = selectedRows.filter(
      ({ user_id: id1 }) =>
        !permissions?.some(({ user_id: id2 }) => id2 === id1)
    );

    let per = difference.reduce((accumulator, currentValue) => {
      accumulator.push({
        user_id: currentValue.user_id,
        can_respond: 1,
        can_view_reports: 0,
        created_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
      });
      return accumulator;
    }, []);
    permissions = permissions?.concat(per);
    setStepTwoOpenLine({
      ...stepTwoOpenLine,
      selectedRowKeys: selectedRowKeys,
      selectedRows: selectedRows,
      chekedValues: permissions,
    });
  };

  const createHotLine = async (isSaveAsDraft=false) => {
    const user = JSON.parse(AppStorage.get("user"));
    const token = authHeader();

    const data = {
      create_openline: {
        name: stepOneOpenLine.openelineName,
        help_text: stepOneOpenLine.description,
        openline_image: stepOneOpenLine.file ? stepOneOpenLine.file[0].base64 : null,
        openline_image_name: stepOneOpenLine.imagename,
        status_id: isSaveAsDraft ? 4 : 1,
        start_date: stepOneOpenLine.sDate[0]
          ? moment(stepOneOpenLine.sDate[0]).format("DD-MM-YYYY")
          : null,
        end_date: stepOneOpenLine.sDate[1]
          ? moment(stepOneOpenLine.sDate[1]).format("DD-MM-YYYY")
          : null,
        created_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
      },
      permissions: stepTwoOpenLine.chekedValues,
    };

    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    console.log("data : ", data)
    axios.post(BASE_URL + "openline/", data, options).then(() => {
      window.location = "/orgcc/openlinedirectory";
    });
  };

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0" data-test="hotlineCreation">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={props} />
            <Content className="">
              <Row className="pt-3">
                <Breadcrumb separator=">">
                  <NavLink to="/orgcc/openlinedirectory">
                    <Breadcrumb.Item>{OPENLINE_DIRECTORY}</Breadcrumb.Item>
                  </NavLink>
                  <Breadcrumb.Item>{CREATE_OPENLINE_DIRECTORY}</Breadcrumb.Item>
                </Breadcrumb>
              </Row>
              <Row className="py-3">
                <h1 className="mb-0">{CREATE_OPENLINE_DIRECTORY_ENTRIES}</h1>
              </Row>
              <Steps className="mt-2" current={current}>
                {steps.map((item) => (
                  <Step key={item.title} title={item.title} />
                ))}
              </Steps>

              <Spin spinning={loader} size="large">
                <div className="mid-content mt-4">
                  <Row>
                    <Col span={24}>
                      <div className="steps-content">
                        {current == 0 ? (
                          <CreateOpenlineStepOne
                            next={next}
                            imageError={imageError}
                            stepOneOpenLineInfo={stepOneOpenLine}
                            handleStepOneOnChange={handleStepOneOnChange}
                            handleStepOneOnSelect={handleStepOneOnSelect}
                            handleStepOneUpload={handleStepOneUpload}
                            onChangeCheckbox={onChangeCheckbox}
                            handleBeforeUpload={handleBeforeUpload}
                            handleStepOnePreview={handleStepOnePreview}
                            handleCancel={handleCancel}
                            handleStepOneDatePickerChange={
                              handleStepOneDatePickerChange
                            }
                            addIcon={addIcon}
                            getIconText={getIconText}
                            setNameValidHandle={setNameValidHandle}
                            hotlineErrorUpdated={hotlineErrorUpdated}
                            data-test="CreateHotlineStepOne"
                            createOpenline={createHotLine}
                          />
                        ) : current == 1 ? (
                          <CreateOpenlineStepTwo
                            next={next}
                            prev={prev}
                            stepOneHotLine={stepOneOpenLine}
                            stepTwoOpenLine={stepTwoOpenLine}
                            handleStepTwoOnSelect={handleStepTwoOnSelect}
                            createOpenline={createHotLine}
                          />
                        ) : current == 2 ? (
                          <CreateOpenlineStepThree
                            prev={prev}
                            createNewHotLine={createHotLine}
                            stepOneOpenLine={stepOneOpenLine}
                            stepTwoOpenLine={stepTwoOpenLine}
                            getIconText={getIconText}
                            handleStepOnePreview={handleStepOnePreview}
                            handleCancel={handleCancel}
                          />
                        ) : (
                          ""
                        )}
                      </div>
                    </Col>
                  </Row>
                </div>
              </Spin>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};
export default OpenlineCreation;
