import React, { useEffect, useState } from "react";
import { Table, Col, Checkbox } from "antd";

import "./table.scss";
import { ACTIONS, NAME, RESPOND } from "../../labels";

const OpenlinePermission = ({ data, deleteRecord, handleCheck }) => {
  const [permissionData, setPermissionaData] = useState();

  useEffect(() => {
    changeDataSource();
  }, [data]);

  const changeDataSource = () => {
    setPermissionaData(data);
  };

  const onDelete = (key, e) => {
    e.preventDefault();
    deleteRecord(key);
  };

  const columns = [
    {
      title: NAME,
      dataIndex: "first_name",
      key: "first_name",
      width: "30%",
      render: (record, response) => {
        return {
          children: response?.user?.first_name,
          props: {
            "data-title": "Name",
          },
        };
      },
    },
    {
      title: RESPOND,
      dataIndex: "can_respond",
      key: "can_respond",
      width: "20%",
      editable: true,
      render: (res, record) => (
        <Col span={12} className="d-flex justify-content-end changeColor">
          <Checkbox
            value="reports"
            checked={res}
            onChange={(e) => handleCheck(record)}
            disabled
          ></Checkbox>
        </Col>
      ),
    },
    // {
    //   title: "Reports",
    //   dataIndex: "can_view_reports",
    //   key: "can_view_reports",
    //   width: "20%",
    //   editable: true,
    //   render: (res) => (
    //     <Col span={12} className="d-flex justify-content-end">
    //       <Checkbox value="reports" checked={res}></Checkbox>
    //     </Col>
    //   ),
    // },
    {
      title: ACTIONS,
      dataIndex: "user_id",
      key: "user_id",
      width: "20%",
      render: (text, record) => (
        <span
          onClick={(e) => {
            onDelete(record, e);
          }}
          className="uploaded-icon icon icon-close d-inline-flex justify-content-end align-items-center"
        ></span>
      ),
    },
  ];
  var locale = {
    emptyText: "  ",
  };
  return (
    <div className="no-bordered-grid">
      <Table
        className="no-bg-table"
        locale={locale}
        rowClassName={() => "editable-row"}
        columns={columns}
        dataSource={permissionData}
        rowKey={"rowkey"}
      />
    </div>
  );
};

export default OpenlinePermission;
