import React from "react";
import { TreeSelect } from "antd";
import { ALL } from "../../values";
import { SELECT_HOTLINE, SELECT_OPENLINE } from "../../messages";

const { SHOW_PARENT } = TreeSelect;

class MultiSelection extends React.Component {
  state = {
    value: ALL,
    treeData: [],
    treeLength: 0,
    selectedLines: [],
  };

  componentDidUpdate(nextProps, prevState) {
    if (
      this.props.lines !== nextProps.lines ||
      prevState.treeLength !== this.state.treeLength
    ) {
      const linesData = [
        {
          title: ALL,
          value: ALL,
          key: ALL,
          disableCheckbox: false,
        },
      ];
      this.props.lines.map((value) => {
        if (this.state.treeLength < 5) {
          linesData.push({
            title: value.line_name,
            value: value.line_id,
            key: value.line_id,
            start_date: value.start_date,
            disableCheckbox: false,
          });
          this.props.setMaxLimitReached(false);
        } else {
          linesData.push({
            title: value.line_name,
            value: value.line_id,
            key: value.line_id,
            start_date: value.start_date,
            disableCheckbox: this.state.selectedLines.includes(value.line_id)
              ? false
              : true,
          });
          if (!this.props.isAllSelected) {
            this.props.setMaxLimitReached(true);
          }
        }
      });
      this.setState({ treeData: linesData });
    }
    if (this.props.data.path !== nextProps.data.path) {
      this.setState({ value: ALL });
    }
  }

  onChange = (e) => {
    this.setState({
      treeLength: e.length,
      selectedLines: e.filter((value) => value !== "All"),
    });
    var data = this.state.treeData;
    let filteredData = [];
    if (e.length > 1 && e[0] === "All") {
      filteredData = e.filter((filter) => filter !== "All");
    } else if (e.length > 1 && e.includes("All")) {
      filteredData = e.filter((filter) => filter === "All");
    } else {
      filteredData = e;
    }
    if (filteredData.length === 0) {
      this.setState({ value: ALL });
    } else {
      this.setState({ value: filteredData });
    }
    const selectedLines = [];
    data.forEach((item) => {
      filteredData.forEach((line) => {
        if (item.value === line) {
          selectedLines.push({
            line_name: item.title,
            line_id: item.value,
            start_date: item.start_date,
          });
        }
      });
    });
    this.props.setSelectedLines(selectedLines);
  };

  render() {
    const tProps = {
      treeData: this.state.treeData,
      value: this.state.value,
      onChange: this.onChange,
      treeCheckable: true,
      placeholder: this.props.data.path.includes("hotline")
        ? SELECT_HOTLINE
        : SELECT_OPENLINE,
      showCheckedStrategy: SHOW_PARENT,
      style: {
        width: "100%",
      },
    };
    return <TreeSelect {...tProps} />;
  }
}

export default MultiSelection;
