import React, { useEffect, useState } from "react";
import { mergePoll } from "../../utils/defaultpoll";
import { connect } from "react-redux";
import { getPollAsync } from "../../actions/poll";
import ShowAllPollView from "./ShowAllPollView";

const PollContainerView = (props) => {
  const dafaultValue = {status_id : [5, 8]}
  const [loader, setLoader] = useState(false);
  const [statusClicked, setStatusClicked] = useState(false);
  const [filter, setFilter] = useState(dafaultValue);
  const [search, setSearch] = useState("");
  const [checked, setChecked] = useState(false);
  const [totalPolls, setTotalPolls] = useState([]);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getPollAsync(filter, search));
  }, [statusClicked, filter, search]);

  useEffect(() => {
    if (props.pollsdata === null) {
      setLoader(true);
    } else if (props.pollsdata !== null) {
      setLoader(false);
    }
    const polls = props.pollsdata !== null ? props.pollsdata : [];
    const isInactivePollPresent = filter?.status_id?.includes(4);
    if (
      search.length > 0 ||
      (filter?.status_id?.length > 0 && !isInactivePollPresent)
    ) {
      setTotalPolls(polls);
    } else {
      setTotalPolls(mergePoll(polls));
    }
  }, [props.pollsdata]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  const setSearchValue = (value) => {
    setSearch(value);
  };

  const setFilterOptions = (value) => {
    setFilter(value);
  };

  const handleCheck = (data) => {
    setChecked(!checked);
    setFilter(data);
  };

  return (
    <ShowAllPollView
      polls={totalPolls}
      data={props}
      loader={loader}
      checked={checked}
      setLoader={setLoader}
      statusOnClicked={statusOnClicked}
      setFilterOptions={setFilterOptions}
      setSearchValue={setSearchValue}
      handleCheck={handleCheck}
    />
  );
};

function mapStateToProps(state) {
  const { pollsdata } = state.poll;
  return {
    pollsdata,
  };
}

export default connect(mapStateToProps)(PollContainerView);
