import React, { useState } from "react";
import { Layout, Steps, Row, Col, Breadcrumb, Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import "../hotlineCreation/hotlineCreation.scss";
import CreateHotlineStepOne from "../form/CreateHotlineStepOne";
import CreateHotlineStepTwo from "../form/CreateHotlineStepTwo";
import CreateHotlineStepThree from "../form/CreateHotlineStepThree";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { NavLink } from "react-router-dom";
import OrgSidebar from "../../containers/OrgSidebar";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import {
  CREATE_HOTLINE_DIRECTORY,
  HOTLINE_DIRECTORY,
  CREATE_HOTLINE_DIRECTORY_ENTRIES,
  STATUS_SET_ACTIVE,
  STATUS_SET_COMPLETE,
  STATUS_SET_SCHEDULED,
} from "../../headings";
import {
  CYBER_BULLYING,
  DISCRIMINATION,
  FIGHT,
  RACISM,
  SEXUAL_HARRASMENT,
  SLAP,
  VIOLENCE,
  HOTLINE_CREATE_STEP1,
  HOTLINE_CREATE_STEP2,
  HOTLINE_CREATE_STEP3,
} from "../../values";

const { Content } = Layout;

const { Step } = Steps;

const steps = [
  {
    title: HOTLINE_CREATE_STEP1,
    content: "First-content",
  },
  {
    title: HOTLINE_CREATE_STEP2,
    content: "Second-content",
  },
  {
    title: HOTLINE_CREATE_STEP3,
    content: "Last-content",
  },
];

var CryptoJS = require("crypto-js");
const HotlineCreation = (props) => {
  const [current, setCurrent] = useState(0);
  const [loader, setLoader] = useState(false);
  const [stepOneHotLine, setStepOneHotLine] = useState({
    directoryName: "",
    description: "",
    status: false,
    statusMessage: "",
    sDate: "",
    tDate: "",
    hotlineIconId: "",
    iconHtml: "",
  });

  const [hotlineErrorUpdated, setHotlineErrorUpdated] = useState("");
  const [stepTwoHotLine, setStepTwoHotLine] = useState({
    selectedRows: "",
    selectedRowKeys: [],
    chekedValues: [],
  });

  const next = () => {
    setCurrent(current + 1);
  };

  const prev = () => {
    setCurrent(current - 1);
  };

  const handleStepOneOnChange = (e) => {
    setStepOneHotLine({ ...stepOneHotLine, [e.target.id]: e.target.value });
  };

  const handleStepOneOnSelect = (value, name) => {
    setStepOneHotLine({ ...stepOneHotLine, [name]: value });
  };

  const onChangeCheckbox = (e) => {
    if (e.target.checked) {
      setStepOneHotLine({
        ...stepOneHotLine,
        status: e.target.checked,
        statusMessage: "",
      });
    } else {
      const currentDate = moment();
      const date = stepOneHotLine.sDate;
      if (date && date[0]?.diff(currentDate, "days") === 0) {
        setStepOneHotLine({
          ...stepOneHotLine,
          status: e.target.checked,
          statusMessage: STATUS_SET_ACTIVE,
        });
      } else if (date && date[0]?.diff(currentDate, "days") > 0) {
        setStepOneHotLine({
          ...stepOneHotLine,
          status: e.target.checked,
          statusMessage: STATUS_SET_SCHEDULED,
        });
      }
    }
  };

  const handleStepOneDatePickerChange = (date, name) => {
    const currentDate = moment();
    if (
      date &&
      date[0]?.diff(currentDate, "days") === 0 &&
      !stepOneHotLine.status
    ) {
      setStepOneHotLine({
        ...stepOneHotLine,
        [name]: date,
        statusMessage: STATUS_SET_ACTIVE,
      });
    } else if (
      date &&
      date[0]?.diff(currentDate, "days") > 0 &&
      !stepOneHotLine.status
    ) {
      setStepOneHotLine({
        ...stepOneHotLine,
        [name]: date,
        statusMessage: STATUS_SET_SCHEDULED,
      });
    } else {
      setStepOneHotLine({
        ...stepOneHotLine,
        [name]: date,
        statusMessage: "",
      });
    }
  };

  const setNameValidHandle = (value, name) => {
    setHotlineErrorUpdated(value);
  };

  const addIcon = (values, id) => {
    setStepOneHotLine({
      ...stepOneHotLine,
      hotlineIconId: id,
      iconHtml: values,
    });
  };

  const getIconText = (id) => {
    switch (id) {
      case "1":
        return SEXUAL_HARRASMENT;
      case "2":
        return VIOLENCE;
      case "3":
        return RACISM;
      case "4":
        return CYBER_BULLYING;
      case "5":
        return FIGHT;
      case "6":
        return DISCRIMINATION;
      case "7":
        return SLAP;
      default:
        return "";
    }
  };

  const handleStepTwoOnSelect = (selectedRowKeys, selectedRows) => {
    let permissions = [];
    const user = JSON.parse(AppStorage.get("user"));
    let difference = selectedRows.filter(
      ({ user_id: id1 }) =>
        !permissions?.some(({ user_id: id2 }) => id2 === id1)
    );

    let per = difference.reduce((accumulator, currentValue) => {
      accumulator.push({
        user_id: currentValue.user_id,
        can_respond: 1,
        can_view_reports: 0,
        created_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
      });
      return accumulator;
    }, []);
    permissions = permissions?.concat(per);
    setStepTwoHotLine({
      ...stepTwoHotLine,
      selectedRowKeys: selectedRowKeys,
      selectedRows: selectedRows,
      chekedValues: permissions,
    });
  };

  const createHotLine = async (isSaveAsDraft=false) => {
    const user = JSON.parse(AppStorage.get("user"));
    const token = authHeader();

    const data = {
      create_hotline: {
        name: stepOneHotLine.directoryName,
        help_text: stepOneHotLine.description,
        start_date: stepOneHotLine.sDate[0]
          ? moment(stepOneHotLine.sDate[0]).format("DD-MM-YYYY")
          : null,
        end_date: stepOneHotLine.sDate[1]
          ? moment(stepOneHotLine.sDate[1]).format("DD-MM-YYYY")
          : null,
        status_id: isSaveAsDraft ? 4 : 1,
        hotline_icon_id: stepOneHotLine.hotlineIconId ? parseInt(stepOneHotLine.hotlineIconId) : "",
        created_by: parseInt(
          CryptoJS.AES.decrypt(
            user.user_details.org_user_id,
            "anonymoose"
          ).toString(CryptoJS.enc.Utf8)
        ),
      },
      permissions: stepTwoHotLine.chekedValues,
    };

    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    console.log("data : ", data)
    axios.post(BASE_URL + "hotline/", data, options).then(() => {
      window.location = "/orgcc/hotlinedirectory";
    });
  };

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0" data-test="hotlineCreation">
        <Layout>
          <Layout>
            <OrgSidebar
              amORGUser
              data={props.noHotlines ? props.data : props}
            />
            <Content className="">
              <Row className="pt-3">
                <Breadcrumb separator=">">
                  <NavLink to="/orgcc/hotlinedirectory">
                    <Breadcrumb.Item>{HOTLINE_DIRECTORY}</Breadcrumb.Item>
                  </NavLink>
                  <Breadcrumb.Item>{CREATE_HOTLINE_DIRECTORY}</Breadcrumb.Item>
                </Breadcrumb>
              </Row>
              <Row className="py-3">
                <h1 className="mb-0">{CREATE_HOTLINE_DIRECTORY_ENTRIES}</h1>
              </Row>
              <Steps className="mt-2" current={current}>
                {steps.map((item) => (
                  <Step key={item.title} title={item.title} />
                ))}
              </Steps>
              <Spin spinning={loader} size="large">
                <div className="mid-content mt-4">
                  <Row>
                    <Col span={24}>
                      <div className="steps-content">
                        {current == 0 ? (
                          <CreateHotlineStepOne
                            next={next}
                            stepOneHotLineInfo={stepOneHotLine}
                            handleStepOneOnChange={handleStepOneOnChange}
                            handleStepOneOnSelect={handleStepOneOnSelect}
                            handleStepOneDatePickerChange={
                              handleStepOneDatePickerChange
                            }
                            onChangeCheckbox={onChangeCheckbox}
                            setNameValidHandle={setNameValidHandle}
                            addIcon={addIcon}
                            getIconText={getIconText}
                            hotlineErrorUpdated={hotlineErrorUpdated}
                            data-test="CreateHotlineStepOne"
                            createHotLine={createHotLine}
                          />
                        ) : current == 1 ? (
                          <CreateHotlineStepTwo
                            next={next}
                            prev={prev}
                            stepOneHotLine={stepOneHotLine}
                            stepTwoHotLine={stepTwoHotLine}
                            handleStepTwoOnSelect={handleStepTwoOnSelect}
                            createHotLine={createHotLine}
                          />
                        ) : current == 2 ? (
                          <CreateHotlineStepThree
                            prev={prev}
                            createNewHotLine={createHotLine}
                            stepOneHotLine={stepOneHotLine}
                            stepTwoHotLine={stepTwoHotLine}
                            getIconText={getIconText}
                          />
                        ) : (
                          ""
                        )}
                      </div>
                    </Col>
                  </Row>
                </div>
              </Spin>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};
export default HotlineCreation;
