import React from "react";
import ReactDOM from "react-dom";
import axios from "axios";
import { LOADING, SELECTED_PLACE } from "../../messages";
import { SearchOutlined } from "@ant-design/icons";
import { Col } from "antd";
import Geocode from "react-geocode";
import { googleMapKey } from "../../userconfig";
// set Google Maps Geocoding API for purposes of quota management. Its optional but recommended.
Geocode.setApiKey(googleMapKey);
// Enable or disable logs. Its optional.
Geocode.enableDebug();

const mapStyles = {
  map: {
    position: "absolute",
    width: "100%",
    height: "100%",
  },
};

class CurrentLocation extends React.Component {
  constructor(props) {
    super(props);
    const { lat, lng } = this.props;
    this.autocomplete = null;
    this.state = {
      currentLocation: {
        lat: lat,
        lng: lng,
        address: "",
      },
      markerArray : []
    };
    this.setAutocomplete = this.setAutocomplete.bind(this);
  }

  componentDidMount() {
    const { lat, lng } = this.state.currentLocation;

    console.log(lat, "=====" , lng);
    console.log("thisss", this.state.currentLocation)
    //setTimeout(()=>{
      this.loadMap(parseFloat(lat),parseFloat(lng));
      if(!this.props.locationOnBroadCastList || this.props.locationOnBroadCastList == ""){
      this.recenterMap(lat, lng);
      this.initPlaceAPI();
      }
   // }, 100);
   //this.addMarkerOnFirstLoad(lat, lng);
  }

  onMarkerDragEnd(props, marker, coords) {
    console.log("test this today ");
    const newLat = coords.latLng.lat();
    const newLng = coords.latLng.lng();
    Geocode.fromLatLng( newLat , newLng ).then(
      response => {
      const address = response.results[0].formatted_address;
          this.setState({
          selectedPlace: address,
          activeMarker: marker,
          showingInfoWindow: true,
        });
        this.props.getCurrenLocationName(address);
        this.props.getMapLocation(newLat, newLng, address);
        console.log("Test this to check now", address,  newLat, newLng);
      });
  }


  addMarkerOnFirstLoad = (lat, lng) => {

    const map = this.map;
    const google = this.props.google;
    const maps = google.maps;

    var myLatlng = new google.maps.LatLng(lat,lng);

    var marker = new google.maps.Marker({
      map: map,
      position: myLatlng,
      title:"",
      draggable: true
    });
    marker.addListener('dragend', this.props.onDragendEvent);
    const markerArrayData =  this.state.markerArray;
    markerArrayData.push(marker);
    this.setState({markerArray: markerArrayData});
  }

  componentDidUpdate(prevProps) {
    if (prevProps?.lat !== this.props?.lat) {
      this.loadMap(this.props.lat, this.props.lng);
      this.recenterMap(this.props.lat, this.props.lng);
      
      this.setState({
        currentLocation: {
          lat: this.props.lat,
          lng: this.props.lng,
          address: "",
        },
      });
      const myApiKey = "AIzaSyBtKD5VNiKZxh9cUkN0l0QmKp50OF5BGSI";
      axios
        .get(
          "https://maps.googleapis.com/maps/api/geocode/json?latlng=" +
            this.props.lat +
            "," +
            this.props.lng +
            "&key=" +
            myApiKey
        )
        .then((response) => {
          this.setState({
            currentLocation: {
              ...this.state.currentLocation,
              address: response,
            },
          });
        });
      this.loadMap(this.props.lat, this.props.lng);
      this.recenterMap(this.props.lat, this.props.lng);
    }
  }

  loadMap(lat, lng) {
    if (this.props && this.props.google) {
      // checks if google is available
      const { google } = this.props;
      const maps = google.maps;
      const mapRef = this.refs.map;

      // reference to the actual DOM element
      const node = ReactDOM.findDOMNode(mapRef);
      let { zoom } = this.props;
      console.log("in load map", lat, "===", lng);
      const center = new maps.LatLng(lat, lng);
      const mapConfig = Object.assign({}, { center: center, zoom: zoom });
      // maps.Map() is constructor that instantiates the map
      this.map = new maps.Map(node, mapConfig);
      this.addMarkerOnFirstLoad(lat, lng);

      this.map.addListener("click", (e) => {
        const myApiKey = "AIzaSyBtKD5VNiKZxh9cUkN0l0QmKp50OF5BGSI";
        axios
          .get(
            "https://maps.googleapis.com/maps/api/geocode/json?latlng=" +
              e.latLng.lat() +
              "," +
              e.latLng.lng() +
              "&key=" +
              myApiKey
          )
          .then((response) => {
            this.setState({
              currentLocation: {
                lat: e.latLng.lat(),
                lng: e.latLng.lng(),
                address: response,
              },
            });
            this.props.getMapLocation(e.latLng.lat(), e.latLng.lng(), response);
          });
      });
    }
  }

  recenterMap(lat, lng) {
    const map = this.map;
    const google = this.props.google;
    const maps = google.maps;

    if (map) {
      let center = new maps.LatLng(lat, lng);
      map.panTo(center);
    }
  }

  initPlaceAPI() {
    const placeInputRef = this.refs.placeInputRef;
    const placeNode = ReactDOM.findDOMNode(placeInputRef);
    const map = this.map;
    // this.autocomplete = new google.maps.places.Autocomplete(placeNode, {"fields": ["formatted_address", "address_components", "geometry", "icon", "name"],"types": ["geocode"]});
    // //this.autocomplete.setFields(["formatted_address"]);
    // this.autocomplete.bindTo("bounds", map);
    // this.autocomplete.addListener("place_changed", this.setAutocomplete);
    console.log("----initPlaceAPI----- first load-----");


      
    this.autocomplete = new google.maps.places.Autocomplete(placeNode, {
      fields: [
        "formatted_address",
        "address_components",
        "geometry",
        "icon",
        "name",
      ],
    });
    //this.autocomplete.setFields(["formatted_address"]);
    //this.autocomplete.bindTo("bounds", map);
    this.autocomplete.addListener("place_changed", this.setAutocomplete);
  }

  removeMarker = (markerArrayData) =>{

    for(let i= 0; i < markerArrayData.length; i++ ){
      markerArrayData[i].setMap(null);
        //marker.setVisible(false);
        console.log("check the remove marker ", i);
     }
  }



  setAutocomplete() {
    const map = this.map;

    //const markesss = new window.google.maps.Marker();
    // markesss.setMap(null);
    // markesss.setVisible(false);

    let markerArrayData = this.state.markerArray;
    console.log("-----markerArrayData----", markerArrayData)
    this.removeMarker(markerArrayData);
  

    let place = this.autocomplete.getPlace();

    if (!place.geometry || !place.geometry.location) {
      // User entered the name of a Place that was not suggested and
      // pressed the Enter key, or the Place Details request failed.
      window.alert("No details available for input: '" + place.name + "'");
      return;
    }

    const marker = new google.maps.Marker({
      map: map,
      position: place.geometry.location,
      title: place.formatted_address,
      draggable: true,
    });
    marker.addListener('dragend', this.props.onDragendEvent);

    markerArrayData.push(marker);
    this.setState({markerArray: markerArrayData});

    var infowindow = new google.maps.InfoWindow();
    marker.addListener("click", () => {
      infowindow.setContent(place.name);
      infowindow.open(map, marker);
    });

    //If the place has a geometry, then present it on a map.
    if (place.geometry.viewport) {
      map.fitBounds(place.geometry.viewport);
    } else {
      map.setCenter(place.geometry.location);
      map.setZoom(17);
    }

    this.setState({
      currentLocation: {
        lat: place.geometry.location.lat(),
        lng: place.geometry.location.lng(),
        address: place.formatted_address,
      },
    });
    this.props.getMapLocation(
      place.geometry.location.lat(),
      place.geometry.location.lng(),
      place.formatted_address
    );
  }

  renderChildren() {
    const { children } = this.props;
    if (!children) return;
    return React.Children.map(children, (c) => {
      if (!c) return;
      return React.cloneElement(c, {
        map: this.map,
        google: this.props.google,
        mapCenter: this.state.currentLocation,
      });
    });
  }

  render() {
    const style = Object.assign({}, mapStyles.map);
    return (
      <div className="search-wrapper">
        {!this.props.locationOnBroadCastList && (
          <Col span={24} className="mb-2 search-location">
            <span className="ant-input-group-wrapper ant-input-search">
              <span className="ant-input-wrapper ant-input-group">
                <input
                  type="text"
                  ref="placeInputRef"
                  className="ant-input"
                  placeholder="Enter your address"
                />
                <span className="ant-input-group-addon" />
                <SearchOutlined />
              </span>
            </span>
          </Col>
        )}
        {/* {this.state.place && <div style={{ marginTop: 20, lineHeight: '25px' }}>
            <div style={{ marginBottom: 10 }}><b>{SELECTED_PLACE}</b></div>
            <div><b>Address:</b> {this.state.place.address}</div>
            <div><b>Lat:</b> {this.state.place.lat}</div>
           <div><b>Lng:</b> {this.state.place.lng}</div>
        </div>} */}
        <div style={style} ref="map">
          {LOADING}
        </div>
        {this.renderChildren()}
      </div>
    );
  }
}

export default CurrentLocation;

CurrentLocation.defaultProps = {
  zoom: 14,
};
