import React from "react";
import { InfoWindow, Marker, GoogleApiWrapper } from "google-maps-react";
import Geocode from "react-geocode";
import { googleMapKey } from "../../userconfig";

// set Google Maps Geocoding API for purposes of quota management. Its optional but recommended.
Geocode.setApiKey(googleMapKey);
// Enable or disable logs. Its optional.
Geocode.enableDebug();

import CurrentLocation from "./CurrentLocation";
const style = {
  width: "100%",
  height: "100%",
};

export class MapContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPosition: { lat: 18.609320, lng: 73.769691 },
      showingInfoWindow: false, // Hides or shows the InfoWindow
      activeMarker: {}, // Shows the active marker upon click
      selectedPlace: {},
    };
  }

  componentDidMount() {
    console.log("this.props.locationOnBroadCastList    --", this.props.locationOnBroadCastList, "---latitude--", this.props.latitude, this.props.longitude);
    if(this.props.locationOnBroadCastList){
      const currentPosition = {
        lat: this.props.latitude,
        lng: this.props.longitude,
      };
      this.setState({
        currentPosition: currentPosition,
      });
    }else{
    navigator.geolocation.getCurrentPosition((position)=>{
      const currentPosition = {
        lat: position.coords.latitude,
        lng: position.coords.longitude,
      };
  
      console.log("currentPosition", currentPosition)
      this.setState({
        currentPosition: currentPosition,
      });

    });
  }
  }

  // success(position) {
  //   const currentPosition = {
  //     lat: position.coords.latitude,
  //     lng: position.coords.longitude,
  //   };

  //   console.log("currentPosition", currentPosition)

  //   this.setState({
  //     currentPosition: currentPosition,
  //   });
  // }

  onMarkerClick = (props, marker, e) => {
    console.log("----onMarkerClick---", props, "--marker---", marker);
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true,
    });
    this.props.getCurrenLocationName(props.name);
  };


  onMarkerDragEnd(props, marker, coords) {
    const newLat = coords.latLng.lat();
    const newLng = coords.latLng.lng();
    Geocode.fromLatLng( newLat , newLng ).then(
      response => {
      const address = response.results[0].formatted_address;
          this.setState({
          selectedPlace: address,
          activeMarker: marker,
          showingInfoWindow: true,
        });
        this.props.getCurrenLocationName(address);
        this.props.getMapLocation(newLat, newLng, address);
        console.log("Test this to check now", address,  newLat, newLng);
      });
  }

  onClose = () => {
    this.setState({
      selectedPlace: {},
      activeMarker: {},
      showingInfoWindow: false,
    });
  };

  render() {
    return (
      <CurrentLocation
      locationOnBroadCastList= {this.props.locationOnBroadCastList}
        lat={
          this.props.latitude
            ? this.props.latitude
            : this.state.currentPosition.lat
        }
        lng={
          this.props.longitude
            ? this.props.longitude
            : this.state.currentPosition.lng
        }
        google={this.props.google}
        getMapLocation={this.props.getMapLocation}
        viaModal={this.props.viaModal}
        onDragendEvent={(event)=>{console.log("event ", event);}}
      >
        {/* <Marker
          onClick={this.onMarkerClick}
          name={this.state.selectedPlace.name}
          draggable={true}
          onDragend={(t, map, coord) => this.onMarkerDragEnd(t, map, coord)}
        /> */}
        {this.state.showingInfoWindow && (
          <InfoWindow
            marker={this.state.activeMarker}
            visible={this.state.showingInfoWindow}
            onCloseClick={this.onClose}
          >
            <div>
              <h4>{this.state.selectedPlace.name}</h4>
            </div>
          </InfoWindow>
        )}
      </CurrentLocation>
    );
  }
}
export default GoogleApiWrapper({
  apiKey: googleMapKey,
})(MapContainer);
