import { LOGIN_SUCCESS, LOGIN_FAIL, LOGOUT } from "../actions/types";
import * as AppStorage from "../utils/AppStorage";

var CryptoJS = require("crypto-js");

let decryptedPassword = "";
const localPwd = AppStorage.get("current");
decryptedPassword = localPwd
  ? CryptoJS.AES.decrypt(localPwd, "anonymoose")
  : "";
const user = JSON.parse(AppStorage.get("user"));
const userFName = user
  ? CryptoJS.AES.decrypt(user?.user_details?.first_name, "anonymoose").toString(
      CryptoJS.enc.Utf8
    )
  : "";
const userLName = user
  ? CryptoJS.AES.decrypt(user?.user_details?.last_name, "anonymoose").toString(
      CryptoJS.enc.Utf8
    )
  : "";
const userEmail = user
  ? CryptoJS.AES.decrypt(user?.user_data?.email, "anonymoose").toString(
      CryptoJS.enc.Utf8
    )
  : "";
const userMobile = user
  ? CryptoJS.AES.decrypt(user?.user_data?.mobile_number, "anonymoose").toString(
      CryptoJS.enc.Utf8
    )
  : "";
const amweb = user
  ? CryptoJS.AES.decrypt(user.user_data?.is_am_web_user, "anonymoose").toString(
      CryptoJS.enc.Utf8
    ) === "true"
  : false;
const amcc = user
  ? CryptoJS.AES.decrypt(user.user_data?.is_amcc_user, "anonymoose").toString(
      CryptoJS.enc.Utf8
    ) === "true"
  : false;
const amsuper = user
  ? CryptoJS.AES.decrypt(user.user_data?.is_super_user, "anonymoose").toString(
      CryptoJS.enc.Utf8
    ) === "true"
  : false;
const amorgcc = user
  ? CryptoJS.AES.decrypt(user.user_data?.is_org_cc_user, "anonymoose").toString(
      CryptoJS.enc.Utf8
    ) === "true"
  : false;
const amidb = user
  ? CryptoJS.AES.decrypt(user.user_data?.is_idb_user, "anonymoose").toString(
      CryptoJS.enc.Utf8
    ) === "true"
  : false;
const amseeded = user
  ? CryptoJS.AES.decrypt(user.user_data?.is_seeded_user, "anonymoose").toString(
      CryptoJS.enc.Utf8
    ) === "true"
  : false;
const amapp = user
  ? CryptoJS.AES.decrypt(user.user_data?.is_app_user, "anonymoose").toString(
      CryptoJS.enc.Utf8
    ) === "true"
  : false;
const userData = {
  ...user,
  user_data: {
    ...user?.user_data,
    email: userEmail,
    mobile_number: userMobile,
    is_am_web_user: amweb,
    is_amcc_user: amcc,
    is_super_user: amsuper,
    is_app_user: amapp,
    is_org_cc_user: amorgcc,
    is_idb_user: amidb,
    is_seeded_user: amseeded,
  },
  user_details: {
    ...user?.user_details,
    first_name: userFName,
    last_name: userLName,
  },
};

const password = decryptedPassword.toString(CryptoJS.enc.Utf8);
const initialState = user
  ? {
      isLoggedIn: true,
      user: userData,
      current: password.replaceAll('^"+|"+$', ""),
    }
  : { isLoggedIn: false, user: null, current: null };
export default function (state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case LOGIN_SUCCESS:
      return {
        ...state,
        isLoggedIn: true,
        user: payload.user,
        current: payload.current,
      };
    case LOGIN_FAIL:
      return {
        ...state,
        isLoggedIn: false,
        user: null,
        current: null,
      };
    case LOGOUT:
      return {
        ...state,
        isLoggedIn: false,
        user: null,
        current: null,
      };
    default:
      return state;
  }
}
