import * as AppStorage from "./AppStorage";

var CryptoJS = require("crypto-js");

export const displayNewConvAtTop = (
  updatedConversations,
  newConvo,
  updatedNewConvo
) => {
  let index = 0;
  // create a replica of updatedConversations
  const dummyArray = updatedConversations;
  // return the index of the new incoming conversation from the list of all conversations.
  updatedConversations.map((value) => {
    if (
      value?.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
      newConvo?.conversation_id
    ) {
      return (index = updatedConversations.indexOf(value));
    }
  });
  // remove that new conversation from the dummy list temporarily.
  dummyArray.splice(index, 1);
  // push that removed incoming conversation on top of the list.
  const finalConversations = [updatedNewConvo, ...dummyArray];
  return finalConversations;
};

export const increaseOverallCount = (
  newConvo,
  conversationsToDisplay,
  selectedConv,
  setUnreadCount
) => {
  const user = JSON.parse(AppStorage.get("user"));
  // return only that new incoming message which comes from a user other than logged in user.
  const newConvFromDiffUser = conversationsToDisplay.filter(
    (filter) =>
      filter?.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
        newConvo?.conversation_id &&
      newConvo.created_by !==
        parseInt(
          CryptoJS.AES.decrypt(user.user_data.user_id, "anonymoose").toString(
            CryptoJS.enc.Utf8
          )
        ) &&
      newConvo.conversation_id !== selectedConv
  );
  // return the new incoming message irrespective of any user.
  const incomingConversation = conversationsToDisplay.filter(
    (item) =>
      item?.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
      newConvo?.conversation_id
  );
  for (var item = 0; item < newConvFromDiffUser.length; item++) {
    if (
      incomingConversation[0]?.conversation_id
        .toString()
        .replace(/(<([^>]+)>)/gi, "") ===
        newConvFromDiffUser[item]?.conversation_id
          .toString()
          .replace(/(<([^>]+)>)/gi, "") &&
      incomingConversation[0].unread_messages_count === 0
    ) {
      setUnreadCount((unreadCount) => unreadCount + 1);
    }
  }
};

export const increaseOverallCountDuringFilters = (
  newState,
  allConversations,
  urgencyFilterData,
  statusFilterData,
  lineFilterData,
  searchString,
  newInitiatedConv,
  setUnreadCount,
  setAllConversations,
  conversation
) => {
  if (
    urgencyFilterData.length > 0 ||
    statusFilterData.length > 0 ||
    lineFilterData.length > 0 ||
    searchString.length > 0
  ) {
    const user = JSON.parse(AppStorage.get("user"));
    // return only that new incoming message which comes from a user other than logged in user.
    const newConvFromDiffUser = allConversations?.filter(
      (filter) =>
        filter?.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "") ===
          newState?.conversation_id &&
        newState.created_by !==
          parseInt(
            CryptoJS.AES.decrypt(user.user_data.user_id, "anonymoose").toString(
              CryptoJS.enc.Utf8
            )
          ) &&
        newState?.conversation_id !== conversation.conversation_id
    );
    // return the new incoming message irrespective of any user.
    const incomingConversation = allConversations?.filter(
      (item) =>
        item?.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "") ===
        newState?.conversation_id
    );
    for (var item = 0; item < newConvFromDiffUser.length; item++) {
      if (
        incomingConversation[0]?.conversation_id
          ?.toString()
          .replace(/(<([^>]+)>)/gi, "") ===
          newConvFromDiffUser[item]?.conversation_id
            ?.toString()
            .replace(/(<([^>]+)>)/gi, "") &&
        incomingConversation[0]?.unread_messages_count === 0
      ) {
        if (!newInitiatedConv.current) {
          setUnreadCount((unreadCount) => unreadCount + 1);
        }
        const updatedAllConversations = allConversations.map((item) =>
          item.conversation_id === newState?.conversation_id
            ? {
                ...item,
                unread_messages_count: item.unread_messages_count + 1,
              }
            : item
        );
        setAllConversations(updatedAllConversations);
      }
    }
  }
};

export const unSetFilters = (via) => {
  AppStorage.remove("urgencyLevels");
  AppStorage.remove("status");
  if (via === "hotline") {
    AppStorage.remove("hotline");
  } else {
    AppStorage.remove("openline");
  }
  AppStorage.remove("searchBy");
  AppStorage.remove("ConvID");
};

export const filterSocket = (
  totalFiltersApplied,
  newConvo,
  conversations,
  conversationIds,
  via
) => {
  const hotlineOrOpenlineFilter =
    via === "hotline"
      ? totalFiltersApplied.hotline
      : totalFiltersApplied.openline;
  const hotlineOrOpenlineConvoId =
    via === "hotline" ? newConvo.hotline_id : newConvo.openline_id;
  const hotlineOrOpenlineConvoName =
    via === "hotline" ? newConvo.hotline_name : newConvo.openline_name;
  let totalConversations = [];
  if (
    (totalFiltersApplied.status?.includes(newConvo.status_id.toString()) &&
      totalFiltersApplied.urgency?.includes(
        newConvo.urgency_level_id.toString()
      ) &&
      hotlineOrOpenlineFilter?.includes(hotlineOrOpenlineConvoId.toString()) &&
      totalFiltersApplied.searchBy?.length > 0 &&
      newConvo.conversation_id
        .toLowerCase()
        .includes(totalFiltersApplied.searchBy?.toLowerCase())) ||
    newConvo.responder_conversation_name
      .toLowerCase()
      .includes(totalFiltersApplied.searchBy?.toLowerCase()) ||
    hotlineOrOpenlineConvoName
      .toLowerCase()
      .includes(totalFiltersApplied.searchBy?.toLowerCase()) ||
    newConvo.message
      .toLowerCase()
      .includes(totalFiltersApplied.searchBy?.toLowerCase())
  ) {
    let conversation_id = newConvo.conversation_id.replace(
      new RegExp(totalFiltersApplied.searchBy, "gi"),
      (match) =>
        `<mark style="background: #2769AA; color: white;">${match}</mark>`
    );
    let responder_conversation_name = newConvo.responder_conversation_name.replace(
      new RegExp(totalFiltersApplied.searchBy, "gi"),
      (match) =>
        `<mark style="background: #2769AA; color: white;">${match}</mark>`
    );
    let lineName = hotlineOrOpenlineConvoName.replace(
      new RegExp(totalFiltersApplied.searchBy, "gi"),
      (match) =>
        `<mark style="background: #2769AA; color: white;">${match}</mark>`
    );
    let itemMessage = newConvo.message.replace(
      new RegExp(totalFiltersApplied.searchBy, "gi"),
      (match) =>
        `<mark style="background: #2769AA; color: white;">${match}</mark>`
    );
    const updatedConversation =
      via === "hotline"
        ? {
            ...newConvo,
            conversation_id: conversation_id,
            responder_conversation_name: responder_conversation_name,
            message: itemMessage,
            hotline: {
              name: lineName,
              hotline_id: hotlineOrOpenlineConvoId,
            },
          }
        : {
            ...newConvo,
            conversation_id: conversation_id,
            responder_conversation_name: responder_conversation_name,
            message: itemMessage,
            openline: {
              name: lineName,
              openline_id: hotlineOrOpenlineConvoId,
            },
          };
    if (
      !conversationIds.includes(
        updatedConversation.conversation_id
          ?.toString()
          .replace(/(<([^>]+)>)/gi, "")
      )
    ) {
      totalConversations = [...conversations, updatedConversation];
    } else {
      totalConversations = conversations;
    }
  } else {
    totalConversations = conversations;
  }
  return totalConversations;
};

export function setAppliedFilters(
  urgencyLevels,
  status,
  line,
  setUrgencyFilterData,
  setStatusFilterData,
  setLineFilterData,
  via
) {
  setUrgencyFilterData(urgencyLevels);
  setStatusFilterData(status);
  setLineFilterData(line);
  AppStorage.set(
    "urgencyLevels",
    Object.values(urgencyLevels)?.map((data) => data.value)
  );
  if (via === "hotline") {
    AppStorage.set(
      "hotline",
      Object.values(line)?.map((data) => data.value)
    );
  } else {
    AppStorage.set(
      "openline",
      Object.values(line)?.map((data) => data.value)
    );
  }
  AppStorage.set(
    "status",
    Object.values(status)?.map((data) => data.value)
  );
}

export const createMarkup = (html) => {
  return { __html: html };
};
