import React from "react";
import ReactDOM from "react-dom";
import { TreeSelect } from "antd";

import {
  COMPLETED,
  SCHEDULED,
  STATUS_ACTIVE,
  STATUS_INACTIVE,
} from "../../values";
import { APPLY_FILTER } from "../../headings";

const { SHOW_PARENT } = TreeSelect;
const treeData = [
  {
    title: STATUS_ACTIVE,
    value: "5",
    key: "1",
  },
  {
    title: COMPLETED,
    value: "8",
    key: "4",
  },
];

class MultipleSelectionView extends React.Component {
  state = {
    value: "",
  };

  onChange = (value) => {
    this.setState({ value });
  };

  render() {
    const tProps = {
      treeData,
      onChange: this.props.handlePollStatusOnSelect,
      treeCheckable: true,
      showCheckedStrategy: SHOW_PARENT,
      placeholder: APPLY_FILTER,
      style: {
        width: "100%",
      },
    };
    return <TreeSelect {...tProps} />;
  }
}

export default MultipleSelectionView;
