import React, { useState, useEffect } from "react";
import { Layout, Row, Col, DatePicker, Spin, Select, Alert } from "antd";
import { LoggedinHeader } from "../../containers";
import VerticalChart from "./VerticalChart";
import DoughNutChart from "./DoughNutChart";
import ResolvedUnresolvedChart from "./ResolvedUnresolvedChart";
import { DownloadOutlined, DownOutlined } from "@ant-design/icons";
import MultiSelection from "./MultiSelection";
import IdbSidebar from "./IdbSidebar";
import {
  CHOOSE_DURATION,
  SINCE_BEGINNING,
  MONTHLY,
  YEARLY,
  LAST_60_DAYS,
  TOTAL_CONVERSATIONS,
  CONVERSATIONS_RESOLVED,
  AVERAGE_RESOLVED_TIME,
  RESOLVED_UNRESOLVED_CONVERSATIONS,
  HOTLINE_REPORTS,
  OPENLINE_REPORTS,
  CONVERSATIONS_BASED_ON_URGENCY_LEVEL,
  CONVERSATION_COUNT,
} from "../../headings";
import authHeader from "../../services/auth-header";
import axios from "axios";
import moment from "moment";
import {
  HOTLINE_CONVERSATIONS_FROM,
  TO,
  HOTLINE_CONVERSATIONS_REPORTING,
  OPENLINE_CONVERSATIONS_REPORTING,
  OPENLINE_CONVERSATIONS_FROM,
  TILL_DATE,
  REPORTS_MSG,
} from "../../messages";
import { BASE_URL } from "../../userconfig";
import {
  JANUARY,
  FEBRUARY,
  MARCH,
  APRIL,
  MAY,
  JUNE,
  JULY,
  AUGUST,
  SEPTEMBER,
  OCTOBER,
  NOVEMBER,
  DECEMBER,
  DAY,
  MONTH,
  YEAR,
} from "../../values";
import html2canvas from "html2canvas";
import { jsPDF } from "jspdf";
import ReportsPopover from "./ReportsPopover";
import { SELECT_DATE_RANGE, RESOLVED_CONVERSATIONS } from "../../labels";
import {
  MAX_HOTLINE_REPORT_LIMIT,
  MAX_OPENLINE_REPORT_LIMIT,
} from "../../errors";

var months = [
  JANUARY,
  FEBRUARY,
  MARCH,
  APRIL,
  MAY,
  JUNE,
  JULY,
  AUGUST,
  SEPTEMBER,
  OCTOBER,
  NOVEMBER,
  DECEMBER,
];
const { Content } = Layout;
const { RangePicker } = DatePicker;
const Reports = (props) => {
  const [loader, setLoader] = useState(false);
  const [date, setDate] = useState("");
  const [selectValue, setSelectValue] = useState("60days");
  const [period, setPeriod] = useState("");
  const [selectedLines, setSelectedLines] = useState([]);
  const [lines, setLines] = useState([]);
  const [dataPoints, setDataPoints] = useState([]);
  const [dateRange, setDateRange] = useState("");
  const [maxLimitReached, setMaxLimitReached] = useState(false);
  const [totalMessagesCount, setTotalMessagesCount] = useState("0");
  const [closedConversationsCount, setClosedConversationsCount] = useState("0");
  const [averageResolvedTime, setAverageResolvedTime] = useState("0");
  const [urgencyLevels, setUrgencyLevels] = useState({});
  const [report, setReport] = useState([]);
  const [report2, setReport2] = useState([]);
  const [isAllSelected, setAllSelected] = useState(true);
  const Messages_From = props.path.includes("hotline")
    ? HOTLINE_CONVERSATIONS_FROM
    : OPENLINE_CONVERSATIONS_FROM;
  const Messages_Reporting = props.path.includes("hotline")
    ? HOTLINE_CONVERSATIONS_REPORTING
    : OPENLINE_CONVERSATIONS_REPORTING;
  const [chartTitle, setChartTitle] = useState(Messages_Reporting);

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const status_id = `[5,8]`;
    const path = props.path.includes("hotline")
      ? `hotline/?filter={"status_id":${status_id}}`
      : `openline/?filter={"status_id":${status_id}}`;
    axios.get(BASE_URL + path, options).then((response) => {
      const lines = [];
      response.data.map((value) => {
        lines.push({
          line_name: props.path.includes("hotline") ? value.name : value.name,
          line_id: props.path.includes("hotline")
            ? value.hotline_id
            : value.openline_id,
          start_date: value.start_date,
        });
      });
      setLines(lines);
    });
    setDate("");
    setMaxLimitReached(false);
    setAllSelected(true);
    setSelectedLines([]);
    setLoader(false);
    setPeriod("");
    setDateRange("days");
    setSelectValue("60days");
    setChartTitle(Messages_Reporting);
    setTotalMessagesCount("0");
    setClosedConversationsCount("0");
    setAverageResolvedTime("0");
  }, [props.path]);

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    let line_ids = [];
    let line_dates = [];
    if (
      selectedLines.length === 0 ||
      (selectedLines.length === 1 && selectedLines[0].line_name === "All")
    ) {
      setAllSelected(true);
      setMaxLimitReached(false);
      line_ids = lines.map((value) => value.line_id);
      line_dates = lines.map((value) => new Date(value.start_date));
    } else {
      setAllSelected(false);
      line_ids = selectedLines.map((value) => value.line_id);
      line_dates = lines.map((value) => new Date(value.start_date));
    }
    let yearValue = "";
    let monthValue = "";
    let dayValue = "";
    const yearlyStartDate = new Date(`01-01-${new Date().getFullYear()}`)
      .toDateString()
      .split(" ");
    var temp = yearlyStartDate[1];
    yearlyStartDate[1] = yearlyStartDate[2];
    yearlyStartDate[2] = temp;
    var updatedYearlyStartDate = yearlyStartDate.toString().replace(/,/g, " ");
    var yearlyStartDateString =
      updatedYearlyStartDate.substring(0, updatedYearlyStartDate.indexOf(" ")) +
      ", " +
      updatedYearlyStartDate.substring(
        updatedYearlyStartDate.indexOf(" "),
        updatedYearlyStartDate.length
      );
    const monthlyStartDate = new Date(
      `${new Date().getMonth() + 1}-01-${new Date().getFullYear()}`
    )
      .toDateString()
      .split(" ");
    var temp = monthlyStartDate[1];
    monthlyStartDate[1] = monthlyStartDate[2];
    monthlyStartDate[2] = temp;
    var updatedMonthlyStartDate = monthlyStartDate
      .toString()
      .replace(/,/g, " ");
    const monthlyStartDateString =
      updatedMonthlyStartDate.substring(
        0,
        updatedMonthlyStartDate.indexOf(" ")
      ) +
      ", " +
      updatedMonthlyStartDate.substring(
        updatedMonthlyStartDate.indexOf(" "),
        updatedMonthlyStartDate.length
      );
    let beginningDate = "";
    if (selectValue === "monthly") {
      yearValue = "0";
      monthValue = "0";
      dayValue = "1";
      setDateRange("days");
      setChartTitle(Messages_From + monthlyStartDateString + TILL_DATE);
    } else if (selectValue === "yearly") {
      yearValue = "0";
      monthValue = "1";
      dayValue = "0";
      setDateRange("months");
      setChartTitle(Messages_From + yearlyStartDateString + TILL_DATE);
    } else if (selectValue === "beginning") {
      const minDate = line_dates.reduce(function (a, b) {
        return a < b ? a : b;
      });
      const minMomentDate = moment(minDate, "YYYY-MM-DD");
      const currentDate = moment(new Date(), "YYYY-MM-DD");
      const differenceInDates = Math.abs(
        minMomentDate.diff(currentDate, "days")
      );
      if (differenceInDates <= 31) {
        setDateRange("days");
        yearValue = "0";
        monthValue = "0";
        dayValue = "1";
      } else if (differenceInDates > 30 && differenceInDates <= 365) {
        setDateRange("months");
        yearValue = "0";
        monthValue = "1";
        dayValue = "0";
      } else {
        setDateRange("years");
        yearValue = "1";
        monthValue = "0";
        dayValue = "0";
      }
      setChartTitle(Messages_From + minDate.toDateString() + TILL_DATE);
      beginningDate = minDate;
    } else if (selectValue === "dateRange") {
      yearValue = dateRange === "years" ? "1" : "0";
      monthValue = dateRange === "months" ? "1" : "0";
      dayValue = dateRange === "days" ? "1" : "0";
    } else if (selectValue === "60days") {
      yearValue = "0";
      monthValue = "0";
      dayValue = "1";
      setDateRange("days");
      setChartTitle(Messages_Reporting);
    } else {
      yearValue = "0";
      monthValue = "0";
      dayValue = "1";
      setDateRange("days");
      setChartTitle(Messages_Reporting);
    }

    let input = {};
    const past60DaysTimeStamp = new Date().setDate(new Date().getDate() - 60);
    const past60Days = new Date(past60DaysTimeStamp);
    if (line_ids.length > 0) {
      if (props.path.includes("hotline")) {
        input = {
          years: yearValue,
          month: monthValue,
          days: dayValue,
          filter: {
            start_date:
              date && date !== ""
                ? moment(date[0], moment.defaultFormat)
                : selectValue === "monthly"
                ? new Date(
                    `${
                      new Date().getMonth() + 1
                    }-01-${new Date().getFullYear()}`
                  )
                : selectValue === "yearly"
                ? new Date(`01-01-${new Date().getFullYear()}`)
                : selectValue === "beginning"
                ? beginningDate
                : past60Days,
            end_date:
              date && date !== ""
                ? moment(date[1], moment.defaultFormat)
                : new Date(),
            hotline_ids:
              selectedLines.length === 0 ||
              (selectedLines.length === 1 &&
                selectedLines[0].line_name === "All")
                ? "all"
                : line_ids,
          },
        };
      } else {
        input = {
          years: yearValue,
          month: monthValue,
          days: dayValue,
          filter: {
            start_date:
              date && date !== ""
                ? moment(date[0], moment.defaultFormat)
                : selectValue === "monthly"
                ? new Date(
                    `${
                      new Date().getMonth() + 1
                    }-01-${new Date().getFullYear()}`
                  )
                : selectValue === "yearly"
                ? new Date(`01-01-${new Date().getFullYear()}`)
                : selectValue === "beginning"
                ? beginningDate
                : past60Days,
            end_date:
              date && date !== ""
                ? moment(date[1], moment.defaultFormat)
                : new Date(),
            openline_ids:
              selectedLines.length === 0 ||
              (selectedLines.length === 1 &&
                selectedLines[0].line_name === "All")
                ? "all"
                : line_ids,
          },
        };
      }

      const path = props.path.includes("hotline")
        ? "hotlineReporting/"
        : "openlineReporting/";
      axios
        .post(BASE_URL + path, input, options)
        .then((res) => {
          setTotalMessagesCount(res.data.total_conversation_count);
          if (res.data.avg_time_to_resolve_conversation > 0) {
            setAverageResolvedTime(
              Math.round(res.data.avg_time_to_resolve_conversation) + 1
            );
          } else {
            setAverageResolvedTime(res.data.avg_time_to_resolve_conversation);
          }
          setClosedConversationsCount(res.data.closed_conversation_count);
          setUrgencyLevels(res.data.urgency_level);
          if (
            selectedLines.length === 0 ||
            (selectedLines.length === 1 && selectedLines[0].line_name === "All")
          ) {
            setReport(res.data.report);
          } else {
            let finalReports = [];
            lines.map((value) => {
              const item = `report_` + value.line_id;
              if (res.data[item]) {
                finalReports.push(res.data[item]);
              } else {
                return finalReports;
              }
            });
            setReport(finalReports);
          }
          setLoader(false);
        })
        .catch((error) => setLoader(false));
    }
  }, [lines, date, selectedLines, selectValue]);

  const onSelect = (e) => {
    setSelectValue(e);
    if (e !== "dateRange") {
      setDate("");
    }
  };

  const downloadChartImage = (type) => {
    var url_base64jp = "";
    var a = "";
    if (type === "barChart") {
      /* Get image of canvas element */
      url_base64jp = document.getElementById("barChart").toDataURL("image/jpg");
      /* Get download button (tag: <a></a>) */
      a = document.getElementById("downloadBarChart");
    } else if (type === "barChart2") {
      /* Get image of canvas element */
      url_base64jp = document
        .getElementById("barChart2")
        .toDataURL("image/jpg");
      /* Get download button (tag: <a></a>) */
      a = document.getElementById("downloadBarChart2");
    } else if (type === "doughNutChart") {
      url_base64jp = document
        .getElementById("doughNutChart")
        .toDataURL("image/jpg");
      a = document.getElementById("downloadDoughNutChart");
    } else {
      url_base64jp = document
        .getElementById("ResolvedUnresolvedChart")
        .toDataURL("image/jpg");
      a = document.getElementById("downloadResolvedUnresolvedChart");
    }
    /* Insert chart image url to download button (tag: <a></a>) */
    a.href = url_base64jp;
  };

  function convertImgToBase64(img1, img2, img3, img4) {
    html2canvas(img1).then((canvas1) => {
      const imgData1 = canvas1.toDataURL("image/png");
      html2canvas(img2).then((canvas2) => {
        const imgData2 = canvas2.toDataURL("image/png");
        html2canvas(img3).then((canvas3) => {
          const imgData3 = canvas3.toDataURL("image/png");
          html2canvas(img4).then((canvas4) => {
            const imgData4 = canvas4.toDataURL("image/png");
            const fullImage = [imgData1, imgData2, imgData3, imgData4];
            let pdf = new jsPDF();
            for (let image = 0; image < 4; image++) {
              const height =
                image === 0
                  ? canvas1.height
                  : image === 1
                  ? canvas2.height
                  : image === 2
                  ? canvas3.height
                  : canvas4.height;
              const width =
                image === 0
                  ? canvas1.width
                  : image === 1
                  ? canvas2.width
                  : image === 2
                  ? canvas3.width
                  : canvas4.width;
              pdf.addImage(fullImage[image], "JPEG", 0, 0, 200, 100);
              if (image !== 3) pdf.addPage();
            }
            const filename = props.path?.includes("hotline")
              ? "hotline-report.pdf"
              : "openline-report.pdf";
            pdf.save(filename);
          });
        });
      });
    });
  }

  const downloadPDF = () => {
    let barElement = document.getElementById("barChart");
    let barElement2 = document.getElementById("barChart2");
    let doughElement = document.getElementById("doughNutChart");
    let pieElement = document.getElementById("ResolvedUnresolvedChart");
    convertImgToBase64(barElement, barElement2, doughElement, pieElement);
  };

  function convertChartDataToCSV(data) {
    var result, ctr, keys, columnDelimiter, lineDelimiter, data;
    data = data || null;
    if (data == null || !data.length) {
      return null;
    }
    columnDelimiter = ",";
    lineDelimiter = "\n";
    keys = Object.keys(data[0]);
    result = "";
    result += keys.join(columnDelimiter);
    result += lineDelimiter;
    data.forEach(function (item) {
      ctr = 0;
      keys.forEach(function (key) {
        if (ctr > 0) result += columnDelimiter;
        result += item[key];
        ctr++;
      });
      result += lineDelimiter;
    });
    return result;
  }

  const sendBackData = (x_data, y_data) => {
    setDataPoints([x_data, y_data]);
  };

  const downloadCSV = () => {
    let points = [];
    let x_index = MONTH;
    dataPoints[0].forEach((xpoint, index) => {
      dataPoints[1].forEach((ypoint, key) => {
        if (index === key) {
          if (dateRange === "months") {
            x_index = MONTH;
          } else if (dateRange === "years") {
            x_index = YEAR;
          } else {
            x_index = DAY;
          }
          points.push({ [x_index]: xpoint, [CONVERSATION_COUNT]: ypoint });
        }
      });
    });
    var data, filename, link;
    var csv = "";
    csv += convertChartDataToCSV(points);
    if (csv == null) return;
    filename = props.path?.includes("hotline")
      ? "hotline-report.csv"
      : "openline-report.csv";
    if (!csv.match(/^data:text\/csv/i)) {
      csv = "data:text/csv;charset=utf-8," + csv;
    }
    data = encodeURI(csv);
    link = document.createElement("a");
    link.setAttribute("href", data);
    link.setAttribute("download", filename);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const showDownloadOptions = (value) => {
    if (value === "pdf") {
      downloadPDF();
    } else {
      downloadCSV();
    }
  };

  const disabledDate = (current) => {
    return current && current > moment().endOf("day");
  };

  const handleDatePickerChange = (date) => {
    setDate(date);
    if (date) {
      var startMonth = parseInt(date[0].format("M"));
      var startDay = parseInt(date[0].format("D"));
      var startYear = parseInt(date[0].format("YYYY"));
      const start_date = [startDay, startMonth - 1, startYear];
      var endMonth = parseInt(date[1].format("M"));
      var endDay = parseInt(date[1].format("D"));
      var endYear = parseInt(date[1].format("YYYY"));
      const end_date = [endDay, endMonth - 1, endYear];
      const differenceInDates = Math.abs(date[0].diff(date[1], "days"));
      if (differenceInDates <= 31) {
        setDateRange("days");
      } else if (differenceInDates > 30 && differenceInDates <= 365) {
        setDateRange("months");
      } else {
        setDateRange("years");
      }
      if (startYear !== endYear) {
        if (startYear > endYear) {
          setChartTitle(
            Messages_From +
              months[end_date[1]] +
              " " +
              end_date[0] +
              ", " +
              end_date[2] +
              TO +
              months[start_date[1]] +
              " " +
              start_date[0] +
              ", " +
              start_date[2]
          );
          setPeriod("greater year");
        } else {
          setChartTitle(
            Messages_From +
              months[start_date[1]] +
              " " +
              start_date[0] +
              ", " +
              start_date[2] +
              TO +
              months[end_date[1]] +
              " " +
              end_date[0] +
              ", " +
              end_date[2]
          );
          setPeriod("lesser year");
        }
      } else if (startYear === endYear && startMonth !== endMonth) {
        if (startMonth > endMonth) {
          setChartTitle(
            Messages_From +
              months[end_date[1]] +
              " " +
              end_date[0] +
              TO +
              months[start_date[1]] +
              " " +
              start_date[0]
          );
          setPeriod("greater month");
        } else {
          setChartTitle(
            Messages_From +
              months[start_date[1]] +
              " " +
              start_date[0] +
              TO +
              months[end_date[1]] +
              " " +
              end_date[0]
          );
          setPeriod("lesser month");
        }
      } else if (
        startYear === endYear &&
        startMonth === endMonth &&
        startDay !== endDay
      ) {
        if (startDay > endDay) {
          setChartTitle(
            Messages_From +
              months[endMonth - 1] +
              " " +
              end_date[0] +
              TO +
              months[startMonth - 1] +
              " " +
              start_date[0]
          );
          setPeriod("greater day");
        } else {
          setChartTitle(
            Messages_From +
              months[startMonth - 1] +
              " " +
              start_date[0] +
              TO +
              months[endMonth - 1] +
              " " +
              end_date[0]
          );
          setPeriod("lesser day");
        }
      } else if (
        startYear === endYear &&
        startMonth === endMonth &&
        startDay === endDay
      ) {
        setChartTitle(
          Messages_From +
            months[startMonth - 1] +
            " " +
            start_date[0] +
            TO +
            months[endMonth - 1] +
            " " +
            end_date[0]
        );
        setPeriod("same day");
      }
    } else {
      setPeriod("");
      setSelectValue("dateRange");
    }
  };

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amIDBUser />
      </Row>
      <Row className="content-center pt-0" data-test="reports">
        <Layout>
          <Layout>
            <IdbSidebar amIDBUser data={props} />
            <Content className="">
              <Row className="py-3">
                <h1 className="mb-0">
                  {props.path.includes("hotline")
                    ? HOTLINE_REPORTS
                    : OPENLINE_REPORTS}
                </h1>
              </Row>
              <Spin spinning={loader} size="large">
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={12} className="search-row mt-2 mt-lg-0">
                      {maxLimitReached ? (
                        <Alert
                          message={
                            props.path?.includes("hotline")
                              ? MAX_HOTLINE_REPORT_LIMIT
                              : MAX_OPENLINE_REPORT_LIMIT
                          }
                          type="error"
                          showIcon
                        ></Alert>
                      ) : null}
                      <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                        <Col
                          span={24}
                          lg={12}
                          className="gutter-row mt-2 mt-lg-0"
                        >
                          <MultiSelection
                            lines={lines}
                            setSelectedLines={setSelectedLines}
                            data={props}
                            setMaxLimitReached={setMaxLimitReached}
                            isAllSelected={isAllSelected}
                          />
                          <DownOutlined className="down-arrow-ico multiselect-arrow sm-arrow" />
                        </Col>
                        <Col
                          span={24}
                          lg={12}
                          className="gutter-row mt-2 mt-lg-2 mt-xl-0"
                        >
                          <>
                            <Select
                              className="w-100"
                              onSelect={(value) => onSelect(value)}
                              placeholder={CHOOSE_DURATION}
                              defaultValue="60days"
                              value={selectValue}
                            >
                              <Select.Option value="60days">
                                {LAST_60_DAYS}
                              </Select.Option>
                              <Select.Option value="monthly">
                                {MONTHLY}
                              </Select.Option>
                              <Select.Option value="yearly">
                                {YEARLY}
                              </Select.Option>
                              <Select.Option value="dateRange">
                                {SELECT_DATE_RANGE}
                              </Select.Option>
                              <Select.Option value="beginning">
                                {SINCE_BEGINNING}
                              </Select.Option>
                            </Select>
                          </>
                          {selectValue === "dateRange" ? (
                            <Col span={24} className="mt-2 mt-lg-0">
                              <RangePicker
                                name="date"
                                value={date}
                                onChange={(value) =>
                                  handleDatePickerChange(value)
                                }
                                disabledDate={disabledDate}
                              />
                            </Col>
                          ) : null}
                        </Col>
                      </Row>
                    </Col>
                    <Col span={24} lg={12} className="mt-2 mt-lg-0">
                      <Row className="justify-content-end">
                        <Col
                          span={24}
                          lg={8}
                          className="gutter-row d-flex align-items-center justify-content-end"
                        >
                          <ReportsPopover
                            showDownloadOptions={showDownloadOptions}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </Col>
                <Row>
                  <Col span={24} className="gutter-row">
                    <div className="mid-content mt-4 h-auto d-flex justify-content-center align-items-center text-center">
                      <Col span={8} className="divider-right font-weight-md6">
                        <div className="font-18">{totalMessagesCount}</div>
                        <div className="font-12">{TOTAL_CONVERSATIONS}</div>
                      </Col>
                      <Col span={8} className="divider-right font-weight-md6">
                        <div className="font-18">
                          {closedConversationsCount}
                        </div>
                        <div className="font-12">{CONVERSATIONS_RESOLVED}</div>
                      </Col>
                      <Col span={8} className="font-weight-md6">
                        <div className="font-18">{averageResolvedTime}d</div>
                        <div className="font-12">{AVERAGE_RESOLVED_TIME}</div>
                      </Col>
                    </div>
                  </Col>
                </Row>
                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                  <Col span={24} lg={24} className="gutter-row">
                    <div>{REPORTS_MSG}</div>
                    <div className="mid-content mt-4">
                      <Col span={24} className="gutter-row">
                        <Row className="d-inine-flex justify-content-between align-items-center mb-3">
                          <h6 className="font-weight-md6 font-14 mb-0">
                            {period !== "" || selectValue !== ""
                              ? chartTitle
                              : Messages_Reporting}
                          </h6>
                          <span>
                            <a
                              id="downloadBarChart"
                              download={
                                props.path?.includes("hotline")
                                  ? "Hotline-report.jpg"
                                  : "Openline-report.jpg"
                              }
                              href=""
                            >
                              <DownloadOutlined
                                className="font-18 cursor-pointer"
                                onClick={() => downloadChartImage("barChart")}
                              />
                            </a>
                          </span>
                        </Row>
                        <VerticalChart
                          months={months}
                          selectedLines={selectedLines}
                          report={report}
                          sendBackData={sendBackData}
                          chartTitle={chartTitle}
                          dateRange={dateRange}
                          isAllSelected={isAllSelected}
                        />
                      </Col>
                    </div>
                  </Col>
                  <Col span={24} lg={12} className="gutter-row">
                    <div className="mid-content mt-4">
                      <Col span={24} className="gutter-row">
                        <Row className="d-inine-flex justify-content-between align-items-center mb-3">
                          <h6 className="font-weight-md6 my-3">
                            {CONVERSATIONS_BASED_ON_URGENCY_LEVEL}
                          </h6>
                          <span>
                            <a
                              id="downloadDoughNutChart"
                              download="Conversations-based-on-urgency-level.jpg"
                              href=""
                            >
                              <DownloadOutlined
                                className="font-18 cursor-pointer"
                                onClick={() =>
                                  downloadChartImage("doughNutChart")
                                }
                              />
                            </a>
                          </span>
                        </Row>
                        <Row>
                          <h6 className="font-weight-md6 my-3">
                            {TOTAL_CONVERSATIONS}: {totalMessagesCount}
                          </h6>
                        </Row>
                        <DoughNutChart
                          urgencyLevels={urgencyLevels}
                          chartTitle={CONVERSATIONS_BASED_ON_URGENCY_LEVEL}
                        />
                      </Col>
                    </div>
                  </Col>

                  <Col span={24} lg={12} className="gutter-row">
                    <div className="mid-content mt-4">
                      <Col span={24} className="gutter-row">
                        <Row className="d-inine-flex justify-content-between align-items-center mb-3">
                          <h6 className="font-weight-md6 my-3">
                            {RESOLVED_CONVERSATIONS}
                          </h6>
                          <span>
                            <a
                              id="downloadResolvedUnresolvedChart"
                              download="Resolved-conversations.jpg"
                              href=""
                            >
                              <DownloadOutlined
                                className="font-18 cursor-pointer"
                                onClick={() =>
                                  downloadChartImage("ResolvedUnresolvedChart")
                                }
                              />
                            </a>
                          </span>
                        </Row>
                        <Row>
                          <h6 className="font-weight-md6 my-3">
                            {TOTAL_CONVERSATIONS}: {totalMessagesCount}
                          </h6>
                        </Row>
                        <ResolvedUnresolvedChart
                          resolvedMessages={closedConversationsCount}
                          totalMessages={totalMessagesCount}
                          chartTitle={RESOLVED_UNRESOLVED_CONVERSATIONS}
                        />
                      </Col>
                    </div>
                  </Col>
                </Row>
              </Spin>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};
export default Reports;
